import ReactECharts from 'echarts-for-react';

function PriceQuxian() {

  const option = {
    // title: {
    //   text: 'LG'
    // },
    tooltip: {
      trigger: 'axis'
    },
    legend: {
      data: ['LG'],
      textStyle: {
        color: 'white',
      },
      icon: 'none',
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '3%',
      top: '12%',
      containLabel: true
    },
    xAxis: [
      {
        type: 'category',
        boundaryGap: false,
        data: ['12.22', '12.23', '12.24', '12.25', '12.26', '12.27', '12.28'],
        axisLabel: {
          color: 'rgb(255, 255, 255,0.4)',
        }
      }
    ],
    yAxis: [
      {
        type: 'value',
        min: 0.08,
        axisLabel: {
          color: 'rgb(255, 255, 255,0.4)'
        },
        splitLine: {   // 设置 x 轴的网格线为不显示
          show: false,
        },
      }
    ],
    series: [
      {
        name: 'LG',
        type: 'line',
        stack: '总量',
        areaStyle: { normal: {} },
        smooth: true,
        data: [0.08, 0.081, 0.082, 0.083, 0.0814, 0.0811, 0.086],
        label: {
          show: true,
          color: 'white',
        },
        itemStyle: {
          color: 'red',
        },
      }

    ],
    backgroundColor: '#6435c9',
  }

  return (
    <div>
      <ReactECharts
        option={option}
        style={{ height: 300 }}
      />
    </div>
  )

}

export default PriceQuxian
