import React,{ Component }  from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import erc20abiZBXH from '../../../ABI/ERC20ZBXH.abi.json';
import {Table, Card ,Button,Input,Popover,Divider,Spin,Modal,Progress} from 'antd';
import { QuestionCircleOutlined,CopyOutlined,FolderOpenOutlined} from '@ant-design/icons';
import TkFactoryZBXH from '../../../ABI/TkFactoryZBXH';
import {CopyToClipboard} from 'react-copy-to-clipboard';
const BigNumber = require('bignumber.js');

class App extends Component {
      state = {
        name:'',
        symbol:'',
        decimals:'18',
        _totalSupply:'',
        tokenOwnerAddress:'',
        user:'',
        fee:'',
        message:'',
        messageConfirm:'', //确认窗信息
        newtokenAdr:'',
        GXvalue:'',
        chainIdDisabled:'',
        onoff2:'none',
        opentoken:'', //发后管理通证地址
        addUser:'',
        delValue:'',
        firstUserList:[],
        loadwindow:false, //加载窗
        loading:'block', //加载中
        complete:'none', //加载完成
        loadmessage:'',  //加载窗信息
        userInfoWindow:false, //用户信息窗
        confirmWindow:false, //核实确认窗
        userInfo:'', //用户地址详情
        burnConfirm:'none', //销毁确认
        startConfirm:'none', //结束准备期确认
        readyConfirm:'none', //进入准备期确认
        start:'',  //通证状态
        onoff1:'none', //特殊操作抽屉开关
        visible2:false, //换管理员确认窗开关
        newAdmin:'', //新管理员地址
        admin:'', //管理员地址
        creatHelp: true, //创建帮助图片开关
      };

  async componentDidMount(){

    this.setState({user:window.defaultAccount});
    this.setState({tokenOwnerAddress:window.defaultAccount});

    const _fee = await TkFactoryZBXH.methods.feeHT().call();
    const fee = parseFloat(web3.utils.fromWei(_fee,"ether")).toFixed(3)
    this.setState({fee});

    const GXvalue = await TkFactoryZBXH.methods.GXvalue().call();
    this.setState({GXvalue});

    const newtokenAdr = await TkFactoryZBXH.methods.getLastERC20().call({from:window.defaultAccount});
    console.log(newtokenAdr)
    if(newtokenAdr === '0x0000000000000000000000000000000000000000'){
      this.setState({newtokenAdr:'您尚未发行过通证'});
    }else{
    const ERC20ZBXH =  new web3.eth.Contract(erc20abiZBXH.abi,newtokenAdr);
    const start =  await ERC20ZBXH.methods.start().call();
    if(start ==='0'){
      this.setState({start:'创建期'})
    }
    if(start ==='1'){
      this.setState({start:'准备期'})
    }
    if(start ==='2'){
      this.setState({start:'完全启动'})
    }
    const firstUser =  await ERC20ZBXH.methods.getFirstUser().call();
    const symbol = await ERC20ZBXH.methods.symbol().call();
    const admin = await ERC20ZBXH.methods.admin().call();
    const firstUserList = [];
    for(var i=0; i<=firstUser.length-1; i++){
      const a = [];
        a[0] = firstUser[i];
      firstUserList.push(a);
    }
    this.setState({newtokenAdr,
      symbol,
      admin,
      opentoken:newtokenAdr,
      firstUserList:firstUserList,
    });
    }



  }

  createERC20 = async () =>{
    const _fee = await TkFactoryZBXH.methods.feeHT().call();
    const _total = new BigNumber(this.state._totalSupply/10**(18-this.state.decimals) ).toFixed() ;
    if(this.state._totalSupply <= 1000000000000000){
    await TkFactoryZBXH.methods.createERC20(
        this.state.name,
        this.state.symbol,
        this.state.decimals,
        web3.utils.toWei(_total,'ether'),
        this.state.tokenOwnerAddress,
        )
    .send({from:window.defaultAccount,value:_fee})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.setState({loadmessage:"打包中……",
          loadwindow: true,
          loading:'block',
          complete:'none'
        });
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.setState({loadmessage:"创建成功",
          loading:'none',
          complete:'block'
        });
      }
    });
    const newtokenAdr = await TkFactoryZBXH.methods.getLastERC20().call({from:window.defaultAccount});
    this.setState({newtokenAdr});
    }else{this.setState({message:"总量不得大于1000万亿"});}
    this.componentDidMount();
  }

  openToken = async() =>{
    this.setState({start:'',symbol:''})
    const ERC20ZBXH =  new web3.eth.Contract(erc20abiZBXH.abi,this.state.opentoken);
    const start =  await ERC20ZBXH.methods.start().call();
    if(start ==='0'){
      this.setState({start:'创建期'})
    }
    if(start ==='1'){
      this.setState({start:'准备期'})
    }
    if(start ==='2'){
      this.setState({start:'完全启动'})
    }
    const firstUser =  await ERC20ZBXH.methods.getFirstUser().call();
    const symbol = await ERC20ZBXH.methods.symbol().call();
    const admin = await ERC20ZBXH.methods.admin().call();
    const firstUserList = [];
    for(var i=0; i<=firstUser.length-1; i++){
      const a = [];
        a[0] = firstUser[i];
      firstUserList.push(a);
    }
    this.setState({symbol,admin,firstUserList:firstUserList});
  }

  //发后管理开关
  open2 = () => {
    if(this.state.onoff2 ==='none'){
      this.setState({onoff2:"block"});
    }else{
      this.setState({onoff2:"none"});
    }
  }

  addFirstUser = async() =>{
    const ERC20ZBXH =  new web3.eth.Contract(erc20abiZBXH.abi,this.state.opentoken);
    await ERC20ZBXH.methods.addFirstUser(
      this.state.addUser)
      .send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({
            loadwindow: true,
            loadmessage:"打包中……",
            loading:'block',
            complete:'none'
          });
        }
      })
      .on('receipt',(receipt)=>{
        if(receipt.status === true){
          this.setState({loadmessage:"添加成功",
            loading:'none',
            complete:'block'
          });
        }
      });
      this.openToken();
  }

  goReady = async() =>{
    const ERC20ZBXH =  new web3.eth.Contract(erc20abiZBXH.abi,this.state.opentoken);
    const balanceOf = await ERC20ZBXH.methods.balanceOf(this.state.tokenPairAddress).call();
    if(balanceOf !=='0' 
      && this.state.tokenPairAddress.toLowerCase() !== window.defaultAccount.toLowerCase())
      {
        await ERC20ZBXH.methods.goReady().send({from:window.defaultAccount})
          .on('transactionHash',(transactionHash)=>{
            if(transactionHash.length !== 0){
              this.setState({
                confirmWindow: false,
                loadwindow: true,
                loadmessage:"打包中……",
                loading:'block',
                complete:'none'
              });
            }
          })
          .on('receipt',(receipt)=>{
            if(receipt.status === true){
              this.setState({loadmessage:"已进入准备期，请添加准备期用户",
                loading:'none',
                complete:'block'
              });
            }
          });
      }else{
        this.setState({messageConfirm:"您并未创建好交易池或输入的地址不是交易池地址"})
      }
      this.openToken();
  }

  gostart = async() =>{
    const ERC20ZBXH =  new web3.eth.Contract(erc20abiZBXH.abi,this.state.opentoken);
    await ERC20ZBXH.methods.gostart().send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({
            confirmWindow: false,
            loadwindow: true,
            loadmessage:"打包中……",
            loading:'block',
            complete:'none'
          });
        }
      })
      .on('receipt',(receipt)=>{
        if(receipt.status === true){
          this.setState({loadmessage:"启动成功",
            loading:'none',
            complete:'block'
          });
        }
      });
      this.openToken();
  }

  transferAdmin = async() =>{
    const ERC20ZBXH =  new web3.eth.Contract(erc20abiZBXH.abi,this.state.opentoken);
    await ERC20ZBXH.methods.transferAdmin(
      this.state.newAdmin
    ).send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({
            confirmWindow: false,
            loadwindow: true,
            loadmessage:"打包中……",
            loading:'block',
            complete:'none'
          });
        }
      })
      .on('receipt',(receipt)=>{
        if(receipt.status === true){
          this.setState({loadmessage:"转换成功",
            loading:'none',
            complete:'block'
          });
        }
      });
      this.openToken();
  }

  burn = async() =>{
    const ERC20ZBXH =  new web3.eth.Contract(erc20abiZBXH.abi,this.state.opentoken);
    const decimals = await ERC20ZBXH.methods.decimals().call();
    const delValue = new BigNumber(this.state.delValue/10**(18-decimals) ).toFixed() ;
    await ERC20ZBXH.methods.burn(web3.utils.toWei(delValue,'ether'))
      .send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({
            confirmWindow: false,
            loadwindow: true,
            loadmessage:"打包中……",
            loading:'block',
            complete:'none'
          });
        }
      })
      .on('receipt',(receipt)=>{
        if(receipt.status === true){
          this.setState({loadmessage:"销毁成功",
            loading:'none',
            complete:'block'
          });
        }
      });
  }

  //加载窗开关
  loadwindowClose = () => {
    this.setState({loadwindow: false})
  };

  //用户信息窗
  userinfoClose = () => {
    this.setState({userInfoWindow: false})
  };

  //关闭核实确认窗
  confirmWindowClose = () => {
    this.setState({confirmWindow: false})
  };
  //打开销毁确认窗
  burnConfirmOpen = () => {
    this.setState({confirmWindow: true, startConfirm:'none', burnConfirm:'block',readyConfirm:'none'})
  };
  //打开结束准备期确认窗
  startConfirmOpen = () => {
    this.setState({confirmWindow: true, startConfirm:'block',burnConfirm:'none',readyConfirm:'none'})
  };
  //打开进入准备期确认窗
  readyConfirm = () => {
    this.setState({confirmWindow: true, startConfirm:'none',burnConfirm:'none',readyConfirm:'block'})
  };

  //打开特殊操作抽屉
  open1 = () => {
    if(this.state.onoff1 ==='none'){
      this.setState({onoff1:"block"});
    }else{
      this.setState({onoff1:"none"});
    }
  }

  //换管理员确认窗关
  transferAdminConfirmClose = () => {
    this.setState({visible2: false})
  };
  //换管理员确认窗开
  transferAdminConfirmOpen = () => {
    this.setState({visible2: true})
  };

  //创建帮助图片
  creatHelpClose = () => {
    if(this.state.creatHelp === true){
      this.setState({creatHelp: false})
    }else{
      this.setState({creatHelp: true})
    }
  };

  render(){

    const columns = [
      {
        title: '准备期用户',
        dataIndex: '0',
        key:'0',
        align: 'center',
        ellipsis: 'true',
      }
    ];

    const content = (
      <div>
        <p>您的当前帐号，方便您核对</p>
      </div>
    );
      const content1 = (
        <div>
          <p>填写通证的全称(支持中文)，比如：bitcoin</p>
          <p>注意这个名称不是钱包或者交易所显示的名称。</p>
        </div>
      );
      const content2 = (
        <div>
          <p>填写通证的简称，比如：BTC</p>
          <p>这个名称才是钱包或者交易所显示的名称</p>
        </div>
      );
      const content3 = (
        <div>
          <p>通证的小数位，通常为18</p>
          <p>建议不要更改</p>
        </div>
      );
      const content4 = (
        <div>
          <p>通证总发行量，最多：1000000000000000</p>
        </div>
      );
      const content5 = (
        <div>
          <p>通证初始持有人</p>
          <p>默认为当前帐号，也可以指定其它帐号</p>
        </div>
      );
      const content6 = (
        <div>
          <Popover content='初始持有人可销毁自己的部分通证'  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          销毁通证
        </div>
      );
      const content7_1 = (
        <div>
          <p> 默认自动打开最后一次创建的通证。</p>
          <p> 如果要管理之前的通证，请输入通证地址，然后点击<FolderOpenOutlined /></p>
        </div>
      );
      const content7 = (
        <div>
          <Popover content={content7_1}  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          通证
        </div>
      );

      const content8 = (
        <div>
          <Popover content='管理员(初始持有人)可以将管理员身份换到其它地址'  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          换管理员
        </div>
      );

  return (
    <div>
      <Card type="inner" size='small' title="含准备期与销毁功能HRC20通证" >
          <div >
              <Input  id='i1' addonBefore="帐号" value={this.state.user} 
                  suffix={
                    <Popover  content={content} trigger="click">
                    <QuestionCircleOutlined id='i1' /> 
                    </Popover>
                  }
                  disabled
              />
          </div>

        <Divider>发行通证</Divider>
          <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                {this.state.message}
          </div>
          <div style={{marginTop:'10px'}}>
                <Input  addonBefore='通证名称'  
                    placeholder="如：bitcoin" size="large"
                    suffix={
                      <Popover content={content1} trigger="click">
                        <QuestionCircleOutlined />  
                      </Popover>
                    }
                    onChange={event=>{this.setState({name:event.target.value})}}/>
          </div>

          <div style={{marginTop:'10px'}}>
                <Input  addonBefore='通证符号'
                    placeholder="如：BTC" size="large"
                    suffix={
                      <Popover content={content2} trigger="click">
                        <QuestionCircleOutlined />  
                      </Popover>
                    }
                    onChange={event=>{this.setState({symbol:event.target.value})}}/>
          </div>

          <div style={{marginTop:'10px'}}>
                <Input  addonBefore='发行总量'
                      placeholder="如：21000000" size="large"
                      suffix={
                        <Popover content={content4} trigger="click">
                          <QuestionCircleOutlined />  
                        </Popover>
                      }
                      onChange={event=>{this.setState({_totalSupply:event.target.value})}}/>
          </div>

          <div style={{marginTop:'10px'}}>
                <Input style={{width:'50%'}} addonBefore='小数位' value={this.state.decimals} 
                      placeholder="通常用 18" size="small"
                      suffix={
                        <Popover content={content3} trigger="click">
                          <QuestionCircleOutlined />  
                        </Popover>
                      }
                      onChange={event=>{this.setState({decimals:event.target.value})}}/>
          </div>

          <div style={{marginTop:'10px'}}>通证接受地址：
                <Popover content={content5} trigger="click">
                      <QuestionCircleOutlined /> 
                </Popover>
          </div>
          <Input  value={this.state.tokenOwnerAddress} placeholder="通证接受地址" 
                onChange={event=>{this.setState({tokenOwnerAddress:event.target.value})}}/>

          <Button  shape='round' block type="primary"  size="middle" style={{marginTop:'10px'}} 
                disabled = {this.state.chainIdDisabled}
                onClick={this.createERC20}>发行通证</Button>
          <div >
              <div style={{marginTop:'5px'}}>最后一次发行的通证地址：
                  <Input id='i1' value={this.state.newtokenAdr} disabled 
                      suffix={
                          <CopyToClipboard  text={this.state.newtokenAdr}>
                          <Button  size="small" ><CopyOutlined/></Button>
                          </CopyToClipboard>
                      }
                    />
              </div>
          </div>

          <Button shape='round' type='primary' block  size="small" 
            style={{marginTop:'10px'}} disabled = {this.state.chainIdDisabled}
            onClick={this.open2}>发后管理
          </Button>
          <div style={{display:this.state.onoff2, marginTop:'5px'}}>
                <Input  addonBefore={content7}  placeholder="通证合约地址" 
                  value={this.state.opentoken}
                  onChange={event=>{this.setState({opentoken:event.target.value})}}
                  suffix={
                      <Button type='primary' size="small"
                        onClick={() => {this.openToken()}}
                        ><FolderOpenOutlined /></Button>
                  }
                />
                <div>
                  当前管理通证：<span style={{color:'green'}}>{this.state.symbol}</span> 
                  &nbsp;&nbsp;状态：<span style={{color:'green'}}>{this.state.start}</span>
                </div>
                <div>
                    <Input  id='i1' addonBefore="管理员" value={this.state.admin} 
                        disabled
                    />
                </div>

                <Divider style={{margin:'15px'}}/>
                <Button  danger block  
                  onClick={this.readyConfirm}>进入({this.state.symbol})准备期
                </Button>
                
                <Table size='small' 
                    pagination={false}
                    columns={columns}  
                    dataSource={this.state.firstUserList} 
                    customRow="click"
                    onRow = {(record) => {
                      return {
                        onClick: () => {
                          console.log(record)
                          this.setState({userInfoWindow: true})
                          this.setState({userInfo: record[0]})
                          }
                        }
                        }
                        }
                    />
                <Modal  visible={this.state.userInfoWindow} 
                  onCancel={this.userinfoClose} 
                  footer={[
                    <div style={{textAlign:'center'}} >
                      <CopyToClipboard text={this.state.userInfo}>
                        <Button type='primary' onClick={this.userinfoClose}>
                          <CopyOutlined/>
                        </Button>
                      </CopyToClipboard>
                    </div>,
                     ]}
                  >
                  <div style={{marginTop:'10px', width:'90%' }}>
                    {this.state.userInfo}
                  </div> 
              </Modal>

                <Input  addonBefore='准备期用户'  placeholder="输入帐号"
                  onChange={event=>{this.setState({addUser:event.target.value})}}
                  suffix={
                      <Button type='primary' size="small"
                        onClick={this.addFirstUser}
                        >添加</Button>
                  }
                />
                <Divider style={{margin:'15px'}}/>
                <Button  danger block  
                  onClick={this.startConfirmOpen}>结束({this.state.symbol})准备期完全启动
                </Button>
                <Divider style={{margin:'15px'}}/>
                <Input  addonBefore={content6}  placeholder="输入数量"
                  onChange={event=>{this.setState({delValue:event.target.value})}}
                  suffix={
                      <Button type='danger' size="small"
                        onClick={this.burnConfirmOpen}
                        >销毁</Button>
                  }
                />

                <Button  shape='round' type="dashed" block  size="small"  
                    style={{marginTop:'12px',marginBottom:'12px'}}
                    onClick={() => {this.open1()}}
                >特殊操作</Button>
                    <div style={{display:this.state.onoff1}}>
                      <Input  addonBefore={content8}  placeholder="输入新管理员地址"
                          onChange={event=>{this.setState({newAdmin:event.target.value})}}
                          suffix={
                              <Button type='danger' size="small"
                                onClick={this.transferAdminConfirmOpen}
                                >转换</Button>
                          }
                      />
                    </div>

            </div>
            
      </Card>
         
         <div style={{marginLeft:'10px'}}>注：
                <Button size="small" danger onClick={this.creatHelpClose}>图片说明</Button>
            <div>发行费用：{this.state.fee}HT(实时浮动)</div>
            <div>请务必将每一项<QuestionCircleOutlined />都点开查看清楚<br/>
                一经发送无法更改与退款<br/>
                如果发生多付款等问题请Email
                </div>
            <div>Email：lgqklxyz@gmail.com 
                <CopyToClipboard text='lgqklxyz@gmail.com'>
                      <Button size='small' ><CopyOutlined/></Button>
                </CopyToClipboard>
            </div>
            <div>
                免责声明：<br/>
                请合法合规使用您发行的通证<br/>
                非法使用与本网站无关
            </div>
        </div>

      {/* 加载窗  */}
      <Modal  visible={this.state.loadwindow} 
        onCancel={this.loadwindowClose} 
        footer={[
          <div style={{textAlign:'center'}} >
            <Button type='primary' onClick={this.loadwindowClose}>关闭</Button>
          </div>,
           ]}
      >
        <div style={{textAlign:'center'}}>
              <div style={{display:this.state.loading}}>
                <Spin size="large" />
                <div style={{color:"#1890ff",fontSize:"13px",textAlign:'center'}}>
                  {this.state.loadmessage}
                </div>
              </div>
              <div style={{display:this.state.complete}}>
                <Progress type="circle" percent={100} width={45}/>
                <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                  {this.state.loadmessage}
                </div>
              </div>
        </div>
      </Modal>
      {/* 确认窗口 */}
      <Modal visible={this.state.confirmWindow}  
          onCancel={this.confirmWindowClose} 
          footer={[
            <div style={{textAlign:'center'}}>
              <Button type='primary' onClick={this.confirmWindowClose}>取消</Button>
            </div>,
             ]}
        >
          <div style={{textAlign:'center',display:this.state.burnConfirm}} >
             <div>您将销毁您帐户中的通证：</div>
             <div>{this.state.delValue}&nbsp;{this.state.symbol}</div>
             <div style={{textAlign:'center',marginTop:'20px'}}>
                <Button type='primary' danger 
                  onClick={this.burn}
                  >核对无误确认</Button>
              </div>
          </div>
          <div style={{textAlign:'center',display:this.state.startConfirm}} >
             <div>您将结束以下通证的准备期</div>
             <div>{this.state.symbol}</div>
             <div style={{textAlign:'center',marginTop:'20px'}}>
                <Button type='primary' danger 
                  onClick={this.gostart}
                  >核对无误确认</Button>
             </div>
          </div>

          <div style={{textAlign:'center',display:this.state.readyConfirm}} >
              <div style={{textAlign:'center',marginTop:'20px'}}>
                  进入准备期之前请务必先在去中心化交易所创建好流动池，请在下方粘贴交易池地址
              </div>
              <Input  placeholder="在此输入交易池地址" 
                  onChange={event=>{this.setState({tokenPairAddress:event.target.value})}}/>
              <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                  {this.state.messageConfirm}
              </div>
              <div style={{textAlign:'center',marginTop:'20px'}}>
                  <Button type='primary' danger 
                    onClick={this.goReady}
                  >确认已创建进入准备期</Button>
             </div>
          </div>
          
        </Modal>

        {/* 换管理员身份时的确认窗口 */}
        <Modal title='换管理员' visible={this.state.visible2}  
          onCancel={this.transferAdminConfirmClose} 
          footer={[
            <Button type='primary' onClick={this.transferAdminConfirmClose}>取消</Button>,
             ]}
        >
        <p>
          <div>该通证的管理员身份将移交给以下地址(请仔细核对)</div>
          <div id='i2'>{this.state.newAdmin}</div>
        </p>
        <div style={{textAlign:'center'}}>
            <Button type='primary' danger onClick={this.transferAdmin}>核对无误确认移交</Button>
        </div>
      </Modal>

      <Modal  visible={this.state.creatHelp}  //创建流程
          onCancel={this.creatHelpClose} 
          footer={[
            <div style={{textAlign:'center'}}>
            <Button type='primary' onClick={this.creatHelpClose}>关闭</Button>
            </div>,
             ]}
        >
          <div style={{textAlign:'center',marginTop:'20px'}}>
              <div className='tokenFactoryZBXHhelp' ></div>
          </div>
        </Modal>
    </div>
  );
}
}

export default App;
