import React from 'react';
import {  Card } from 'antd';

function Edu() {
  return(
    <div>
      <Card>
        ​​​​<b>手握私钥的快感</b>
        <h5>ahr999  2018-09-28</h5>
        <p>把财富储存在一串“虚幻的”私钥字符串上，是人类储值方式的重大革命。</p>
        <p>​​​如果你认同前面四篇文章的观点：</p>

        <p>《比特币与理想主义》我们在参与一场社会实验，它存在失败的可能性，但是我们无怨无悔。</p>
        <p>《下车太早只因愿景太小》这场实验的目标很大，如果一切顺利，比特币的价格可能会在20年后涨到1.6亿人民币。</p>
        <p>《囤比特币：你离财富自由还有多远？》我们没有其他能耐，只能靠囤积比特币，并耐心地等待属于自己的财富自由。</p>
        <p>《囤比特币：冲动、孤独、无聊与矛盾》虽然会经历冲动、孤独、无聊和矛盾等心理考验，但是我们已经做好了准备囤币。</p>
        <p>那么接下来，你会遇到一个问题：你到底应该把比特币储存在哪里？</p>

        <p>如果我们囤积黄金，我们可能有两种方式。一种是在银行租个保险柜，把黄金存进去；另一种是在家里买个保险柜，把黄金存进去。答案可能需要视情况而定。</p>

        <p>但是，对于囤比特币而言，答案永远只有一个——囤在自己手里。</p>

        <p>什么叫囤在自己手里？有且仅有你掌握了能够动用这些比特币的私钥。</p>

        <p>需要特别说明的是：</p>

        <p>此处特指囤币，而且是长期囤币。如果你只是想炒币，那么你可以把币存交易所，毕竟对交易者来说，时间就是金钱。</p>
        <p>市面上有很多所谓的“钱包”，并不是严格意义上的钱包，因为你并不掌握私钥。它们容易跟真正的钱包混淆，因为界面可以做到完全一样，甚至易用性还要更好一些。我不推荐你使用这类“钱包”囤币。【注：不是说这类“钱包”不能用，它们有它们的用途，比如放少量币供平时消费时使用。】</p>
        <p>还有一些“钱包”会付给你利息，你需要时刻想想 p2p 理财的风险。感兴趣也可以百度“比特币存钱罐”。</p>
        <p>如果你不愿意自己掌握比特币私钥，或许是两个原因，一怕风险，二怕麻烦。</p>

        <p>所谓风险是与技能相关的，对于掌握了私钥管理方法的人而言，其实没什么风险，反而是不掌握私钥更具风险。曾经有个哥们存了900个币在 Mt.Gox，当然那会儿币也不值钱，后来币价涨到了1000美元，这哥们才发现 Mt.Gox 发生了挤兑，提不出币。原本是个美好的故事，结局却很悲惨，Mt.Gox 宣布倒闭，这哥们自杀了。</p>

        <p>所谓麻烦是与意义相关的，对于有意义的事情而言，我们从来不会感到麻烦。关于私钥管理有多麻烦，我们以后再谈。本文想表达的是，无论掌握私钥多麻烦，都值得拥有。你之所以怕麻烦，仅仅是因为还没有意识到这件事情有多重要罢了。</p>

        <p>李笑来曾经说过：</p>

        <p>比特币是人类历史上第一次，用技术手段实现了私有财产神圣不可侵犯。</p>
        <p>我非常赞同这句话，但它并不太好理解，尤其是对于那些从来没有接触过比特币私钥的人而言。我个人的理解如下：</p>

        <p>比特币第一次使得我们可以把财富储存在一串“虚幻的”私钥字符串上，这是革命性的，它意味着人类社会最大的需求之一，每个人都有的储存财富的需求，其方式发生了重大革新。</p>
        <p>如果你用现金储存财富，当拥有巨额财富时，你的现金可能会放满1间屋子。实际上，你并不拥有这些财富，因为你拿不走。你的现金会慢慢发霉，或某天被人开卡车拖走，并且烧坏几十台验钞机。</p>
        <p>如果你用银行存款储值，提取转账都很方便，但你的账户存在被银行冻结的风险。</p>
        <p>即使你现金不多，能随身携带，存款也不被冻结，你依然存在被增发货币稀释财富的风险。20年前的1万元存单，放到今天还剩下多少购买力？</p>
        <p>如果你用黄金储值，你同样无法拿走，于是你可能会跟某证券大佬一样，偷运44公斤黄金出海关被查。</p>
        <p>如果你用不动产储值，那就不仅仅是无法带走的问题了，还有被强拆强征的风险。</p>
        <p>如果你用股票储值，你也很危险，弄不好会意外死亡，股份被捐给慈善机构。</p>
        <p>只有比特币，完美的解决了上述问题。</p>

        <p>无论是把私钥藏在书里，还是记在脑子里，你都可以携带财富，全球畅通无阻。而且，这些财富是正在属于你的，只要你不说，就没人能抢走，甚至都没人知道你拥有这些财富。</p>

        <p>俗话说，钱财乃身外之物。这话其实只适用于2009年之前。比特币诞生之后，钱财不再是身外之物。虽然生，还是不能带来，但是死，你却可以带走。</p>

        <p>如果你不想把比特币遗传给子孙后代，你只需要毁掉私钥，其结果等同于捐了，因为全世界持币者都是受益者。</p>

        <p>因此，如果说这世界上还有一样东西是真正属于你的，那它就是比特币。当然，这里有一个前提条件，那就是你自己掌握了私钥。</p>

        <p>如果你不掌握私钥，你就无法享受这革命性的储值方式。</p>
        <p>如果你不掌握私钥，你就无法真正理解这场社会实验的意义。</p>
        <p>如果你不掌握私钥，你就没有真正的拥有过比特币，更不要说什么长期囤币。</p>
        <p>如果你不掌握私钥，你就体会不到那种“你再也不能把我怎么着”的快感。</p>
        <p>当然，自由是要付出代价的，掌握私钥也有代价，那就是一切后果自负。听起来很吓人，其实也没你想象的那么严重，我就从来没有丢过币。</p>

        <p>假如你生活不能自理，那么放手让你自己照顾自己，风险确实很大。但是当你学会自己照顾自己时，放手就没什么风险了。</p>

        <p>现在问题来了，你是想永远生活不能自理，一直依赖别人，还是想尽快学会自己照顾自己，以便放飞自己，享受真正的自由呢？</p>

        <p>几年前，我第一次制作冷钱包的场景，仍然历历在目。作为一个小白，我当时使用了最笨的办法，在一台废旧笔记本电脑上重装了系统，下载Core钱包（那时候叫QT），断网之后安装钱包，生成新地址，备份钱包到2个格式化过的U盘，然后发币到钱包地址。从此，我拥有了冷钱包，我掌握了私钥，而且还有两个备份。我对当时既兴奋又怀疑，还有一点点不安的心情，记忆犹新。</p>

        <p>直到很久之后，我才终于弄明白哈希函数、不对称加密和椭圆曲线，那种对安全性的怀疑和不安逐渐被打消。而随着时间的推移，1年，2年，3年，它一直很安全，我对它的信心越来越强，对私钥管理越来越有心得。</p>

        <p>掌握私钥，你会渐渐对比特币产生全新的认识。你会不由自主地关注比特币的去中心化和安全性，因为对于你而言，这些才最重要。去中心化保证比特币不被增发，不被冻结，安全性保证比特币不被破解。而所有其它那些花里胡哨的属性全是次要的。你也终将明白，币王就是币王，其它那些花里胡哨的币，绝大部分是垃圾，是一些踏空比特币的人创造的垃圾。</p>

        <p>你会打开一个全新的世界，你真正拥有了自己的财富。你甚至会开始怀着怜悯的心情看待旧世界的人们，他们忙忙碌碌，却仍没有意识到，并没有什么东西是真正属于他们自己的。</p>

        <p>他们依然对比特币的价值持怀疑态度，或许是因为他们从来没有体验过手握私钥的那种快感罢。</p>
      </Card>
    </div>
  )

}

export default Edu;
