import React,{ Component }  from 'react';
import '../../../App.css';
import { Card ,Button,Input,Spin,Modal} from 'antd';
import LP_OK from '../../../img/LP_OK.gif'
import cellLP_OK1 from '../../../img/cellLP_OK1.gif'
import cellLP_OK2 from '../../../img/cellLP_OK2.gif'
import cellLP_OK3 from '../../../img/cellLP_OK3.gif'
import LPToken from '../../../ABI/LP';
import web3 from "../../../ABI/web3";
import LG from '../../../ABI/LG';
import '../../../components/global'

class Hangqinadd extends Component {
      state = {
        loading:'none', //加载中
        loadmessage:'打包中……',  //加载窗信息
        error:'none',
        getModal:false,
        getOK:'none',
        getLPAmount:'',
        cellLPAmount:'',
        cellModal:false,
        cellOK1:'none',
        cellOK2:'none',
        cellOK3:'none',
        depositAmount:'',
        withdrawAmount:'',
        LPbalance:'',
        isApprove:'',
        LPadr:global.constants.LPAdr
      };

  async componentDidMount(){

    //获取空投池LP余额
    const LPbalance = await LPToken.methods.balanceOf(window.defaultAccount).call();
    this.setState({LPbalance});

      //判断LG通证有无授权
    const _allowance = await LG.methods.allowance(window.defaultAccount,this.state.LPadr).call();
    const allowance = web3.utils.fromWei(_allowance,'ether');
    if (allowance < 100000000){
      this.setState({
        Approvedisplay: 'block',
        isApprove:"disabled"
      }) 
      }else{
      this.setState({Approvedisplay: 'none',isApprove:""}) 
      }
  }

  deposit = async () =>{
    await LPToken.methods.deposit(this.state.depositAmount).send({from:window.defaultAccount})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.setState({loadmessage:"打包中……",
        loading:'block',
        getOK:'none'
      })
      }
    })
    .on('receipt',(receipt)=>{
      const amount = parseFloat(web3.utils.fromWei(receipt.events.Transfer[1].raw.data,"ether")).toFixed(0)
      if(receipt.status === true){
        this.setState({
          getOK:'block',
          loading:'none',
          getLPAmount: amount
      })
      }
    })
    .on('error',(error,receipt)=>{
      // console.log({error:error,receipt:receipt})
      if(receipt.status === false){
        this.setState({
          loading:'none',
          error:'block',
          loadmessage:"出错！请检查"
      })
      }
    })
  }

  withdraw = async () =>{
    await LPToken.methods.withdraw(this.state.withdrawAmount).send({from:window.defaultAccount})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.setState({loadmessage:"打包中……",
        loading:'block',
      })
      }
    })
    .on('receipt',(receipt)=>{
      const amount = parseFloat(web3.utils.fromWei(receipt.events.Transfer[1].raw.data,"ether")).toFixed(0)
      if(receipt.status === true){
        this.setState({
          loading:'none',
          cellLPAmount: amount +'LP已永远离开了你'
      })
      }
    })
    .on('error',(error,receipt)=>{
      // console.log({error:error,receipt:receipt})
      if(receipt.status === false){
        this.setState({
          loading:'none',
          error:'block',
          loadmessage:"出错！请检查"
      })
      }
    })
  }

  Approve = async () =>{
    await LG.methods.approve(this.state.LPadr, web3.utils.toWei('10000000000000000000','ether'))
    .send({from:window.defaultAccount})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.setState({
          loadmessage:"打包中……",
          loading:'block'
        });
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.setState({
          Approvedisplay: 'none',
          loading:'none',
          isApprove:''
        });
      }
    })
  }

  getModal = () => {
    this.setState({getModal: false})
  };

  cellModal = () => {
    this.setState({cellModal: false})
  };

  render(){

  return (
    <div>
        <Card type="inner" size='small' title="LP" >
            <div>
              <div >
                <div>
                    <div style={{textAlign:'center'}}>
                        <div className='LP' ></div>
                        <div>{this.state.LPbalance/1e18}</div>
                    </div>
                </div>
              </div>
              <div style={{width:'45%', marginTop:'6px', float:'left'}}>
                <Button block type="primary"  size="middle" onClick={() => {this.setState({getModal:true})}}
                  >迎接
                </Button>
              </div>
              <div style={{width:'45%', marginTop:'6px', float:'right'}}>
                <Button  type="primary"  size="middle"  block onClick={() => {this.setState({cellModal:true})}}
                  >离去
                </Button>
              </div>
              <div style={{clear:'both'}}></div>
            </div>
         </Card>

        {/* 迎接对话框  */}
        <Modal visible={this.state.getModal} onCancel={this.getModal} 
          footer={[
            <div style={{textAlign:'center'}} >
              <Button type='primary' onClick={this.getModal}>关闭</Button>
            </div>
          ]}
        >
          <div style={{textAlign:'center'}}>
            <div style={{marginTop:'20px'}}>
              <div style={{display:this.state.Approvedisplay,marginBottom:'8px' }}>
                <Button  shape='round' block type="primary"  size="middle" 
                      onClick={() => {this.Approve()}}>先授权LG</Button>
              </div>
              <Input  placeholder="输入迎接LP数量,1000LG=1LP"
                onChange={event=>{this.setState({depositAmount:event.target.value})}}
                suffix={
                  <Button type="primary" size="middle" disabled = {this.state.isApprove}
                    onClick={this.deposit} 
                    >迎接
                  </Button>
                }
              />
              <div style={{marginTop:'5px',display:this.state.loading}}>
                <Spin size="large" />
                <div style={{color:"#1890ff",fontSize:"15px",textAlign:'center'}}>
                  {this.state.loadmessage}
                </div>
              </div>
              <div style={{marginTop:'5px',display:this.state.error}}>
                <div style={{color:"red",fontSize:"15px",textAlign:'center'}}>
                  {this.state.loadmessage}
                </div>
              </div>
              <div style={{marginTop:'5px',display:this.state.getOK}}>
                <img style={{maxWidth:'220px', height:'auto'}} src={LP_OK} alt=""/>
                <div style={{color:"red",fontSize:"15px",textAlign:'center'}}>
                  迎接{this.state.getLPAmount}LP成功
                </div>
              </div>
            </div>
          </div>
        </Modal>

        {/* 离去对话框  */}
        <Modal visible={this.state.cellModal} onCancel={this.cellModal} 
          footer={[
            <div style={{textAlign:'center'}} >
              <Button type='primary' onClick={this.cellModal}>关闭</Button>
            </div>
          ]}
        >
          <div style={{textAlign:'center'}}>
            <div style={{marginTop:'20px'}}>
              <Input  placeholder="输入离开数量,1LP=1000LG"
                onChange={event=>{this.setState({withdrawAmount:event.target.value})}}
                suffix={
                  <Button type="primary" size="middle" 
                    onClick={()=>{this.setState({cellOK1:'block',cellOK2:'none',cellOK3:'none'})}}
                    >离去
                  </Button>
                }
              />
              <div style={{marginTop:'5px',display:this.state.loading}}>
                <Spin size="large" />
                <div style={{color:"#1890ff",fontSize:"15px",textAlign:'center'}}>
                  {this.state.loadmessage}
                </div>
              </div>
              <div style={{marginTop:'5px',display:this.state.error}}>
                <div style={{color:"red",fontSize:"15px",textAlign:'center'}}>
                  {this.state.loadmessage}
                </div>
              </div>
              <div style={{marginTop:'5px',display:this.state.cellOK1}}>
                <img style={{maxWidth:'220px', height:'auto'}} src={cellLP_OK1} alt=""/>
                <div>
                  <Button type="primary" size="middle" style={{marginTop:'5px'}}
                    onClick={()=>{this.setState({cellOK2:'block',cellOK1:'none',cellOK3:'none'})}}
                    >我不可爱吗？
                  </Button>
                </div>
              </div>
              <div style={{marginTop:'5px',display:this.state.cellOK2}}>
                <img style={{maxWidth:'220px', height:'auto'}} src={cellLP_OK2} alt=""/>
                <div>
                  <Button type="primary" size="middle" style={{marginTop:'5px'}}
                    onClick={()=>{this.setState({cellOK3:'block',cellOK2:'none',cellOK1:'none'})}}
                    >你真的忍心吗？
                  </Button>
                </div>
              </div>
              <div style={{marginTop:'5px',display:this.state.cellOK3}}>
                <img style={{maxWidth:'220px', height:'auto'}} src={cellLP_OK3} alt=""/>
                <div style={{color:"red",fontSize:"15px",textAlign:'center'}}>
                  {this.state.cellLPAmount}
                </div>
                <div>
                  <Button type="primary" size="middle" style={{marginTop:'5px'}}
                    onClick={this.withdraw} 
                    >没有我的日子你要照顾好自己！
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </Modal>

    </div>
  );
}
}

export default Hangqinadd;
