import React, { Component } from 'react'
import { connect } from "react-redux"
import { Toast } from 'antd-mobile'
import { List, Icon, Input, Popup, TextArea, Form, Button, Modal } from 'semantic-ui-react'
import web3 from '../../../../ABI/web3';
import bridgeAbi from '../../../../ABI/Bridge.abi.json';
import { getChainInfo } from '../../../../components/global/functions';
import Loading from '../../../../components/loadingReport';
import { Multicall } from 'ethereum-multicall';
const BigNumber = require('bignumber.js');

export class MoreCode extends Component {
  constructor(props) {
    super(props)
    this.state = {
      bridgeAdr: '',
      MoreCode: [],
      tipWindow: false,
      decimals: 18,
      tipContent: '',
      codeList: [],
      BridgeCode: '',
      multicallAdr: '',
      Loading: false,
      moreDisplay: 'none',
      inputData: '',
      MoreToChain: 1
    }
  }

  async componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.ChainID !== this.props.ChainID
      || nextProps.UserAdr !== this.props.UserAdr
    ) {
      this.setState({ moreDisplay: 'none', BridgeCode: '', MoreToChain: 1 })
      await this.first()
    }
  }

  async componentDidMount() {
    await this.first()
  }

  getChain = async () => {
    await getChainInfo(window.ChainID).then((result) => {
      this.setState({
        bridgeAdr: result.ChainAdr.bridge,
        multicallAdr: result.ChainAdr.multicall,
      })
    })
  }

  first = async () => {
    await this.getChain()
    const bridgeContract = new web3.eth.Contract(bridgeAbi.abi, this.state.bridgeAdr)
    this.setState({ bridgeContract })
  }

  handleOnceCode = async () => {
    let state = this.state
    //判断链码有无输入链码
    if (state.inputData === '') {
      return Toast.show({ content: '未输入跨链码' })
    }
    this.setState({ Loading: true })
    //链码处理成单数组
    let moreCode = state.inputData.split(',')
    //判断链码是否正确
    let code = moreCode[0].split('&')
    if (code.length !== 7) {
      this.setState({ Loading: false })
      return Toast.show({ content: '跨链码不正确' })
    }
    //链码处理成多数组
    let MoreCode = []
    for (let i = 0; i < moreCode.length; i++) {
      let _moreCode = moreCode[i].split('&')
      MoreCode.push(_moreCode)
    }
    this.setState({ MoreCode })
    //判断是否有多种目标链
    let firstToChain =await MoreCode[0][1]
    let moreToChain = []
    moreToChain.push(firstToChain)
    for (let i = 0; i < MoreCode.length; i++) {
      if (firstToChain !== MoreCode[i][1]) {
        moreToChain.push(MoreCode[i][1])
      }
    }
    //目标链转化成名字
    let moreToChainName = []
    for (let i = 0; i < moreToChain.length; i++) {
      let ToChain = '0x' + (Number(moreToChain[i])).toString(16)
      let _ToChainName = await getChainInfo(ToChain)
      let ToChainName = _ToChainName.ChainName
      moreToChainName.push(ToChainName)
    }
    //判断当前链是否选择正确
    let curChain = parseInt(this.props.ChainID, 16) + ''
    let curChainYes = moreToChain.some((x) => { return x === curChain })
    if (curChainYes === false) {
      this.setState({
        tipWindow: true,
        tipContent: `当前链不正确，请切换链至${moreToChainName.join('或')}`,
        Loading: false
      })
      return
    }
    //处理清单
    let codeList = []
    for (let i = 0; i < MoreCode.length; i++) {
      let FromChain = '0x' + (Number(MoreCode[i][0])).toString(16)
      let _FromChainName = await getChainInfo(FromChain)
      let FromChainName = _FromChainName.ChainName

      let ToChain = '0x' + (Number(MoreCode[i][1])).toString(16)
      let _ToChainName = await getChainInfo(ToChain)
      let ToChainName = _ToChainName.ChainName

      let decimals = state.decimals
      let __Value = new BigNumber(MoreCode[i][5] * 10 ** (18 - decimals)).toFixed()
      let _Value = new BigNumber(web3.utils.fromWei(__Value, 'ether'))
      let Value = _Value.toFixed(_Value > 0.1 ? 3 : 8)
      let list =
        <List.Item>
          <nobr>
            <Icon name='circle' />{FromChainName}
            <Icon name='angle double right' />{ToChainName}
            <Icon name='hand point right outline' />{Value}
          </nobr>
        </List.Item>
      codeList.push(list)
    }
    this.setState({ codeList, moreDisplay: 'block' })
    //如果存在多种目标链则提示
    if (moreToChainName.length > 1) {
      this.setState({
        tipWindow: true,
        tipContent: `共存在${moreToChainName.length}种目标链，您需在${moreToChainName.join('和')}都进行同样操作`,
        MoreToChain: moreToChainName.length
      })
    }
    //过滤出当前链下的跨链码
    let curChainCode = []
    for (let i = 0; i < MoreCode.length; i++) {
      if (MoreCode[i][1] === curChain) {
        curChainCode.push(MoreCode[i])
      }
    }
    //处理需发送的最终数据
    let FromChain = []
    let ToChain = []
    let _tokenAdr = []
    let who = []
    let time = []
    let value = []
    let password = []
    for (let i = 0; i < curChainCode.length; i++) {
      FromChain.push(curChainCode[i][0])
      ToChain.push(curChainCode[i][1])
      _tokenAdr.push(curChainCode[i][2])
      who.push(curChainCode[i][3])
      time.push(curChainCode[i][4])
      value.push(curChainCode[i][5])
      password.push(curChainCode[i][6])
    }
    this.setState({
      BridgeCode: { FromChain, ToChain, _tokenAdr, who, time, value, password },
      Loading: false
    })
  }

  delete = () => {
    this.setState({ moreDisplay: 'none', inputData: '', BridgeCode: '', MoreToChain: 1 })
  }

  moreUNlockToken = async () => {
    let state = this.state
    if (state.BridgeCode === '') {
      return Toast.show({
        content:
          <div>请先点击<Icon name='search' /></div>
      })
    }
    let code = state.BridgeCode
    console.log(code)
    const multicall = new Multicall({
      web3Instance: web3, tryAggregate: true,
      multicallCustomContractAddress: this.state.multicallAdr
    });
    //判断是否都已发送
    let _calls = []
    _calls.push({
      reference: 'Owner',
      methodName: 'Owner',
      methodParameters: [code._tokenAdr[0]]
    })
    for (let i = 0; i < code.password.length; i++) {
      let call = {
        reference: 'isUNlockToken',
        methodName: 'isUNlockToken',
        methodParameters: [code.password[i]]
      }
      _calls.push(call)
    }
    const call1 = [{
      reference: 'isUNlockToken',
      contractAddress: state.bridgeAdr,
      abi: bridgeAbi.abi,
      calls: _calls
    }]
    const Call = await multicall.call(call1)
    let callResult = Call.results.isUNlockToken.callsReturnContext
    console.log(callResult)
    let isSend = 0
    for (let i = 1; i < callResult.length; i++) {
      if (callResult[i].returnValues[0] === true) {   
        isSend++
      }
    }
    console.log(isSend,callResult.length)
    if (isSend === callResult.length - 1) {
      return Toast.show({
        duration:state.MoreToChain === 1 ? 2000:5000,
        content: state.MoreToChain === 1 ? '已全部发送' : '当前目标链已全部分送，请再切换到其它链发送'
      })
    }
    //判断是否为操作员帐户
    let owner = callResult[0].returnValues
    let isOwner = owner.some((x) => { return x.toLowerCase() === this.props.UserAdr.toLowerCase() })
    if (isOwner !== true) {
      return Toast.show({ content: '帐户非该通证操作员' })
    }
    //发送
    await state.bridgeContract.methods.moreUNlockToken(
      code.FromChain,
      code.ToChain,
      code._tokenAdr,
      code.who,
      code.time,
      code.value,
      code.password
    ).send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', async (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！")
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }

  render() {
    let state = this.state
    return (
      <div>
        <Form style={{ background: 'white', borderRadius: '5px' }}>
          <TextArea placeholder='粘贴多笔跨链码，手动输入多笔单个跨链码则以英文","分隔'
            style={{ width: '100%', minHeight: 100 }}
            value={state.inputData}
            onChange={event => { this.setState({ inputData: event.target.value }) }}
          />
          <div style={{ margin: '0 auto 0px auto' }}>
            <div style={{ width: '34%', float: 'left' }}>
              <Button floated='left' icon onClick={() => this.delete()}>
                <Icon name='trash alternate outline' />
              </Button>
            </div>
            <div style={{ width: '32%', float: 'left', marginTop: '1px' }}>
              <Popup
                trigger={
                  <Input fluid value={state.decimals} label='小数位' size='small'
                    onChange={event => { this.setState({ decimals: event.target.value }) }}
                  />
                }
                content='通证小数位，只有下方数量显示不正确时才需要更改'
                inverted
                on='focus'
              />
            </div>
            <div style={{ width: '34%', float: 'right' }}>
              <Button floated='right' icon positive loading={state.Loading}
                onClick={() => { this.handleOnceCode() }}>
                <Icon name='search' />
              </Button>
            </div>
            <div style={{ clear: 'both' }} />
          </div>
        </Form>

        <Button positive fluid onClick={() => { this.moreUNlockToken() }}
          style={{ marginTop: '3px', marginBottom: '3px' }}
        >发送</Button>

        <div style={{ display: state.moreDisplay, marginTop: '3px' }}>
          <List divided inverted relaxed style={{ color: 'white' }}>
            {state.codeList}
          </List>
        </div>

        <div>
          <Modal
            size='mini'
            open={state.tipWindow}
            onClose={() => this.setState({ tipWindow: false })}
          >
            <Modal.Content>
              <h4>{state.tipContent}</h4>
              <Button positive floated='right' style={{ marginBottom: '3px' }} icon
                onClick={() => this.setState({ tipWindow: false })}>
                <Icon name='remove' />
              </Button>
            </Modal.Content>
          </Modal>
        </div>
        <Loading ref={loadwindow => { this.loading = loadwindow }} />
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(MoreCode) 
