import React,{ Component }  from 'react';
import '../../../App.css';
import { Table, Card ,Button,Input,Modal} from 'antd';
import Message from '../../../ABI/message';
import Head from './head';
import MyCrypt from '../../../components/crypt';
import Introduce from './components/introduce';
import {CopyOutlined} from '@ant-design/icons';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import { Link } from 'react-router-dom';
import Load from '../../../components/loading';
import web3 from '../../../ABI/web3';
import Loading from '../../../components/loadingReport';

const { TextArea } = Input;


class Index extends Component {
  constructor(props){
    super(props);
    this.state = {
      Inbox:[],
      _sender:'',
      msgcontent:'',
      msgWindow:false,
      _inbox:'',
    };
  }

  async componentDidMount(){
    if(localStorage['MsgQianmin'+window.defaultAccount] === 'true'){
      this.load.loadingOpen(true);
      const _inbox = await Message.methods.inbox().call({from:window.defaultAccount})
      this.setState({_inbox})
      await this.msgMake() 
      this.load.loadingOpen(false);
    }else{
      this.loading.loadingOpen('none','none','block',"首次进入请签名核实身份")
      this.qianmin();
    }
  }

  qianmin = async()=>{
    let user = window.defaultAccount
    await web3.eth.personal.sign("lgqkl",user, "lgqklxyz2021NO1")
    .then((receipt)=>{
      this.setState({test:receipt})
      if(receipt.length>=10){
        localStorage['MsgQianmin'+user] = 'true';
        this.loading.loadingClose();
        this.componentDidMount();
      }
    })
  }

  msgMake=async()=>{
    const _data=[]
    const _inbox = this.state._inbox
    for(var i=_inbox[0].length; i>0;i--){
      const a=[];
      var x = _inbox[0][i-1].substring(0,6) + '…' + _inbox[0][i-1].substring(_inbox[0][i-1].length-4);
      const msg = await this.MyCrypt.Decrypt(_inbox[1][i-1]);
      a[0]=_inbox[0][i-1];
      a[1]=x;
      a[2]=msg;
      a[3]=i;
      if(localStorage['isRead'+window.defaultAccount]!==undefined){
        var _isRead= localStorage['isRead'+window.defaultAccount].split(',');
        if(_isRead.includes(String(i))===true){
          a[4]=1 
        }else{a[4]=0}
      }else{a[4]=0}
      _data.push(a);
    }
    this.setState({Inbox:_data})
  }

  msgWindowClose = () => {
    if(this.state.msgWindow===false){
        this.setState({msgWindow: true})
    }else{
        this.setState({msgWindow: false})
    }
  };

  allRead=async()=>{
    const inboxCount = await Message.methods.inboxCount().call({from:window.defaultAccount});
    for(var i=1;i<=inboxCount;i++){
      if(localStorage['isRead'+window.defaultAccount]===undefined){
        localStorage['isRead'+window.defaultAccount] = String(i)+',';
      }else{
        var _isRead= localStorage['isRead'+window.defaultAccount].split(',');
        if(_isRead.includes(String(i))===false){
          localStorage['isRead'+window.defaultAccount] += String(i)+',';
        }
    }
  }
  this.componentDidMount()
}

  render(){
    const columns = [
      {
        title: '发件人',
        dataIndex: '1',
        key:'1',
        width: 110,
        align: 'left',
        ellipsis: 'true',
      },
      {
        title: '内容',
        dataIndex: '2',
        ellipsis: 'true',
      },
    ];
    const replayAdr = '/messageNew/' + this.state._sender;
  return (
    <div> 
      <Head/>
      <Card  type="inner" size='small' className='cardMsg' >
        <div style={{textAlign:'center',fontWeight:'800'}}>收件箱</div>
        <Table size='small' 
          columns={columns}  
          dataSource={this.state.Inbox} 
          customRow="click"
          bordered 
          pagination={false}
          rowClassName={record=>record[4]===0?'MsgIsRead':''}
          onRow = {(record) => {
            return {
              onClick: () => {
                this.setState({
                  msgWindow:true,
                  _sender:record[0],
                  msgcontent: record[2],
                })
                if(localStorage['isRead'+window.defaultAccount]===undefined){
                  localStorage['isRead'+window.defaultAccount] = record[3]+','
                }else{
                  var _isRead= localStorage['isRead'+window.defaultAccount].split(',');
                  if(_isRead.includes(String(record[3]))===false){
                    localStorage['isRead'+window.defaultAccount] += record[3]+','
                  }
                }
                this.msgMake()
                }
              }
              }
              }
        />
        <Button style={{float:'right',background:'#ff4dbb9c',color:'white'}} onClick={this.allRead}>全部已读</Button>

      </Card>

      <Modal visible={this.state.msgWindow} 
        bodyStyle={{
          backgroundColor:'#ff4dbb9c',
          borderRadius:'2px',
        }}
        style={{borderRadius:'10px'}}
        onCancel={this.msgWindowClose} 
        closable={true}
        footer={
          null
          }
      >
      <div >
          <div>发件人：</div>
          
            <Input id='i1'  value={this.state._sender} disabled="true" 
              style={{paddingRight:'0px'}}
              suffix={
                <CopyToClipboard  text={this.state._sender}>
                  <Button shape='circle' size="small" ><CopyOutlined/></Button>
                </CopyToClipboard>
              }
            />
          <div>内&nbsp;&nbsp;&nbsp;&nbsp;容：
            <CopyToClipboard  text={this.state.msgcontent} style={{float:'right'}}>
              <Button shape='circle' size="small" ><CopyOutlined/></Button>
            </CopyToClipboard>
            <TextArea id='i1' rows={12} value={this.state.msgcontent} disabled/>
          </div>
          <Button  type="primary" block color='green' size="middle" style={{marginTop:'5px'}} >
            <Link to={replayAdr}>回复</Link>
          </Button>
      </div>
      </Modal>

      <Introduce open='none'/>
      <Load ref={Load=>{this.load=Load}} />
      <MyCrypt ref={MyCrypt=>{this.MyCrypt=MyCrypt}} />
      <Loading ref={loadwindow=>{this.loading=loadwindow}}/>
    </div>
  );
}
}

export default Index;
