import React, { Component } from 'react';
import { QuestionCircleOutlined } from '@ant-design/icons';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { Button, Header, Icon, Modal, Popup } from 'semantic-ui-react'
import { Toast } from 'antd-mobile'

class index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      window: false,
    }
  }

  //窗开关
  windowClose = () => {
    this.setState({ window: false })
  };
  windowOpen = () => {
    this.setState({ window: true })
  };
  render() {

    return (

      <Modal
        basic
        open={this.state.window}
        size='mini'
        style={{ wordWrap: 'break-word' }}
      >
        <Header >
          跨链码
          <Popup
            trigger={<QuestionCircleOutlined />}
            content='该笔跨链的交易存证，可将该跨链码通过链短信或其它方式发送给该通证管理员催促操作'
            inverted
            on='click'
          />
        </Header>
        <Modal.Content >
          <div >
            {this.props.currentCode}
          </div>
        </Modal.Content>
        <Modal.Actions style={{ float: 'right' }}>
          <CopyToClipboard text={this.props.currentCode}>
            <Button color='green' inverted onClick={() => {
              Toast.show({
                content: '已复制',
              })
            }}>复制跨链码</Button>
          </CopyToClipboard>
          <Button basic color='red' inverted onClick={() => this.windowClose(false)}>
            <Icon name='remove' /> 关闭
          </Button>
        </Modal.Actions>
      </Modal>
    )
  }
}

export default index
