import React,{ Component }  from 'react';
import {Button,Modal} from 'antd';
import { Link } from 'react-router-dom';

class getLGCwindow extends Component {
    constructor(props){
        super(props);
        this.state={
            window:false,
            msgColor:'#ff4d4f',
            message:''
        }
}

  //加载窗开关
  Close = () => {
    this.setState({window: false})
  };
  Open = (msg) => {
    this.setState({
      window: true,
      message:msg
    })
  };

render(){
  return(
    <div>
      <Modal  visible={this.state.window} 
        closable={true}
        width={250}
        onCancel={this.Close} 
        footer={null}
      >
        <div style={{textAlign:'center'}}>
          <div style={{color:'#ff4d4f',fontSize:"13px",textAlign:'center',marginBottom:'10px'}}>
              {this.state.message}
          </div>
          <Button type='primary'><Link to='/LGCtransfer'>前往申请</Link></Button>
        </div>
      </Modal>
    </div>
  );
}
}

export default getLGCwindow;

