import React, { Component } from 'react'
import Loading from '../loadingReport';
import web3 from '../../ABI/web3';
import erc20abi from '../../ABI/ERC20.abi.json';

class fun extends Component {
  Approve = async (adr, spender) => {
    console.log(adr,spender)
    const ERC20 = await new web3.eth.Contract(erc20abi.abi, adr);
    await ERC20.methods.approve(spender, '115792089237316195423570985008687907853269984665640564039457584007913129639935')
    .send({ from: window.defaultAccount, gas:'100000'})
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "授权成功，可进行下一步操作")
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }


  render() {
    return (
      <div>
        <Loading ref={loadwindow => { this.loading = loadwindow }} />
      </div>
    )
  }
}

export default fun











