import React, { Component } from 'react';
import '../../App.css';
import { Card, Button } from 'antd';
import { Link } from 'react-router-dom';
import { getChainInfo } from '../../components/global/functions';

class mainCardShentai extends Component {
  state = {
    Bip39: '',
  };

  async componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.ChainID !== this.props.ChainID
      || nextProps.UserAdr !== this.props.UserAdr
    ) {
      await this.first()
    }
  }

  async componentDidMount() {
    await this.first()
  }

  first = async () => {
    await this.getChain()
    const url = await window.location.host;

    if (url === 'lgqkl.github.io') {
      this.setState({ Bip39: 'https://' + url + '/LG-QKL/BIP39/index.html' });
    } else {
      this.setState({ Bip39: 'https://' + url + '/BIP39/index.html' });
    }

  }

  getChain = async () => {
    await getChainInfo(window.ChainID).then((result) => {
      this.setState({
        ChainName: result.ChainName
      })
    })
  }



  render() {
    return (
      <Card type="inner"
        style={{ borderRadius: '10px' }}
        headStyle={{ borderTopLeftRadius: '10px', borderTopRightRadius: '10px' }}
      >
        <div>
          <div style={{ marginTop: '15px' }}>

            <div style={{ width: '25%', float: 'left' }}>
              <div style={{ textAlign: 'center' }}>
                {/* <a href={`${process.env.PUBLIC_URL}/BIP39`}> */}
                <Link to="/BIP39">
                  <div className='bip39' />
                  </Link>
                <div>帐户工具</div>
              </div>
            </div>
            <div style={{ width: '25%', float: 'left' }}>
              <div style={{ textAlign: 'center' }}>
                <Link to="/GetGas">
                  <div className='getGas' />
                </Link>
                <div>抢燃料</div>
              </div>
            </div>
            <div style={{ clear: 'both' }} />
          </div>
        </div>
        <Link to="/edu">
          <Button type='primary' danger block>知识库</Button>
        </Link>
        <Link to="/KnowledgeBase">
          <Button block style={{ marginTop: '10px' }}>常用链接</Button>
        </Link>

      </Card>
    );
  }
}

export default mainCardShentai;
