import React from 'react';
import { Card } from 'antd';
import img1 from './TBTCb1.jpg';
import img2 from './TBTCb2.jpg';
import img3 from './TBTCb3.jpg';

function Edu() {
  return (
    <div>
      <Card>
        <b>基本价格模型</b>
        <h5>ahr999  </h5>
        <p>比特币的价格到底由什么决定？为什么产量减半会导致比特币价格暴涨？</p>
        <p>现在，我们已经达到了囤比特币的入门标准：明确了比特币的目标，愿意参与实验，自愿承
          担风险，并开始利用比特币储值，自己掌握比特币私钥，并进行冷储存。</p>
        <p>那么，接下来，我们什么都不用做了，等着就行了。</p>
        <p>但是，傻傻的等确实太闲了，所以我们继续学点东西，本文分享我个人理解的比特币价格模型。
          需要指出的是，我并非经济学科班出身，所以你可以认为我是民科，欢迎批评指正。</p>
        <p>首先，我们看看比特币过去的价格走势（图 1）。</p>
        <div >
          <img style={{ maxWidth: '100%', height: 'auto' }} src={img1} alt="" />
        </div>
        <p>这个走势完全可以用指数级上涨来拟合，并且根据拟合结果，预测 20 年后的比特币价格应
          该在 700 万美元，即 5000 万人民币左右。与我们在《下车太早只因愿景太小》中预测的基本一致。</p>
        <p>考虑到比特币储值相对于黄金、货币和不动产的绝对优势，每个比特币将涨到 750 万美元，
          折合 5000 万人民币。</p>
        <p>那么到底比特币价格的根源在哪里？答案是：你。</p>
        <p>我们可以简单把比特币市场分为 3 类参与者：囤币、矿工、炒币（图 2）。</p>
        <div >
          <img style={{ maxWidth: '100%', height: 'auto' }} src={img2} alt="" />
        </div>
        <p>囤币是买方，矿工是卖方。最初也只有这两类人，由于这两类人有交易需求，才产生了交易
          所和第三类人：炒币。炒币摇摆不定，行情向上就去滑到囤币一边，行情向下就滑到矿工一边。</p>
        <p>当然，某一个人可能同时是囤币、矿工和炒币。但是，为了简化模型，我们可以把他拆分成
          3 个人来考虑.</p>
        <p>囤币是比特币一切价值得来源。有人囤，才会有人挖；有人挖，才有了交易；有了交易，才
          有了炒作，才有了资讯、商家等等。如果没有人囤币，市场马上就会崩盘，也就不会有现在的比
          特币了。</p>
        <p>比特币持续不断流入囤币者手中。这是必然趋势，因为囤币是只买不卖的。在总量被限制的
          情况下，流通比特币只会越来越少，只有 9% 的币，其币龄小于 1 个月，只有 2% 的币，其币龄
          小于 1 天，（图 3）</p>
        <div >
          <img style={{ maxWidth: '100%', height: 'auto' }} src={img3} alt="" />
        </div>
        <p>那么，为什么会有人囤币呢？由于现在的你已经是个合格的囤币者了，你肯定有自己的答案。</p>
        <p>我的答案是，我们的储值需求是与生俱来的。我们努力赚钱总会有一些消费不完的钱，这些
          钱总是需要存起来以备不时之需。我们越是富有，这个需求就越大。而比特币的去中心化和安全
          特性，使得它成为了我储值的第一选择。</p>
        <p>世界上最大的囤币者是谁？是中本聪。据不完全统计，中本聪囤积了超过百万比特币，从来
          没有动过。</p>
        <p>理解了比特币的一切价值来源是囤币，我们就应该自豪，因为我们自己就是比特币一切价值
          的来源，我们赋予了比特币价值，我们都是中本聪。</p>
        <p>而且，一旦开始囤币，很多长期困扰你的疑问就有了答案。</p>
        <p>比如：既然囤币是比特币一切价值的来源，那么作为一个长期囤币者，你最关心的问题是什
          么？</p>
        <p>你一定最关心去中心化和安全性，去中心化保证比特币不被增发，不被冻结，安全性保证比
          特币不被破解。而主链交易是否够快，是否便宜，你其实是不太在乎的，毕竟你一年也转不了几次帐。
          这就是莱特币、狗狗币、XXX 币、等等，永远无法超越比特币的根本原因。</p>
        <p>对于这个问题，如果你不亲自囤币，你是理解不了的。随便被人忽悠一下，你就跑偏了。不
          要看了中本聪的白皮书，就开始抠字眼。你不应该看中本聪怎么说，而应该看中本聪怎么做。</p>
        <p>我们也很容易理解，为什么每次产量减半，比特币价格必涨。</p>
        <p>一般情况下，如果没有人炒来炒去，比特币的价格应该是囤币和矿工之间的平衡。例如：现
          在每天产出 1800 个比特币，那么每天囤币者需要 1000 万美元的投入，才能维持比特币 6000 美
          元的价格。1000 万美元实际上非常少，如果全球 100 万囤币者，每人每天只需要投入 10 美元。</p>
        <p>假设比特币价格原本处于平衡状态，现在开始产量减半，每天产出 900 个比特币，请问价格
          会如何变化？价格由供求关系决定，囤币者的投入又不会变，傻子都知道会涨。</p>
        <p>上面我们只是考虑了每天的产量，你可能会问，难道存量比特币就不会被抛售吗？</p>
        <p>图 3 显示，90% 以上的币超过 1 个月没有动过，75% 以上的币超过半年没动过，其实大家
          都在囤，根本就没有什么存量币会被抛售。</p>
        <p>接下来，我们再分析下一个问题，为什么每次产量减半比特币都不止涨一倍，而是涨很多倍？</p>
        <p>这就要从比特币的奇葩供求关系说起了。</p>
        <p>首先，比特币的供给是完全固定的。任何一个实体商品，都是价格越高，供给越大。这很好理解，
          商品价格涨，利润空间变大，促使更多的人投入生产，于是就有了更多的产出，增大的供给会遏
          制商品的暴涨。黄金就是这样，价格涨导致供应增加。经济学上有个名词描述它，叫作供给的价
          格弹性，指的是由价格变化引起的商品供给的变化。比特币的供给的价格弹性是零，无论价格怎
          么涨，供给始终不变，这非常变态。</p>
        <p>其次，比特币的需求更变态。任何一个实体商品，都是价格越高，需求越小。这也很好理解，
          商品价格升高会逼迫人们寻找替代商品。举个例子，你每天都吃肉，如果肉价涨了，你可能就会
          改变习惯，减少吃肉，隔天吃一次鱼，所以，你总体上对肉的需求就降低了。这个叫需求的价格
          弹性，比特币的需求的价格弹性居然是负的，也就是说币价越高，需求越旺盛。因为盘子越大，
          越适合储值。</p>
        <p>再加上每一次产量减半，人们都预期比特币会暴涨，于是炒币大军疯狂涌入，推波助澜。</p>
        <p>总而言之，每一次减半都打破了供需平衡，而一旦平衡打破，供给不变，需求反而增加，再
          加上炒币大军涌入，怎么可能不暴涨？</p>
        <p>那么反过来也是一样的。如果泡沫形成，比特币价格太高，矿工的抛压超过了囤币的投入，
          币价就会下跌。一旦价格下跌趋势形成，需求就会开始减少，因为盘子越小，越不适合储值。从
          而造成，比特币价格的进一步下跌。</p>
        <p>囤比特币是只买不卖的，我们总是希望能用有限的资金购买更多的币。那么比特币的基本价
          格模型，能否帮助我们寻找合适的比特币购买时机呢？请看下回分解。</p>
      </Card>
    </div>
  )

}

export default Edu;
