import React, { useState, useEffect, useCallback, useRef } from 'react'
import { Card, Button, Input, Dropdown, ButtonGroup, Divider } from 'semantic-ui-react'
import { connect } from "react-redux"
import Head from './components/head';
import '../../../App.css';
import UNIswapList from '../../../tokenList/UNIswapList/index.json';
import { getChainInfo, isApprove } from '../../../components/global/functions';
import { Toast } from 'antd-mobile'
import web3 from "../../../ABI/web3";
import UNISwapRouterABI from "../../../ABI/UNI/UNISwapRouter.json";
import UniswapV3FactoryABI from "../../../ABI/UNI/UniswapV3Factory.json";
import UniswapV3PoolABI from "../../../ABI/UNI/UniPool.json";
import { AbiItem } from 'ethereum-multicall/dist/esm/models';
import { Contract } from 'web3-eth-contract';
import Fun from "../../../components/global/function";
import LoadingReport from '../../../components/loadingReport';
import { chackTokenBlance, TokenPriceThisChain, getTokenBalance, FromWeiToEther } from "../../../components/global/functionTS"
import { MATICadr } from '../../../components/global/globals';
import erc20abi from '../../../ABI/ERC20.abi.json';
import LGoracle from '../../../ABI/LGOracle.json'
import BigNumber from 'bignumber.js';

interface Token {
    LogoONOFF: boolean,
    name: string,
    adr: string,
    value: string,
    price: number,
    decimals: number
}


function LockCreat(props: any) {

    const tokenList = UNIswapList.MATIC

    const slippages = [
        {
            "key": 1,
            "text": '5',
            "value": 5
        },
        {
            "key": 2,
            "text": '10',
            "value": 10
        },
        {
            "key": 3,
            "text": "15",
            "value": 15
        },
        {
            "key": 4,
            "text": "20",
            "value": 20
        },
        {
            "key": 5,
            "text": "25",
            "value": 25
        },
        {
            "key": 6,
            "text": "30",
            "value": 30
        }
    ]
    const [slippage, setSlippage] = useState<number>(15);

    const [tokenIn, setTokenIN] = useState<Token>({ LogoONOFF: true, name: 'WMATIC', adr: MATICadr.WETH, value: '', price: 0, decimals: 18 });
    const [tokenOut, setTokenOut] = useState<Token>({ LogoONOFF: true, name: 'LG', adr: MATICadr.LG, value: '', price: 0, decimals: 18 });
    const [tokenInBalance, setTokenInBalance] = useState<string>('0');

    const inPutToken = async (value: any): Promise<Token> => {
        const selectedTokenName = tokenList.find(token => token.text === value);
        const selectedTokenAdr = tokenList.find(token => token.value.toLowerCase() === value.toLowerCase());
        let token: Token = { LogoONOFF: false, name: 'unknow', adr: '', value: '', price: 0, decimals: 0 }
        if (selectedTokenAdr) {
            token = { LogoONOFF: true, name: selectedTokenAdr.text, adr: selectedTokenAdr.value, value: '', price: 0, decimals: selectedTokenAdr.decimals }
        }
        if (selectedTokenName) {
            token = { LogoONOFF: true, name: selectedTokenName.text, adr: selectedTokenName.value, value: '', price: 0, decimals: selectedTokenName.decimals }
        }
        if (!selectedTokenName && !selectedTokenAdr) {
            try {
                const ERC20 = new web3.eth.Contract(erc20abi.abi as AbiItem[], value)
                const name = await ERC20.methods.symbol().call()
                token = { LogoONOFF: false, name: name, adr: value, value: '', price: 0, decimals: await ERC20.methods.decimals().call() }
            } catch (error) {
                console.log(error)
                token = { LogoONOFF: true, name: 'unknow', adr: 'unknow', value: '', price: 0, decimals: 0 }
            }
        }
        return token
    }

    const inputTokenIn = async (value: any) => {
        let result = await inPutToken(value)
        await tokenInIsApprove(value)
        setTokenIN({
            ...tokenIn,
            LogoONOFF: result.LogoONOFF,
            name: result.name,
            adr: result.adr,
            value: result.value,
            decimals: result.decimals
        })
        setTokenOut({
            ...tokenOut,
            value: ''
        })
        getTokenInBalance(result.adr, props.UserAdr, result.decimals)
    }

    const getTokenInBalance = async (tokenAdr: string, userAdr: string, tokenDecimals: number) => {
        let _balance = await getTokenBalance(tokenAdr, userAdr)
        let balance = await FromWeiToEther(userAdr, tokenDecimals, _balance)
        setTokenInBalance(balance)
    }

    const delTokenIn = () => {
        setTokenIN({
            ...tokenIn,
            LogoONOFF: true,
            name: 'unknow',
            adr: '',
            value: ''
        })
    }

    const inputTokenOut = async (value: any) => {
        let result = await inPutToken(value)
        setTokenOut({
            ...tokenOut,
            LogoONOFF: result.LogoONOFF,
            name: result.name,
            adr: result.adr,
            value: result.value,
            decimals: result.decimals
        })
        setTokenIN({
            ...tokenIn,
            value: ''
        })
    }

    const delTokenOut = () => {
        setTokenOut({
            ...tokenOut,
            LogoONOFF: true,
            name: 'unknow',
            adr: '',
            value: ''
        })
    }

    const [globalState, setGlobalState] = useState
        ({
            UNISwapRouterAdr: '',
            UNISwapRouterContract: undefined as Contract | undefined,
            UNIFactoryContract: undefined as Contract | undefined,
            tokenInisApprove: false,
            LGOracleContract: undefined as Contract | undefined,
            swapOnOff: true,
        });

    const tokenInIsApprove = useCallback(async (tokenadr: string) => {
        const chainInfo = await getChainInfo(props.ChainID)
        const tokenIsApprove = await isApprove(tokenadr, chainInfo.ChainAdr.UNISwapRouter)
        setGlobalState(prevState => ({
            ...prevState,
            tokenInisApprove: tokenIsApprove === 'yes' ? true : false,
            swapOnOff: tokenIsApprove === 'yes' ? false : true
        }))

    }, [props.ChainID])

    useEffect(() => {
        if (tokenIn.adr) {
            tokenInIsApprove(tokenIn.adr)
        }
    }, [tokenIn.adr, tokenInIsApprove])

    //加载初始化数据
    const first = useCallback(async () => {
        try {
            const chainInfo = await getChainInfo(props.ChainID)
            const _UNISwapRouterContract = new web3.eth.Contract(UNISwapRouterABI as AbiItem[], chainInfo.ChainAdr.UNISwapRouter)
            const _UNIFactoryContract = new web3.eth.Contract(UniswapV3FactoryABI as AbiItem[], chainInfo.ChainAdr.UniswapV3Factory)
            const _LGOracleContract = new web3.eth.Contract(LGoracle as AbiItem[], chainInfo.ChainAdr.LGOracle)
            setGlobalState(prevState => ({
                ...prevState,
                UNISwapRouterAdr: chainInfo.ChainAdr.UNISwapRouter,
                UNISwapRouterContract: _UNISwapRouterContract,
                UNIFactoryContract: _UNIFactoryContract,
                LGOracleContract: _LGOracleContract
            }))
            LoadingReportRef.current.loadingClose()
        } catch (error) {
            console.log(error)
        }
    }, [props.ChainID])

    useEffect(() => {
        first()
        getTokenInBalance(MATICadr.WETH, props.UserAdr, 18)
    }, [first, props.UserAdr, props.ChainID]);

    const FunRef = useRef<any>(null);
    const LoadingReportRef = useRef<any>(null);

    const Approve = async (tokenAdr: string) => {
        await FunRef.current.Approve(tokenAdr, globalState.UNISwapRouterAdr)
        tokenInIsApprove(tokenAdr)
    }

    const [advSet, setAdvSet] = useState<boolean>(false)

    const onChangeValueIn = async (value: string) => {
        setTokenIN({
            ...tokenIn,
            value: value
        })
        setTimeout(async () => {
            try {
                let priceIn = parseInt(await TokenPriceThisChain(tokenIn.adr, MATICadr.LGOracle))
                let priceOut = parseInt(await TokenPriceThisChain(tokenOut.adr, MATICadr.LGOracle))
                let valueOut = new BigNumber((priceIn / priceOut) * Number(value)).toFixed(18)
                setTokenIN(prevState => ({
                    ...prevState,
                    price: priceIn / 1e8
                }))
                setTokenOut({
                    ...tokenOut,
                    value: valueOut.toString(),
                    price: priceOut / 1e8
                })
            } catch (error) {
                console.log(error)
            }
        }, 500);

    }

    const onChangeValueOut = async (value: string) => {
        setTokenOut({
            ...tokenOut,
            value: value
        })
        setTimeout(async () => {
            try {
                let priceIn = parseInt(await TokenPriceThisChain(tokenIn.adr, MATICadr.LGOracle))
                let priceOut = parseInt(await TokenPriceThisChain(tokenOut.adr, MATICadr.LGOracle))
                let valueIn = new BigNumber((priceOut / priceIn) * Number(value)).toFixed(18)
                setTokenOut(prevState => ({
                    ...prevState,
                    price: priceOut / 1e8
                }))
                setTokenIN({
                    ...tokenIn,
                    value: valueIn.toString(),
                    price: priceIn / 1e8
                })
            } catch (error) {

            }
        }, 500)
    }

    // const address0 = '0x0000000000000000000000000000000000000000'

    const poolFee = async (tokenInAdr: string, tokenOutAdr: string): Promise<string | null> => {
        try {
            const pool = await globalState.LGOracleContract?.methods.getTargetPool(tokenInAdr, tokenOutAdr).call()
            const poolContract = new web3.eth.Contract(UniswapV3PoolABI as AbiItem[], pool)
            const poolFee = await poolContract?.methods.fee().call()
            return poolFee
        } catch (error) {
            console.log(error)
            return null
        }
    }

    const singPath = (tokenIn: string, poolFee: string, tokenOut: string) => {
        const poolFeeHex = Number(poolFee).toString(16).padStart(6, '0');
        const tokenInHex = tokenIn.slice(2).toLowerCase()
        const tokenOutHex = tokenOut.slice(2).toLowerCase()
        return "0x" + tokenInHex + poolFeeHex + tokenOutHex
    };

    const computerPath = async (tokenIn: string, tokenOut: string): Promise<string | void> => {
        const fee = await poolFee(tokenIn, tokenOut)
        if (fee) {
            const path = singPath(tokenIn, fee, tokenOut)
            return path
        } else {
            return Toast.show({ content: '无兑换路径' })
        }
    }

    const swap = async () => {
        let Params = {
            path: '',
            recipient: '',
            deadline: 0,
            amountIn: '',
            amountOutMinimum: ''
        }
        try {
            let amountIn = new BigNumber(Number(tokenIn.value) * 1e18 / 10 ** (18 - tokenIn.decimals)).toFixed(0)
            if (!tokenIn.value) { return Toast.show({ content: '未输入进去的数量' }) }
            if (!tokenOut.value) { return Toast.show({ content: '未计算出出来的数量,可以输入0,但会影响出来的数量' }) }
            if (await chackTokenBlance(tokenIn.adr, props.UserAdr, amountIn) === false) { return Toast.show({ content: `您的"${tokenIn.name}"余额不足` }) }
            const latestBlockNumber = await web3.eth.getBlockNumber();
            const latestBlock = await web3.eth.getBlock(latestBlockNumber);
            let _outMinimumValue = new BigNumber(Number(tokenOut.value) * (100 - slippage) / 100).toFixed(18)
            let outMinimumValue = new BigNumber(Number(_outMinimumValue) * 1e18 / 10 ** (18 - tokenOut.decimals)).toFixed(0)
            const path = await computerPath(tokenIn.adr, tokenOut.adr)
            if (path) {
                Params = {
                    path: path,
                    recipient: props.UserAdr,
                    amountIn: amountIn,
                    deadline: Number(latestBlock.timestamp) + 600,
                    amountOutMinimum: outMinimumValue
                }
            }
        } catch (error) {
            console.log(error)
        }
        try {
            if (globalState.UNISwapRouterContract && Params.path !== '') {
                await globalState.UNISwapRouterContract.methods.exactInput(
                    Params
                ).send({ from: props.UserAdr, gas: '200000' })
                    .on('transactionHash', (transactionHash: string | any[]) => {
                        if (transactionHash.length !== 0) {
                            LoadingReportRef.current.loadingOpen('block', 'none', 'none', "打包中……")
                        }
                    })
                    .on('receipt', (receipt: { status: boolean; }) => {
                        if (receipt.status === true) {
                            LoadingReportRef.current.loadingOpen('none', 'block', 'none', "成功")
                            getTokenInBalance(tokenIn.adr, props.UserAdr, tokenIn.decimals)
                        }
                    })
                    .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                        if (receipt !== undefined && receipt.status === false) {
                            LoadingReportRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                        }
                    })
            }
        } catch (error) {
            console.error(error);
        }
    }


    const inputAllBalance = async () => {
        onChangeValueIn(tokenInBalance)
    }



    useEffect(() => { return () => { }; }, []);

    return (
        <div>
            <Head />
            <Card type="inner" style={{ width: 'inherit', minWidth: '312px', maxWidth: '500px', margin: '0px auto auto auto', backgroundColor: 'rgba(33,133,208,0.5)' }}>

                <div style={{ display: 'flex' }}>
                    <ButtonGroup color='blue' style={{ marginBottom: '3px', marginRight: '0px' }}>
                        <Button icon style={{ borderRadius: '0px 0px 0px 0px ' }}>进</Button>
                        <Button icon style={{ padding: '4px 3px 4px 3px' }}>
                            {tokenIn.LogoONOFF &&
                                <div >
                                    <img
                                        src={`${process.env.PUBLIC_URL}/logo/${tokenIn.name}.svg`}
                                        alt='auto' width='auto' height='30px'
                                    />
                                </div>
                            }
                            {!tokenIn.LogoONOFF &&
                                <div>
                                    {tokenIn.name}
                                </div>
                            }
                        </Button>
                    </ButtonGroup>

                    <Button
                        color='blue'
                        icon
                        style={{ margin: '0px 0px 3px 2px', padding: '11px 5px 11px 5px', borderRadius: '5px 0px 0px 5px ' }}
                    >
                        <Dropdown
                            options={tokenList}
                            value={tokenIn.adr}
                            onChange={(e, { value }) => inputTokenIn(value)}
                        />
                    </Button>
                    <Input
                        fluid
                        placeholder="选择或输入通证合约地址"
                        style={{ marginBottom: '3px', flex: '1', borderRadius: '0px 0px 0px 0px ' }}
                        value={tokenIn.adr}
                        onChange={(e, { value }) => inputTokenIn(value)}
                    />

                    <Button icon='trash alternate outline' onClick={delTokenIn} color='blue'
                        style={{ marginBottom: '3px', marginRight: '0px' }}
                    />
                </div>

                <div style={{ display: 'flex' }}>
                    <ButtonGroup color='blue' style={{ marginBottom: '3px', marginRight: '0px' }}>
                        <Button icon style={{ borderRadius: '0px 0px 0px 0px ' }}>进去数量</Button>
                    </ButtonGroup>

                    <Input fluid
                        value={tokenIn.value}
                        placeholder="输入数量"
                        type="number"
                        style={{ marginBottom: '3px', flex: '1' }}
                        onChange={(e, { value }) => { onChangeValueIn(value) }}
                    />
                    <Input style={{ width: '20%', marginBottom: '3px' }} value={tokenInBalance} readOnly
                        onClick={inputAllBalance}
                    />
                    {/* <Button icon color='blue' style={{ marginBottom: '3px', marginRight: '0px' }}>余额:1111111111111111111111</Button> */}
                </div>

                <div style={{ display: 'flex' }}>
                    <Button fluid size='mini'
                        color='blue' icon='long arrow alternate down'
                        style={{ margin: 'auto', width: '30px' }}
                    />
                </div>

                <div style={{ display: 'flex', marginTop: '3px' }}>
                    <ButtonGroup color='blue' style={{ marginBottom: '3px', marginRight: '0px' }}>
                        <Button icon style={{ borderRadius: '0px 0px 0px 0px ' }}>出</Button>
                        <Button icon style={{ padding: '4px 3px 4px 3px' }}>
                            {tokenOut.LogoONOFF &&
                                <div >
                                    <img
                                        src={`${process.env.PUBLIC_URL}/logo/${tokenOut.name}.svg`}
                                        alt='auto' width='auto' height='30px'
                                    />
                                </div>
                            }
                            {!tokenOut.LogoONOFF &&
                                <div>
                                    {tokenOut.name}
                                </div>
                            }
                        </Button>
                    </ButtonGroup>

                    <Button
                        color='blue'
                        icon
                        style={{ margin: '0px 0px 3px 2px', padding: '11px 5px 11px 5px', borderRadius: '5px 0px 0px 5px ' }}
                    >
                        <Dropdown
                            options={tokenList}
                            value={tokenOut.adr}
                            onChange={(e, { value }) => inputTokenOut(value)}
                        />
                    </Button>
                    <Input
                        fluid
                        placeholder="选择或输入通证合约地址"
                        style={{ marginBottom: '3px', flex: '1', borderRadius: '0px 0px 0px 0px ' }}
                        value={tokenOut.adr}
                        onChange={(e, { value }) => inputTokenOut(value)}
                    />

                    <Button icon='trash alternate outline' onClick={delTokenOut} color='blue'
                        style={{ marginBottom: '3px', marginRight: '0px' }}
                    />
                </div>


                <div style={{ display: 'flex' }}>
                    <ButtonGroup color='blue' style={{ marginBottom: '3px', marginRight: '0px' }}>
                        <Button icon style={{ borderRadius: '0px 0px 0px 0px ' }}>出来数量</Button>

                    </ButtonGroup>

                    <Input fluid
                        value={tokenOut.value}
                        placeholder="输入数量"
                        type="number"
                        style={{ marginBottom: '3px', flex: '1' }}
                        onChange={(e, { value }) => { onChangeValueOut(value) }}
                    />

                </div>

                {!globalState.tokenInisApprove && <Button fluid
                    color='teal'
                    size='tiny'
                    style={{ width: '200px', margin: 'auto', marginBottom: '3px' }}
                    onClick={() => { Approve(tokenIn.adr) }}
                >授权进的通证
                </Button>
                }
                <Button fluid
                    disabled={globalState.swapOnOff}
                    color='teal'
                    style={{ width: '200px', margin: 'auto' }}
                    onClick={() => { swap() }}
                >发 送
                </Button>

                <Divider horizontal onClick={() => { setAdvSet(!advSet) }} >专家设置</Divider>

                {advSet && <div style={{ display: 'flex', margin: 'auto' }}>
                    <ButtonGroup color='blue' style={{ marginBottom: '3px', marginRight: '0px' }}>
                        <Button color='blue'>滑点</Button>
                        <Button color='blue' icon
                        >
                            <Dropdown
                                options={slippages}
                                value={slippage}
                                onChange={(e, { value }) => setSlippage(value as number)}
                            />
                        </Button>
                    </ButtonGroup>
                </div>
                }

                <Card type="inner" style={{ width: 'inherit', minWidth: '312px', maxWidth: '500px', margin: '5px auto auto auto', backgroundColor: '#cce2ff' }}>
                    <div style={{ marginLeft: '10px' }}>
                        <div>
                            <b>免责声明：</b>
                            <div>&emsp;
                                本工具仅为链上交易所UniSwap的接口, 所有资产并非在本平台内。
                            </div>
                            <div>&emsp;
                                请合法合规使用该工具，如您所在地相关法律法规不允许兑换相关通证、资产、等，请立即停止使用。
                            </div>
                            <div>&emsp;
                                区块链技术属高前沿性技术，存在各种无法预知的风险,
                                本平台不承担任何原因导致的您的资产损失
                            </div>
                            <div>
                                <a href="https://bafybeifvo6feafgzml423kyegp43hr7fss4il43hmdzxzlmdaapjcxr3nu.ipfs.dweb.link/">
                                    <Button color='teal' size='mini' style={{ marginBottom: '3px' }} >UNI官方APP</Button>
                                </a>
                            </div>
                        </div>
                    </div>
                </Card>

            </Card>
            <Fun ref={FunRef} />
            <LoadingReport ref={LoadingReportRef} />
        </div>
    )

}

const mapStateToProps = (state: any) => {
    return {
        ChainID: state.ChainID,
        UserAdr: state.UserAdr,
        LGbalance: state.LGbalance,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        sendStore(tp: any, val: any) {
            const action = { type: tp, value: val }
            dispatch(action)
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LockCreat)



