import React, { Component } from 'react'
import { connect } from "react-redux"
import { Toast } from 'antd-mobile'
import { List, Icon, Input, Popup, Button, Label } from 'semantic-ui-react'
import web3 from '../../../../ABI/web3';
import bridgeAbi from '../../../../ABI/Bridge.abi.json';
import { getChainInfo } from '../../../../components/global/functions';
import { Multicall } from 'ethereum-multicall';
import erc20abi from '../../../../ABI/ERC20.abi.json';
const BigNumber = require('bignumber.js');

export class OnceCode extends Component {
  constructor(props) {
    super(props)
    this.state = {
      bridgeAdr: '',
      onceBridgeCode: {
        FromChain: '',
        ToChain: '',
        _tokenAdr: '',
        who: '',
        time: '',
        value: '',
        password: '',
        FromChainName: '',
        ToChainName: '',
        Value: '', //整数
        tokenName: '',
      },
      multicallAdr: '',
      onceLoading: false,
      onceDisplay: 'none',
      inputData: ''
    }
  }

  async componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.ChainID !== this.props.ChainID
      || nextProps.UserAdr !== this.props.UserAdr
    ) {
      await this.first()
    }
  }

  async componentDidMount() {
    await this.first()
  }

  handleOnceCode = async () => {
    if (this.state.inputData === '') {
      return Toast.show({ content: '未输入跨链码' })
    }
    this.setState({ onceLoading: true })
    let code = this.state.inputData.split('&')
    if (code.length !== 7) {
      this.setState({ onceLoading: false })
      return Toast.show({ content: '跨链码不正确' })
    }
    let FromChain = '0x' + (Number(code[0])).toString(16)
    let ToChain = '0x' + (Number(code[1])).toString(16)
    let _FromChainName = await getChainInfo(FromChain)
    let FromChainName = _FromChainName.ChainName
    let _ToChainName = await getChainInfo(ToChain)
    let ToChainName = _ToChainName.ChainName
    if (ToChain !== this.props.ChainID) {
      this.setState({ onceLoading: false })
      return Toast.show({ content: `请切换至${ToChainName}链` })
    }
    const multicall = new Multicall({
      web3Instance: web3, tryAggregate: true,
      multicallCustomContractAddress: this.state.multicallAdr
    });
    //获取选中通证各信息
    const call1 = [{
      reference: 'onceCode',
      contractAddress: this.state.bridgeAdr,
      abi: bridgeAbi.abi,
      calls: [
        {
          reference: 'isUNlockToken',
          methodName: 'isUNlockToken',
          methodParameters: [code[6]]
        },
        {
          reference: 'tokenCurAdr',
          methodName: 'tokenAdr',
          methodParameters: [code[2], ToChain]
        },
        {
          reference: 'Owner',
          methodName: 'Owner',
          methodParameters: [code[2]]
        }
      ]
    }]
    const Call = await multicall.call(call1);
    let callResult = Call.results.onceCode.callsReturnContext
    if (callResult[0].returnValues[0] === true) {
      this.setState({ onceLoading: false })
      return Toast.show({ content: '该跨链码已发送' })
    }
    let owner = callResult[2].returnValues
    let isOwner = owner.some((x) => { return x.toLowerCase() === this.props.UserAdr.toLowerCase() })
    if (isOwner !== true) {
      this.setState({ onceLoading: false })
      return Toast.show({ content: '帐户非该通证操作员' })
    }
    const call2 = [{
      reference: 'tokenName',
      contractAddress: callResult[1].returnValues[0],
      abi: erc20abi.abi,
      calls: [
        {
          reference: 'symbol',
          methodName: 'symbol'
        },
        {
          reference: 'decimals',
          methodName: 'decimals'
        }
      ]
    }]
    const Call2 = await multicall.call(call2);
    let Call2Result = Call2.results.tokenName.callsReturnContext
    let tokenName = Call2Result[0].returnValues[0]
    let decimals = Call2Result[1].returnValues[0]
    let __Value = new BigNumber(code[5] * 10 ** (18 - decimals)).toFixed()
    let _Value = new BigNumber(web3.utils.fromWei(__Value, 'ether'))
    let Value = _Value.toFixed(_Value > 0.1 ? 3 : 8)
    this.setState({
      onceBridgeCode: {
        FromChain,
        ToChain,
        _tokenAdr: code[2],
        who: code[3],
        time: code[4],
        value: code[5],
        password: code[6],
        FromChainName,
        ToChainName,
        Value,
        tokenName,
      }
    })
    this.props.pushData(FromChain, ToChain, code[2], code[3], code[4], code[5],
      code[6], FromChainName, ToChainName, Value, tokenName)
    this.setState({ onceLoading: false, onceDisplay: 'block' })
  }

  getChain = async () => {
    await getChainInfo(window.ChainID).then((result) => {
      this.setState({
        bridgeAdr: result.ChainAdr.bridge,
        multicallAdr: result.ChainAdr.multicall,
      })
    })
  }

  delete = () => {
    this.setState({ onceDisplay: 'none',inputData: ''})
    this.props.pushData('')
  }

  first = async () => {
    await this.getChain()
    const bridgeContract = new web3.eth.Contract(bridgeAbi.abi, this.state.bridgeAdr)
    this.setState({ bridgeContract })
  }

  render() {
    let state = this.state
    let once = this.state.onceBridgeCode
    return (
      <div>
        <Popup
          trigger={
            <Input
              value={state.inputData}
              onChange={event => { this.setState({ inputData: event.target.value }) }}
              labelPosition='right'
              fluid
              placeholder='粘贴跨链码'
            >
              <Label onClick={() => this.delete()} >
                <Icon name='trash alternate outline' />
              </Label>
              <input />
              <Button loading={state.onceLoading} positive onClick={() => { this.handleOnceCode() }}
                style={{
                  borderRadius: '0 4px 4px 0',
                  marginRight: '0px'
                }}
                icon
              ><Icon name='search' /></Button>
            </Input>
          }
          content={this.props.popup}
          inverted
          on='focus'
        />

        <div style={{ display: state.onceDisplay, marginTop: '3px' }}>
          <List divided inverted relaxed style={{ color: 'white' }}>
            <List.Item>
              <nobr>
                <Icon name='circle' />{once.FromChainName}
                <Icon name='angle double right' />{once.ToChainName}
                <Icon name='hand point right outline' />{once.Value}{once.tokenName}
              </nobr>
            </List.Item>
          </List>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(OnceCode) 
