import React, { Component } from 'react'
import { connect } from "react-redux"
import { Card } from 'antd';
import { Toast } from 'antd-mobile'
import { Button, Icon, List } from 'semantic-ui-react'
import ChainList from "../../../components/ChainSelect"
import web3 from '../../../ABI/web3';
import bridgeAbi from '../../../ABI/BridgeV2.json';
import { getChainInfo } from '../../../components/global/functions';
import Loading from '../../../components/loadingReport';
import OnceCode from './components/OnceCode';
import erc20abi from '../../../ABI/ERC20.abi.json';
const BigNumber = require('bignumber.js');

export class bridgeSend extends Component {
  constructor(props) {
    super(props)
    this.state = {
      bridgeAdr: '',
      onceBridgeCode: {
        FromChain: '',
        ToChain: '',
        _tokenAdr: '',
        who: '',
        time: '',
        value: '',
        password: '',
        FromChainName: '',
        ToChainName: '',
        Value: '', //整数
        tokenName: '',
      },
      multicallAdr: '',
      loading: false
    }
  }

  async componentDidUpdate(nextProps, nextState) {
    if (nextProps.ChainID !== this.props.ChainID
      || nextProps.UserAdr !== this.props.UserAdr
    ) {
      this.setState({ loading: true })
      await this.first()
      this.setState({ loading: false })
    }
  }

  async componentDidMount() {
    this.setState({ loading: true })
    await this.first()
    this.setState({ loading: false })
  }

  pushData = async (FromChain, ToChain, _tokenAdr, who, time, value, password,
    FromChainName, ToChainName, Value, tokenName) => {
    this.setState({
      LGadr:'',
      onceBridgeCode: {
        FromChain,
        ToChain,
        _tokenAdr,
        who,
        time,
        value,
        password,
        FromChainName,
        ToChainName,
        Value,
        tokenName,
      }
    })
  }

  UNlockToken = async () => {
    await this.first()

    if (this.state.onceBridgeCode.FromChain === '') {
      return Toast.show({
        content:
          <div>请先点击<Icon name='search' /></div>
      })
    }

    if (this.props.isApprove === 'block') {
      return Toast.show({
        content: "请先<授权LG>'"
      })
    }

    const ERC20 = await new web3.eth.Contract(erc20abi.abi, this.state.LGadr)
    console.log(this.props.UserAdr)
    let _balance = await ERC20.methods.balanceOf(this.props.UserAdr).call()
    const tokenDecimals = await ERC20.methods.decimals().call()
    let balance = await new BigNumber(_balance / 10 ** (tokenDecimals)).toFixed()
    let feeLG = Number(this.props.feeLG).toFixed()
    console.log(feeLG)

    let LGbalance = Number(balance).toFixed()
    console.log(LGbalance)
    if (Number(feeLG) > Number(LGbalance) ) {
      return Toast.show({
        content: "您的LG余额不足以支付手续费"
      })
    }

    let code = this.state.onceBridgeCode
    await this.state.bridgeContract.methods.UNlockToken(
      code.FromChain,
      code.ToChain,
      code._tokenAdr,
      code.who,
      code.time,
      code.value,
      code.password
    ).send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', async (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！")
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }

  getChain = async () => {
    await getChainInfo(window.ChainID).then((result) => {
      this.setState({
        LGadr: result.ChainAdr.LG,
        bridgeAdr: result.ChainAdr.bridgeV2,
        multicallAdr: result.ChainAdr.multicall,
      })
    })
  }

  first = async () => {
    await this.getChain()
    const bridgeContract = new web3.eth.Contract(bridgeAbi, this.state.bridgeAdr)
    this.setState({ bridgeContract })

  }

  render() {
    let state = this.state
    let props = this.props
    return (
      <div>
        <Card type="inner" size='small' className='bridgeUser' style={{ maxWidth: '500px', margin: '0px auto auto auto' }}>
          <div style={{ textAlign: 'center', margin: '0 auto 5px auto' }}>
            <Button.Group >
              <Button > 接收</Button>
              <Button loading={state.loading} primary icon><Icon name='circle outline' /></Button>
              <Button>
                <ChainList />
              </Button>
            </Button.Group>
          </div>
          <OnceCode 
          pushData={this.pushData} popup='粘贴单笔跨链码' />

          {props.ApproveButton}

          <Button positive fluid onClick={() => { this.UNlockToken() }}
            style={{ marginTop: '6px', marginBottom: '5px' }}
          >接&nbsp;收</Button>
          <List divided inverted relaxed style={{ color: 'white', wordWrap: 'break-word', marginBottom: '5px' }}>
            <List.Item >
              <nobr>
                手续费：{this.props.feeLG}LG/笔；
              </nobr>
            </List.Item>
          </List>
        </Card>

        <Loading ref={loadwindow => { this.loading = loadwindow }} />
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(bridgeSend) 
