import React, { useState, useEffect, useCallback, useRef } from 'react'
import { Modal, List, ListItem, ListContent, ListHeader, ModalHeader, ModalContent, Input, Button, ButtonGroup } from 'semantic-ui-react'
import userAdrBookAbi from '../../../../../ABI/userAdrBook.json'
import { CELOadr } from '../../../../../components/global/globals'
import web3 from "../../../../../ABI/web3";
import { AbiItem } from 'ethereum-multicall/dist/esm/models';
import LoadingReport from '../../../../../components/loadingReport';
import { Toast } from 'antd-mobile'
import { useHistory } from "react-router-dom";
import { getChainInfo } from '../../../../../components/global/functions';
import lockV2ABI from "../../../../../ABI/LockV2.json";
import { getLockInfoList, getUserLocksID, getAllLocks } from './lockListFun';
import BigNumber from 'bignumber.js';

interface Props {
    open: boolean;
    onClose: () => void;
    userAdr: string;
    SendDataToParent: (list: any) => void;
    customerTel: string;
    customerAdr: string;
    setUserTELandAdr: (tel: string, Adr: string) => void;
    ChainID: any;
}


function CreatConfim({ open, onClose, userAdr, SendDataToParent, customerTel, customerAdr, setUserTELandAdr, ChainID }: Props) {

    const [tel, setTel] = useState<string>(customerTel);
    const [Adr, setAdr] = useState<string | undefined>(customerAdr);
    useEffect(() => {
        setTel(customerTel)
        setAdr(customerAdr)
    }, [customerAdr, customerTel]);

    const SetTel = (value: string) => {
        setTel(value)
    }
    const SetUserAdr = (value: string) => {
        setAdr(value)
    }

    const LoadingReportRef = useRef<any>(null);

    const [saveLoading, setSaveLoading] = useState(false);
    const saveUser = async () => {
        // 检查是否有重复的逻辑
        if (userBook.some(user => user.userTEL === tel)) {
            Toast.show({ content: '该备注名已存在于列表中' })
            return;
        }
        if (userBook.some(user => user.userAdr === Adr)) {
            Toast.show({ content: '该地址已存在于列表中' })
            return;
        }
        try {
            const userAdrBookContract = new web3.eth.Contract(userAdrBookAbi as AbiItem[], CELOadr.userAdrBook)
            await userAdrBookContract.methods.saveUser(tel, Adr).send({ from: userAdr, gas: '100000' })
                .on('transactionHash', (transactionHash: string | any[]) => {
                    if (transactionHash.length !== 0) {
                        setSaveLoading(true)
                    }
                })
                .on('receipt', (receipt: { status: boolean; }) => {
                    if (receipt.status === true) {
                        setSaveLoading(false)
                        readData()

                    }
                })
                .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                    if (receipt !== undefined && receipt.status === false) {
                        setSaveLoading(false)
                        LoadingReportRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                    }

                })
        } catch (error) {
            Toast.show({ content: '地址不合法' })
            console.log(error)
        }
    }


    const [delLoading, setDelLoading] = useState(false)
    const delUser = useCallback(async (id: number) => {
        try {
            const userAdrBookContract = new web3.eth.Contract(userAdrBookAbi as AbiItem[], CELOadr.userAdrBook)
            await userAdrBookContract.methods.delUser(id).send({ from: userAdr, gas: '100000' })
                .on('transactionHash', (transactionHash: string | any[]) => {
                    if (transactionHash.length !== 0) {
                        setDelLoading(true)
                    }
                })
                .on('receipt', (receipt: { status: boolean; }) => {
                    if (receipt.status === true) {
                        setDelLoading(false)
                        readData()
                    }
                })
                .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                    if (receipt !== undefined && receipt.status === false) {
                        setDelLoading(false)
                        LoadingReportRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                    }
                })
        } catch (error) {
            console.log(error)
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [userAdr])

    const [userBookList, setUserBookList] = useState<[]>([]);
    const [userBook, setUserBook] = useState<{
        userTEL: string,
        userAdr: string
    }[]>([])

    const history = useHistory();

    const toOtherUserList = useCallback((otherUserAdr: string, otherUserName: string) => {
        history.push(`/LockCeloOtherUser/${otherUserAdr}/${otherUserName}`);
    }, [history])

    const toBalance = useCallback((otherUserAdr: string,) => {
        history.push(`/LockCeloBalanceOtherUser/${otherUserAdr}`)
    }, [history])

    const readData = useCallback(async () => {
        try {
            const userAdrBookContract = new web3.eth.Contract(userAdrBookAbi as AbiItem[], CELOadr.userAdrBook)
            const result = await userAdrBookContract.methods.getUserRecords(userAdr).call({ from: userAdr })
            SendDataToParent(result)
            let list = result.map((item: any, index: any) => (
                <ListItem key={index}>
                    <ListContent>
                        <ListHeader>{item.userTEL}</ListHeader>
                        {item.userAdr}
                        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                            <Button icon size='mini' loading={delLoading} color='red' onClick={() => { delUser(index) }}>删除</Button>
                            <ButtonGroup icon size='mini'>
                                <Button icon size='mini' loading={delLoading} onClick={() => { toOtherUserList(item.userAdr, item.userTEL) }}>礼盒</Button>
                                <Button icon size='mini' loading={delLoading} onClick={() => { toBalance(item.userAdr) }}>余额</Button>
                            </ButtonGroup>
                            <Button icon size='mini' color='teal' onClick={() => { setUserTELandAdr(item.userTEL, item.userAdr) }}>选择</Button>
                        </div>

                    </ListContent>
                </ListItem>
            ));
            setUserBookList(list)
            setUserBook(result)
        } catch (error) {
            console.log(error)
        }

    }, [SendDataToParent, delLoading, delUser, setUserTELandAdr, toBalance, toOtherUserList, userAdr])

    const [multiClamLoading, setMultiClamLoading] = useState(false)
    const multiClamUser = useCallback(async () => {
        setMultiClamLoading(true)
        let usersAdr: string[] = []
        for (let i = 0; i < userBook.length; i++) {
            usersAdr.push(userBook[i].userAdr)
        }
        const chainInfo = await getChainInfo(ChainID)
        let unLockIDs: string[] = []
        for (let i = 0; i < usersAdr.length; i++) {
            let locksUserID = await getUserLocksID(usersAdr[i], ChainID);
            unLockIDs.push(...locksUserID)
        }
        console.log(usersAdr, unLockIDs)
        const lockListResult = await getLockInfoList(ChainID, unLockIDs);
        console.log(lockListResult)

        let canClamLock = []
        for (let i = 0; i < lockListResult.length; i++) {
            if (new BigNumber(lockListResult[i].remainTime).toNumber() === 0) {
                canClamLock.push(lockListResult[i].id)
            }
        }
        if (canClamLock.length === 0) {
            setMultiClamLoading(false)
            return Toast.show({ content: '没有到期的礼品盒' })
        }

        try {
            const lockV2Contract = new web3.eth.Contract(lockV2ABI as AbiItem[], chainInfo.ChainAdr.lockV2)
            await lockV2Contract.methods.multiClam(canClamLock).send({ from: userAdr, gas: `${canClamLock.length * 100000}` })
                .on('transactionHash', (transactionHash: string | any[]) => {
                    if (transactionHash.length !== 0) {
                        LoadingReportRef.current.loadingOpen('block', 'none', 'none', "开盒中……")
                    }
                })
                .on('receipt', (receipt: { status: boolean }) => {
                    if (receipt.status === true) {
                        LoadingReportRef.current.loadingOpen('none', 'block', 'none', `开盒全部完成`)
                    }
                })
                .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                    if (receipt !== undefined && receipt.status === false) {
                        LoadingReportRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                    }
                })
        } catch (error) {
            console.error("Error creating lock:", error);
        }
        setMultiClamLoading(false)
    }, [ChainID, userAdr, userBook])

    const [ClamAllLoading, setClamAllLoading] = useState(false)
    const ClamAllUser = useCallback(async () => {
        setClamAllLoading(true)
        const chainInfo = await getChainInfo(ChainID)
        const lockV2Contract = new web3.eth.Contract(lockV2ABI as AbiItem[], chainInfo.ChainAdr.lockV2)
        let id = await lockV2Contract.methods.id().call()

        const lockIDs = Array.from({ length: id }, (_, i) => (i + 1).toString());
        const lockListResult = await getLockInfoList(ChainID, lockIDs);

        let canClamLock = []
        for (let i = 0; i < lockListResult.length; i++) {
            if (new BigNumber(lockListResult[i].remainTime).toNumber() === 0) {
                canClamLock.push(lockListResult[i].id)
            }
        }
        if (canClamLock.length === 0) {
            setClamAllLoading(false)
            return Toast.show({ content: '没有到期的礼品盒' })
        }
        const firstCanClamLock = canClamLock.slice(0, Math.min(300, canClamLock.length));

        try {
            const lockV2Contract2 = new web3.eth.Contract(lockV2ABI as AbiItem[], chainInfo.ChainAdr.lockV2)
            await lockV2Contract2.methods.multiClam(firstCanClamLock).send({ from: userAdr, gas: `${firstCanClamLock.length * 100000}` })
                .on('transactionHash', (transactionHash: string | any[]) => {
                    if (transactionHash.length !== 0) {
                        LoadingReportRef.current.loadingOpen('block', 'none', 'none', "开盒中……")
                    }
                })
                .on('receipt', (receipt: { status: boolean }) => {
                    if (receipt.status === true) {
                        LoadingReportRef.current.loadingOpen('none', 'block', 'none', `开盒全部完成`)
                    }
                })
                .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                    if (receipt !== undefined && receipt.status === false) {
                        LoadingReportRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                    }
                })
        } catch (error) {
            console.error("Error creating lock:", error);
        }
        setClamAllLoading(false)
    }, [ChainID, userAdr])

    useEffect(() => {
        readData()
    }, [readData]);

    useEffect(() => {
        // 执行一些副作用，比如订阅事件或设置定时器
        // 返回一个清理函数，在组件卸载时执行
        return () => {
            // 取消订阅事件
            // subscription.unsubscribe();

            // 清除定时器
            // clearInterval(timer);
        };
    }, []);

    return (
        <>
            <Modal
                closeIcon
                size='mini'
                open={open}
                onClose={onClose}
                style={{ position: 'fixed', top: '100px' }}
            >
                <ModalHeader style={{ textAlign: 'center', fontSize: '1.2em', padding: '0.5em 0' }}>地址本</ModalHeader>
                <ModalContent>
                    <div style={{ display: 'flex' }}>
                        <Input fluid
                            action={{
                                color: 'blue',
                                labelPosition: 'left',
                                icon: 'pencil alternate',
                                content: '备注',
                            }}
                            actionPosition='left'
                            value={tel}
                            placeholder="输入用户备注"
                            style={{ marginBottom: '3px', flex: '1' }}
                            onChange={(e, { value }) => SetTel(value)}
                        />

                    </div>
                    <div style={{ display: 'flex' }}>
                        <Input fluid
                            action={{
                                color: 'blue',
                                labelPosition: 'left',
                                icon: 'address book',
                                content: '地址',
                            }}
                            actionPosition='left'
                            value={Adr}
                            placeholder="输入地址"
                            style={{ marginBottom: '3px', flex: '1' }}
                            onChange={(e, { value }) => SetUserAdr(value)}
                        />

                    </div>
                    <div style={{ display: 'flex' }}>
                        <Button fluid
                            loading={saveLoading}
                            color='teal'
                            style={{ width: '200px', margin: 'auto' }}
                            onClick={saveUser}
                        >添 加
                        </Button>
                    </div>
                    <List divided relaxed>
                        {userBookList}
                    </List>
                    <div style={{ display: 'flex' }}>
                        <Button fluid
                            loading={multiClamLoading}
                            color='teal'
                            style={{ width: '200px', margin: 'auto' }}
                            onClick={multiClamUser}
                        >替我的用户开所有礼品盒
                        </Button>
                    </div>
                    {/* {userAdr.toLowerCase() === '0x74ad724055e8f08ce781b9dbf9c96a747ae013be'&& */}
                        <div style={{ display: 'flex' , marginTop: '3px'}}>
                        <Button fluid
                            loading={ClamAllLoading}
                            color='teal'
                            style={{ width: '200px', margin: 'auto' }}
                            onClick={ClamAllUser}
                        >开所有礼品盒
                        </Button>
                    </div>
                     {/* } */}
                </ModalContent>
            </Modal>
            <LoadingReport ref={LoadingReportRef} />
        </>
    )

}

export default CreatConfim

