import React, { Component } from 'react'
import { getChainInfo } from '../../../../components/global/functions';
import { Dropdown } from 'semantic-ui-react'
import web3 from "../../../../ABI/web3"
import bridgeAbi from "../../../../ABI/BridgeV2.json"
import { connect } from "react-redux"
import erc20abi from '../../../../ABI/ERC20.abi.json';
import { Multicall } from 'ethereum-multicall';
import { Toast } from 'antd-mobile'
import tokenList from '../../../../tokenList/bridgeTokenList/index.json'

export class tokensSelect extends Component {
  constructor(props) {
    super(props)
    this.state = {
      bridgeAdr: '',
      LGadr: '',
      bridgeContract: '',
      currentCoin: global.MATIC.LG,
      multicallAdr: '',
      TOKENLIST: '',
      selectCoin: ''
    }
  }

  async componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.ChainID !== this.props.ChainID
      || nextProps.UserAdr !== this.props.UserAdr) {
      this.setState({ TOKENLIST: '' })
      this.first()
    }
  }

  async componentDidMount() {
    await this.first()
    this.props.sendStore("selectCoinOrAdr", this.state.currentCoin)
  }

  getChain = async () => {
    await getChainInfo(window.ChainID).then((result) => {
      this.setState({
        bridgeAdr: result.ChainAdr.bridgeV2,
        LGadr: result.ChainAdr.LG,
        multicallAdr: result.ChainAdr.multicall,
      })
    })
  }

  first = async () => {
    if (window.ChainID === null) {
      return Toast.show({ content: '未连接钱包' })
    }
    await this.getChain()
    if (this.state.bridgeAdr === undefined) {
      return Toast.show({ content: '当前链暂未部署跨链桥' })
    }
    const bridgeContract = new web3.eth.Contract(bridgeAbi, this.state.bridgeAdr)
    this.setState({ bridgeContract })
    this.TokenList()
  }

  TokenList = async () => {

    const allToken = await this.state.bridgeContract.methods.getallToken().call()
    console.log(allToken)
    console.log(this.state.bridgeAdr)
    //多呼
    const multicall = new Multicall({
      web3Instance: web3, tryAggregate: true,
      multicallCustomContractAddress: this.state.multicallAdr
    });
    //获取当前链下的各通证地址
    const calls1 = []
    for (let i = 0; i < allToken.length; i++) {
      const call = {
        reference: 'tokenAdr',
        methodName: 'tokenAdr',
        methodParameters: [allToken[i]]
      }
      calls1.push(call)
    }
    const CallCurChainTokenAdr = [{
      reference: 'Adr',
      contractAddress: this.state.bridgeAdr,
      abi: bridgeAbi,
      calls: calls1
    }]
    const curChainTokensAdr = await multicall.call(CallCurChainTokenAdr);
    let result1 = curChainTokensAdr.results.Adr.callsReturnContext
    const chainTokens = [];
    for (let i = 0; i < result1.length; i++) {
      chainTokens.push({
        orAdr: result1[i].methodParameters[0],
        curAdr: result1[i].returnValues[0],
        name: '',
        URI: ''
      })
    }

    // 获取通证列表的通证名字
    const Calls = []
    for (let i = 0; i < chainTokens.length; i++) {
      const call = {
        reference: i,
        orChainAdr: chainTokens[i].orAdr,
        contractAddress: chainTokens[i].curAdr,
        abi: erc20abi.abi,
        calls: [{
          reference: chainTokens[i],
          methodName: 'symbol'
        }]
      }
      Calls.push(call)
    }
    const names = await multicall.call(Calls);
    let namesReuslt = names.results
    for (let i in namesReuslt) {
      chainTokens[i].name = namesReuslt[i].callsReturnContext[0].returnValues[0]
    }

    let TokenList = tokenList.tokens  //本地存储的通证列表

    // 创建一个 Map，以 chainTokens 的 orAdr 作为键，TokenList 的 logoURI 作为值
    const tokenMap = new Map(TokenList.map(token => [token.address, token.logoURI]));
    for (let i = 0; i < chainTokens.length; i++) {
      const orAdr = chainTokens[i].orAdr;
      // 在 Map 中查找对应的 logoURI
      if (tokenMap.has(orAdr)) {
        chainTokens[i].URI = tokenMap.get(orAdr);
      }
    }

    let TOKENList = []
    for (let i = 0; i < chainTokens.length; i++) {
      let URI = chainTokens[i].URI
      const token = {
        key: i,
        flag: <img style={{ margin: '0px 5px 0px 0px' }}
          src={URI === '' ? `${process.env.PUBLIC_URL}/logo/unknow.svg` : `${process.env.PUBLIC_URL}/logo/${URI}`}
          alt='auto' width='auto' height='15px'
        />,
        text: chainTokens[i].name,
        value: chainTokens[i].orAdr
      }
      TOKENList.push(token)
    }
    this.setState({ TOKENLIST: TOKENList })
  }

  selectChang = async (value) => {
    this.props.sendStore("selectCoinOrAdr", value)

  }

  render() {
    return (
      <div>
        <Dropdown 
          // search
          defaultValue={this.state.currentCoin} options={this.state.TOKENLIST}
          onChange={(e, { value }) => this.selectChang(value)}
        />
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(tokensSelect) 
