import React, { Component } from 'react'
import { Menu, Button, Icon, Modal } from 'semantic-ui-react'
import { Link } from 'react-router-dom';

export class head extends Component {
  constructor(props) {
    super(props)
    this.state = {
      window: false
    }
  }


  render() {
    return (
      <div>
        <Menu inverted className='bridgeUser' style={{
          maxWidth: '500px', margin: '2px auto auto auto',
          background: '#171f31cc', borderRadius: '10px'
        }}>
          <Link to="/bridgeV2">
            <Menu.Item
              name='用户'
            />
          </Link>
          <Menu.Menu position='right' onClick={() => this.setState({ window: true })}>
            <Menu.Item
              name='后台'
            />
          </Menu.Menu>
        </Menu>

        <Modal
          basic
          open={this.state.window}
          size='mini'
          style={{ wordWrap: 'break-word' }}
        >
          <Modal.Content >
            <div style={{ textAlign: 'center' }}>
              后台仅用于通证运营方对通证的管理.<br />
              普通用户无需操作
            </div>
          </Modal.Content>
          <Modal.Actions style={{ float: 'right' }}>
            <Link to="/BridgeQueryV2">
              <Button color='green' inverted  >确定进入</Button>
            </Link>
            <Button basic color='red' inverted onClick={() => this.setState({ window: false })}>
              <Icon name='remove' /> 取消
            </Button>
          </Modal.Actions>
        </Modal>
      </div>
    )
  }
}

export default head
