import React, { Component } from 'react'
import '../../../../App.css';
import '../NFT.css';
import { Card, Button } from 'antd'
// import { EditOutlined, EllipsisOutlined, SettingOutlined } from '@ant-design/icons';
// const { Meta } = Card;

export class test3Card extends Component {
  constructor(props) {
    super(props)

    this.state = {
      a: this.props.a
    }
  }

  render() {
    return (
      <div>
        <Card size='small'
          style={{ background: 'rgba(3,173,144,0.3)', borderRadius: '10px', padding: '5px'}}
          bodyStyle={{ padding: '0px' }}
          bordered={false}
          hoverable={true}
          cover={
            <div style={{textAlign:'center'}}>
              <img
                style={{ background: 'rgb(3,173,144)', borderRadius: '7px',height:'250px',width:'auto'}}
                alt=""
                src="https://gateway.pinata.cloud/ipfs/Qmcqbo9HmTg3yMz6tzvs6m8rbuaNywM8WsZVPfmNEEtTFg"
              />
              </div>
          }
        >
          <div style={{ marginTop: '5px', textAlign: 'center' }}>
            链改网主页收藏
          </div>
          {/* <div style={{ textAlign: 'left' }}>
            介绍：链改网发展历程主页
          </div> */}
          <div style={{ float: 'center', color: 'rgba(3,173,144,1)' }}>售价：100LG</div>
          <div >
            <Button type='primary' style={{ float: 'left', borderRadius: '5px', borderColor: 'white' }} size='small'>竟价</Button>
            
            <Button type='primary' style={{ float: 'right', borderRadius: '5px', borderColor: 'white' }} size='small'>提货</Button>
            <div style={{ clear: 'both' }} />
          </div>
        </Card>
      </div>
    )
  }
}

export default test3Card
