import React, { useState, useEffect, useCallback } from 'react'; 
import { Image, Table, Card, Button, Header, Modal } from 'semantic-ui-react'; 
import { connect } from "react-redux"; 
import erc20abi from '../../../ABI/ERC20.abi.json'; 
import LGOracleABI from '../../../ABI/LGOracle.json'; 
import BigNumber from 'bignumber.js'; 
import { getChainInfo, ChangeChain } from '../../../components/global/functions'; 
import tokenList from '../../../tokenList/hangQinTokenList/index.json'; 
import { processPrice, createMulticall, AllToken } from './components/computer'; 
import PriceQuan from './components/priceQuxian';

// 定义本地价格的接口
interface LocalPrice { 
  prices: { address: string; price: number }[]; 
  time: number; 
}

// 定义组件
function Index(props: any) { 
  // 定义状态变量
  const [Tip, setTip] = useState('none'); 
  const [LocalPrice, setLocalPrice] = useState<LocalPrice>({ prices: [], time: 0 }); 
  const [List, setList] = useState<{ 
    address: string; 
    name: string; 
    URI: string; 
    balance: number; 
    originPrice: number; 
    price: number | string; 
    upDown: number 
  }[]>([]); 
  const [titleHour, setTitleHour] = useState<number>(1); 
  const [historyPrice, setHistoryPrice] = useState<{ 
    address: string; 
    price: number[]; 
    time: number[]; 
  }[]>([]); 
  const [priceModal, setPriceModal] = useState(false);

  // 切换链的方法
  const changeChain = useCallback(async () => { 
    const result = await getChainInfo(props.ChainID); 
    if (result.ChainName !== 'MATIC') { 
      await ChangeChain('MATIC'); 
    } 
  }, [props.ChainID]);

  // 组件挂载时执行
  useEffect(() => { 
    changeChain(); 
  }, [changeChain]);

    // 更新标题小时
    const updateTitleHour = () => { 
      const parsedData = getLocalPrice(); 
      if (parsedData && parsedData.time !== undefined) { 
        const time = getTime(); 
        const titleHour = Number(((time - parsedData.time) / 3600).toFixed(0)) + 1; 
        setTitleHour(titleHour); 
      } 
    };

  // 初始化列表和更新标题小时
  useEffect(() => { 
    const newItems = tokenList.tokens.map(token => ({ 
      address: token.address, 
      name: token.symbol, 
      URI: token.logoURI, 
      balance: 0, 
      originPrice: 0, 
      price: 0, 
      upDown: 0, 
    })); 
    setList(newItems); 
    updateTitleHour(); 
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);


  // 更新余额
  const updateBalance = useCallback(async () => { 
    const result = await getChainInfo(props.ChainID); 
    const alltoken = await AllToken(result.ChainAdr.LGOracle); 
    const multicall = createMulticall(result.ChainAdr.multicall); 
    const Calls = alltoken.map(token => ({ 
      reference: token, 
      contractAddress: token, 
      abi: erc20abi.abi, 
      calls: [{ 
        reference: token, 
        methodName: 'balanceOf', 
        methodParameters: [props.UserAdr] 
      }] 
    })); 
    const balance = await multicall.call(Calls); 
    const balances = balance.results; 
    const balanceList = Object.entries(balances).map(([key, value]) => ({ 
      key: key, 
      balance: value.callsReturnContext[0].returnValues[0] 
    }));

    // 更新列表中的余额
    setList(prevList => prevList.map(item => { 
      const balanceItem = balanceList.find(balanceListItem => balanceListItem.key === item.address); 
      if (balanceItem) { 
        const balance = new BigNumber(balanceItem.balance.hex).dividedBy(10 ** 18).toNumber().toFixed(2); 
        return { ...item, balance: Number(balance) }; 
      } 
      return item; 
    })); 
  }, [props.ChainID, props.UserAdr]);

  // 获取当前时间的时间戳
  const getTime = (): number => Math.floor(new Date().getTime() / 1000);

  // 更新价格
  const updatePrice = useCallback(async () => { 
    const time = getTime(); 
    const result = await getChainInfo(props.ChainID); 
    setTip(result.ChainName === 'MATIC' ? 'none' : 'block'); 
    if (result.ChainName !== 'MATIC') return;

    const alltoken = await AllToken(result.ChainAdr.LGOracle); 
    const multicall = createMulticall(result.ChainAdr.multicall); 
    const Calls = alltoken.map(token => ({ 
      reference: token, 
      methodName: 'PriceRMB', 
      methodParameters: [token] 
    })); 
    const priceCall = [{ 
      reference: 'PriceRMB', 
      contractAddress: result.ChainAdr.LGOracle, 
      abi: LGOracleABI, 
      calls: Calls 
    }]; 
    const _prices = await multicall.call(priceCall); 
    const prices = _prices.results.PriceRMB.callsReturnContext;

    const localPrices = getLocalPrice();

    // 更新列表中的价格和涨跌幅
    setList(prevList => prevList.map(item => { 
      const priceItem = prices.find(priceItem => priceItem.reference === item.address); 
      if (priceItem) { 
        const price = new BigNumber(priceItem.returnValues[0].hex).dividedBy(10 ** 8).toNumber(); 
        const priceStr = processPrice(price); 
        let upDown = 0; 
        if (localPrices) { 
          const localPrice = localPrices.prices.find((lp: { address: string; }) => lp.address === item.address); 
          if (localPrice) { 
            upDown = Number(((price / localPrice.price - 1) * 100).toFixed(2)); 
          } 
        } 
        return { ...item, originPrice: price, price: priceStr, upDown: upDown }; 
      } 
      return item; 
    }));

    // 更新本地价格
    if(time - localPrices.time >= 86400){
      updateLocalPrice(prices.map(priceItem => ({ 
        address: priceItem.reference, 
        price: new BigNumber(priceItem.returnValues[0].hex).dividedBy(10 ** 8).toNumber() 
      })), time);
    }else{
      console.log(time - localPrices.time)
    }
    
    // 更新历史价格
    setHistoryPrice(prevHistoryPrice => { 
      const updatedHistoryPrice = [...prevHistoryPrice]; 
      prices.forEach(priceItem => { 
        const price = new BigNumber(priceItem.returnValues[0].hex).dividedBy(10 ** 8).toNumber(); 
        const index = updatedHistoryPrice.findIndex(hist => hist.address === priceItem.reference); 
        if (index !== -1) { 
          updatedHistoryPrice[index].price.push(price); 
          updatedHistoryPrice[index].time.push(time); 
        } else { 
          updatedHistoryPrice.push({ 
            address: priceItem.reference, 
            price: [price], 
            time: [time], 
          }); 
        } 
      }); 
      return updatedHistoryPrice; 
    }); 
  }, [props.ChainID, setList]);

  // 获取本地存储的价格
  const getLocalPrice = () => { 
    const storedData = localStorage.getItem('localPrice'); 
    return storedData ? JSON.parse(storedData) : null; 
  };

  // 更新本地存储的价格
  const updateLocalPrice = (prices: { address: string; price: number }[], time: number) => { 
    // const localTime = getLocalPrice().time;
    // console.log(localTime)
    const newLocalPrice = { prices, time }; 
    setLocalPrice(newLocalPrice); 
    localStorage.setItem('localPrice', JSON.stringify(newLocalPrice)); 
  };

  // 组件挂载时更新价格和余额
  useEffect(() => { 
    updatePrice(); 
    updateBalance(); 
  }, [updatePrice, updateBalance]);

  // 涨跌样式
  const zhangStyle = { 
    color: 'white', 
    fontWeight: 900, 
    backgroundColor: 'green' 
  };

  // 跌样式
  const dieStyle = { 
    color: 'white', 
    fontWeight: 900, 
    backgroundColor: 'red' 
  };

  // 切换价格模态框的显示状态
  const turnPriceModal = () => setPriceModal(!priceModal);

  // 渲染表格行
  const renderTableRow = (item: any) => ( 
    <Table.Row key={item.address}> 
      <Table.Cell> 
        <Header as='h4' image> 
          <Image src={`${process.env.PUBLIC_URL}/logo/${item.URI}`} rounded style={{ width: '22px', height: '22px' }} />
          <Header.Content style={{ color: 'white' }}>
            {item.name}
          </Header.Content>
        </Header>
      </Table.Cell>
      <Table.Cell>{item.balance}</Table.Cell>
      <Table.Cell textAlign="right" onClick={turnPriceModal}>{item.price}</Table.Cell>
      <Table.Cell style={{ paddingTop: '2px', paddingBottom: '2px' }}>
        <Button icon onClick={turnPriceModal} floated='right' style={item.upDown >= 0 ? zhangStyle : dieStyle}>{item.upDown}%</Button>
      </Table.Cell>
    </Table.Row>
  );

  // 提示切换链的样式
  const style1: React.CSSProperties = {
    textAlign: 'center',
    color: 'red',
    display: Tip,
    fontSize: '20px'
  };

  return (
    <div>
      <div style={style1}>
        请切换至MATIC链
      </div>
      <Card type="inner" style={{ width: 'inherit', minWidth: '312px', maxWidth: '500px', margin: '0px auto auto auto', backgroundColor: '#6435c93d' }}>
        <div>
          <Table celled fixed singleLine unstackable color='violet' inverted>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell textAlign="center" style={{ width: '85px' }}>通证</Table.HeaderCell>
                <Table.HeaderCell>余额</Table.HeaderCell>
                <Table.HeaderCell textAlign="right" style={{ width: '80px' }}>价格</Table.HeaderCell>
                <Table.HeaderCell textAlign="right" style={{ width: '90px' }}>{titleHour}H↑↓</Table.HeaderCell>
              </Table.Row>
            </Table.Header>
            <Table.Body>
              {List.map((item) => renderTableRow(item))}
            </Table.Body>
          </Table>
        </div>

        <Modal
          style={{ maxWidth: '500px', margin: '80px auto auto auto' }}
          centered={false}
          open={priceModal}
          onClose={turnPriceModal}
          size='small'
          dimmer={false}
          content={
            <PriceQuan />
          }
        />
      </Card>
    </div>
  );
}

// 从 Redux state 中获取所需的数据
const mapStateToProps = (state: any) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
    LGbalance: state.LGbalance,
  };
};

// 发送 Redux action 的方法
const mapDispatchToProps = (dispatch: any) => {
  return {
    sendStore(tp: any, val: any) {
      const action = { type: tp, value: val };
      dispatch(action);
    }
  };
};

// 连接 Redux 并导出组件
export default connect(mapStateToProps, mapDispatchToProps)(Index);
