import React, { Component } from 'react'
import { connect } from "react-redux"
import { Card, Input } from 'antd';
import { Button,Divider } from 'semantic-ui-react'
import web3 from '../../../../ABI/web3';
import bridgeAbi from '../../../../ABI/Bridge.abi.json';
import { getChainInfo } from '../../../../components/global/functions';
import { Multicall } from 'ethereum-multicall';
import Loading from '../../../../components/loadingReport';
import ChainSelectIDto from '../../../../components/ChainSelect/chainSelectIDto'

export class bridgeAdmin extends Component {
  constructor(props) {
    super(props)
    this.state = {
      operatordisplay: 'no',
      bridgeAdr: '',
      LGadr: '',
      multicallAdr: '',
      bridgeContract: '',
      selectCoinOrAdr: '',
      curAdr: '',
      balance: '',
      tokenPayTime: '',
      minValue: '',
      firstOwner: '',
      Owner: '',
      ownerList: '',
      Timefee: '',
      buyMonth: '',
      ChangeMinValue: '0',
      minValueOpen: false,
      tokenPayTimeOpen: false,
      ChangeFirstOwner: '',
      ChangeFirstOwnerOpen: false,
      delOwerOpen: false,
      newOwner: '',
      FromChain: '',
      codeTime: '',
      codeValue: '',
      codePassword: '',
      ToChain: '',
      burnValue: '',
      ChainTo: '0x100',
      HQadr: '',
      admin: '',
      LG: '',
      passwordAdr: '',
      minFee: '',
      changMinFee: '',

      Creatfee: '',
      changCreatfee: '',
      timefee: '',
      changTimefee: '',
      fee: '',
      changFee: '',
      unUsedTokenAdr: '',
      tokenUsed: '?',
    }
  }

  async componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.ChainID !== this.props.ChainID
      || nextProps.UserAdr !== this.props.UserAdr
    ) {
      this.first()
    }
  }

  async componentDidMount() {
    this.first()
  }

  getChain = async () => {
    await getChainInfo(window.ChainID).then((result) => {
      this.setState({
        bridgeAdr: result.ChainAdr.bridge,
        multicallAdr: result.ChainAdr.multicall,
      })
    })
  }

  first = async () => {
    await this.getChain()
    const bridgeContract = new web3.eth.Contract(bridgeAbi.abi, this.state.bridgeAdr)
    this.setState({ bridgeContract })
    await this.getData()
  }

  getData = async () => {
    const multicall = new Multicall({
      web3Instance: web3, tryAggregate: true,
      multicallCustomContractAddress: this.state.multicallAdr
    });
    //获取选中通证各信息
    const Call1 = [{
      reference: 'bridge',
      contractAddress: this.state.bridgeAdr,
      abi: bridgeAbi.abi,
      calls: [
        {
          reference: 'HQadr',
          methodName: 'HQadr',
        },
        {
          reference: 'admin',
          methodName: 'admin',
        },
        {
          reference: 'LG',
          methodName: 'LG',
        },
        {
          reference: 'passwordAdr',
          methodName: 'passwordAdr',
        },
        {
          reference: 'minFee',
          methodName: 'minFee'
        },
        {
          reference: 'Creatfee',
          methodName: 'Creatfee'
        },
        {
          reference: 'timefee',
          methodName: 'timefee'
        }
      ]
    }]
    const bridgeCall = await multicall.call(Call1);
    let result = bridgeCall.results.bridge.callsReturnContext
    let HQadr = result[0].returnValues[0]
    let admin = result[1].returnValues[0]
    let LG = result[2].returnValues[0]
    let passwordAdr = result[3].returnValues[0]
    let minFee = parseInt(result[4].returnValues[0].hex, 16)
    let Creatfee = parseInt(result[5].returnValues[0].hex, 16)
    let timefee = parseInt(result[6].returnValues[0].hex, 16)
    this.setState({
      HQadr,
      admin,
      LG,
      passwordAdr,
      minFee,
      Creatfee,
      timefee
    })
  }

  chainSelectIDto = async (chainId) => {
    await this.setState({ ChainTo: chainId })
    this.Fee()
  }

  Fee = async () => {
    const fee = await this.state.bridgeContract.methods.Fee(this.state.ChainTo).call()
    this.setState({ fee })
  }

  setMinFee = async () => {
    await this.state.bridgeContract.methods.setMinFee(this.state.changMinFee)
    .send({ from: window.defaultAccount})
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！")
          this.first()
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }

  setCreatfee = async () => {
    await this.state.bridgeContract.methods.setCreatfee(this.state.changCreatfee)
    .send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！")
          this.first()
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }

  setTimefee = async () => {
    await this.state.bridgeContract.methods.setTimefee(this.state.changTimefee)
    .send({ from: window.defaultAccount})
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！")
          this.first()
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }

  setFee = async () => {
    await this.state.bridgeContract.methods.setFee(
      this.state.ChainTo,
      this.state.changFee
    ).send({ from: window.defaultAccount})
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！")
          this.Fee()
          this.first()
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }

  usedTokenAdr = async (adr) => {
    if (adr !== '') {
      const isUse = await this.state.bridgeContract.methods.usedTokenAdr(
        adr
      ).call()
      console.log(isUse);
      if (isUse === true) {
        this.setState({ tokenUsed: '已', isUseToken: adr })
      }
      if (isUse === false) {
        this.setState({ tokenUsed: '未', isUseToken: adr })
      }
    } else {
      this.setState({ tokenUsed: '?', isUseToken: adr })
    }
  }

  unUsedTokenAdr = async () => {
    await this.state.bridgeContract.methods.unUsedTokenAdr(
      this.state.isUseToken
    ).send({ from: window.defaultAccount})
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！")
          this.usedTokenAdr(this.state.isUseToken)
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }


  render() {
    let state = this.state
    return (
      <div>
        <Card type="inner" size='small' className='bridgeUser' >
          <Input id='i1' addonBefore='行情'
            value={state.HQadr} disabled
          />
          <Input id='i1' addonBefore='管理'
            value={state.admin} disabled
          />
          <Input id='i1' addonBefore='LG'
            value={state.LG} disabled
          />
          <Input id='i1' addonBefore='密码'
            value={state.passwordAdr} disabled
          />
          <Input id='i1' addonBefore='最小GAS'
            placeholder={'￥' + state.minFee + '分'}
            onChange={event => { this.setState({ changMinFee: event.target.value }) }}
            suffix={
              <Button size='mini' positive
                onClick={() => { this.setMinFee() }}
              >修改</Button>
            }
          />
          <Input id='i1' addonBefore='建新通证'
            placeholder={'￥' + state.Creatfee + '分'}
            onChange={event => { this.setState({ changCreatfee: event.target.value }) }}
            suffix={
              <Button size='mini' positive
                onClick={() => { this.setCreatfee() }}
              >修改</Button>
            }
          />
          <Input id='i1' addonBefore='每月收费'
            placeholder={'￥' + state.timefee + '分'}
            onChange={event => { this.setState({ changTimefee: event.target.value }) }}
            suffix={
              <Button size='mini' positive
                onClick={() => { this.setTimefee() }}
              >修改</Button>
            }
          />

          <Divider />
          <Input id='i1' addonBefore={<ChainSelectIDto chainSelectIDto={this.chainSelectIDto} />}
            placeholder={'(GAS费)￥' + state.fee + '分(默认100分)'}
            onChange={event => { this.setState({ changFee: event.target.value }) }}
            suffix={
              <Button size='mini' positive
                onClick={() => { this.setFee() }}
              >设定</Button>
            }
          />
          <Input id='i1'
            addonBefore={state.tokenUsed}
            placeholder='清除错误添加的TO合约地址'
            onChange={event => { this.usedTokenAdr(event.target.value) }}
            suffix={
              <Button size='mini' positive
              onClick={()=>this.unUsedTokenAdr()}
              >清除</Button>
            }
          />

        </Card>
        <Loading ref={loadwindow => { this.loading = loadwindow }} />
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
    LGbalance: state.LGbalance,
    selectCoinOrAdr: state.selectCoinOrAdr
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(bridgeAdmin) 
