import { useRef, useState } from 'react'
import { Modal, ModalHeader, ModalContent, Button, ModalActions, Input, Divider } from 'semantic-ui-react'
import web3 from "../../../../../ABI/web3";
import BigNumber from 'bignumber.js';
import { AbiItem } from 'ethereum-multicall/dist/esm/models';
import lockV2ABI from "../../../../../ABI/LockV2.json";
import { getChainInfo } from '../../../../../components/global/functions';
import LoadingReport from './LoadingOpen';
import LoadingReportGlobal from '../../../../../components/loadingReport';
import { Toast } from 'antd-mobile'
import { SecondToDay } from "../../../../../components/global/functionTS"

interface LockInfo {
    id: number
    tokenAdr: string
    symbol: string
    tokenLogo: string
    balance: number
    valueRMB: number
    remainTime: number
    unlockTime: number
    betweenTime: number
    onceAmount: number
    user: string
}

interface Props {
    ChainID: string;
    open: boolean;
    onClose: () => void;
    lockInfo: LockInfo;
    userAdr: string;
    refreshLockList: (ChainID: string, userAdr: string) => void;
    isHome: boolean;
    operatorAdr: string;
    isOtherUser: boolean;
    isUser:boolean
}

function LockInfoWindow({ userAdr, ChainID, open, onClose, lockInfo, refreshLockList, isHome, isOtherUser, operatorAdr,isUser }: Props) {
    const clam = async () => {
        if (lockInfo.remainTime > 0) { return Toast.show({ content: '未到期' }) }
        try {
            const chainInfo = await getChainInfo(ChainID)
            const lockV2Contract = new web3.eth.Contract(lockV2ABI as AbiItem[], chainInfo.ChainAdr.lockV2)
            await lockV2Contract.methods.clam(lockInfo.id).send({ from: isOtherUser === true ? operatorAdr : userAdr, gas: '100000' })

                .on('transactionHash', (transactionHash: string | any[]) => {
                    if (transactionHash.length !== 0) {
                        onClose()
                        LoadingReportRef.current.loadingOpen('block', 'none', 'none', "开盒中……")
                    }
                })
                .on('receipt', (receipt: { status: boolean; events: any; }) => {
                    if (receipt.status === true) {
                        refreshLockList(ChainID, userAdr)
                        let receiptValue = web3.utils.fromWei(receipt.events[1].raw.data, 'ether')
                        LoadingReportRef.current.loadingOpen('none', 'block', 'none', isOtherUser === true ? `开盒完成，用户已收到${receiptValue}` : `开盒完成，收到${receiptValue}`)
                    }
                })
                .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                    if (receipt !== undefined && receipt.status === false) {
                        LoadingReportRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                    }
                })

        } catch (error) {
            console.error("Error creating lock:", error);

        }
    }

    const newUser = async () => {
        try {
            const chainInfo = await getChainInfo(ChainID)
            const lockV2Contract = new web3.eth.Contract(lockV2ABI as AbiItem[], chainInfo.ChainAdr.lockV2)
            await lockV2Contract.methods.newUser(lockInfo.id, newUserAdr).send({ from: userAdr, gas: '100000' })

                .on('transactionHash', (transactionHash: string | any[]) => {
                    if (transactionHash.length !== 0) {
                        onClose()
                        LoadingReportGlobalRef.current.loadingOpen('block', 'none', 'none', "打包中……")
                    }
                })
                .on('receipt', (receipt: { status: boolean; events: any; }) => {
                    if (receipt.status === true) {
                        refreshLockList(ChainID, userAdr)
                        LoadingReportGlobalRef.current.loadingOpen('none', 'block', 'none', "转让成功")
                    }
                })
                .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                    if (receipt !== undefined && receipt.status === false) {
                        LoadingReportGlobalRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                    }
                })

        } catch (error) {
            console.error("Error creating lock:", error);

        }
    }

    const LoadingReportRef = useRef<any>(null);
    const LoadingReportGlobalRef = useRef<any>(null);


    const [newUserAdr, setNewUserAdr] = useState<string>('');
    const [newUserPage, setNewUserPage] = useState<boolean>(false);



    return (
        <>
            <Modal
                size='mini'
                open={open}
                onClose={onClose}
                closeIcon
                style={{ position: 'fixed', top: '180px' }}
            >
                <ModalHeader style={{ fontSize: '15px', textAlign: 'center' }}>
                    礼品盒详情
                </ModalHeader>
                <ModalContent style={{ padding: '10px' }}>
                    <div>礼品通证：{lockInfo.symbol}</div>
                    <div>礼盒余量：{lockInfo.balance}</div>
                    <div>礼盒余值：{lockInfo.valueRMB}</div>
                    <div>单次领取：{lockInfo.onceAmount}</div>
                    <div>间隔时间：{SecondToDay((new BigNumber(lockInfo.betweenTime).toNumber()))}</div>
                    <div>可领倒计：{SecondToDay((lockInfo.remainTime))}</div>
                    <div style={{ wordBreak: 'break-all' }}>用户地址：{lockInfo.user}</div>
                </ModalContent>
                {isHome !== true && <ModalActions style={{
                    display: 'flex', justifyContent: 'space-between', padding: '0.5em 0.5em', alignItems: 'center'
                }}>
                    <Button onClick={onClose} >
                        取消
                    </Button>
                    <Button positive
                        onClick={clam}
                    >
                        领 取
                    </Button>
                </ModalActions>
                }
                {isUser === true && <div>
                    <Divider horizontal onClick={() => newUserPage === false ? setNewUserPage(true) : setNewUserPage(false)}>转让</Divider>

                    {newUserPage === true && <div style={{ display: 'flex' }}>

                        <Input fluid
                            value={newUserAdr}
                            placeholder="输入新用户地址"
                            style={{ marginBottom: '3px', flex: '1' }}
                            onChange={(e, { value }) => setNewUserAdr(value)}
                        />
                        <Button positive onClick={newUser}>转让</Button>
                    </div>
                    }
                </div>
                }
            </Modal>
            <LoadingReport ref={LoadingReportRef} />
            <LoadingReportGlobal ref={LoadingReportGlobalRef} />
        </>
    )

}

export default LockInfoWindow;




