import React, { useState, useEffect, useCallback } from 'react';
import { Card, Input, Button } from 'antd';
import '../../App.css';
import { connect } from "react-redux"
import Logo from '../../logo192.png';

function Top(props: any) {

  const opUser = useCallback(async () => {
    if (props.UserAdr && props.LGbalance) {
      const _user = props.UserAdr;
      let userLength = _user.length
      let user = _user.substring(0, 6) + '……' + _user.substring(userLength - 4);
      setUser(user)
      setOringUser(_user)
      if (Number(props.LGbalance) < 1000) {
        setColor('#001e78')
      }
      if (Number(props.LGbalance) > 1000) {
        setColor('#5fac40')
      }
      if (Number(props.LGbalance) > 10000) {
        setColor('#e8c036')
      }

    }
  }, [props.LGbalance, props.UserAdr])

  const changUser = () => {
    setUser(oringUser)
  }
  const [User, setUser] = useState('?')
  const [oringUser, setOringUser] = useState('')
  const [Color, setColor] = useState('#001e78')

  useEffect(() => {
    opUser()
  }, [opUser])

  return (
    <>
      <Card type="inner" size='small' style={{
        borderRadius: '21px',
        background: Color
      }}
        bodyStyle={{ padding: '5px 6px 5px 6px' }}>
        <div style={{ display: 'flex' }}>
          <Button style={{ float: 'left', width: '27%', borderRadius: '16px 0 0 16px' }}
            onClick={props.reIn}
          >
            {props.ChainName}
          </Button>
          <Input value={User} onClick={changUser} />

          <Button style={{
            float: 'right', width: '27%', borderRadius: '0 16px 16px 0 ',
            paddingLeft: '3px',
          }}
            onClick={props.reIn}
          >
            <img style={{ maxWidth: 'auto', height: '23px' }} src={Logo} alt="" />
            {props.LGbalance}
          </Button>
        </div>
      </Card>
    </>
  )

}


const mapStateToProps = (state: { ChainID: any; UserAdr: any; LGbalance: any; ChainName: any; }) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
    LGbalance: state.LGbalance,
    ChainName: state.ChainName
  }
}

const mapDispatchToProps = (dispatch: (arg0: { type: any; value: any; }) => void) => {
  return {
    sendStore(tp: any, val: any) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Top)