import web3more from '../../ABI/web3more';
import web3 from '../../ABI/web3';
import erc20abi from '../../ABI/ERC20.abi.json';
const BigNumber = require('bignumber.js');

export const ChangeChain = async (id) => {
  switch (id) {
    case 'HECO':
      await window.ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{
          chainId: '0x80'
        }],
      })
      return ["HECO", '0x80', '']

    case 'HECOT':
      await window.ethereum.request({
        method: 'wallet_addEthereumChain',
        params: [{
          chainId: '0x100',
          chainName: 'HECOT',
          nativeCurrency: {
            name: 'HT',
            symbol: 'HT',
            decimals: 18,
          },
          rpcUrls: ['https://http-testnet.hecochain.com'],
          blockExplorerUrls: ['https://testnet.hecoinfo.com/'],
        }],
      });
      return ["HECOT", '0x100', '']

    case 'CELO':
      try {
        await window.ethereum.request({
          method: 'wallet_switchEthereumChain',
          params: [{
            chainId: '0xa4ec'
          }],
        })
        return ["CELO", '0xa4ec', '']
      } catch (error) { }
      await window.ethereum.request({
        method: 'wallet_addEthereumChain',
        params: [{
          chainId: '0xa4ec',
          chainName: 'CELO',
          nativeCurrency: {
            name: 'CELO',
            symbol: 'CELO',
            decimals: 18,
          },
          rpcUrls: ['https://1rpc.io/celo', 'https://rpc.ankr.com/celo', 'https://forno.celo.org'],
          blockExplorerUrls: ['https://celoscan.io'],
        }],
      });
      return ["CELO", '0xa4ec', '']

    case 'BSC':
      await window.ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{
          chainId: '0x38'
        }],
      });
      return ["BSC", '0x38', '']

    case 'BSCT':
      await window.ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{
          chainId: '0x61'
        }],
      });
      return ["BSCT", '0x38', '']

    case 'OEC':
      await window.ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{
          chainId: '0x42'
        }],
      });
      return ["OEC", '0x38', '']

    case 'OECT':
      await window.ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{
          chainId: '0x41',
        }],
      });
      return ["OECT", '0x41', '']

    case 'FIL':
      await window.ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{
          chainId: '0x13a',
        }],
      });
      return ["FIL", '0x13a', '']

    case 'MATIC':
      await window.ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{
          chainId: '0x89',
        }],
      });
      return ["MATIC", '0x89', '']

    default:
      break;
  }
}



export const SelectWeb3 = async (chainid) => {
  let CurWeb3
  switch (chainid) {
    case '0x80': CurWeb3 = web3more.web3HECO; break;
    case '0x38': CurWeb3 = web3more.web3BSC; break;
    case '0x42': CurWeb3 = web3more.web3OEC; break;
    case '0x100': CurWeb3 = web3more.web3HECOT; break;
    case '0x61': CurWeb3 = web3more.web3BSCT; break;
    case '0x41': CurWeb3 = web3more.web3OECT; break;
    case '0x89': CurWeb3 = web3more.web3MATIC; break;
    case '0x13a': CurWeb3 = web3more.web3FIL; break;
    case '0xa4ec': CurWeb3 = web3more.web3CELO; break;
    default:
      break;
  }
  return CurWeb3
}

export const isApprove = async (tokenAdr, spender) => {
  try {
    const ERC20 = new web3.eth.Contract(erc20abi.abi, tokenAdr)
    const _allowance = await ERC20.methods.allowance(window.defaultAccount, spender).call();
    const allowance = new BigNumber(web3.utils.fromWei(_allowance, 'ether')).toFixed(0);
    if (allowance < 100000000) {
      return 'no'
    } else {
      return 'yes'
    }
  } catch (error) {
    console.log(error)
  }

}

export const getChainInfo = async (chainid) => {
  let ChainName
  let ChainID
  let ChainAdr
  switch (chainid) {
    case '0x1': ChainName = 'ETH'; ChainID = '0x1'; ChainAdr = global.ETH; break;
    case '0x80': ChainName = 'HECO'; ChainID = '0x80'; ChainAdr = global.HECO; break;
    case '0x38': ChainName = 'BSC'; ChainID = '0x38'; ChainAdr = global.BSC; break;
    case '0x42': ChainName = 'OEC'; ChainID = '0x42'; ChainAdr = global.OEC; break;
    case '0x89': ChainName = 'MATIC'; ChainID = '0x89'; ChainAdr = global.MATIC; break;
    case '0xa': ChainName = 'OP'; ChainID = '0xa'; ChainAdr = global.OP; break;
    case '0xa4b1': ChainName = 'ARB'; ChainID = '0xa4b1'; ChainAdr = global.ARB; break;
    case '0xa4ec': ChainName = 'CELO'; ChainID = '0xa4ec'; ChainAdr = global.CELO; break;
    case '0x13a': ChainName = 'FIL'; ChainID = '0x13a'; ChainAdr = global.FIL; break;

    case '0x100': ChainName = 'HECOT'; ChainID = '0x100'; ChainAdr = global.HECOT; break;
    case '0x61': ChainName = 'BSCT'; ChainID = '0x61'; ChainAdr = global.BSCT; break;
    case '0x41': ChainName = 'OECT'; ChainID = '0x41'; ChainAdr = global.OECT; break;
    case undefined : ChainName = '刷新/重进'; ChainID = chainid; ChainAdr = '';break;
    default: ChainName = '不支持该链'; ChainID = chainid; ChainAdr = '';  break;
    
  }
  return { 'ChainName': ChainName, 'ChainID': ChainID, 'ChainAdr': ChainAdr }
}

export const getLGbalance = async () => {
  try {
    let LGadr
    let balance
    await getChainInfo(window.ChainID).then((result) => {
      LGadr = result.ChainAdr.LG
    })
    const ERC20 = new web3.eth.Contract(erc20abi.abi, LGadr)
    const _balance = await ERC20.methods.balanceOf(window.defaultAccount).call()
    balance = parseFloat(web3.utils.fromWei(_balance, "ether")).toFixed(0)
    return balance
  } catch (error) {
    console.log(error)
  }
}

