import React, { Component } from 'react'
import { connect } from "react-redux"
import { Card } from 'antd';
import { Modal, Input, Button, List, Popup, Table, Divider, Icon } from 'semantic-ui-react'
import Head from './components/headAdmin';
import { Link } from 'react-router-dom';
import ChainList from "../../../components/ChainSelect"
import TokensSelect from "./components/tokensSelect"
import { Toast } from 'antd-mobile'
import web3 from '../../../ABI/web3';
import bridgeAbi from '../../../ABI/Bridge.abi.json';
import { getChainInfo } from '../../../components/global/functions';
import { Multicall } from 'ethereum-multicall';
import erc20abi from '../../../ABI/ERC20.abi.json';
import Loading from '../../../components/loadingReport';
import OnceCode from './components/OnceCode';
import ChainSelectIDto from '../../../components/ChainSelect/chainSelectIDto'
import "./components/bridge.css"
const BigNumber = require('bignumber.js');

export class bridgeManage extends Component {
  constructor(props) {
    super(props)

    this.state = {
      operatordisplay: 'no',
      bridgeAdr: '',
      LGadr: '',
      multicallAdr: '',
      bridgeContract: '',
      selectCoinOrAdr: '',
      curAdr: '',
      curTokenName: '',
      balance: '',
      tokenPayTime: '',
      minValue: '',
      firstOwner: '',
      ownerList: '',
      Timefee: '',
      buyMonth: '',
      changMinValue: '0',
      minValueOpen: false,
      tokenPayTimeOpen: false,
      changFirstOwner: '',
      changFirstOwnerOpen: false,
      delOwerOpen: false,
      burnModalOpen: false,
      newOwner: '',
      FromChain: '',
      codeTime: '',
      codeValue: '',
      codePassword: '',
      ToChain: '',
      ToChainName: '',
      burnValue: '',
      onceBridgeCode: {
        FromChain: '',
        ToChain: '',
        _tokenAdr: '',
        who: '',
        time: '',
        value: '',
        password: '',
        FromChainName: '',
        ToChainName: '',
        Value: '', //整数
        tokenName: '',
      },
      loading: false
    }
  }

  async componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.ChainID !== this.props.ChainID
      || nextProps.UserAdr !== this.props.UserAdr
      || nextProps.selectCoinOrAdr !== this.props.selectCoinOrAdr
    ) {
      if (nextProps.selectCoinOrAdr !== '' && nextProps.selectCoinOrAdr !== undefined) {
        this.setState({ loading: true })
        await this.first(nextProps.selectCoinOrAdr)
        this.setState({ loading: false })
      }
    }
    console.log(nextProps.selectCoinOrAdr)
  }

  async componentDidMount() {
    if (this.props.selectCoinOrAdr !== '' && this.props.selectCoinOrAdr !== undefined) {
      this.setState({ loading: true })
      await this.first(this.props.selectCoinOrAdr)
      this.setState({ loading: false })
    }
  }

  getChain = async () => {
    await getChainInfo(window.ChainID).then((result) => {
      this.setState({
        bridgeAdr: result.ChainAdr.bridge,
        LGadr: result.ChainAdr.LG,
        multicallAdr: result.ChainAdr.multicall,
      })
    })
  }

  first = async (selectOrAdr) => {
    await this.getChain()
    const bridgeContract = new web3.eth.Contract(bridgeAbi.abi, this.state.bridgeAdr)
    this.setState({ bridgeContract })
    await this.getData(selectOrAdr)
  }

  getData = async (selectOrAdr) => {
    this.setState({
      selectCoinOrAdr: selectOrAdr,
      curAdr: '',
      balance: '',
      tokenPayTime: '',
      minValue: '',
      firstOwner: '',
      ownerList: '',
      Timefee: ''
    })
    const multicall = new Multicall({
      web3Instance: web3, tryAggregate: true,
      multicallCustomContractAddress: this.state.multicallAdr
    });
    //获取选中通证各信息
    const Call1 = [{
      reference: 'bridge',
      contractAddress: this.state.bridgeAdr,
      abi: bridgeAbi.abi,
      calls: [
        {
          reference: 'curAdr',
          methodName: 'tokenAdr',
          methodParameters: [selectOrAdr, this.props.ChainID]
        },
        {
          reference: 'tokenPayTime',
          methodName: 'TokenPayTime',
          methodParameters: [selectOrAdr]
        },
        {
          reference: 'minValue',
          methodName: 'minValue',
          methodParameters: [selectOrAdr]
        },
        {
          reference: 'firstOwner',
          methodName: 'firstOwner',
          methodParameters: [selectOrAdr]
        },
        {
          reference: 'Owner',
          methodName: 'Owner',
          methodParameters: [selectOrAdr]
        },
        {
          reference: 'timefee',
          methodName: 'timefee'
        }
      ]
    }]
    const bridgeCall = await multicall.call(Call1);
    let result = bridgeCall.results.bridge.callsReturnContext
    let curAdr = result[0].returnValues[0]
    let _tokenPayTime = result[1].returnValues[0].hex
    let tokenPayTime = (_tokenPayTime / 86400).toFixed(1) + '天'
    let _minValue = result[2].returnValues[0].hex
    let minValue = new BigNumber(web3.utils.fromWei(_minValue, 'ether')).toFixed(3)
    let firstOwner = result[3].returnValues[0]
    let Owner = result[4].returnValues
    let timefee = result[5].returnValues[0].hex
    let _Timefee = await this.state.bridgeContract.methods.feeLG(timefee).call()
    let Timefee = new BigNumber(web3.utils.fromWei(_Timefee, 'ether')).toFixed(2);
    const ERC20 = new web3.eth.Contract(erc20abi.abi, curAdr)
    const _balance = await ERC20.methods.balanceOf(this.state.bridgeAdr).call();
    const balance = new BigNumber(web3.utils.fromWei(_balance, 'ether')).toFixed(2);
    const ownerList = []
    for (let i = 0; i < Owner.length; i++) {
      ownerList.push(
        <List.Item >
          {Owner[i]}
          <Button negative size='mini' floated='right'
            onClick={() => { this.delOwer(Owner[i], Owner.length) }}
          >删除</Button>
        </List.Item>
      )
    }
    this.setState({
      selectCoinOrAdr: selectOrAdr,
      curAdr,
      balance,
      tokenPayTime,
      minValue,
      firstOwner,
      ownerList,
      Timefee
    })
  }

  //操作员页面开关
  operator = () => {
    if (this.state.operatordisplay === 'no') {
      this.setState({ operatordisplay: 'yes' })
    } else {
      this.setState({ operatordisplay: 'no' })
    }
  }

  //购买租期
  BuyTokenTime = async () => {
    if (this.state.buyMonth < 1) {
      return Toast.show({ content: '最少输入1' })
    }
    if (this.props.UserAdr.toLowerCase() !== this.state.firstOwner.toLowerCase()) {
      return Toast.show({ content: '仅该通证管理员可操作' })
    }
    await this.state.bridgeContract.methods.BuyTokenTime(
      this.state.selectCoinOrAdr,
      this.state.buyMonth
    ).send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', async (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！")
          let TokenPayTime = await this.state.bridgeContract.methods.TokenPayTime(
            this.state.selectCoinOrAdr).call();
          let tokenPayTime = (TokenPayTime / 86400).toFixed(1) + '天'
          this.setState({ tokenPayTimeOpen: false, tokenPayTime })
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }

  //调整最小跨链量
  setMinValue = async () => {
    let changMinValue = web3.utils.toWei(this.state.changMinValue, 'ether')
    if (this.state.changMinValue === '0') {
      return Toast.show({ content: '未输入数量' })
    }
    if (this.props.UserAdr.toLowerCase() !== this.state.firstOwner.toLowerCase()) {
      return Toast.show({ content: '仅该通证管理员可操作' })
    }
    await this.state.bridgeContract.methods.setMinValue(
      this.state.selectCoinOrAdr,
      changMinValue
    ).send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', async (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！")
          let _minValue = await this.state.bridgeContract.methods.minValue(
            this.state.selectCoinOrAdr).call();
          let minValue = new BigNumber(web3.utils.fromWei(_minValue, 'ether')).toFixed(3)
          this.setState({ tokenPayTimeOpen: false, minValue })
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }

  //换通证管理员
  transferFirstOwner = async () => {
    if (this.state.changFirstOwner === '') {
      return Toast.show({ content: '未输入地址' })
    }
    if (this.props.UserAdr.toLowerCase() !== this.state.firstOwner.toLowerCase()) {
      return Toast.show({ content: '仅该通证管理员可操作' })
    }
    await this.state.bridgeContract.methods.transferFirstOwner(
      this.state.selectCoinOrAdr,
      this.state.changFirstOwner
    ).send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', async (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！")
          let firstOwner = await this.state.bridgeContract.methods.firstOwner(
            this.state.selectCoinOrAdr).call();
          this.setState({ changFirstOwnerOpen: false, firstOwner })
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }

  //删除操作员
  delOwer = async (oldOwner, OwnerNum) => {
    if (this.props.UserAdr.toLowerCase() !== this.state.firstOwner.toLowerCase()) {
      return Toast.show({ content: '仅该通证管理员可操作' })
    }
    if (OwnerNum <= 1) {
      return Toast.show({ content: '至少保留一名' })
    }

    await this.state.bridgeContract.methods.delOwer(
      this.props.ChainID,
      this.state.selectCoinOrAdr,
      oldOwner
    ).send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', async (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！")
          this.setState({ delOwerOpen: false })
          this.first(this.props.selectCoinOrAdr)
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }
  //添加操作员
  addOwer = async () => {
    if (this.props.UserAdr.toLowerCase() !== this.state.firstOwner.toLowerCase()) {
      return Toast.show({ content: '仅该通证管理员可操作' })
    }
    await this.state.bridgeContract.methods.addOwer(
      this.props.ChainID,
      this.state.selectCoinOrAdr,
      this.state.newOwner
    ).send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', async (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！")
          this.first(this.props.selectCoinOrAdr)
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }

  //链码生成
  mintToken = async () => {
    let code = this.state.onceBridgeCode
    if (code.FromChain === '') {
      return Toast.show({
        content:
          <div>请先点击<Icon name='search' /></div>
      })
    }
    if (this.props.UserAdr.toLowerCase() !== this.state.firstOwner.toLowerCase()) {
      return Toast.show({ content: '仅该通证管理员可操作' })
    }
    if (this.props.selectCoinOrAdr.toLowerCase() !== code._tokenAdr.toLowerCase()) {
      return Toast.show({ content: '通证选择不正确' })
    }
    await this.state.bridgeContract.methods.mintToken(
      code.FromChain,
      code.ToChain,
      code._tokenAdr,
      code.time,
      code.value,
      code.password
    ).send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', async (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！该笔通证已发送至您的帐户")
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }

  //跨链销毁
  burnToken = async () => {
    if (this.props.UserAdr.toLowerCase() !== this.state.firstOwner.toLowerCase()) {
      return Toast.show({ content: '仅该通证管理员可操作' })
    }
    if (this.state.burnValue === '') {
      return Toast.show({ content: '未输入数量' })
    }
    if (this.state.ToChain === '') {
      return Toast.show({ content: '未选择目标链' })
    }
    if (this.props.ChainID === this.state.ToChain) {
      return Toast.show({ content: '目标链不能与当前链相同' })
    }
    let curAdr = await this.state.bridgeContract.methods.tokenAdr(
      this.props.selectCoinOrAdr, this.props.ChainID
    ).call()
    const ERC20 = await new web3.eth.Contract(erc20abi.abi, curAdr);
    const decimals = await ERC20.methods.decimals().call();
    let value = new BigNumber(this.state.burnValue / 10 ** (18 - decimals)) + ''
    await this.state.bridgeContract.methods.burnToken(
      this.props.ChainID,
      this.state.ToChain,
      this.state.selectCoinOrAdr,
      web3.utils.toWei(value, 'ether')
    ).send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', async (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！跨链码请至跨链桥首页查询")
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })
  }

  pushData = async (FromChain, ToChain, _tokenAdr, who, time, value, password,
    FromChainName, ToChainName, Value, tokenName) => {
    this.setState({
      onceBridgeCode: {
        FromChain,
        ToChain,
        _tokenAdr,
        who,
        time,
        value,
        password,
        FromChainName,
        ToChainName,
        Value,
        tokenName,
      }
    })
  }

  chainSelectIDto = async (chainId) => {
    await getChainInfo(chainId).then((result) => {
      this.setState({
        ToChainName: result.ChainName
      })
    })
    this.setState({ ToChain: chainId })
  }

  burnModalOpen = async () => {
    if (this.props.UserAdr.toLowerCase() !== this.state.firstOwner.toLowerCase()) {
      return Toast.show({ content: '仅该通证管理员可操作' })
    }
    if (this.state.burnValue === '') {
      return Toast.show({ content: '未输入数量' })
    }
    if (this.state.ToChain === '') {
      return Toast.show({ content: '未选择目标链' })
    }
    if (this.props.ChainID === this.state.ToChain) {
      return Toast.show({ content: '目标链不能与当前链相同' })
    }
    const ERC20 = await new web3.eth.Contract(erc20abi.abi, this.state.curAdr);
    let curTokenName = await ERC20.methods.symbol().call();
    this.setState({ curTokenName, burnModalOpen: true })
  }


  render() {
    let state = this.state
    return (
      <div>
        <Head />
        <Card type="inner" size='small' className='bridgeUser' style={{ maxWidth: '500px', margin: '0px auto auto auto' }}>
          <div style={{ textAlign: 'center', margin: '0 auto auto auto' }}>
            <Button.Group >
              <Button >
                <ChainList />
              </Button>
              <Button loading={state.loading} primary icon
              ><Icon name='circle outline' /></Button>
              <Button style={{ padding: '5px 10px 5px 18px' }}>
                <TokensSelect />
              </Button>
            </Button.Group>
          </div>
          <Table inverted selectable fixed singleLine unstackable='false' style={{ background: 'rgba(255,255,255,0.1)', marginBottom: '2px' }}>
            <Table.Header>
              <Table.Row>
                <Table.Cell width={3} textAlign='left' >原链</Table.Cell>
                <Table.Cell width={16} textAlign='left' onClick={() => {
                  Toast.show({
                    position: 'top',
                    duration: 3000,
                    content: state.selectCoinOrAdr
                  })
                }}
                >{state.selectCoinOrAdr}</Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell textAlign='left' >当链</Table.Cell>
                <Table.Cell textAlign='left' onClick={() => {
                  Toast.show({
                    position: 'top',
                    duration: 3000,
                    content: state.curAdr
                  })
                }}
                >{state.curAdr}</Table.Cell>
              </Table.Row>
            </Table.Header>
          </Table>
          <Table inverted selectable fixed singleLine unstackable='false' style={{ background: 'rgba(255,255,255,0.1)', marginTop: '0px' }}>
            <Table.Body>
              <Table.Row>
                <Popup
                  trigger={
                    <Table.Cell width={3} textAlign='left' >余额</Table.Cell>
                  }
                  content='该链跨链池余额，余额太少时需添加。流程：原链跨链>链码生成>跨回原链'
                  inverted
                  on='focus'
                />
                <Table.Cell textAlign='center'>{state.balance}</Table.Cell>
                <Table.Cell width={3} style={{ padding: '3px' }} >
                  <Link to="/bridge">
                    <Button size='mini' floated='right' positive>添加</Button>
                  </Link>
                </Table.Cell>
              </Table.Row>

              <Table.Row>
                <Popup
                  trigger={<Table.Cell textAlign='left' >余期</Table.Cell>}
                  content='LG跨链桥对每种通证收取一定的LG/月，租期不足时请及时续期'
                  inverted
                  on='focus'
                />
                <Table.Cell textAlign='center'>{state.tokenPayTime}</Table.Cell>
                <Table.Cell style={{ padding: '3px' }}>
                  <Modal style={{ maxWidth: '400px' }}
                    trigger={<Button size='mini' floated='right' positive
                      onClick={() => this.setState({ tokenPayTimeOpen: true })}
                    >续期</Button>}
                    size='small'
                    open={state.tokenPayTimeOpen}
                    onClose={() => this.setState({ tokenPayTimeOpen: false })}
                    header={`延长通证跨链桥租期(费用:${state.Timefee}LG/月)`}
                    content={
                      <Popup
                        trigger={
                          <Input style={{ margin: '3px 10px 20px 10px' }}
                            fluid
                            focus
                            placeholder='输入月数，如："1"'
                            onChange={event => { this.setState({ buyMonth: event.target.value }) }}
                            action={
                              <Button positive
                                onClick={() => { this.BuyTokenTime() }}
                              >续期</Button>
                            }
                          />
                        }
                        content='输入延长月数'
                        inverted
                        on='focus'
                      />
                    }
                  />
                </Table.Cell>
              </Table.Row>

              <Table.Row>
                <Popup
                  trigger={<Table.Cell textAlign='left' >底量</Table.Cell>}
                  content='用户最小跨链量，避免用户小额跨链造成不必要的GAS费消耗'
                  inverted
                  on='focus'
                />
                <Table.Cell textAlign='center'>{state.minValue}</Table.Cell>
                <Table.Cell style={{ padding: '3px' }}>
                  <Modal style={{ maxWidth: '400px' }}
                    open={state.minValueOpen}
                    onClose={() => this.setState({ minValueOpen: false })}
                    trigger={<Button size='mini' floated='right' positive
                      onClick={() => this.setState({ minValueOpen: true })}
                    >调整</Button>}
                    size='small'
                    header='调整用户最低跨链数量,0为不限制'
                    content={
                      <Popup
                        trigger={
                          <Input style={{ margin: '3px 10px 20px 10px' }}
                            fluid
                            focus
                            placeholder='输入数量，如："100"'
                            onChange={event => { this.setState({ changMinValue: event.target.value }) }}
                            action={
                              <Button positive
                                onClick={() => { this.setMinValue() }}
                              >调整</Button>
                            }
                          />
                        }
                        content='输入用户最低跨链数量'
                        inverted
                        on='focus'
                      />
                    }
                  />
                </Table.Cell>
              </Table.Row>
            </Table.Body>
          </Table>

          <Card type="inner" size='small' style={{ background: '#535c6b', borderColor: 'rgba(255,255,255,0)' }} >
            <Button fluid size='mini' primary style={{ marginBottom: '5px' }}
              onClick={() => this.operator()}
            >查询操作员</Button>
            <div style={{ display: this.state.operatordisplay === 'yes' ? 'block' : 'none' }}>
              <List divided inverted relaxed style={{ color: 'white', wordWrap: 'break-word' }}>
                <List.Item >
                  {state.firstOwner}
                  <Modal style={{ maxWidth: '480px' }}
                    trigger={<Button size='mini' floated='right'
                      onClick={() => this.setState({ changFirstOwnerOpen: true })}
                    >更换</Button>}
                    size='small'
                    open={state.changFirstOwnerOpen}
                    onClose={() => this.setState({ changFirstOwnerOpen: false })}
                    header='更换管理员'
                    content={
                      <Popup
                        trigger={
                          <Input style={{ margin: '3px 10px 20px 10px' }}
                            onChange={event => { this.setState({ changFirstOwner: event.target.value }) }}
                            fluid
                            focus
                            size='big'
                            placeholder='输入地址，如："0x123..."'
                            action={
                              <Button negative onClick={() => { this.transferFirstOwner() }}>更换</Button>
                            }
                          />
                        }
                        content='将管理员身份转移到其它帐户'
                        inverted
                        on='focus'
                      />
                    }
                  />
                </List.Item>
                {state.ownerList}
              </List>
              <Popup
                trigger={
                  <Input size='mini'
                    fluid
                    onChange={event => { this.setState({ newOwner: event.target.value }) }}
                    placeholder='输入地址，如"0x123……"'
                    action={
                      <Button positive size='mini'
                        onClick={() => { this.addOwer() }}
                      >添加</Button>
                    }
                  />
                }
                content='添加操作员，操作员可发送用户的跨链请求'
                inverted
                on='focus'
              />
            </div>
          </Card>

          <Divider />

          <OnceCode pushData={this.pushData} popup='原链跨链至本链生成通证，通过在原链发起跨链请求获得跨链码。' />
          <Button positive fluid onClick={() => { this.mintToken() }}
            style={{ marginTop: '3px' }}
          >链码生成</Button>
          <div style={{ clear: 'both' }} />

          <Divider />

          <Popup
            trigger={
              <Input
                value={state.burnValue}
                onChange={event => { this.setState({ burnValue: event.target.value }) }}
                fluid

                placeholder='输入销毁数量'
              >
                <Button positive style={{ marginRight: '0px', borderRadius: '4px 0 0 4px' }}>
                  <ChainSelectIDto chainSelectIDto={this.chainSelectIDto} />
                </Button>
                <input style={{ borderRadius: '0px' }} />
                <Modal style={{ maxWidth: '400px' }}
                  trigger={
                    <Button positive
                      onClick={() => this.burnModalOpen()}
                      style={{ marginRight: '0px', borderRadius: '0 4px 4px 0' }}
                    >跨链销毁</Button>
                  }
                  size='small'
                  open={state.burnModalOpen}
                  onClose={() => this.setState({ burnModalOpen: false })}
                  content={
                    <div style={{ padding: '10px' }}>
                      <h3>
                        将销毁{state.burnValue}{state.curTokenName}跨链至{state.ToChainName}，请确认跨链桥目标链{state.ToChainName}支持该通证。
                        目标链选择错误{state.burnValue}{state.curTokenName}将无法找回！
                      </h3>
                      <Button positive fluid style={{ marginBottom: '10px' }}
                        onClick={() => { this.burnToken() }}
                      >跨链销毁</Button>
                    </div>
                  }
                />
              </Input>
            }
            content="管理员可以销毁自己手里的通证跨到其它链"
            inverted
            on='focus'
          />
        </Card>
        <Loading ref={loadwindow => { this.loading = loadwindow }} />
      </div >
    )
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
    LGbalance: state.LGbalance,
    selectCoinOrAdr: state.selectCoinOrAdr
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(bridgeManage) 
