import React, { useState, useEffect } from 'react'
import { Table, Card, ButtonGroup, Button, ButtonOr, Input, Divider, Modal, ModalContent, Radio } from 'semantic-ui-react'
import { Toast } from 'antd-mobile'
import { generateMnemonic, mnemonicToSeedSync, setDefaultWordlist, validateMnemonic } from 'bip39';
import { fromSeed } from 'bip32';
import Wallet from 'ethereumjs-wallet';
import { toChecksumAddress } from 'ethereumjs-util';
import QRcode from 'qrcode';


interface ADR {
    id: number,
    address: string
    privateKey: string,
}
function BIP39Tool() {
    useEffect(() => { return () => { }; }, []);

    const [mnemonic, setMnemonic] = useState('');

    //wordList: 'chinese_simplified' 'english' 'french' 'italian' 'japanese' 'korean' 'spanish'
    const GenerateMnemonic = async (wordList: string) => {
        setDefaultWordlist(wordList);
        setMnemonic(generateMnemonic());
        setTableData([])
    }

    const onChangeMnemonic = (value: string) => {
        setMnemonic(value)
        setTableData([])
    }

    const openAddress = (number: number, fromId: number) => {
        if (mnemonic === '') {
            return Toast.show({ content: '无助记词' })
        }
        if (bip39Standard) {
            const isBip39 = validateMnemonic(mnemonic)
            console.log(isBip39)
            if (isBip39 === false) {
                return Toast.show({ content: '助记词不符合BIP39标准,如助记词非BIP39标准请将左侧“标准”勾掉' })
            }
        }
        const seed = mnemonicToSeedSync(mnemonic)
        const rootNode = fromSeed(seed);
        const numOfAddresses = number;
        const adrTemp: ADR[] = [];
        for (let i = fromId; i < numOfAddresses; i++) {
            const childNode = rootNode.derivePath(`m/44'/60'/0'/0/${i}`);
            const address = getAddress(childNode);
            const privateKey = getPrivateKey(childNode);
            const item: ADR = {
                id: i,
                address: address,
                privateKey: privateKey
            }
            adrTemp.push(item)

        }
        setTableData(adrTemp);
    }

    const addAddress = (number: number) => {
        if (tableData.length === 0) {
            return Toast.show({ content: '无助记词' })
        }
        const fromId = tableData.length
        const seed = mnemonicToSeedSync(mnemonic)
        const rootNode = fromSeed(seed);
        const numOfAddresses = fromId + number;
        const adrTemp: ADR[] = [];
        for (let i = fromId; i < numOfAddresses; i++) {
            const childNode = rootNode.derivePath(`m/44'/0'/0'/0/${i}`);
            const address = getAddress(childNode);
            const privateKey = getPrivateKey(childNode);
            const item: ADR = {
                id: i,
                address: address,
                privateKey: privateKey
            }
            adrTemp.push(item)
        }
        console.log(adrTemp)
        setTableData(prevData => [...prevData, ...adrTemp]);
    }

    const renderTableRow = (item: ADR) => (
        <Table.Row >
            <Table.Cell textAlign="left" style={{ padding: '3px', textAlign: 'center' }}>
                {item.id}
            </Table.Cell>
            <Table.Cell textAlign="left" style={{ padding: '3px' }} onClick={() => { generateQRCode(item.id, item.address) }} >
                <Input style={{ width: '100%' }} value={item.address} readOnly />
            </Table.Cell>
            <Table.Cell textAlign="right" style={{ padding: '3px' }} onClick={() => { generateQRCode(item.id, item.privateKey) }} >
                <Input style={{ width: '100%' }} value={item.privateKey} readOnly />
            </Table.Cell>
        </Table.Row>
    )

    const [tableData, setTableData] = useState<ADR[]>([])

    const getAddress = (node: any): string => {
        const wallet = Wallet.fromPrivateKey(node.privateKey);
        const address = wallet.getAddressString(); // 使用ethereumjs-wallet生成以太坊地址
        return toChecksumAddress(address);
    }

    const getPrivateKey = (node: any): string => {
        return node.privateKey.toString('hex');
    }

    const generateQRCode = async (ID: number, value: string) => {
        setWindowOpen(true)
        setWindeowValue(value)
        setWindeowID(ID)
        try {
            const qrCodeDataUrl = await QRcode.toDataURL(value);
            setQrCodeDataUrl(qrCodeDataUrl)
        } catch (error) {
            console.error("Error generating QR code:", error);
        }
    }
    const [qrCodeDataUrl, setQrCodeDataUrl] = useState('');
    const [windowOpen, setWindowOpen] = useState(false)
    const [windeowValue, setWindeowValue] = useState('')
    const [windeowID, setWindeowID] = useState(NaN)
    const [bip39Standard, setBip39Standard] = useState(true)

    return (
        <div>
            <Card type="inner" style={{ width: 'inherit', minWidth: '312px', maxWidth: '500px', margin: '0px auto auto auto', backgroundColor: '#b6e0fe' }}>
                <div style={{ marginLeft: '10px' }}>
                    <div style={{ display: 'flex', justifyContent: 'center' }}>免责声明</div>
                    <div >
                        <li style={{ color: 'red' }}>请尽量在断网下使用该工具</li>
                        <li>在使用此工具时，请确保您的设备安全可靠。</li>
                        <li>不要将助记词或私钥泄露给任何人或任何未经验证的网站或应用。</li>
                        <li>请在安全的环境中使用此工具，避免在公共网络或不受信任的设备上使用。</li>
                        <li style={{ color: 'red' }}>使用该工具如您出现任何损失，无论何种原因本平台均不承担任何责任</li>
                        <a href={`${process.env.PUBLIC_URL}/BIP39`}><Button size='mini'>其它帐户工具</Button></a>
                    </div>
                </div>
                <Divider />
                <div style={{ display: 'flex', justifyContent: 'center', marginBottom: '1px' }}>
                    <ButtonGroup >
                        <Button onClick={() => { GenerateMnemonic('chinese_simplified') }}>生成组记词"中文"</Button>
                        <ButtonOr />
                        <Button onClick={() => { GenerateMnemonic('english') }}>生成组记词"英文"</Button>
                    </ButtonGroup>
                </div>
                <div style={{ display: 'flex', alignItems: 'center', marginBottom: '1px' }}>
                    <div style={{ display: 'flex', alignItems: 'center',background:'#00b5ad',borderRadius: '5px'}}>
                        <Radio checked={bip39Standard} onClick={() => { setBip39Standard(bip39Standard ? false : true) }} />
                        <div style={{color:'white'}}>标准</div>
                    </div>
                    <Input style={{ width: '100%' }} value={mnemonic} onChange={(e, { value }) => onChangeMnemonic(value)}
                        action={{ icon: 'folder open', color: 'teal', onClick: () => { openAddress(10, 0) } }}
                        placeholder='输入助记词或点上方生成'
                    />
                </div>
                <div>
                    <Table celled fixed singleLine unstackable color='blue' inverted>
                        <Table.Header  >
                            <Table.Row >
                                <Table.HeaderCell textAlign="center" style={{ width: '12%' }}>ID</Table.HeaderCell>
                                <Table.HeaderCell textAlign="left" style={{ width: '60%' }}>地址</Table.HeaderCell>
                                <Table.HeaderCell textAlign="right" >私钥</Table.HeaderCell>
                            </Table.Row>
                        </Table.Header>
                        <Table.Body >
                            {tableData.map((item) => renderTableRow(item))}
                        </Table.Body>
                    </Table>
                </div>
                <div style={{ display: 'flex', justifyContent: 'center' }}>
                    <Button primary onClick={() => { addAddress(10) }}>再打开10个地址</Button>
                </div>

            </Card>


            <Modal
                size='mini'
                open={windowOpen}
                onClose={() => setWindowOpen(false)}
                style={{ position: 'fixed', top: '180px' }}
            >
                <ModalContent >
                    <div style={{ display: 'flex', justifyContent: 'center' }}>
                        ID: {windeowID}
                    </div>
                    <div style={{ wordWrap: 'break-word' }}>
                        {windeowValue}
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'center' }}>
                        {qrCodeDataUrl && <img src={qrCodeDataUrl} alt="QR Code" />}
                    </div>
                </ModalContent>

            </Modal>

        </div>



    )
}

export default BIP39Tool