import React from 'react';
import {  Card } from 'antd';

function Edu() {
  return(
    <div>
        <Card>
          <div>
            <b>牛市起点的故事</b>
            <p>2015年11月，牛市起点，19个人，19个故事。</p>
            <p>前几天，我发表文章《上一轮熊市》，引起广泛的关注和讨论，似乎大家很关心上一轮熊市里发生的故事。</p>

            <p>今天，我来分享更多牛市起点的故事，但都不是我自己的故事。</p>

            <p>2015年11月，我曾经发起了一次征文活动。起因是某一天，大佬@比特币星空 莫名奇妙打赏我 0.5 BTC。那个时候的打赏在今天看来全是大手笔，我在2015年光打赏也花出去了约 1 BTC。</p>

            <p>拿到@比特币星空 的打赏之后，我觉得无功不受禄，就想把这 0.5 BTC 分发出去，就在币科技论坛搞了一次征文活动。</p>

            <p>站在今天的角度看，这个时间节点有着特殊意义，当时是牛市的起点，是黎明前的黑暗。但是，并没有多少人能意识到这一点。</p>

            <p>征文活动相关的帖子已经全部被删除了，印象中我还写了一篇总结《#征文大赛大结局# 理性的逐利》。后来我退出这个论坛，并且要求把我的账号和帖子全部删掉。所幸的是，币科技论坛仍然保存了所有征文文章，尽管文章中的图片和后面的回帖都没有了。</p>

            <p>征文的题目是《我有X个比特币，这是我的故事》，要求讲述自己与比特币的故事。</p>

            <p>奖金是这样设置的：一等奖（1名）= 0.2 BTC，二等奖（1名）= 0.1 BTC，三等奖（1名）= 0.05 BTC，参与奖 = 0.01 BTC。</p>

            <p>结果只有 19 位参加者。这活动要放在今天，少说几百个参加者吧？</p>

            <p>不过，大家不要失望，有些故事写得非常精彩。大家很有福，今天可以一口气读19个故事，而且这19个人都穿越了上一次熊市，来到了牛市的起点。所有文章的链接和作者见本文最后的列表。</p>

            <p>最有趣的文章来自@hibitcoin ，作者是鱼池联合创始人王纯，通篇文章都是料，而且包括@比特币星空 吴老板的料。</p>

            <p>《我有N个比特币，这是我的故事》by @hibitcoin</p>
            <p>节选部分内容如下：</p>

            <p style={{color:'#808080'}}>2012年初是最难熬的，最初投入了大约30万买币买矿机，但是币价一路走低，在bitcoinica上做多又多次暴仓，2011年10月又遭遇俄罗斯黑客侵入邮箱从mtgox上偷走了300个币，到2012年春节，手里的币只值4万块了，显卡也越挖越少，剩下20多G算力每天产出十几个比特币，值不到200块钱，而电费和房租一天就要100块。吴老板也亏了，说花了几十万买了3000多个币，如今一个币却跌到2美元，非常后悔。</p>
            <p style={{color:'#808080'}}>挖矿挖了一年多，一共挖到7000多个币，去除吃喝交电费、暴仓、被盗，总共攒下2700多个币。2012年底减半后币价没有立即上涨，有些失望，2013年1月初，币价刚刚从11美元涨到13美元然后横盘，神鱼在QQ上问我怎么看，我回答，我看就这样了，涨不上去了。又过了几周，比特币突破17美元，2700多个币终于值回了最初投入的30万人民币，回本了。于是我就把这些币全部卖掉，只留了100多个币，算下来还赚了一万多块钱。</p>

            <p>还有两位作者现在已经分叉了，成为垃圾币的宣传主力。今天再读他们的故事，发现他俩有个共性，不知道是不是巧合，他们都在借贷买币。当时并没有觉得有什么问题，但今天再回顾，凡事借贷买币，可能都不是有多看好比特币，而是想赌一把，想赚快钱，因而会走入歧途。</p>

            <p>作为币圈老人，我再次读这些故事时，也颇有感触，别有一番滋味。作为币圈新人的你们，读到这些故事，有什么感想呢？</p>

            <p>或许好消息是，很多老人只有个位数的币，甚至没有币，很容易被你们超越，如果你们场外赚钱能力还比较强的话。</p>

            <p>决定比特币持有量的是三个因素：入场时间，定力，赚钱能力。入场时间是运气决定，我们控制不了，但是，没有定力和赚钱能力，有好运气也没什么用。</p>

            <p>穿越牛熊，老人们的故事已经成为过去。展望未来，还有更多的传奇等待我们大家一起去书写。</p>
          </div>
        </Card>
    </div>
  )

}

export default Edu;
