import React, { Component } from 'react'
import {Card ,Button} from 'antd';

export class introduce extends Component {
    constructor(props) {
        super(props)
        this.state = {
            open:this.props.open
        }
    }
    
    open=()=>{
        if(this.state.open==='none'){
          this.setState({open:'block'})
        }else{
          this.setState({open:'none'})
        }
    }

    render() {
        return (
            <div>
                <Card type="inner" size='small' style={{borderRadius:'10px'}} >
                    <Button shape='round' type="dashed" block  size="small"  onClick={() => {this.open()}}>说明</Button>
                    <div style={{display:this.state.open}}>
                        <div>
                            <div style={{marginTop:'5px'}}><b>为什么需要发送链短信？</b></div>
                            <div>链短信，百分百确定该信息的真实性。
                                比如对方付给您一些LG购买您的商品，您需要对方提供收货地址。
                                如果您让对方通过互联网发地址给您，您如何百分百确定对方就是该帐号的所有人？
                                而链短信就可以百分百相信!从此与假消息说拜拜!
                            </div>
                            <div style={{marginTop:'5px'}}><b>所有以太坊标准链皆可用</b></div>
                            <div>由于与以太坊相同标准的链（以太坊链、币安链、火币链、OK链、等）地址都相同。
                                故，无论使用哪一条链在链改网上发送的短信都可以确定对方的真实性。
                                比如对方通过以太坊链给您支付ETH，对方在链改网上发送信息，
                                您同样可以百分百相信该信息的真实性。
                            </div>
                            <div style={{marginTop:'5px'}}><b>收费标准</b></div>
                            <div>优惠期内每个地址可免费发送5条短信。免费条数用完后采取包月式购买使用时效
                            </div>
                            <div style={{marginTop:'5px'}}><b>邀请奖励</b></div>
                            <div>凡是您之前或以后邀请的伙伴，您皆可获得对方购买使用时效的30%LG。
                                需要注意的是，绑定被邀请人必须让对方申请信用分或领取新人空投或每日空投
                            </div>
                        </div>
                    </div>
                </Card>
            </div>
        )
    }
}

export default introduce
