import { Component } from 'react';
import { Modal } from 'antd';

class loading extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loadwindow: false,
      loading: 'none',
      complete: 'none',
      msgColor: '#ff4d4f',
      error: 'none',
      loadmessage: ''
    }
  }

  //加载窗开关
  loadingClose = () => {
    this.setState({ loadwindow: false })
  };
  loadingOpen = (loading, complete, error, msg) => {
    this.setState({
      loadwindow: true,
      loading,
      complete,
      error,
      loadmessage: msg
    })
  };

  render() {
    return (
      <>
        <Modal
          visible={this.state.loadwindow}
          closable={false}
          width={250}
          onCancel={this.loadingClose}
          footer={null}
        >
          <div style={{ textAlign: 'center' }}>
            <div style={{ display: this.state.loading }}>
              <div className='loadingOpen' />
              <div style={{ color: "rgb(43,153,255)", fontSize: "13px", textAlign: 'center' }}>
                {this.state.loadmessage}
              </div>
            </div>
            <div style={{ display: this.state.complete }}>
              <div className='loadingOpenComplete' />
              <div style={{ color: "green", fontSize: "13px", textAlign: 'center' }}>
                {this.state.loadmessage}
              </div>
            </div>
            <div style={{ color: '#ff4d4f', fontSize: "13px", textAlign: 'center', display: this.state.error }}>
              {this.state.loadmessage}
            </div>
          </div>
        </Modal>
      </>
    );
  }
}

export default loading;

