import { connect } from "react-redux"
import Head from './components/head';

import LockUser from "./components/lockList"

function LockMy(props: any) {
    const otherUserAdr: string = '';
    return (
        <div>
            <Head />
            <LockUser ChainID={props.ChainID} UserAdr={props.UserAdr} otherUserAdr={otherUserAdr} isUser={true} />
        </div>
    )

}

const mapStateToProps = (state: any) => {
    return {
        ChainID: state.ChainID,
        UserAdr: state.UserAdr,
        LGbalance: state.LGbalance,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        sendStore(tp: any, val: any) {
            const action = { type: tp, value: val }
            dispatch(action)
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LockMy)

