import { AbiItem } from 'ethereum-multicall/dist/esm/models';
import { createMulticall } from "../../../../../components/global/functionTS"
import lockV2ABI from "../../../../../ABI/LockV2.json";
import { getChainInfo } from '../../../../../components/global/functions';
import web3 from "../../../../../ABI/web3";

interface lockInfo {
  id: number
  tokenAdr: string;
  user: string;
  unlockTime: string;
  betweenTime: string;
  onceAmount: string;
  balance: string;
  remainTime: string;
}

export const getUserLocksID = async (userAdr: string, ChainID: string): Promise<string[]> => {
  let userLocksID: string[] = []
  try {
    const chainInfo = await getChainInfo(ChainID)
    const lockV2Contract = new web3.eth.Contract(lockV2ABI as AbiItem[], chainInfo.ChainAdr.lockV0)
    let locksUserIDs = await lockV2Contract.methods.getUserLocks(userAdr).call()
    userLocksID = locksUserIDs
  } catch (error) {
    console.log('获取用户礼品盒ID失败', error)
  }
  return userLocksID
}

export const getAllLocks = async (ChainID: string): Promise<string> => {
  let AllLockslocksID: string = '0'
  try {
    const chainInfo = await getChainInfo(ChainID)
    const lockV2Contract = new web3.eth.Contract(lockV2ABI as AbiItem[], chainInfo.ChainAdr.lockV0)
    AllLockslocksID = await lockV2Contract.methods.id().call()
  } catch (error) {
    console.log('获取用户礼品盒ID失败', error)
  }
  return AllLockslocksID
}

export const getLockInfoList = async (ChainID: string, locksID: string[]): Promise<lockInfo[]> => {
  const lockInfos: lockInfo[] = [];
  try {
    const chainInfo = await getChainInfo(ChainID)
    const multicall = createMulticall(chainInfo.ChainAdr.multicall)
    const calls: any[] = [];
    for (let i = 0; i < locksID.length; i++) {
      const call = {
        reference: locksID[i],
        methodName: 'getLockInfo',
        methodParameters: [locksID[i]]
      }
      calls.push(call)
    }
    const Call = [{
      reference: 'LocksInfo',
      contractAddress: chainInfo.ChainAdr.lockV0,
      abi: lockV2ABI,
      calls: calls
    }]
    const locksInfo = await multicall.call(Call)
    const LocksInfo = locksInfo.results.LocksInfo.callsReturnContext

    let lockBalanceIsZero: string[] = [];
    const lockBalanceIsZeroString = localStorage.getItem('lockBalanceIsZero');
    if (lockBalanceIsZeroString !== null) {
      lockBalanceIsZero = JSON.parse(lockBalanceIsZeroString);
    }
    LocksInfo.forEach((lockInfoData: any) => {
      if (Number(lockInfoData.returnValues[5].hex) !== 0) {
        const lockInfo: lockInfo = {
          id: lockInfoData.reference,
          tokenAdr: lockInfoData.returnValues[0],
          user: lockInfoData.returnValues[1],
          unlockTime: lockInfoData.returnValues[2].hex,
          betweenTime: lockInfoData.returnValues[3].hex,
          onceAmount: lockInfoData.returnValues[4].hex,
          balance: lockInfoData.returnValues[5].hex,
          remainTime: lockInfoData.returnValues[6].hex
        };
        lockInfos.push(lockInfo);
      } else {
        if (lockBalanceIsZero.every(item => item !== lockInfoData.reference)) {
          lockBalanceIsZero.push(lockInfoData.reference)
        }
      }
    })
    const arrayToStoreString = JSON.stringify(lockBalanceIsZero);
    localStorage.setItem('lockBalanceIsZero', arrayToStoreString);
  } catch (error) {
    console.log(error)
  }
  return lockInfos;
}



