import React from 'react';
import { Card, Divider, BackTop } from 'antd';

function Genxin() {
  return (
    <div>
      <Card style={{ borderRadius: '10px' }}>

        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>公告2024-08-13:</h3>
          <div>
            停止HECO链上的LG跨链至其它链
          </div>
          <br />
          <div>
            自2023年11月22日HECO链官方出现被盗事件后，HECO链逐渐停摆，HECO官方也已基本放弃维护。<br />
            在HCEO链上的其它主流资产也全部无法跨链至其它链。<br />
            <br />
            遂链改网启动了逐步脱离HECO链计划。<br />
            2023年12月10日跨链桥上线，同时LG可跨链至MATIC或CELO链。<br />
            为了链改网的发展，故决定自2024年8月13日起停止HECO链上的LG跨链至其它链。<br />
            <div style={{ color: "red" }}>链改网将于2025年5月1日之后下线HECO链上的所有应用以及对HECO链的支持。</div>
          </div>
        </div>

        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>更新2024-03-28:</h3>
          <div>
            礼品盒全新上线。
          </div>
          <br />
          <div>
            礼品盒，赋能实体经济的神盒！
          </div>
        </div>

        <Divider />

        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>更新2023-12-10:</h3>
          <div>
            全自动化跨链桥正式上线。
          </div>
          <br />
          <div>
            该跨链桥由链改网自主研发，历时1年多实现任何token在以太坊系列链之间的自由跨链，
            它不同于市面上的跨链需要第三方验证。
            创新性的实现了跨链的完全去中心化，无需第三方验证，用户完全自主完成跨链。
          </div><br />
          <div>
            当然它也有不足，有被黑客破解的可能性，虽然可能性极小，
            但还请使用该跨链桥的通证管理者控制存留在跨链桥内的通证数量。
          </div><br />
          <div>
            跨链桥目前支持MATIC、HECO、CELO链。
          </div>
        </div>

        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>更新2022-4-3:</h3>
          <p>
            <p>&emsp;跨链桥正式上线。跨链桥实现任何token在以太坊系列链之间的自由跨链。
              同时在OEC链上线LG交易所。
              跨链桥暂时只部署了HECO与OEC，使用一段时间后将部署至多条链
            </p>
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>更新2021-9-15:</h3>
          <p>
            <p>&emsp;大幅提升个人锁仓机“我的锁仓”页面的加载速度</p>
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>更新2021-8-31:</h3>
          <p>
            <p>&emsp;上线链短信</p>
            <div>&emsp;<b>链短信介绍</b></div>
            <div style={{ marginTop: '5px' }}><b>为什么需要发送链短信？</b></div>
            <div>链短信，百分百确定该信息的真实性。
              比如对方付给您一些LG购买您的商品，您需要对方提供收货地址。
              如果您让对方通过互联网发地址给您，您如何百分百确定对方就是该帐号的所有人？
              而链短信就可以百分百相信！从此与假消息说拜拜！
            </div>
            <div style={{ marginTop: '5px' }}><b>所有以太坊标准链皆可用</b></div>
            <div>由于与以太坊相同标准的链（以太坊链、币安链、火币链、OK链、等）地址都相同。
              故，无论使用哪一条链在链改网上发送的短信都可以确定对方的真实性。
              比如对方通过以太坊链给您支付ETH，对方在链改网上发送信息，
              您同样可以百分百相信该信息的真实性。
            </div>
            <div style={{ marginTop: '5px' }}><b>收费标准</b></div>
            <div>优惠期内每个地址可免费发送5条短信。免费条数用完后采取包月式购买使用时效
            </div>
            <div style={{ marginTop: '5px' }}><b>邀请奖励</b></div>
            <div>凡是您之前或以后邀请的伙伴，您皆可获得对方购买使用时效的30%LG。
              需要注意的是，绑定被邀请人必须让对方申请信用分或领取新人空投或每日空投
            </div>
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>更新2021-8-14:</h3>
          <p>
            <p>&emsp;七夕节上线娱乐通证LP，发行量0，用户通过LG兑换LP，1000LG=1LP。同时上线LG交易所</p>
            <p>&emsp;行情页已实现LOGO功能，后续会更新合约，使用户可以自行添加LOGO</p>
            <p>&emsp;行情页实现涨跌幅</p>
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>更新2021-8-7:</h3>
          <p>
            <p>&emsp;添加知识库，将不定期将区块链知识系统性的整理在一起方便大家查阅</p>
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>通知2021-7-26:</h3>
          <p>
            <p>&emsp;为了杜绝撸客，增强LG共识。LG空投添加规则，如果您的LG余额少于您伙伴的LG余额，您将无法获得该伙伴领空投时的奖励。</p>
            <p>&emsp;个人搭建节点域名，在您每次更新时将获得一定的LG奖励（当前为28LG/次）</p>
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>通知2021-7-21:</h3>
          <p>
            上线个人版锁仓机
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>通知2021-7-12:</h3>
          <p>
            <b>&emsp;上线LGDEX交易所</b>
            <p>&emsp;链改网为什么要上线自己的交易所？因为现阶段除了UNISWAP以外的各种交易所杂乱无章，
              乱收费等等问题。作为一个有情怀的创业团队，我们无法忍受，所以上线LGDEX。<br />
              &emsp;LGDEX完全复制UNISWAP的2.0版交易所，合约部分未做任何修改，大家可以放心使用！
            </p>

            <b>&emsp;入口部分全新改版</b>
            <p>&emsp;入口的作用是将现阶段市面上流行的DAPP整合到一起，方便大家的使用。
              整合不是简单的链接，而是将整个官方前端部署到链改网。<br />
              &emsp;这样做的好处是，访问更流畅，不用担心链接打不开等等问题。<br />
              &emsp;需注意的是部分DAPP含有中心化内容，故而DAPP内中心化部分可能会存在异常，去中心化部分可放心使用
            </p>
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>通知2021-6-20:</h3>
          <p>
            <p>&emsp;添加节点域名去中心化管理系统</p>
            <p>&emsp;任何人都可以运行自己的节点并将获得奖励。</p>
            <p>&emsp;实现了运行的完全去中心化</p>
            <p>&emsp;需要注意的是，一个地址只能上报一个域名</p>
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>通知2021-6-11:</h3>
          <p>
            <p>&emsp;每日一领规则调整</p>
            <p>&emsp;每日一领出发点是奖励持续关注链改网进程的用户。然而行业里投机份子与撸客盛行。
              链改网小本创业，经不起撸，还望理解。
            </p>
            <p> 现调整规则如下：
              <div>&emsp;每日一领领取的额度将记录在区块链中，并显示为“累额”。<br />
                &emsp;您需始终保持您的帐户中的LG余额不低于该值。
                否则就不可继续领取，如想继续领取需使帐户余额大于该值。</div>
            </p>
            <p>&emsp;赋能实体经济是我们的方向！打造钱包内的第一必备网站是我们的梦想！
              愿与有梦想的您同行！
            </p>
            <p>&emsp;另，由于您懂的原因，cn.lg-qkl.xyz域名不能正常使用。请使用主域名lg-qkl.xyz<br />
              同时，如果您推荐好友，请务必在主页重新复制您的邀请链接。
            </p>
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>通知2021-6-5:</h3>
          <p>
            <p>一键发币添加新类型通证</p>
            <p>添加含准备期与销毁功通证。给创业者或实体企业提供一个前期交易区间，有效防止机器人抢单行为。</p>
            <p>同时包含销毁功能，创始人可将自己帐户内的通证进行部分销毁</p>
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>通知2021-5-31:</h3>
          <p>
            <p>上线锁仓机，解决企业/创业者与投资者/消费者之间的信任</p>
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>通知2021-5-17:</h3>
          <p>
            <p>红包系统升级</p>
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>通知2021-5-10:</h3>
          <p>
            <p>上线一键发币生态</p>
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>通知2021-4-15:</h3>
          <p>
            <p>上线信用制度</p>
          </p>
        </div>

        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>通知2021-4-12:</h3>
          <p>
            <p>为了永绝撸客，链改网将上线信用制度。</p>
            <p>每日一领中的额度
              领完后将在信用制度上线后按原来的制度(领取秒到)再次开放，
              请尽量在满10枚到帐后不再领取。</p>
            <p>理论上每个人都可以满10枚，
              如果您手气实在太差未足额10枚而未到帐，只能表示遗憾^_^!</p>
            <p>敬请理解与支持。</p>
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>
          <h3 style={{ color: "blue" }}>更新2021-3-30:</h3>
          <p>
            上线行情，任何人可以把喜欢的通证加入行情页
          </p>
        </div>
        <Divider />
        <div style={{ color: "black", fontSize: "13px" }}>


          <h3 style={{ color: "blue" }}>更新2021-3-27:</h3>
          <p>
            上线红包生态，开启发红包挖矿。
          </p>
        </div>

        <div style={{ color: "black", fontSize: "13px" }}>
          <Divider />
          <h3 style={{ color: "blue" }}>更新2021-3-12:</h3>
          <p>
            将每日一领的额度改为随机额度，您将会随机得到LG,每天试试手气享受乐趣，拿到最佳手气记得向朋友分享喜悦哦。
          </p>

          <p>
            也是希望能够留住大家对本站的关注。本站不圈钱、不募资，只想踏踏实实深耕区块链，由浅至深，将区块链赋能实体经济是我们的方向。
          </p>
          <p>
            新的生态上线以及界面美化等等需要不少时间，还希望大家保持耐心，支持真正的区块链创业者。
          </p>
        </div>
        <BackTop />
      </Card>
    </div>
  )

}

export default Genxin;
