import React, { Component } from 'react'
import { connect } from "react-redux"
import { Card } from 'antd';
import { Table, Button, Icon, Popup } from 'semantic-ui-react'
import Head from './components/headAdmin';
import ChainList from "../../../components/ChainSelect"
import TokensSelect from "./components/tokensSelect"
import web3 from '../../../ABI/web3';
import erc20abi from '../../../ABI/ERC20.abi.json';
import { getChainInfo } from '../../../components/global/functions';
import { Toast } from 'antd-mobile'
import bridgeAbi from '../../../ABI/Bridge.abi.json';
import CodeWindow from './components/CodeWindow';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { QuestionCircleOutlined } from '@ant-design/icons';
const BigNumber = require('bignumber.js');
const maxQueryBlock = 2000  //最大单次查询区块数
const historyTime = 2   //几小时至今

export class bridgeQuery extends Component {
  constructor(props) {
    super(props)

    this.state = {
      bridgeContract: '',
      bridgeAdr: '',
      multicallAdr: '',
      loadHistoryComplete: 'disabled',
      loadingHistory: false,
      history: [],
      hasKickHistory: 0,
      blockCount: maxQueryBlock,
      hasKickHistoryTime: historyTime,
      loading: false,
      curCoinAdr: '',
      currentCode: '',
      moreCode: ''
    }
  }

  async componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.ChainID !== this.props.ChainID
      || nextProps.UserAdr !== this.props.UserAdr
      || nextProps.selectCoinOrAdr !== this.props.selectCoinOrAdr
    ) {
      if (nextProps.selectCoinOrAdr !== '' && nextProps.selectCoinOrAdr !== undefined) {
        this.setState({ loading: true, curCoinAdr: nextProps.selectCoinOrAdr, moreCode: '' })
        await this.first()
        this.setState({ loading: false })
      }
    }
  }

  async componentDidMount() {
    if (this.props.selectCoinOrAdr !== '' && this.props.selectCoinOrAdr !== undefined) {
      await this.setState({ loading: true, curCoinAdr: this.props.selectCoinOrAdr })
      await this.first()
      this.setState({ loading: false })
    }

    

    let a = await web3.eth.getTransaction("0x56d4d1b3402cb51438c0dc96a61b55fb41ed3d00ac1ae0cb1163e7b1e5c673dc")
    console.log(a)
    let b = "85224527997676450784337712379575346975337010464124963598932383425024375160714"
    let password = '0x'+ new BigNumber(b).toString(16)
    console.log(password)
    await this.state.bridgeContract.getPastEvents('Lock', {
      fromBlock: a.blockNumber,
      toBlock: a.blockNumber + 1000,
      topics: [null, null, null, password]
    },
      async (error, events) => {
        console.log(events)
        console.log(error)
      }
    )



  }

  getChain = async () => {
    await getChainInfo(window.ChainID).then((result) => {
      this.setState({
        bridgeAdr: result.ChainAdr.bridge,
        multicallAdr: result.ChainAdr.multicall,
      })
    })
  }

  first = async () => {
    await this.getChain()
    const bridgeContract = new web3.eth.Contract(bridgeAbi.abi, this.state.bridgeAdr)
    this.setState({ bridgeContract, hasKickHistory: 0, history: [] })
    await this.seeHistory()
  }

  codeWindow = async (code) => {
    this.CodeWindow.windowOpen();
    this.setState({ currentCode: code })
  }

  seeHistory = async () => {
    this.setState({ loadHistoryComplete: 'disabled', loadingHistory: true })
    let topicsAdr = '0x' + (Array(64).join(0) + this.state.curCoinAdr.slice(2)).slice(-64)
    let lastBlock
    let fromBlock
    let toBlock
    const historyList = this.state.history
    await web3.eth.getBlockNumber().then((block) => { lastBlock = block });
    if (this.state.hasKickHistory === 0) {
      fromBlock = lastBlock - maxQueryBlock
      toBlock = lastBlock
    } else {
      this.setState({
        blockCount: this.state.blockCount + maxQueryBlock,
        hasKickHistoryTime: this.state.hasKickHistoryTime + historyTime
      })
      fromBlock = lastBlock - this.state.blockCount
      toBlock = fromBlock + maxQueryBlock
    }
    await this.state.bridgeContract.getPastEvents('Lock', {
      fromBlock: fromBlock,
      toBlock: toBlock,
      topics: [null, topicsAdr, null, null]
    },
      async (error, events) => {
        console.log(events)
        console.log(error)
        if (events !== undefined && events.length !== 0) {
          for (var i = events.length - 1; i >= 0; i--) {
            let event = events[i].returnValues
            let ToChainName
            let num2 = Number(event.ToChain)
            let ToChain = '0x' + (num2).toString(16)
            await getChainInfo(ToChain).then((result) => {
              ToChainName = result.ChainName
            })
            let tokenAdr = await this.state.bridgeContract.methods.tokenAdr(event.tokenAdr, this.props.ChainID).call()
            const ERC20 = await new web3.eth.Contract(erc20abi.abi, tokenAdr)
            let decimals = await ERC20.methods.decimals().call()
            let _value = await new BigNumber(event.value * 10 ** (18 - decimals)).toFixed()
            let value = await web3.utils.fromWei(_value, 'ether')
            let bridgeCode = event.FromChain + '&' + event.ToChain + '&' + event.tokenAdr + '&' + event.who + '&' + event.time + '&' + event.value + '&' + event.password
            let timeNumber = event.time
            let timeNum = '.' + timeNumber.substring(timeNumber.length - 6);
            historyList.push(
              <Table.Row textAlign='center'>
                <Table.Cell
                  onClick={() => {
                    Toast.show({
                      content: timeNumber,
                    })
                  }}
                >{timeNum}</Table.Cell>
                <Table.Cell>{ToChainName}</Table.Cell>
                <Table.Cell onClick={() => {
                  if (value.length >= 6) {
                    Toast.show({
                      content: value,
                    })
                  }
                }} >{value}</Table.Cell>
                <Table.Cell onClick={() => { this.codeWindow(bridgeCode) }}>{bridgeCode}</Table.Cell>
              </Table.Row>
            )
            let moreCode = this.state.moreCode
            let isMore = moreCode === '' ? '' : ','
            moreCode = bridgeCode + isMore + moreCode
            this.setState({ moreCode })
          }
        } this.setState({ history: historyList, hasKickHistory: 1, loadHistoryComplete: '', loadingHistory: false })
      })
  }

  render() {
    let state = this.state
    return (
      <div>
        <Head />
        <Card type="inner" size='small' className='bridgeUser' style={{ maxWidth: '500px', margin: 'auto auto 2px auto' }}>
          <div style={{ textAlign: 'center', margin: '0 auto 5px auto' }}>
            <Button.Group >
              <Button >
                <ChainList />
              </Button>
              <Button loading={state.loading} primary icon
              ><Icon name='circle outline' /></Button>
              <Button style={{ padding: '5px 10px 5px 18px' }}>
                <TokensSelect />
              </Button>
            </Button.Group>
          </div>

          <CopyToClipboard text={this.state.moreCode}>
            <Button fluid positive onClick={() => {
              Toast.show({
                content: '已复制',
              })
            }}>复制以下全部跨链码</Button>
          </CopyToClipboard>

          <Table celled inverted selectable fixed singleLine unstackable='false' style={{ background: 'rgba(255,255,255,0.1)' }}>
            <Table.Header >
              <Table.Row textAlign='center'>
                <Popup
                  trigger={<Table.HeaderCell>Num</Table.HeaderCell>}
                  content='该笔记录时间点(秒)'
                  inverted
                  on='click'
                />
                <Table.HeaderCell width={4}>TO</Table.HeaderCell>
                <Table.HeaderCell width={4}>数量</Table.HeaderCell>
                <Table.HeaderCell width={3}>链码</Table.HeaderCell>
              </Table.Row>
            </Table.Header>
            <Table.Body >
              {state.history}
            </Table.Body>
          </Table>
          <Button fluid disabled={this.state.loadComplete} size='mini'
            onClick={() => { this.seeHistory() }}
          >跨链记录({this.state.hasKickHistoryTime}小时至今)&nbsp;&nbsp;
            <Popup
              trigger={<QuestionCircleOutlined />}
              content='每点一次加载过去约2小时记录'
              inverted
              on='click'
            />
          </Button>

        </Card>
        <CodeWindow ref={window => { this.CodeWindow = window }} currentCode={this.state.currentCode} />
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
    LGbalance: state.LGbalance,
    selectCoinOrAdr: state.selectCoinOrAdr
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(bridgeQuery)

