import React,{ Component }  from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Link } from 'react-router-dom';
import { Table, Card ,Divider, Modal,Button,Input,Popover} from 'antd';
import { QuestionCircleOutlined, CopyOutlined} from '@ant-design/icons';
import erc20abi from '../../../ABI/ERC20.abi.json';
import LockFactory from '../../../ABI/LockFactory';
import LockLoad from '../../../ABI/LockLoad';
import LockABI from '../../../ABI/Lock.abi.json';
import {CopyToClipboard} from 'react-copy-to-clipboard';

class App extends Component {
      state = {
        dataList:[],
        onoff2:'none',
        message:"",
        visible:false,
        symbol:'',
        tokenAdr:'',
        lockAdr:'',
        admin:'',
        owner:'',
        balance:'',
        time:'',
        amount:'',
        day:'',
        time2:'',
        AllLocks:'',
        searchAdr:'',
        searchLock:'',
        visible2:false,
        tokenLocksNum:'',
        tokenLocksTotal:'',
        tokenTotalSupply:'',
        LocksNumdispaly:'block',
        tokenLocksReport:'none',
        pagefrom:'',
        pageto:'',
        lastpageButtom:'上一页',
        nextpageButtom:'下一页'
      };

  async componentDidMount(){

    const AllLocks = await LockFactory.methods.getAllLocks().call(); //获取全部锁  //66
    this.setState({AllLocks})
    //加载最后10个锁
    const _num = (AllLocks.length-10 >0) ? AllLocks.length-10 : 0;
    const dataSource = [];
    for(var i=AllLocks.length-1; i>=_num; i-- ){
      const getLockInfo = await LockLoad.methods.getLockInfo(AllLocks[i]).call();
      if(getLockInfo[0] === '1' && getLockInfo[2] !== '0'){
      const a = [];
        a[0] = getLockInfo[3];   //通证符号
        a[1] = getLockInfo[5];  //通证地址
        a[2] = AllLocks[i];  //锁地址
        a[5] = String( (web3.utils.fromWei(getLockInfo[2],'ether'))*10**(18-getLockInfo[4]).toFixed(0) ); //锁仓余额
        a[6] = String( Math.ceil(getLockInfo[1]/3600/24) );  //锁仓倒计时
      dataSource.push(a);
      }
    this.setState({message:"加载中……"+ i + '=>' + _num});
  }
    this.setState({
      pagefrom:AllLocks.length-1,
      pageto:_num,
      message:"",
      dataList:dataSource,
  });

  }

  // 加载细则
  loadOneInfo = async () =>{
    if(this.state.lockAdr.toLowerCase() !== '0x93e612a005d808878e0b15dde671dc0e6646f3c6'){
      const Lock =  new web3.eth.Contract(LockABI.abi,this.state.lockAdr);
      const ERC20 =  new web3.eth.Contract(erc20abi.abi,this.state.tokenAdr);
      const decimals = await ERC20.methods.decimals().call();
      const amount = await Lock.methods.amount().call();
      const day = await Lock.methods.day().call();
      const owner = await Lock.methods.owner().call();
      const admin = await Lock.methods.admin().call();
      const time2 = await Lock.methods.getTime2().call();
      this.setState({
        admin, //指导员
        owner, //拥有者
        amount:String( (web3.utils.fromWei(amount,'ether'))*10**(18-decimals).toFixed(0) ), //释放量
        day, //释放间隔天数
        time2:String( Math.ceil(time2/3600/24) ),  //释放间隔倒计时
      })
    }else{
        this.setState({
          amount:'100000', //释放量
          day:'30',
        })}
  }

  nextpage = async () =>{
    const dataSource = [];
    const num = (this.state.pageto-10 >0) ? this.state.pageto-10 : 0;  //46
    const _i = (num+9<this.state.AllLocks.length)?num+9:this.state.AllLocks.length-1;
    for(var i=_i; i>=num; i-- ){
      const getLockInfo = await LockLoad.methods.getLockInfo(this.state.AllLocks[i]).call();
      if(getLockInfo[0] === '1' && getLockInfo[2] !== '0' ){
      const a = [];
        a[0] = getLockInfo[3];   //通证符号
        a[1] = getLockInfo[5];  //通证地址
        a[2] = this.state.AllLocks[i];  //锁地址
        a[5] = String( (web3.utils.fromWei(getLockInfo[2],'ether'))*10**(18-getLockInfo[4]).toFixed(0) ); //锁仓余额
        a[6] = String( Math.ceil(getLockInfo[1]/3600/24) );  //锁仓倒计时
      dataSource.push(a);
      }
      this.setState({message:"加载中……"+ i});
    }
    this.setState({
      pagefrom:_i,  //55
      pageto:num, //46
      dataList:dataSource,
      message:"",
      LocksNumdispaly:'block',
      tokenLocksReport:'none',
    });
    (num===0)?this.setState({nextpageButtom:'最后一页'}) : this.setState({nextpageButtom:'下一页'});
    (_i===this.state.AllLocks.length-1)?this.setState({lastpageButtom:'第一页'}) : this.setState({lastpageButtom:'上一页'});
  }

  lastpage = async () =>{
    const dataSource = [];
    const num = (this.state.pagefrom+10 <this.state.AllLocks.length) 
      ? this.state.pagefrom+10 : this.state.AllLocks.length-1; //65
    const _i = (num-9>0)?num-9:0;
    for(var i=num; i>=_i; i--){
      const getLockInfo = await LockLoad.methods.getLockInfo(this.state.AllLocks[i]).call();
      if(getLockInfo[0] === '1' && getLockInfo[2] !== '0' ){
      const a = [];
        a[0] = getLockInfo[3];   //通证符号
        a[1] = getLockInfo[5];  //通证地址
        a[2] = this.state.AllLocks[i];  //锁地址
        a[5] = String( (web3.utils.fromWei(getLockInfo[2],'ether'))*10**(18-getLockInfo[4]).toFixed(0) ); //锁仓余额
        a[6] = String( Math.ceil(getLockInfo[1]/3600/24) );  //锁仓倒计时
      dataSource.push(a);
      }
      this.setState({message:"加载中……"+ i});
    }
    this.setState({
      pagefrom:num,  //55
      pageto:_i, //46
      dataList:dataSource,
      message:"",
      LocksNumdispaly:'block',
      tokenLocksReport:'none',
    });
    (_i===0)?this.setState({nextpageButtom:'最后一页'}) : this.setState({nextpageButtom:'下一页'});
    (num===this.state.AllLocks.length-1)?this.setState({lastpageButtom:'第一页'}) : this.setState({lastpageButtom:'上一页'});
  }

  //通过通证的地址查询
  searchByAdr = async () =>{
    const Adr = []; //该通证下的所有锁
    const ERC20 = new web3.eth.Contract(erc20abi.abi,this.state.searchAdr);
    const symbol = await ERC20.methods.symbol().call();
    const decimals = await ERC20.methods.decimals().call();
    const tokenTotalSupply = await ERC20.methods.totalSupply().call()
    var tokenLocksTotal = 0 ;
    const dataSource = [];
    for(var i=this.state.AllLocks.length-1; i>=0; i--){
      const Lock =  new web3.eth.Contract(LockABI.abi,this.state.AllLocks[i]);
      const tokenAdr = await Lock.methods.tokenAdr().call();
      if(tokenAdr.toLowerCase() === this.state.searchAdr.toLowerCase()
        ){
          Adr.push(this.state.AllLocks[i]);
        }
      this.setState({message:"搜索中……"+ i});
    }
    for(var ii=Adr.length-1; ii>=0; ii-- ){
      const Lock =  new web3.eth.Contract(LockABI.abi,Adr[ii]);
      const lock1 = await Lock.methods.lock1().call();
      const balance = await ERC20.methods.balanceOf(Adr[ii]).call();
      if(lock1 === '1'  && balance !== '0' ){
      const getTime = await Lock.methods.getTime().call();
      const a = [];
        a[0] = symbol;   //通证符号
        a[1] = this.state.searchAdr;  //通证地址
        a[2] = Adr[ii];  //锁地址
        a[5] = String( (web3.utils.fromWei(balance,'ether'))*10**(18-decimals).toFixed(0) ); //锁仓余额
        a[6] = String( Math.ceil(getTime/3600/24) );  //锁仓倒计时
      dataSource.push(a);
      tokenLocksTotal = tokenLocksTotal + parseInt(a[5]);
      }
      this.setState({message:"加载中……"+ i});
    }

    const LGAdr = [
      "0x25dac981520ce00cd64336db2045c25e03e4cb69",
      "0xf2d3d487257e83bd850ba2110fa9956ca4eea6eb",
      "0x1183c167163a43d71f5610ccb4c2292b9d91777f"
      ];
    if(this.state.searchAdr.toLowerCase() === '0x7cc0c1a34b1ae55f9bd2a19ece9d4b166b66e70c'){
      for(var iii=0; iii<=LGAdr.length-1; iii++ ){
        const LGLock =  new web3.eth.Contract(LockABI.abi,LGAdr[iii]);
        const balance = await ERC20.methods.balanceOf(LGAdr[iii]).call();
        const getTime = await LGLock.methods.getTime().call();
        const LG = [];
        LG[0] = symbol;   //通证符号
        LG[1] = this.state.searchAdr;  //通证地址
        LG[2] = LGAdr[iii];  //锁地址
        LG[5] = String( (web3.utils.fromWei(balance,'ether'))*10**(18-decimals).toFixed(0) ); //锁仓余额
        LG[6] = String( Math.ceil(getTime/3600/24) );  //锁仓倒计时
        dataSource.push(LG);
        tokenLocksTotal = tokenLocksTotal + parseInt(LG[5]);
      }
        const LGadr2 = '0x93e612a005d808878e0b15dde671dc0e6646f3c6';
        const balance = await ERC20.methods.balanceOf(LGadr2).call();
        const LG = [];
        LG[0] = symbol;   //通证符号
        LG[1] = this.state.searchAdr;  //通证地址
        LG[2] = LGadr2;  //锁地址
        LG[5] = String( (web3.utils.fromWei(balance,'ether'))*10**(18-decimals).toFixed(0) ); //锁仓余额
        LG[6] = '0'
        dataSource.push(LG);
        tokenLocksTotal = tokenLocksTotal + parseInt(LG[5]);
    }
    this.setState({
      dataList:dataSource,
      message:"",
      tokenLocksNum:Adr.length,
      tokenLocksTotal,
      tokenTotalSupply:String( (web3.utils.fromWei(tokenTotalSupply,'ether'))*10**(18-decimals).toFixed(0) ),
      LocksNumdispaly:'none',
      tokenLocksReport:'block',
    });
  }

  //通过锁的地址查询
  searchByLockAdr = async () =>{
    const dataSource = [];
    this.setState({message:"搜索中……"});
      const Lock =  new web3.eth.Contract(LockABI.abi,this.state.searchLock);
      const lock1 = await Lock.methods.lock1().call();
      const tokenAdr = await Lock.methods.tokenAdr().call();
      const ERC20 =  new web3.eth.Contract(erc20abi.abi,tokenAdr);
      const balance = await ERC20.methods.balanceOf(this.state.searchLock).call();
      if(lock1 === '1' && balance !== '0' ){
      const symbol = await ERC20.methods.symbol().call();
      const decimals = await ERC20.methods.decimals().call();
      const getTime = await Lock.methods.getTime().call();
      const a = [];
        a[0] = symbol;   //通证符号
        a[1] = tokenAdr;  //通证地址
        a[2] = this.state.searchLock;  //锁地址
        a[5] = String( (web3.utils.fromWei(balance,'ether'))*10**(18-decimals).toFixed(0) ); //锁仓余额
        a[6] = String( Math.ceil(getTime/3600/24) );  //锁仓倒计时
      dataSource.push(a);
      }
    this.setState({
      dataList:dataSource,
      message:"",
      LocksNumdispaly:'none',
      tokenLocksReport:'none',
    });
  }

  //锁仓机介绍开关
  open2 = () => {
    if(this.state.onoff2 ==='none'){
      this.setState({onoff2:"block"});
    }else{
      this.setState({onoff2:"none"});
    }
  }

  //弹窗开关
  handleCancel = () => {
    this.setState({visible: false,
        admin:'', //指导员
        owner:'', //拥有者
        amount:'', //释放量
        day:'', //释放间隔天数
        time2:''
    })
  };

  handleCance2 = () => {
    if(this.state.visible2 === true){
      this.setState({visible2: false})
    }else{
      this.setState({visible2: true})
    }
  };


 
  render(){

    const columns = [
      {
        title: '通证',
        dataIndex: '0',
        key:'0',
        width: 50,
        align: 'left',
        ellipsis: 'true',
      },
      {
        title: '锁额',
        dataIndex: '5',
        ellipsis: 'true',
        align: 'center',
      },
      {
        title: '天数',
        dataIndex: '6',
        width: 60,
        ellipsis: 'true',
        align: 'center',
      },
      {
        title: '锁地址',
        dataIndex: '2',
        ellipsis: 'true',
        align: 'center',
      },
    ];

    
      const content2 = (
        <div>
          通证名：{this.state.symbol}&emsp;&emsp;
          <CopyToClipboard text={this.state.tokenAdr}>
          <Button size='small' type='primary' >复制通证地址</Button>
          </CopyToClipboard>
        </div>
      );

      const content3_1 = (
        <div>
            <div>操作指导员</div>
            <div>无通证持有权</div>
        </div>
      );
      const content3 = (
        <div>
          <Popover content={content3_1}  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          指导
        </div>
      );

  return (
    <div>
           <div style={{marginTop:'10px'}}>
              <Button   color='green' size="middle">
                <Link to="/Lockhome">所有锁仓</Link>
              </Button>
              &nbsp;
              <Button  color='green' size='middle'  >
                <Link to="/LockMy">我的锁仓</Link>
              </Button>
              <Button   type="primary"  color='green' size="middle" style={{marginBottom:'5px',float:'right'}} >
                <Link to="/LockCreat">创建新锁</Link>
              </Button>
          </div>
    <Card  type="inner" size='small'  >
          
          <div >
                <Input  placeholder="输入通证地址查询"
                    onChange={event=>{this.setState({searchAdr:event.target.value})}}
                    suffix={
                      <Button type="primary" size="middle" 
                        onClick={this.searchByAdr} 
                        >查询
                      </Button>
                  }
                />

              <Input style={{marginTop:'10px'}}
                    onChange={event=>{this.setState({searchLock:event.target.value})}}
                    placeholder="输入单个锁地址查询"
                    suffix={
                      <Button type="primary" size="middle" 
                        onClick={this.searchByLockAdr}
                        >查询
                      </Button>
                  }
                />
          </div>
          <div style={{display:this.state.tokenLocksReport}} >
            <div style={{textAlign:'center', fontWeight:'bold'}}>
              {this.state.symbol}锁仓报告
            </div>
            锁具数:{this.state.tokenLocksNum}；锁仓额:{this.state.tokenLocksTotal},
            占发行量的{(this.state.tokenLocksTotal/this.state.tokenTotalSupply*100).toFixed(2)}%
          </div>
          <Table size='small' 
              columns={columns}  
              dataSource={this.state.dataList} 
              pagination={false}
              customRow="click"
              onRow = {(record) => {
                return {
                  onClick: async() => {
                       await this.setState({
                        symbol: record[0],
                        tokenAdr: record[1],
                        lockAdr: record[2],
                        balance: record[5],
                        time: record[6],
                      })
                      this.loadOneInfo()
                      this.setState({visible: true})
                    }
                  }
                  }
                  }
              />
              <div style={{textAlign:'left',float:'left'}}>
                <Button  type='primary'  size="small"  onClick={() => {this.lastpage()}}>
                    {this.state.lastpageButtom}
                </Button>
              </div>
              <div style={{textAlign:'right',float:'rigth'}}>
                <Button  type='primary'  size="small"  onClick={() => {this.nextpage()}}>
                    {this.state.nextpageButtom}
                </Button>
              </div>
              <div style={{clear:'both'}}></div>
          <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
            {this.state.message}
          </div>
      </Card>
    

    
    <Card type="inner" size='small'  >
      <Button shape='round' type='primary' block  size="small"  onClick={() => {this.open2()}}>锁仓机介绍</Button>
      <div style={{display:this.state.onoff2}}>
            <div style={{marginTop:'10px'}}>
                  <Button size="small" danger onClick={this.handleCance2}>图片说明</Button>
            </div>
            <b>为什么需要锁仓机？</b>
            <div>&emsp;锁仓机可以将项目方承诺锁仓的通证锁住，所有相关用户可在链改网上查看，
                从而解决项目方与投资者之间的信任。</div>
          <Divider  style={{marginTop:'8px',marginBottom:'8px'}} />
            <b>如何查询</b>
            <div>&emsp;凡是通过锁仓机创建的锁均显示在列表中(按时间倒序排列)。
              <br/>
              &emsp;您也可以输入通证的合约地址查询该通证的所有锁仓(第一栏查询框)。
              <br/>
              &emsp;或者输入单个锁的地址查询该地址下的锁仓详情(第二栏查询框)。</div>
          <Divider  style={{marginTop:'8px',marginBottom:'8px'}} />
            <b>名词解释</b>
            <div>&emsp;通证地址：通证所对应的地址，相当于身份证，是通证的唯一身份证明。
              例：'0x7cc0c...e70c'</div>   
            <div>&emsp;锁额：锁住的通证数量</div> 
            <div>&emsp;天数：解锁剩余天数</div>
            <div>&emsp;锁地址：该锁的地址</div>
          <Divider  style={{marginTop:'8px',marginBottom:'8px'}} />
            <b>免责声明</b>
            <div>&emsp;
              您通过本工具创建的锁并将通证锁在该锁下等同于该通证由该锁合约与Heco链共同保管，
              本平台并没有保管您的通证。</div>
              <div>&emsp;
              同时区块链技术属高前沿性技术，存在各种无法预知的风险,
              本平台不承担任何原因导致的您的资产损失
            </div>
      </div>
    </Card>
    <Modal title={content2} visible={this.state.visible} 
        onCancel={this.handleCancel} 
        footer={[
          <Button type='primary' onClick={this.handleCancel}>关闭</Button>,
           ]}
    >
        <p>
          <Input id='i1' addonBefore="锁地址" value={this.state.lockAdr} 
            disabled
            suffix={
                <CopyToClipboard  text={this.state.lockAdr}>
                <Button  size="small" ><CopyOutlined /></Button>
                </CopyToClipboard>
            }
          />
        </p>
        <p>
          <Input id='i1' addonBefore={content3} value={this.state.admin} 
            disabled
            suffix={
                <CopyToClipboard  text={this.state.admin}>
                <Button  size="small" ><CopyOutlined /></Button>
                </CopyToClipboard>
            }
          />
        </p>
        <p>
          <Input id='i1' addonBefore="拥有者" value={this.state.owner} 
            disabled
            suffix={
                <CopyToClipboard  text={this.state.owner}>
                <Button  size="small" ><CopyOutlined/></Button>
                </CopyToClipboard>
            }
          />
        </p>
        <p>
          <Input id='i1' addonBefore="锁仓余额" value={this.state.balance}
            disabled
          />
        </p>
        <p>
          <Input id='i1' addonBefore="解锁倒计时" value={this.state.time + '天'} 
            disabled
          />
        </p>
        <p>
          <Input id='i1' addonBefore="释放规则" value={this.state.amount + '/' + this.state.day + '天'}
            disabled
          />
        </p>
        <p>
          <Input id='i1' addonBefore="释放间隔倒计时" value={this.state.time2 + '天'}
            disabled
          />
        </p>
        
      </Modal>

      <Modal  visible={this.state.visible2}  //图片简介
          onCancel={this.handleCance2} 
          footer={[
            <Button type='primary' onClick={this.handleCance2}>关闭</Button>,
             ]}
        >
          <div style={{textAlign:'center'}}>
              <div className='lockinfo' ></div>
          </div>
        </Modal>
    </div>
  );
}
}

export default App;
