import { useState, useEffect } from "react";
import { NavLink, useLocation } from "react-router-dom";
import { Menu } from 'semantic-ui-react';


function LockCeloHead() {
  const location = useLocation();
  const [activeItem, setActiveItem] = useState('');

  useEffect(() => {
    setActiveItem(location.pathname);
  }, [location]);
  return (
    <div>
      <Menu inverted className='bridgeUser' style={{
        maxWidth: '500px', margin: '2px auto auto auto',
        borderRadius: '10px',
        background: 'rgba(28,100,177,1)'
      }}>
        <NavLink to="/LockCeloHome" activeClassName="active">
          <Menu.Item
            name='主页'
            active={activeItem === '/LockCeloHome'}
            onClick={() => setActiveItem('/LockCeloHome')}
          />
        </NavLink>
        <NavLink to="/LockCeloCreat" activeClassName="active">
          <Menu.Item
            name='封装'
            active={activeItem === '/LockCeloCreat'}
            onClick={() => setActiveItem('/LockCeloCreat')}
          />
        </NavLink>
        <Menu.Menu position='right' >
          <NavLink to="/LockCeloBalance" activeClassName="active">
            <Menu.Item
              name='余额'
              active={activeItem === '/LockCeloBalance'}
              onClick={() => setActiveItem('/LockCeloBalance')}
            />
          </NavLink>
          <NavLink to="/LockCeloMy" activeClassName="active">
            <Menu.Item
              name='我的'
              active={activeItem === '/LockCeloMy'}
              onClick={() => setActiveItem('/LockCeloMy')}
            />
          </NavLink>

        </Menu.Menu>
      </Menu>
    </div>
  );
}

export default LockCeloHead;