import React, { Component } from 'react'
import { Button } from 'antd';
import { Group } from 'antd/lib/avatar';
import { Link } from 'react-router-dom';

export class head extends Component {
    
    render() {
        return (
            <div style={{background:'#ff4dbb9c',padding:'3px',borderRadius:'5px'}}>
                <Group >
                    <Button    color='green' size="middle"  >
                        <Link to="/messageHome">收件箱</Link>
                    </Button>&nbsp;&nbsp;
                    {/* <Link to="/HBgive">
                        <Button style={{paddingRight:'5px'}}  color='green' size="middle">留言墙&nbsp;
                            <div style={{borderRadius:'11px', background:'#ff4d4f',color:'white',paddingLeft:'5px',paddingRight:'5px', float:'right'}}>
                                99+
                            </div>
                        </Button>
                    </Link> */}
                </Group>
                <Button type="primary"  color='green' size="middle" style={{float:'right'}} >
                        <Link to="/messageNew/0">写短信</Link>
                </Button>
            </div>
        )
    }
}

export default head
