import React, { Component } from 'react'
import { Menu } from 'semantic-ui-react'
import { Link } from 'react-router-dom';

export class head extends Component {

  render() {
    return (
      <Menu inverted className='bridgeUser' style={{
        maxWidth: '500px', margin: '2px auto auto auto',
        background: '#171f31cc', borderRadius: '10px'
      }}>
        <Link to="/BridgeQueryV2">
          <Menu.Item
            name='查询'
          />
        </Link>

        <Menu.Menu position='right'>
 
          <Link to="/BridgeCreateV2">
            <Menu.Item
              name='添加'
            />
          </Link>
          <Link to="/BridgeManageV2">
            <Menu.Item
              name='管理'
            />
          </Link>
        </Menu.Menu>
      </Menu>
    )
  }
}

export default head
