import React, { Component } from 'react'
import { Menu } from 'semantic-ui-react'
import { Link } from 'react-router-dom';

export class head extends Component {

  render() {
    return (
      <Menu inverted className='bridgeUser' style={{
        maxWidth: '500px', margin: '2px auto auto auto',
        background: '#171f31cc', borderRadius: '10px'
      }}>
        <Link to="/BridgeQuery">
          <Menu.Item
            name='查询'
          />
        </Link>
        <Link to="/BridgeSend">
          <Menu.Item
            name='发送'
          />
        </Link>
        <Link to="/bridgeAuto">
          <Menu.Item
            name='自动化'
          />

        </Link>
        <Menu.Menu position='right'>
          <div style={{display:'none'}}>
            <Link to="/BridgeCreate">
              <Menu.Item
                name='LG'
              />
            </Link>
          </div>
          <Link to="/BridgeCreate">
            <Menu.Item
              name='添加'
            />
          </Link>
          <Link to="/BridgeManage">
            <Menu.Item
              name='管理'
            />
          </Link>
        </Menu.Menu>
      </Menu>
    )
  }
}

export default head
