import { Modal, ModalHeader, ModalContent, Button, ModalActions } from 'semantic-ui-react'

interface Props {
    open: boolean;
    onClose: () => void;
    tokenName: string | undefined,
    user: string | undefined,
    unlockTime: number | undefined,
    betweenTime: number | undefined,
    onceAmount: number | undefined,
    balance: number | undefined,
    CreateLock: () => Promise<void>
}


function CreatConfim({ open, onClose, tokenName, user, unlockTime, betweenTime, onceAmount, balance, CreateLock }: Props) {

    return <>
            <Modal
                size='mini'
                open={open}
                style={{ position: 'fixed', top: '180px' }}
            >
                <ModalHeader style={{ textAlign: 'center', fontSize: '1.2em', padding: '0.5em 0'}}>请确认</ModalHeader>
                <ModalContent >
                    <div>封装通证：{tokenName}</div>
                    <div>封装总量：{balance}</div>
                    <div>单次释放：{onceAmount}</div>
                    <div>锁盒天数：{unlockTime}</div>
                    <div>间隔天数：{betweenTime}</div>
                    <div style={{ wordBreak: 'break-all' }}>用户地址：{user}</div>
                </ModalContent>
                <ModalActions style={{ display: 'flex', justifyContent: 'flex-end', padding: '0.5em 0.5em'}}>
                    <Button onClick={onClose} >
                        取消
                    </Button>
                    <Button positive >
                        确认并发送
                    </Button>
                </ModalActions>
            </Modal>
        {/* </Card> */}
    </>

}

export default CreatConfim