import React from 'react';
import {  Card } from 'antd';

function Edu() {
  return(
    <div>
        <Card>
        <b>知之非难，行之不易</b>
            <h5>ahr999  2018-10-01</h5>
            <p>决定把《囤比特币》写成一个完整的、永不过时的系列，本文是序。</p>
            <p>2018年的国庆节，阳光明媚，秋高气爽。我最喜欢北京的秋天，原本这是个应该进行户外活动的好日子，我早早的起床。却临时决定宅在家中，写点东西。</p>
            <p>最近一段时间，我把全部业余时间都用来写《囤比特币》系列了。</p>

            <p>写作自然希望有人读。很庆幸，《囤比特币》系列确实有读者。咱们真的跟四年前不一样了，那时我写的文章最多三五条评论。</p>

            <p>当然，也不能因为有人愿意读，我就去写，10万+的垃圾文章比比皆是，我并不希望再为这个世界多创造垃圾。我必须要有实实在在的干货可以分享。很庆幸，我确实有。谈起囤比特币这个话题，我就思如泉涌。</p>

            <p>原本已经不写关于比特币的文章很久了。机缘巧合写了一篇回忆的文章《四年一个轮回，不光有世界杯，还有比特币》，居然获得了25万的阅读量（我感觉微博数据有水分）。我没有做过任何推广，这个阅读量大大出乎了我的预料。表面上看，现在和四年前都是熊市，但是形势或许真的与四年前不一样了，至少关注度是不可同日而语的。</p>

            <p>最近，我花了点时间重新关注比特币圈子里的动态，感觉也与当年有了巨大差异。</p>

            <p>就从币圈媒体说起吧，虽然各类公众号一大堆，但是发的全是些没营养的内容。很多文章，随便瞟一眼就知道作者是个小白，啥也不懂，瞎扯淡。其它的要么搞行情分析，明天币价除了涨就是跌，要不就横盘；要么就推销币，错过了比特币，不能再错过ABCDEFG，全是一个套路。</p>

            <p>读这些东西，难道不会让人变傻么？那些比特币早期玩家都去哪里了？就没有人能够给大家写点干货么？</p>

            <p>环顾四周，大家不是忙着发币，就是忙着推销币。有良知的，会告诉你，仍然看好比特币。没良知的，基本提都不提比特币，全部推销各种垃圾。</p>

            <p>任何一个流量入口，无论是媒体，交易所，还是所谓的钱包，全都在发币。</p>

            <p>作者们已经不能踏踏实实的写作了，一看就是个卖软文的；媒体已经不能踏踏实实的做资讯了，一看就是个卖站台费的；交易所已经不能踏踏实实提供交易了，一看就是个卖上币费的。</p>

            <p>能圈到钱当然也是本事。但是，我很奇怪的是，难道就没有人还记得当初的情怀吗？</p>

            <p>好吧，我错了，情怀确实不能当饭吃。</p>

            <p>在中文比特币圈里，我们不仅仅是缺少程序员，缺少自愿维护比特币的核心开发者，而且我们缺少布道者，缺少像 Andreas M. Antonopoulos 这样的灵魂人物。</p>

            <p>当然啦，我自己也要检讨。虽然比特币带给我巨大的经济回报，但是我却没有为它做过太多有意义的事情。</p>

            <p>我总想等待别人出头。</p>

            <p>因为我知道我不可能成为 Andreas M. Antonopoulos 那样的人。我的所有收入都不是来源于圈内，我还需要赚更多钱，囤更多币。比特币不是我的主业，我还有其它梦想要实现，而实现那个梦想不仅需要大量的钱，而且需要了解很多领域的技术储备。</p>

            <p>但是，如果我不站出来试试的话，还有另外一个人会站出来吗？虽然我知道比我更适合做这事的人大有人在，但是，我又完全看不到有人要站出来的迹象。</p>

            <p>同时，我过去一直很怀疑，我站出来就有意义吗？从大的方向看，很多事情的结局是注定的。我知道很多人会倾家荡产，这种事过去四年一直发生，多少人含泪流血离场。我写还是不写，并不可能影响事情的发展。是的，我其实有些麻木。</p>

            <p>但是同时，我也很乐观，因为来一轮熊市，等牛鬼蛇神全部死翘翘，必然又迎来新的春天。我写还是不写，比特币都会自顾自的发展，高速的发展。</p>

            <p>你看，结局是注定的，我理应什么都不做，耐心等待。</p>

            <p>但是，有时候我会很难过。全局的发展从来不关心个体的死活，伴随着成千上万人倾家荡产，比特币仍旧高速发展，这很残酷。</p>

            <p>如果我写的东西能够挽救那么3-5个人呢？虽然对全局而言没有任何意义，但是或许可以挽救某个个体的一生。</p>

            <p>好吧，哪怕挽救1个人也是好的，这是我决定写《囤比特币》的初衷。</p>

            <p>我写得内容，要么是我一直坚信的，要么是我亲身经历的。是我希望分享的经验和思考，也是我对比特币倾注的全部感情。 </p>

            <p>刚开始我以为我只需要写几篇，后来我发现能写的内容越来越多。已经完成的有6篇，它们构成第一个部分：入门，目的是帮助大家入门。</p>

            <p>《比特币与理想主义》我们在参与一场社会实验，它存在失败的可能性，但是我们无怨无悔。</p>
            <p>《下车太早只因愿景太小》这场实验的目标很大，如果一切顺利，比特币的价格可能会在20年后涨到1.6亿人民币。</p>
            <p>《囤比特币：你离财富自由还有多远？》我们没有其他能耐，只能靠囤积比特币，并耐心地等待属于自己的财富自由。</p>
            <p>《囤比特币：冲动、孤独、无聊与矛盾》虽然会经历冲动、孤独、无聊和矛盾等心理考验，但是我们已经做好准备囤币。</p>
            <p>《囤比特币：手握私钥的快感》虽然掌握私钥有点麻烦，但是我们仍然准备自己对自己负责。</p>
            <p>《囤比特币：如何管理私钥？》管理私钥其实并没有想象的那么麻烦，但我们需要把握好几个原则。</p>
            <p>囤比特币，入门的标准是：明确比特币的目标，愿意参与实验，自愿承担风险，并开始利用比特币储值，自己掌握比特币私钥，并进行冷储存。</p>

            <p>后面的内容，我大概列了个清单，发现还可以写10篇左右。考虑到写作过程中还能产生新的思路，而且不断的有人问我问题，内容只会更多。</p>

            <p>囤比特币绝对不是一件轻松的事。知之非难，行之不易。所以，我才会有这么多的内容可以写。</p>

            <p>我的目标是写出永不过时的经典。所以，我尽量写与时事无关的事情，越是热点事件，我就越不会写。我希望，多年以后，当你再次拿出我的文章时，仍然觉得它不过时，而且愿意把它分享给那时的新人。</p>

            <p>写作也不是一件轻松的事。知之非难，行之不易。我希望自己能把这个作品完成。</p>

            <p>祝大家国庆节愉快！</p>

            <p>2018年10月1日</p>

            <p>于北京</p>​​​​
        </Card>
    </div>
  )

}

export default Edu;
