import { Multicall } from 'ethereum-multicall';
import web3 from "../../ABI/web3";
import { AbiItem } from 'ethereum-multicall/dist/esm/models';
import { SelectWeb3 } from '../../components/global/functions';
import erc20abi from '../../ABI/ERC20.abi.json';
import LGOracleABI from '../../ABI/LGOracle.json';
import { MATICadr, CELOadr } from './globals';
import { getChainInfo } from '../../components/global/functions';
import BigNumber from 'bignumber.js';

export const processPrice = (p: number): number | string => {
  if (p < 0.01) {
    let pString: string = p.toString();
    let zeroCount = pString.split('.')[1].match(/^0*/)?.[0].length || 0;
    let decimalPart = (pString.split('.')[1].replace(/^0+/, '')).substring(0, 2);
    return `0.(${zeroCount})${decimalPart}`;
  } else if (p > 1000) {
    return p.toFixed(0);
  } else {
    return p.toFixed(3);
  }
};

export const createMulticall = (adr: string) => {
  const multicall = new Multicall({
    web3Instance: web3, tryAggregate: true,
    multicallCustomContractAddress: adr
  });
  return multicall;
}

export const createMulticallForPrice = async () => {
  const selectWeb3 = await SelectWeb3('0x89')
  const chainInfo = await getChainInfo('0x89')
  const multicall = new Multicall({
    web3Instance: selectWeb3, tryAggregate: true,
    multicallCustomContractAddress: chainInfo.ChainAdr.multicall
  });
  return multicall;
}

export const To18decimals = async (tokenAdr: string, value: number): Promise<string> => {
  const ERC20 = new web3.eth.Contract(erc20abi.abi as AbiItem[], tokenAdr);
  const decimals = await ERC20.methods.decimals().call();
  let Value = new BigNumber(value / 10 ** (18 - decimals)) + ''
  return Value;
}

export const FromWeiToEther = async (tokenAdr: string, decimals: number, value: string): Promise<string> => {
  let Decimals: number | undefined = decimals
  let Value: string = '';
  if (decimals === 0 ) {
    const ERC20 = new web3.eth.Contract(erc20abi.abi as AbiItem[], tokenAdr);
    Decimals = Number(await ERC20.methods.decimals().call());
    Value = new BigNumber(Number(value) / 10 ** (Decimals)) + ''
  }
  Value = new BigNumber(Number(value) / 10 ** (Decimals)) + ''
  return Value;
}

export const getTokenBalance = async (tokenAdr: string, account: string): Promise<string> => {
  const ERC20 = new web3.eth.Contract(erc20abi.abi as AbiItem[], tokenAdr);
  const balance = await ERC20.methods.balanceOf(account).call();
  return balance;
}

//value 单位为wei
export const chackTokenBlance = async (tokenAdr: string, account: string, value: string): Promise<boolean> => {
  const ERC20 = new web3.eth.Contract(erc20abi.abi as AbiItem[], tokenAdr)
  const balance = await ERC20.methods.balanceOf(account).call();
  return new BigNumber(balance).isGreaterThanOrEqualTo(new BigNumber(value))
}


export const TokenPrice = async (tokenAdr: string): Promise<string> => {
  const selectWeb3 = await SelectWeb3('0x89')
  let price
  if (selectWeb3) {
    const LGOracle = new selectWeb3.eth.Contract(LGOracleABI as AbiItem[], MATICadr.LGOracle);
    price = await LGOracle.methods.PriceRMB(tokenAdr).call();
  }
  return price;
}

export const TokenPriceThisChain = async (tokenAdr: string, LGOracleAdr: string): Promise<string> => {
  let price
  const LGOracle = new web3.eth.Contract(LGOracleABI as AbiItem[], LGOracleAdr);
  price = await LGOracle.methods.PriceRMB(tokenAdr).call();
  return price;
}

//用于获取token在MATIC上面的地址
export const getTokenPriceAdr = async (tokenAdr: string): Promise<string> => {
  let PriceAdr: string | undefined = undefined
  switch (tokenAdr) {
    case CELOadr.LG: PriceAdr = MATICadr.LG; break;
    case CELOadr.JF: PriceAdr = MATICadr.JF; break;
    case CELOadr.YHT: PriceAdr = MATICadr.YHT; break;
  }
  if (typeof PriceAdr === 'string') {
    return PriceAdr; // 在 switch 语句之后返回 PriceAdr 的值
  } else {
    return PriceAdr = '0'
  }
}

//用于将秒转成天、时、分、秒
export const SecondToDay = (second: number): string => {
  let res: string = ''
  if (second >= 0) {
    if (second > 86400) {
      res = Math.floor(second / 86400).toString() + '天'
    }
    else if (second > 3600) {
      res = Math.floor(second / 3600).toString() + '时'
    }
    else if (second > 60) {
      res = Math.floor(second / 60).toString() + '分'
    }
    else {
      res = second.toString() + '秒'
    }
  }
  return res
}
