import React, { Component } from 'react';
import '../../App.css';
import { connect } from "react-redux"
import { getChainInfo } from '../../components/global/functions';
import { Button } from 'antd';
import { HashRouter as Router } from 'react-router-dom';
import Logo from '../../logo192.png';
import { Toast } from 'antd-mobile'

export class foot extends Component {
  constructor(props) {
    super(props)
    this.state = {
      ChainName: ''
    }
  }

  async componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.ChainID !== this.props.ChainID
      || nextProps.UserAdr !== this.props.UserAdr
    ) {
      await this.getChain()
    }
  }

  async componentDidMount() {
    await this.getChain()
  }

  getChain = async () => {
    await getChainInfo(window.ChainID).then((result) => {
      this.setState({
        ChainName: result.ChainName
      })
    })
  }

  chackChainId = async (chainName, to, msg) => {
    if (this.state.ChainName !== chainName) {
      return Toast.show({ content: msg })
    } else {
      window.location.href = to  //当前页面打开URL页面
    }
  }

  render() {
    const toAdmin = `${process.env.PUBLIC_URL}/#/messageNew/` + global.constants.admin;
    return (
      <div >
        <img style={{ maxWidth: '25px', height: 'auto' }} src={Logo} alt="" />
        LG链改网
        <Button size='small'>
          <Router>
            <div onClick={() => { this.chackChainId('HECO', toAdmin, '请切换链至HECO链') }}>
              <div>联系我们</div>
            </div>
          </Router>
        </Button>

      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
    LGbalance: state.LGbalance,
    selectCoinOrAdr: state.selectCoinOrAdr
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(foot) 
