import React, { Component } from 'react'
import CryptoJS from 'crypto-js';


class indexV2 extends Component {
    constructor(props) {
        super(props)
        this.state = {
            secretKey: '123456',
            decryptedString:'1',
            bytes32Data:'2',
            inputString:"连哥",

        }
    }

    // 加密函数，将字符串加密为 bytes32
    encryptStringToBytes32 = () => {
        const key = CryptoJS.SHA256("123456").toString(CryptoJS.enc.Hex).slice(0, 32);
        const iv = CryptoJS.lib.WordArray.random(128 / 8);
        const encryptedData = CryptoJS.AES.encrypt("连哥", key, { iv: iv });
        const bytes32Data = '0x' + iv.concat(encryptedData.ciphertext).toString(CryptoJS.enc.Hex);
        console.log(bytes32Data)
        const ciphertextWithIV = bytes32Data.replace(/^0x/, '');
        const iv2 = CryptoJS.enc.Hex.parse(ciphertextWithIV.slice(0, 32));
        const cipherTextWithIV = CryptoJS.enc.Hex.parse(ciphertextWithIV);
        const key2 = CryptoJS.SHA256("123456").toString(CryptoJS.enc.Hex).slice(0, 32);
        const decryptedData = CryptoJS.AES.decrypt({ ciphertext: cipherTextWithIV }, key2, { iv: iv2  });
        console.log(decryptedData)
        const decryptedString = decryptedData.toString(CryptoJS.enc.Utf8);
        console.log(decryptedString);
        this.setState({decryptedString})
        return decryptedString
    };





    render() {
        return (
            <>
                <button onClick={this.encryptStringToBytes32}>解密</button>
                {this.state.decryptedString}
            </>
        )
    }
}

export default indexV2











