import React, { Component } from 'react'
import { Dropdown } from 'semantic-ui-react'

export class chainSelectIDfrom extends Component {
  constructor(props) {
    super(props)

    this.state = {
      chainList: global.chainList,
      chainID: '0x00'
    }
  }


  render() {
    return (
      <div>
        <Dropdown
          defaultValue={this.state.chainID} options={this.state.chainList}
          onChange={(e, { value }) => this.props.chainSelectFrom(value)}
        />
      </div>
    )
  }
}

export default chainSelectIDfrom
