import React,{ Component }  from 'react';
import {Spin,Modal} from 'antd';

class loading extends Component {
    constructor(props){
        super(props);
        this.state={
            loadwindow:false,
        }
}

  loadingOpen = (loading) => {
    this.setState({
      loadwindow: loading,
    })
  };

render(){
  return(
    <div>
      <Modal visible={this.state.loadwindow}
        closable={false}
        width={100}
        footer={null}
        style={{  top: '200px'}}
      >
        <div style={{textAlign:'center'}}>
          <Spin size="large"/>
          <div style={{color:'#1890ff'}}>加载中</div>
        </div>
      </Modal>
    </div>
  );
}
}

export default loading;

