import React, { Component } from 'react';
import TestCard from './test3Card';
import { Row, Col,Card,BackTop } from 'antd';

export class test3 extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             
        }
    }

    componentDidMount(){

    }
 
    render() {
        let cardList=[] ;
        for(var i=0;i<300;i++){
            cardList.push(
                <Col xs={{ span: 12}}>
                    <TestCard a={i}/>
                </Col>
            )
        }
        return (
            <div>
                <Card style={{textAlign:'center' }} size='small'>
                {/* {cardList} */}
                <Row gutter={[5, 8]} justify="space-between" style={{ background:'yellow'}}>
                    {cardList}
                </Row>
                </Card>
                <BackTop />
            </div>
        )
    }
}

export default test3
