import React, { Component } from 'react'
import { Dropdown, Button, Modal, Icon } from 'semantic-ui-react'
import { ChangeChain, getChainInfo } from '../global/functions';
import '../global';
import { connect } from "react-redux"

export class ChainSelect extends Component {
  constructor(props) {
    super(props)
    this.state = {
      chainList: [],
      window: false,
      ChainNameTip: ''
    }
  }

  async componentWillReceiveProps(nextProps) {
    if (nextProps.ChainID !== this.props.ChainID) {
      this.chainId(await window.ethereum.request({ method: 'eth_chainId' }))
    }
  }

  async componentDidMount() {
    const _list = global.chainList
    const list = []
    for (var i = 1; i < _list.length; i++) {
      list.push(_list[i])
    }
    this.setState({ chainList: list })
    this.chainId(await window.ethereum.request({ method: 'eth_chainId' }))
  }

  chainId = async (chainId) => {
    this.props.sendStore("ChainID", chainId)
    await getChainInfo(chainId).then((result) => {
      this.props.sendStore("ChainName", result.ChainName)
    })
  }

  chainSelect = async (chainId) => {
    let chainID = await window.ethereum.request({ method: 'eth_chainId' })
    if (chainID !== chainId) {
      const result = await getChainInfo(chainId)
      this.windowOpen(result.ChainName)
    }
  }

  ChangChain = async () => {
    await ChangeChain(this.state.ChainNameTip)
    this.setState({ window: false })
    setTimeout(async () => {
      this.chainId(await window.ethereum.request({ method: 'eth_chainId' }))
    }, 2500)
  }

  //窗开关
  windowClose = () => {
    this.setState({ window: false })
  };
  windowOpen = (ChainName) => {
    this.setState({ window: true, ChainNameTip: ChainName })
  };

  render() {
    return (
      <div>
        <Dropdown
          options={this.state.chainList}
          text={this.props.ChainName}
          onChange={(e, { value }) => this.chainSelect(value)}
        />
        <div>
          <Modal
            basic
            open={this.state.window}
            size='mini'
            style={{ wordWrap: 'break-word' }}
          >
            <Modal.Content >
              <div style={{ textAlign: 'center' }}>
                将切换至{this.state.ChainNameTip}链
              </div>
            </Modal.Content>
            <Modal.Actions style={{ float: 'right' }}>
              <Button color='green' inverted onClick={() => { this.ChangChain() }} >切换</Button>
              <Button basic color='red' inverted onClick={() => this.windowClose(false)}>
                <Icon name='remove' /> 取消
              </Button>
            </Modal.Actions>
          </Modal>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    ChainName: state.ChainName,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ChainSelect) 
