import React, { useState, useEffect, useCallback } from 'react'
import { Image, Table, Card, Header } from 'semantic-ui-react'
import erc20abi from '../../../../../ABI/ERC20.abi.json';
import web3 from "../../../../../ABI/web3";
import { AbiItem, ContractCallResults } from 'ethereum-multicall/dist/esm/models';
import tokenList from '../../../../../tokenList/lockTokenList/balance.json'
import Head from './head';
import { FromWeiToEther, getTokenPriceAdr, createMulticall, createMulticallForPrice, SecondToDay } from "../../../../../components/global/functionTS"
import BalanceWindow from './balanceWindow';
import { getChainInfo } from '../../../../../components/global/functions';
import { MATICadr } from '../../../../../components/global/globals';
import LGOracleABI from '../../../../../ABI/LGOracle.json';

interface balance {
    key: number,
    address: string,
    symbol: string,
    balance: string,
    rmb: number,
    decimals: number
}

interface AddOrSub {
    key: number;
    blockNumber: number
    time: string;
    addOrSub: string;
    value: string;
}

interface Props {
    ChainID: string
    UserAdr: string
    isOtherUser: boolean
}

function Balance(props: Props) {

    const [List, setList] = useState<balance[]>([])

    const getBalance = useCallback(async () => {

        if (props.isOtherUser !== true) {
            const lockBalanceMy = localStorage.getItem('lockBalanceMy');
            if (lockBalanceMy !== null) {
                setList(JSON.parse(lockBalanceMy))
            }
        }


        let balances: balance[] = []
        if (tokenList && tokenList.tokens) {
            let tokens: any[] = tokenList.tokens
            balances = tokens.map(token => ({
                key: token.key,
                address: token.address,
                symbol: token.symbol,
                balance: '?', // 设置初始余额为0
                rmb: 0,
                decimals: token.decimals
            }));
        }

        const Call: any[] = [];
        const chainInfo = await getChainInfo(props.ChainID)
        const multicall = createMulticall(chainInfo.ChainAdr.multicall)
        for (let i = 0; i < balances.length; i++) {
            const call = {
                reference: balances[i].address,
                contractAddress: balances[i].address,
                abi: erc20abi.abi,
                calls: [{
                    // reference: balances[i].value,
                    methodName: 'balanceOf',
                    methodParameters: [props.UserAdr]
                }]
            }
            Call.push(call)
        }
        const result: ContractCallResults = await multicall.call(Call)
        if (result) {
            for (let i = 0; i < balances.length; i++) {
                const balanceResult = Object.keys(result.results).find((item: string) => item === balances[i].address)
                if (balanceResult !== undefined) {
                    let _balance = result.results[balanceResult].callsReturnContext[0].returnValues[0].hex
                    let balance = Number(await FromWeiToEther('', balances[i].decimals, _balance)).toFixed(1)
                    balances[i].balance = balance
                }
            }

        }
        setList([...balances])

        const call2: any[] = [];
        const multicall2 = await createMulticallForPrice()
        for (let i = 0; i < balances.length; i++) {
            const tokenPriceAdr = await getTokenPriceAdr(balances[i].address);
            const _call = {
                reference: balances[i].address,
                methodName: 'PriceRMB',
                methodParameters: [tokenPriceAdr]
            }
            call2.push(_call)
        }
        const Call2 = [{
            reference: 'prices',
            contractAddress: MATICadr.LGOracle,
            abi: LGOracleABI,
            calls: call2
        }]

        const result2: ContractCallResults = await multicall2.call(Call2)
        if (result) {
            for (let i = 0; i < balances.length; i++) {
                const priceResult = result2.results.prices.callsReturnContext.find((item) => item.reference === balances[i].address)
                if (priceResult !== undefined) {
                    let _price = priceResult.returnValues[0].hex
                    let price = _price / 1e8
                    let rmb = (Number(balances[i].balance) * price).toFixed(1)
                    balances[i].rmb = Number(rmb)
                }
            }

        }
        setList([...balances])

        if (props.isOtherUser !== true) {
            const arrayToStoreString = JSON.stringify(balances);
            localStorage.setItem('lockBalanceMy', arrayToStoreString);
        }

    }, [props.ChainID, props.UserAdr, props.isOtherUser])

    useEffect(() => {
        getBalance()
    }, [getBalance])

    const findHistory = async (index: balance) => {
        setWindowOpen(true)
        setWindowMsg("加载中……请稍后")
        setAddOrSubList([])
        setLoading(true)
        let addOrSubList: AddOrSub[] = []
        setTokenName(index.symbol)
        const ERC20 = new web3.eth.Contract(erc20abi.abi as AbiItem[], index.address);
        let userAdr = '0x' + (Array(64).join('0') + props.UserAdr.slice(2)).slice(-64)
        let lastBlock = await web3.eth.getBlockNumber()

        await ERC20.getPastEvents('Transfer', {
            fromBlock: lastBlock - 2000,
            toBlock: lastBlock,
            topics: [null, null, userAdr] //放右边是加
        }, async (error: any, events: any) => {
            let Events = events.reverse()
            for (let i = 0; i < Events.length; i++) {
                addOrSubList.push({
                    key: i,
                    blockNumber: Events[i].blockNumber,
                    time: SecondToDay((lastBlock - Events[i].blockNumber) * 5),
                    addOrSub: '+',
                    value: Number(web3.utils.fromWei(Events[i].returnValues.value, 'ether')).toFixed(2)
                })
            }
        })

        await ERC20.getPastEvents('Transfer', {
            fromBlock: lastBlock - 2000,
            toBlock: lastBlock,
            topics: [null, userAdr, null] //放中间是减
        }, async (error: any, events: any) => {
            let Events = events.reverse()
            for (let i = 0; i < Events.length; i++) {

                addOrSubList.push({
                    key: i,
                    blockNumber: Events[i].blockNumber,
                    time: SecondToDay((lastBlock - Events[i].blockNumber) * 5),
                    addOrSub: '-',
                    value: Number(web3.utils.fromWei(Events[i].returnValues.value, 'ether')).toFixed(2)
                })
            }
        })
        const sortedArray = addOrSubList.sort((a, b) => b.blockNumber - a.blockNumber);
        setAddOrSubList(sortedArray)
        if (addOrSubList.length > 0) {
            setLoading(false)
        } else {
            setLoading(true)
            setWindowMsg("最近2小时无纪录")
        }

    }

    const [windowMsg, setWindowMsg] = useState('')
    const [windowOpen, setWindowOpen] = useState(false)
    const [tokenName, setTokenName] = useState('')
    const [addOrSubList, setAddOrSubList] = useState<AddOrSub[]>([])
    const [loading, setLoading] = useState(false)  //记录余额变化加载情况

    const renderTableRow = (item: balance) => (
        <Table.Row key={item.key} onClick={() => findHistory(item)}>
            <Table.Cell >
                <Header as='h4' image>
                    <Image src={`${process.env.PUBLIC_URL}/logo/${item.symbol}.svg`} rounded style={{ width: '22px', height: '22px' }} />
                    <Header.Content style={{ color: 'white' }}>
                        {item.symbol}
                    </Header.Content>
                </Header>
            </Table.Cell>
            <Table.Cell textAlign="right">
                {item.balance}
            </Table.Cell>
            <Table.Cell textAlign="right">{item.rmb}


            </Table.Cell>

        </Table.Row>

    );

    useEffect(() => {
        // 执行一些副作用，比如订阅事件或设置定时器
        // 返回一个清理函数，在组件卸载时执行
        return () => {
          // 取消订阅事件
          // subscription.unsubscribe();
      
          // 清除定时器
          // clearInterval(timer);
        };
      }, []);

    return (
        <div>
            <Head />
            <Card type="inner" style={{ width: 'inherit', minWidth: '312px', maxWidth: '500px', margin: '0px auto auto auto', backgroundColor: '#6435c93d' }}>
                <div>
                    <Table celled fixed singleLine unstackable color='blue' inverted>
                        <Table.Header  >
                            <Table.Row >
                                <Table.HeaderCell textAlign="left" style={{ width: '30%' }}>礼品</Table.HeaderCell>
                                <Table.HeaderCell textAlign="right">余额</Table.HeaderCell>
                                <Table.HeaderCell textAlign="right" >价值</Table.HeaderCell>
                            </Table.Row>
                        </Table.Header>
                        <Table.Body >
                            {List.map((item) => renderTableRow(item))}
                        </Table.Body>
                    </Table>
                </div>
            </Card>
            <BalanceWindow
                open={windowOpen}
                onClose={() => setWindowOpen(false)}
                tokenName={tokenName}
                addOrSubList={addOrSubList}
                loading={loading}
                windowMsg={windowMsg}
            />
        </div>
    )

}

export default Balance

