import React, { Component } from 'react'
import {Card} from 'antd'
import '../../../App.css';

export class test3Card extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             a:this.props.a
        }
    }
    
    render() {
        return (
            <div>
                <Card className='cardMsg'>
                    {this.state.a}<br/>
                    {this.state.a}<br/>
                    {this.state.a}
                </Card>
            </div>
        )
    }
}

export default test3Card
