import { useState, useEffect } from "react";
import { NavLink, useLocation } from "react-router-dom";
import { Menu } from 'semantic-ui-react';


function LockCeloHead() {
  const location = useLocation();
  const [activeItem, setActiveItem] = useState('');

  useEffect(() => {
    setActiveItem(location.pathname);
  }, [location]);
  return (
    <div>
      <Menu inverted className='bridgeUser' style={{
        maxWidth: '500px', margin: '2px auto auto auto',
        borderRadius: '10px',
        background: 'rgba(28,100,177,1)'
      }}>
        <NavLink to="/LockCeloHomeV0" activeClassName="active">
          <Menu.Item
            name='主页'
            active={activeItem === '/LockCeloHomeV0'}
            onClick={() => setActiveItem('/LockCeloHomeV0')}
          />
        </NavLink>
        <NavLink to="/LockCeloCreatV0" activeClassName="active">
          <Menu.Item
            name='封装'
            active={activeItem === '/LockCeloCreatV0'}
            onClick={() => setActiveItem('/LockCeloCreatV0')}
          />
        </NavLink>
        <Menu.Menu position='right' >
          <NavLink to="/LockCeloBalanceV0" activeClassName="active">
            <Menu.Item
              name='余额'
              active={activeItem === '/LockCeloBalanceV0'}
              onClick={() => setActiveItem('/LockCeloBalanceV0')}
            />
          </NavLink>
          <NavLink to="/LockCeloMyV0" activeClassName="active">
            <Menu.Item
              name='我的'
              active={activeItem === '/LockCeloMyV0'}
              onClick={() => setActiveItem('/LockCeloMyV0')}
            />
          </NavLink>

        </Menu.Menu>
      </Menu>
    </div>
  );
}

export default LockCeloHead;