import { connect } from "react-redux"
import BalancePage from './components/balancePage'
import { useParams } from 'react-router-dom';


function Balance(props: any) {
    const { OtherUserAdr }: { OtherUserAdr: string} = useParams();
    console.log(OtherUserAdr)
    return (
        <div>
            <BalancePage ChainID={props.ChainID} UserAdr={OtherUserAdr} isOtherUser={true} />
        </div>
    )

}

const mapStateToProps = (state: any) => {
    return {
        ChainID: state.ChainID,
        UserAdr: state.UserAdr,
        LGbalance: state.LGbalance,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        sendStore(tp: any, val: any) {
            const action = { type: tp, value: val }
            dispatch(action)
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Balance)

