import React,{ Component }  from 'react';
import { Spin,Modal,Progress} from 'antd';

class loading extends Component {
    constructor(props){
        super(props);
        this.state={
            loadwindow:false,
            loading:'none',
            complete:'none',
            msgColor:'#ff4d4f',
            error:'none',
            loadmessage:''
        }
}

  //加载窗开关
  loadingClose = () => {
    this.setState({loadwindow: false})
  };
  loadingOpen = (loading,complete,error,msg) => {
    this.setState({
      loadwindow: true,
      loading,
      complete,
      error,
      loadmessage:msg
    })
  };

render(){
  return(
    <div>
      <Modal  visible={this.state.loadwindow} 
        closable={true}
        width={250}
        onCancel={this.loadingClose} 
        footer={null}
      >
        <div style={{textAlign:'center'}}>
          <div style={{display:this.state.loading}}>
            <Spin size="large" />
            <div style={{color:"rgb(43,153,255)",fontSize:"13px",textAlign:'center'}}>
              {this.state.loadmessage}
            </div>
          </div>
          <div style={{display:this.state.complete}}>
            <Progress type="circle" percent={100} width={45}/>
            <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
              {this.state.loadmessage}
            </div>
          </div>
          <div style={{color:'#ff4d4f',fontSize:"13px",textAlign:'center',display:this.state.error}}>
              {this.state.loadmessage}
          </div>
        </div>
      </Modal>
    </div>
  );
}
}

export default loading;

