import React, { Component } from 'react';
import { connect } from "react-redux"
import '../../App.css';
import { Card, Badge } from 'antd';
import { Link, withRouter } from 'react-router-dom';
import Message from '../../ABI/message';
import Function from "../../components/global/function.js"
import { getChainInfo, ChangeChain } from '../../components/global/functions';
import { Toast } from 'antd-mobile'

class mainCardShentai extends Component {
  constructor(props) {
    super(props);
    this.state = {
      LGDEX: '',
      Bip39: '',
      msgCount: '',
      inboxCount: '',
      ChainID: '',
      ChainName: '',
      UNI: 'https://app.uniswap.org/'
    }
  }

  async componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.ChainID !== this.props.ChainID
      || nextProps.UserAdr !== this.props.UserAdr
    ) {
      await this.first()
    }
  }

  async componentDidMount() {
    await this.first()
  }

  first = async () => {
    await this.getChain()
    // const url = await window.location.host;
    this.setState({ LGDEX: `${process.env.PUBLIC_URL}/LGDEX/${this.state.ChainName}/index.html` });
    this.setState({ BIP39: `${process.env.PUBLIC_URL}/BIP39/index.html` });
    // if (url === 'lgqkl.github.io') {
    //   this.setState({ LGDEX: `${process.env.PUBLIC_URL}/LGDEX/${this.state.ChainName}/index.html` });
    //   // this.setState({ BIP39: `https://${url}/LG-QKL/BIP39/index.html` });
    //   // this.setState({ UNI: `https://${url}/LG-QKL/UNI/index.html`});
    // } else {
    //   this.setState({ LGDEX: 'https://' + url + '/LGDEX/' + this.state.ChainName + '/index.html' });
    //   // this.setState({ BIP39: 'https://' + url + '/BIP39/index.html' });
    //   // this.setState({ UNI: 'https://' + url + '/UNI/index.html' });
    // }

    try {
      await Message.methods.inboxCount().call({ from: window.defaultAccount }).then(
        (inboxCount) => {
          if (localStorage['isRead' + window.defaultAccount] !== undefined) {
            var _isRead = localStorage['isRead' + window.defaultAccount].split(',');
            this.setState({ msgCount: inboxCount - _isRead.length + 1 })
          } else {
            this.setState({ msgCount: inboxCount })
          }
          this.setState({ ChainID: window.ChainID })
        }
      )
    } catch (error) {
      // console.log(error)
    }
  }

  getChain = async () => {
    await getChainInfo(window.ChainID).then((result) => {
      this.setState({
        ChainName: result.ChainName
      })
    })
  }

  chackChainId = async (chainName, to, msg) => {
    if (this.state.ChainName !== chainName) {
      return Toast.show({ content: msg })
    } else {
      this.props.history.push(to)
    }
  }

  changChain = async (chainName, to, msg) => {
    const result = await getChainInfo(this.props.ChainID)
    if (result.ChainName !== chainName) {
      await ChangeChain(chainName)
      this.props.history.push(to)
    }
    else {
      this.props.history.push(to)
    }
  }


  render() {
    return (
      <div>
        <Card type="inner" title="" size='small' style={{ borderRadius: '10px' }}>
          <div>
            <div >
              <div style={{ width: '25%', float: 'left' }}>
                <div style={{ textAlign: 'center' }}>
                  <div onClick={() => { this.changChain('CELO', '/LockCeloHome', '请切换链至CELO链') }}>
                    <div className='giftBox' />
                  </div>
                  <div>礼品盒</div>
                </div>
              </div>

              <div style={{ width: '25%', float: 'left' }}>
                <div style={{ textAlign: 'center' }}>
                  <div onClick={() => { this.chackChainId('HECO', '/hongbao', '请切换链至HECO链') }}>
                    <div className='hongbao' />
                  </div>
                  <div>抢红包</div>
                </div>
              </div>

              <div style={{ width: '25%', float: 'left' }}>
                <div style={{ textAlign: 'center' }}>
                  <Link to="/BridgeV2">
                    <div className='bridge' />
                  </Link>
                  <div>跨链桥</div>
                </div>
              </div>

              <div style={{ width: '25%', float: 'left' }}>
                <div style={{ textAlign: 'center' }}>
                  <div onClick={() => { this.chackChainId('HECO', '/messageHome', '请切换链至HECO链') }}>
                    <div className='lgmessage' >
                      <div style={{ float: 'right' }} >
                        <Badge count={this.state.msgCount} size='default' />
                      </div>
                    </div>
                  </div>
                  <div>链短信</div>
                </div>
              </div>
              <div style={{ clear: 'both' }}></div>
            </div>
            <div style={{ marginTop: '15px' }}>

              <div style={{ width: '25%', float: 'left' }}>
                <div style={{ textAlign: 'center' }}>
                  <div onClick={() => { this.changChain('MATIC', '/LGswap', '请切换链至MATIC链') }}>
                    <div className='UNI' />
                  </div>
                  <div>UNI交易所</div>
                </div>
              </div>

              <div style={{ width: '25%', float: 'left' }}>
                <div style={{ textAlign: 'center' }}>
                  <Link to="/OtherShentaiCard">
                    <div className='other' />
                  </Link>
                  <div>更多…</div>
                </div>
              </div>

              <div style={{ clear: 'both' }} />
            </div>

            <div style={{ marginTop: '15px' }}>

              <div style={{ clear: 'both' }} />
            </div>
          </div>
        </Card>
        <Function ref={Function => { this.Function = Function }} />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
    LGbalance: state.LGbalance,
    selectCoinOrAdr: state.selectCoinOrAdr
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(mainCardShentai))
