import React, { Component } from 'react';
import { connect } from "react-redux"
import { Table, Popup, Input, Icon, List, Button, Modal } from 'semantic-ui-react'
import { QuestionCircleOutlined } from '@ant-design/icons';
import '../../../App.css';
import { Card } from 'antd';
import { Toast } from 'antd-mobile'
import Head from './components/head';
import CodeWindow from './components/CodeWindow';
import web3 from '../../../ABI/web3';
import Loading from '../../../components/loadingReport';
import erc20abi from '../../../ABI/ERC20.abi.json';
import bridgeAbi from '../../../ABI/BridgeV2.json';
import { getChainInfo, isApprove, SelectWeb3 } from '../../../components/global/functions';
import TokensSelect from "./components/tokensSelect"
import ChainList from "../../../components/ChainSelect"
import ChainSelectIDto from '../../../components/ChainSelect/chainSelectIDto'
import { Multicall } from 'ethereum-multicall';
import Fun from "../../../components/global/function";
import BridgeSend from './BridgeSend';
const BigNumber = require('bignumber.js');
const maxQueryBlock = 2000  //最大单次查询区块数
const historyTime = 2   //几小时至今

export class bridgeindex extends Component {
  constructor(props) {
    super(props)
    this.state = {
      bridgeAdr: '',
      LGadr: '',
      LGApproveDisplay: 'none', //LG授权按钮
      bridgeContract: '',
      selectTokenDisplay: 'none',
      blockCount: maxQueryBlock,
      history: [],
      hasKickHistory: 0,
      hasKickHistoryTime: historyTime,
      currentCode: '',
      loadHistoryComplete: 'disabled',
      loadingHistory: false,
      value: '',
      ChainTo: '0x00',
      feeLG: '?',
      ownersList: '',
      minValue: '?',
      operatordisplay: 'no',
      tokenInfo: false,
      selectCurTokenAdr: '',
      multicallAdr: '',
      loadComplete: true,
      loading: false,
      selectWeb3: '',
      dangerousSend: false,
      sendButton: '发  送',
      inputData: '1',
    }
  }

  async componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.ChainID !== this.props.ChainID
      || nextProps.UserAdr !== this.props.UserAdr
      || nextProps.selectCoinOrAdr !== this.props.selectCoinOrAdr
    ) {
      console.log('object')
      if (nextProps.selectCoinOrAdr !== '' && nextProps.selectCoinOrAdr !== undefined) {
        this.setState({ loadComplete: true, loading: true })
        await this.setdefault()
        this.setState({ minValue: '?' })
        await this.first(nextProps.selectCoinOrAdr)
        this.setState({ loadComplete: false, loading: false })
        console.log('object')
      }
    }
  }

  async componentDidMount() {
    if (this.props.selectCoinOrAdr !== '' && this.props.selectCoinOrAdr !== undefined) {
      this.setState({ loadComplete: true, loading: true })
      await this.first(this.props.selectCoinOrAdr)
      this.setState({ loadComplete: false, loading: false })
    }

  }

  getChain = async () => {
    await getChainInfo(window.ChainID).then((result) => {
      this.setState({
        bridgeAdr: result.ChainAdr.bridgeV2,
        LGadr: result.ChainAdr.LG,
        multicallAdr: result.ChainAdr.multicall,
      })
    })
  }

  first = async (selectOrAdr) => {
    if (window.ChainID === null) {
      return Toast.show({ content: '未连接钱包' })
    }
    this.setState({ test: 0.1 })
    await this.getChain()
    this.setState({ test: 0.2 })
    if (this.state.bridgeAdr === undefined) {
      return Toast.show({ content: '当前链暂未部署跨链桥' })
    }
    let bridgeContract = await new web3.eth.Contract(bridgeAbi, this.state.bridgeAdr)
    await this.setState({ bridgeContract })
    this.setState({ test: 0.3 })
    //判断是否已授权LG
    await isApprove(this.state.LGadr, this.state.bridgeAdr).then((result) => {
      if (result === 'no') {
        this.setState({ LGApproveDisplay: 'block' })
      } else {
        this.setState({ LGApproveDisplay: 'none' })
      }
    })
    console.log(1)
    this.setState({ test: 1 })
    await this.setState({ test4: this.state.multicallAdr })
    const multicall = new Multicall({
      web3Instance: web3, tryAggregate: true,
      multicallCustomContractAddress: this.state.multicallAdr
    });
    const call = [{
      reference: 'bridge',
      contractAddress: this.state.bridgeAdr,
      abi: bridgeAbi,
      calls: [
        {
          reference: 'selectCurTokenAdr',
          methodName: 'tokenAdr',
          methodParameters: [selectOrAdr]
        },
        {
          reference: 'Fee',
          methodName: 'Fee',
          methodParameters: []
        }
      ]
    }]
    const Call = await multicall.call(call)
    this.setState({ test: 2 })
    console.log(2)
    let result = Call.results.bridge.callsReturnContext
    let selectCurTokenAdr = result[0].returnValues[0]
    this.setState({ selectCurTokenAdr })
    await this.isApproveSelect(selectCurTokenAdr)
    let fee = result[1].returnValues[0]
    console.log(3)
    this.setState({ test: 3 })
    const _feeLG = await this.state.bridgeContract.methods.feeLG(fee).call()
    console.log(_feeLG)
    console.log(4)
    this.setState({ test: 4 })
    const feeLG = (Number(_feeLG)/1e18).toFixed(1)
    console.log(feeLG)
    this.setState({ feeLG })
    this.setState({ loadHistoryComplete: '', loadingHistory: false })
  }

  Owners = async (selectOrAdr) => {
    let ownersList = []
    const owner = await this.state.bridgeContract.methods.Owner(selectOrAdr).call()
    for (let i = 0; i < owner.length; i++) {
      let list = (<List.Item >{owner[i]}</List.Item>)
      ownersList.push(list)
    }
    this.setState({ ownersList })
  }

  fee = async () => {
    this.setState({ feeLG: '?' })
    const fee = await this.state.bridgeContract.methods.Fee().call()
    const _feeLG = await this.state.bridgeContract.methods.feeLG(fee).call()
    const feeLG = parseFloat(web3.utils.fromWei(await _feeLG, "ether")).toFixed(1)
    this.setState({ feeLG })
  }

  //判断是否已授权选择的通证
  isApproveSelect = async (adr) => {
    await isApprove(adr, this.state.bridgeAdr).then((result) => {
      if (result === 'no') {
        this.setState({ selectTokenDisplay: 'block' })
      } else {
        this.setState({ selectTokenDisplay: 'none' })
      }
    })
  }

  Approve = async (adr) => {
    await this.Fun.Approve(adr, this.state.bridgeAdr)
    this.first(this.props.selectCoinOrAdr)
  }

  seeHistory = async () => {
    this.setState({ loadHistoryComplete: 'disabled', loadingHistory: true })
    let user = window.defaultAccount
    let topicsAdr = '0x' + (Array(64).join(0) + user.slice(2)).slice(-64)
    let lastBlock
    let fromBlock
    let toBlock
    const historyList = this.state.history
    await web3.eth.getBlockNumber().then((block) => { lastBlock = block });
    if (this.state.hasKickHistory === 0) {
      fromBlock = lastBlock - maxQueryBlock
      toBlock = lastBlock
    } else {
      this.setState({
        blockCount: this.state.blockCount + maxQueryBlock,
        hasKickHistoryTime: this.state.hasKickHistoryTime + historyTime
      })
      fromBlock = lastBlock - this.state.blockCount
      toBlock = fromBlock + maxQueryBlock
    }
    await this.state.bridgeContract.getPastEvents('Lock', {
      fromBlock: fromBlock,
      toBlock: toBlock,
      topics: [null, null, topicsAdr, null]
    },
      async (error, events) => {
        if (events !== undefined && events.length !== 0) {
          for (let i = events.length - 1; i >= 0; i--) {
            let event = events[i].returnValues
            let ToChainName
            let num = Number(event.ToChain)
            let ToChain = '0x' + (num).toString(16)
            await getChainInfo(ToChain).then((result) => {
              ToChainName = result.ChainName
            })
            let tokenAdr = await this.state.bridgeContract.methods.tokenAdr(event.tokenAdr).call()
            const ERC20 = await new web3.eth.Contract(erc20abi.abi, tokenAdr)
            let tokenSymbol = await ERC20.methods.symbol().call()
            let decimals = await ERC20.methods.decimals().call()
            let _value = await new BigNumber(event.value * 10 ** (18 - decimals)).toFixed()
            let value = await web3.utils.fromWei(_value, 'ether')
            let bridgeCode = event.FromChain + '&' + event.ToChain + '&' + event.tokenAdr + '&' + event.who + '&' + event.time + '&' + event.value + '&' + event.password
            historyList.push(
              <Table.Row >
                <Table.Cell>{ToChainName}</Table.Cell>
                <Table.Cell >{tokenSymbol}</Table.Cell>
                <Table.Cell onClick={() => {
                  if (value.length >= 6) {
                    Toast.show({
                      content: value,
                    })
                  }
                }} >{value}</Table.Cell>
                <Table.Cell onClick={() => { this.codeWindow(bridgeCode) }}>{bridgeCode}</Table.Cell>
              </Table.Row>
            )
            console.log(i)
            if (i === events.length - 1) {
              this.setState({ inputData: bridgeCode })
            }
          }
        }
        this.setState({ history: historyList, hasKickHistory: 1, loadHistoryComplete: '', loadingHistory: false })
        console.log(this.state.inputData)
      })
    console.log(this.state.inputData)
  }

  lockToken = async () => {
    if (this.props.ChainID === this.state.ChainTo) {
      return Toast.show({ content: '目标链不能与出发链相同' })
    }
    if (this.state.ChainTo === '0x00') {
      return Toast.show({ content: '未选择目标链' })
    }
    console.log('001')
    //判断目标链是否部署该通证
    let isResult = await this.isHasToken(this.props.selectCoinOrAdr, this.state.ChainTo)
    console.log(11)
    if (isResult === 'noBridge') {
      return Toast.show({ content: '该目标链暂未部署跨链桥' })
    }
    if (isResult === 'noToken') {
      return Toast.show({ content: '目标链未有该通证' })
    }
    if (this.state.value === '' || this.state.value === '0') {
      return Toast.show({ content: '未输入数量' })
    }
    if (this.state.selectTokenDisplay === 'block') {
      return Toast.show({ content: '未授权该通证' })
    }
    //判断用户余额是否充足
    try {
      const ERC20 = await new web3.eth.Contract(erc20abi.abi, this.state.selectCurTokenAdr)
      let _balance = await ERC20.methods.balanceOf(this.props.UserAdr).call()
      const tokenDecimals = await ERC20.methods.decimals().call()
      let balance = await new BigNumber(_balance / 10 ** (tokenDecimals)).toFixed()
      if (Number(this.state.value) > Number(balance)) {
        this.setState({ loadComplete: 'disabled' })
        return Toast.show({ content: '您的该通证余额不足' })
      } else {
        this.setState({ loadComplete: '' })
      }
    } catch (e) { console.log(e) }

    let curAdr = await this.state.bridgeContract.methods.tokenAdr(
      this.props.selectCoinOrAdr
    ).call()
    const ERC20 = await new web3.eth.Contract(erc20abi.abi, curAdr);
    const decimals = await ERC20.methods.decimals().call();
    let value = new BigNumber(this.state.value / 10 ** (18 - decimals)) + ''
    console.log(value)
    console.log(this.props.ChainID)
    console.log(this.state.ChainTo)
    console.log(this.props.selectCoinOrAdr)
    if (this.props.ChainID === '0x80'
      || this.state.ChainTo === '0x80' 
      ){
        console.log(11)
        if (this.props.selectCoinOrAdr === global.MATIC.LG){
          console.log(22)
          return Toast.show({ content: 
            '自2024年8月13日起,HECO链上的LG将停止跨链至其它链。其它链上的LG也停止跨链至HECO链。' })
        }
    }
    await this.state.bridgeContract.methods.lockToken(
      this.props.ChainID,
      this.state.ChainTo,
      this.props.selectCoinOrAdr,
      web3.utils.toWei(value, 'ether')
    ).send({ from: window.defaultAccount, gas: '200000' })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "打包中……")
        }
      })
      .on('receipt', (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "成功！复制跨链码=>切换至目标链=>粘贴跨链码至接受窗")
          this.setdefault()
          this.seeHistory()
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
        }
      })

    this.setState({ inputData: '' })
  }

  setdefault = async () => {
    this.setState({
      blockCount: maxQueryBlock,
      history: [],
      hasKickHistory: 0,
      hasKickHistoryTime: historyTime,
      value: '',
      dangerousSend: false,
      sendButton: '发  送',
    })
  }

  chainSelectIDto = async (chainId) => {
    const selectWeb3 = await SelectWeb3(chainId)
    this.setState({ selectWeb3, ChainTo: chainId, sendButton: '发  送', dangerousSend: false, value: '' })
    this.fee()
    this.isHasToken(this.props.selectCoinOrAdr, chainId)
  }
  //判断目标链是否部署该通证
  isHasToken = async (tokenAdr, chainID) => {
    let state = this.state
    let bridgeToAdr
    await getChainInfo(chainID).then((result) => {
      bridgeToAdr = result.ChainAdr.bridgeV2
    })
    if (bridgeToAdr === undefined) {
      return (Toast.show({ content: '该目标链暂未部署跨链桥' }), 'noBridge')
    }
    const birdgeToContract = await new state.selectWeb3.eth.Contract(bridgeAbi, bridgeToAdr)
    let allToken = await birdgeToContract.methods.getallToken().call()
    let _is = allToken.some((x) => { return x.toLowerCase() === tokenAdr.toLowerCase() })
    if (_is !== true) {
      return (Toast.show({ content: '该目标链暂未部署该通证' }), 'noToken')
    }
  }

  //判断目标链跨链桥该通证余额是否充足
  isBalanceSufficient = async (value) => {
    this.setState({ loadComplete: true })
    this.setState({ value: value })
    console.log('2b')
    let chainID = this.state.ChainTo
    console.log(chainID)
    const selectWeb3 = await SelectWeb3(chainID)
    console.log(selectWeb3)
    let bridgeAdr;
    let tokenOrAdr = this.props.selectCoinOrAdr
    await getChainInfo(chainID).then((result) => {
      bridgeAdr = result.ChainAdr.bridgeV2
    })
    if (chainID === '0x00') {
      this.setState({ loadComplete: 'disabled' })
      return Toast.show({ content: '未选择目标链' })
    }
    try {
      let bridgeContract = new selectWeb3.eth.Contract(bridgeAbi, bridgeAdr)
      console.log("a")
      const erc20Adr = await bridgeContract.methods.tokenAdr(tokenOrAdr).call()
      console.log("b")
      const ERC20 = await new this.state.selectWeb3.eth.Contract(erc20abi.abi, erc20Adr)
      console.log("c")
      let _balance = await ERC20.methods.balanceOf(bridgeAdr).call()
      console.log("d")
      const tokenDecimals = await ERC20.methods.decimals().call()
      let balance = await new BigNumber(_balance / 10 ** (tokenDecimals)).toFixed()
      if (Number(value) > Number(balance)) {
        this.setState({ dangerousSend: true, sendButton: '目标链余额不足但仍然发送' })
        this.setState({ loadComplete: false })
        return Toast.show({ content: '目标跨链桥该通证余额不足' })
      } else {
        this.setState({ dangerousSend: false, sendButton: '发  送' })
        this.setState({ loadComplete: false })
      }
    } catch (e) { console.log(e) }
    this.setState({ loadComplete: false })
  }

  codeWindow = async (code) => {
    this.CodeWindow.windowOpen();
    this.setState({ currentCode: code })
  }


  render() {
    let state = this.state
    let historyHead = { padding: '2px 11px 2px 11px' }
    return (
      <div>
        <Head />
        <Card type="inner" size='small' className='bridgeUser' style={{ maxWidth: '500px', margin: '0px auto auto auto' }}>
          <div style={{ textAlign: 'center', margin: '0 auto 10px auto' }}>
            <Button.Group >
              <Button >
                发送
              </Button>
              <Button loading={state.loading} primary icon><Icon name='circle outline' /></Button>
              <Button style={{ padding: '5px 10px 5px 18px' }}>
                <TokensSelect />
              </Button>
              <Modal style={{ maxWidth: '400px' }}
                centered={false}
                open={state.tokenInfo}
                onClose={() => this.setState({ tokenInfo: false })}
                trigger={<Button
                  onClick={() => this.setState({ tokenInfo: true })}
                  icon='info' color='yellow'
                />}
                size='small'
                content={
                  <Card type="inner" size='small'
                    style={{ maxWidth: '500px', margin: '0px auto auto auto' }}>
                    <List divided inverted relaxed style={{ wordWrap: 'break-word' }}>
                      <List.Item >
                        <div style={{ color: 'red' }}>警示：任何人都可将任何币添加到跨链桥，请注意分辨合约地址</div>
                      </List.Item>
                      <List.Item >
                        <div>原链:</div>
                        {this.props.selectCoinOrAdr}
                      </List.Item>
                      <List.Item >
                        <div>当链:</div>
                        {state.selectCurTokenAdr}
                      </List.Item>
                    </List>
                  </Card>
                }
              />

            </Button.Group>
          </div>

          <div style={{ display: state.selectTokenDisplay, marginTop: '6px' }}>
            <Button.Group fluid>
              <Button positive
                onClick={() => { this.Approve(this.state.selectCurTokenAdr) }}>授权所选通证</Button>
            </Button.Group>
          </div>

          <div style={{ textAlign: 'center', margin: '10px auto 10px auto' }}>

            <Button positive floated='left' size='large'>
              <ChainList />
            </Button >
            <Icon name='angle double right'
              style={{ fontSize: '28px', color: 'white' }}
            />
            <Button positive floated='right' size='large' >
              <ChainSelectIDto chainSelectIDto={this.chainSelectIDto} />
            </Button>
            <div style={{ clear: 'both' }} />
          </div>

          <div style={{ marginBottom: '10px' }}>
            <Input fluid value={this.state.value}
              placeholder="输入数量" labelPosition='left' label='数量'
              onChange={event => { this.isBalanceSufficient(event.target.value) }}
            />
          </div>
          <Button fluid
            positive
            negative={state.dangerousSend}
            disabled={this.state.loadComplete}
            style={{ marginBottom: '5px' }}
            onClick={() => { this.lockToken() }}
          >{state.sendButton}
          </Button>

        </Card >

        <BridgeSend feeLG={state.feeLG}
          isApprove={this.state.LGApproveDisplay}
          LGbalance={this.props.LGbalance}
          ApproveButton={
            <div style={{ display: state.LGApproveDisplay, marginTop: '6px' }}>
              <Button.Group fluid>
                <Button positive
                  onClick={() => { this.Approve(this.state.LGadr) }}>先授权LG用于支付跨链费</Button>
              </Button.Group>
            </div>
          } />

        <Card type="inner" size='small' className='bridgeUser' style={{ maxWidth: '500px', margin: 'auto auto 2px auto' }}>
          <div >
            <Button fluid size='small' disabled={state.loadHistoryComplete}
              loading={state.loadingHistory}
              onClick={() => { this.seeHistory() }}
            >发送记录({this.state.hasKickHistoryTime}小时至今)&nbsp;&nbsp;
              <Popup
                trigger={<QuestionCircleOutlined />}
                content='每点一次加载过去约2小时记录'
                inverted
                on='click'
              />
            </Button>
          </div>
          <div style={{ display: this.state.history.length === 0 ? 'none' : 'block' }}>
            <Table celled inverted fixed singleLine unstackable='false' style={{ background: 'rgba(255,255,255,0.1)' }}>
              <Table.Header>
                <Table.Row >
                  <Table.HeaderCell style={historyHead}>目标</Table.HeaderCell>
                  <Table.HeaderCell style={historyHead}>通证</Table.HeaderCell>
                  <Table.HeaderCell style={historyHead}>数量</Table.HeaderCell>
                  <Table.HeaderCell style={historyHead}>跨链码</Table.HeaderCell>
                </Table.Row>
              </Table.Header>
              <Table.Body>
                {this.state.history}
              </Table.Body>
            </Table>
          </div>
        </Card>
        <CodeWindow ref={window => { this.CodeWindow = window }} currentCode={this.state.currentCode} />
        <Loading ref={loadwindow => { this.loading = loadwindow }} />
        <Fun ref={Fun => { this.Fun = Fun }} />

      </div >
    )
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
    LGbalance: state.LGbalance,
    selectCoinOrAdr: state.selectCoinOrAdr
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(bridgeindex) 
