import React, { useState, useEffect, useCallback, useRef } from 'react'
import { Card, Button, Input } from 'semantic-ui-react'
import { connect } from "react-redux"
import Head from './components/head';
import '../../../App.css';
import { getChainInfo } from '../../../components/global/functions';
import { Toast } from 'antd-mobile'
import web3 from "../../../ABI/web3";
import WETHABI from "../../../ABI/WETH.json";
import { AbiItem } from 'ethereum-multicall/dist/esm/models';
import { Contract } from 'web3-eth-contract';
import Fun from "../../../components/global/function";
import LoadingReport from '../../../components/loadingReport';

function WETH(props: any) {

    const [globalState, setGlobalState] = useState
        ({
            WETHContractAdr: '',
            WETHContract: undefined as Contract | undefined,
            ETHBalance: '',
            WETHBalance: ''
        });


    //加载初始化数据
    const first = useCallback(async () => {
        try {
            const chainInfo = await getChainInfo(props.ChainID)
            const WETHContract = new web3.eth.Contract(WETHABI as AbiItem[], chainInfo.ChainAdr.WETH)
            const ETHBalance = await web3.eth.getBalance(props.UserAdr)
            const WETHBalance = await WETHContract?.methods.balanceOf(props.UserAdr).call()
            setGlobalState(prevState => ({
                ...prevState,
                WETHContractAdr: chainInfo.ChainAdr.WETH,
                WETHContract: WETHContract,
                ETHBalance: web3.utils.fromWei(ETHBalance, 'ether'),
                WETHBalance: web3.utils.fromWei(WETHBalance, 'ether')
            }))
            LoadingReportRef.current.loadingClose()
        } catch (error) {
            console.log(error)
        }
    }, [props.ChainID, props.UserAdr])


    useEffect(() => {
        first()
    }, [first, props.UserAdr, props.ChainID]);

    const FunRef = useRef<any>(null);
    const LoadingReportRef = useRef<any>(null);


    const [ETHInValue, setETHInValue] = useState<string>('')
    const [WETHInValue, setWETHInValue] = useState<string>('')

    const onChangeETHBalance = async (value: string) => {
        setETHInValue(value)
    }

    const onChangeWETHBalance = async (value: string) => {
        setWETHInValue(value)
    }

    const WETHtoETH = async () => {
        if (!WETHInValue) { return Toast.show({ content: '未输入数量' }) }
        if (WETHInValue > globalState.WETHBalance) { return Toast.show({ content: '余额不足' }) }
        try {
            await globalState.WETHContract?.methods.withdraw(web3.utils.toWei(WETHInValue, 'ether')).send({ from: props.UserAdr, gas: '50000' })
                .on('transactionHash', (transactionHash: string | any[]) => {
                    if (transactionHash.length !== 0) {
                        LoadingReportRef.current.loadingOpen('block', 'none', 'none', "打包中……")
                    }
                })
                .on('receipt', (receipt: { status: boolean; }) => {
                    if (receipt.status === true) {
                        LoadingReportRef.current.loadingOpen('none', 'block', 'none', "成功")
                        first()
                    }
                })
                .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                    if (receipt !== undefined && receipt.status === false) {
                        LoadingReportRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                    }
                })
        } catch (error) {
            console.log(error)
        }
    }

    const ETHtoWETH = async () => {
        if (!ETHInValue) { return Toast.show({ content: '未输入数量' }) }
        if (ETHInValue > globalState.ETHBalance) { return Toast.show({ content: '余额不足' }) }
        try {
            await globalState.WETHContract?.methods.deposit().send({ from: props.UserAdr, gas: '50000', value: web3.utils.toWei(ETHInValue, 'ether') })
                .on('transactionHash', (transactionHash: string | any[]) => {
                    if (transactionHash.length !== 0) {
                        LoadingReportRef.current.loadingOpen('block', 'none', 'none', "打包中……")
                    }
                })
                .on('receipt', (receipt: { status: boolean; }) => {
                    if (receipt.status === true) {
                        LoadingReportRef.current.loadingOpen('none', 'block', 'none', "成功")
                        first()
                    }
                })
                .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                    if (receipt !== undefined && receipt.status === false) {
                        LoadingReportRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                    }
                })
        } catch (error) {
            console.log(error)
        }
    }

    const inputAllETHBalance = async () => {
        setETHInValue(globalState.ETHBalance)
    }

    const inputAllWETHBalance = async () => {
        setWETHInValue(globalState.WETHBalance)
    }

    useEffect(() => { return () => { }; }, []);

    return (
        <div>
            <Head />
            <Card type="inner" style={{ width: 'inherit', minWidth: '312px', maxWidth: '500px', margin: '0px auto auto auto', backgroundColor: 'rgba(33,133,208,0.5)' }}>

                <div style={{ display: 'flex', marginBottom: '10px' }}>
                    <Button icon color='teal' style={{ marginBottom: '3px', marginRight: '0px' }}
                    >MATIC</Button>
                    <Input style={{ width: '20%', marginBottom: '3px' }} value={globalState.ETHBalance} readOnly
                        onClick={inputAllETHBalance}
                    />
                    <Input fluid
                        value={ETHInValue}
                        placeholder="输入要换成WMATIC的数量"
                        type="number"
                        style={{ marginBottom: '3px', flex: '1' }}
                        onChange={(e, { value }) => { onChangeETHBalance(value) }}
                    />
                    <Button icon color='teal' style={{ marginBottom: '3px', marginRight: '0px' }}
                        onClick={ETHtoWETH}
                    >换WMATIC</Button>
                </div>

                <div style={{ display: 'flex', marginBottom: '10px' }}>
                    <Button icon color='teal' style={{ marginBottom: '3px', marginRight: '0px' }}
                    >WMATIC</Button>
                    <Input style={{ width: '20%', marginBottom: '3px' }} value={globalState.WETHBalance} readOnly
                        onClick={inputAllWETHBalance}
                    />
                    <Input fluid
                        value={WETHInValue}
                        placeholder="输入要换成MATIC的数量"
                        type="number"
                        style={{ marginBottom: '3px', flex: '1' }}
                        onChange={(e, { value }) => { onChangeWETHBalance(value) }}
                    />
                    <Button icon color='teal' style={{ marginBottom: '3px', marginRight: '0px' }}
                        onClick={WETHtoETH}
                    >换MATIC</Button>
                </div>
            </Card>
            <Fun ref={FunRef} />
            <LoadingReport ref={LoadingReportRef} />
        </div>
    )

}

const mapStateToProps = (state: any) => {
    return {
        ChainID: state.ChainID,
        UserAdr: state.UserAdr,
        LGbalance: state.LGbalance,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        sendStore(tp: any, val: any) {
            const action = { type: tp, value: val }
            dispatch(action)
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(WETH)



