import React, { Component } from 'react'
import { connect } from "react-redux"
import HangQinV2 from './HangQinV2TS';

export class HangQin extends Component {
  constructor(props) {
    super(props)
    this.state = {
        HangQinPage:<HangQinV2/>
    }
  }

  

  render() {
    return (
      <div>
        {this.state.HangQinPage}
      </div>
    )
  }
}

const mapStateToProps = (state) => {
    return {
      ChainID: state.ChainID,
      UserAdr: state.UserAdr,
      LGbalance: state.LGbalance,
      ChainName: state.ChainName
    }
  }
  
  const mapDispatchToProps = (dispatch) => {
    return {
      sendStore(tp, val) {
        const action = { type: tp, value: val }
        dispatch(action)
      }
    }
  }
  
  export default connect(mapStateToProps, mapDispatchToProps)(HangQin)
  
