import React, { useState, useEffect, useCallback, useRef } from 'react'
import { Modal, List, ListItem, ListContent, ListHeader, ModalHeader, ModalContent, Input, Button, ButtonGroup } from 'semantic-ui-react'
import userAdrBookAbi from '../../../../../ABI/userAdrBook.json'
import { CELOadr } from '../../../../../components/global/globals'
import web3 from "../../../../../ABI/web3";
import { AbiItem } from 'ethereum-multicall/dist/esm/models';
import LoadingReport from '../../../../../components/loadingReport';
import { Toast } from 'antd-mobile'
import { useHistory } from "react-router-dom";

interface Props {
    open: boolean;
    onClose: () => void;
    userAdr: string;
    SendDataToParent: (list: any) => void;
    customerTel: string;
    customerAdr: string;
    setUserTELandAdr: (tel: string, Adr: string) => void;
}


function CreatConfim({ open, onClose, userAdr, SendDataToParent, customerTel, customerAdr, setUserTELandAdr }: Props) {

    const [tel, setTel] = useState<string>(customerTel);
    const [Adr, setAdr] = useState<string | undefined>(customerAdr);
    useEffect(() => {
        setTel(customerTel)
        setAdr(customerAdr)
    }, [customerAdr, customerTel]);

    const SetTel = (value: string) => {
        setTel(value)
    }
    const SetUserAdr = (value: string) => {
        setAdr(value)
    }

    const LoadingReportRef = useRef<any>(null);

    const [saveLoading, setSaveLoading] = useState(false);
    const saveUser = async () => {
        // 检查是否有重复的逻辑
        if (userBook.some(user => user.userTEL === tel)) {
            Toast.show({ content: '该备注名已存在于列表中' })
            return;
        }
        if (userBook.some(user => user.userAdr === Adr)) {
            Toast.show({ content: '该地址已存在于列表中' })
            return;
        }
        try {
            const userAdrBookContract = new web3.eth.Contract(userAdrBookAbi as AbiItem[], CELOadr.userAdrBook)
            await userAdrBookContract.methods.saveUser(tel, Adr).send({ from: userAdr, gas: '100000' })
                .on('transactionHash', (transactionHash: string | any[]) => {
                    if (transactionHash.length !== 0) {
                        setSaveLoading(true)
                    }
                })
                .on('receipt', (receipt: { status: boolean; }) => {
                    if (receipt.status === true) {
                        setSaveLoading(false)
                        readData()

                    }
                })
                .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                    if (receipt !== undefined && receipt.status === false) {
                        setSaveLoading(false)
                        LoadingReportRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                    }

                })
        } catch (error) {
            Toast.show({ content: '地址不合法' })
            console.log(error)
        }
    }


    const [delLoading, setDelLoading] = useState(false)
    const delUser = useCallback(async (id: number) => {
        try {
            const userAdrBookContract = new web3.eth.Contract(userAdrBookAbi as AbiItem[], CELOadr.userAdrBook)
            await userAdrBookContract.methods.delUser(id).send({ from: userAdr, gas: '100000' })
                .on('transactionHash', (transactionHash: string | any[]) => {
                    if (transactionHash.length !== 0) {
                        setDelLoading(true)
                    }
                })
                .on('receipt', (receipt: { status: boolean; }) => {
                    if (receipt.status === true) {
                        setDelLoading(false)
                        readData()
                    }
                })
                .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                    if (receipt !== undefined && receipt.status === false) {
                        setDelLoading(false)
                        LoadingReportRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                    }
                })
        } catch (error) {
            console.log(error)
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [userAdr])

    const [userBookList, setUserBookList] = useState<[]>([]);
    const [userBook, setUserBook] = useState<{
        userTEL: string,
        userAdr: string
    }[]>([])


    const history = useHistory();


    // 双击事件处理函数
    // const clickCountRef = useRef(0);
    // const toOtherUserList2 = useCallback((otherUserAdr: string, otherUserName: string) => {
    //     // 点击计数加一
    //     clickCountRef.current += 1;
    //     // 在一定时间内检测是否连续触发了两次点击
    //     setTimeout(() => {
    //         if (clickCountRef.current === 2) {
    //             // 触发双击事件，执行双击事件的处理逻辑
    //             // toOtherUserList(otherUserAdr);
    //             history.push(`/LockCeloOtherUser/${otherUserAdr}/${otherUserName}`);
    //         }
    //         // 重置点击计数
    //         clickCountRef.current = 0;
    //     }, 500); // 300 毫秒内判断是否触发了双击事件
    // }, [history])

    const toOtherUserList = useCallback((otherUserAdr: string, otherUserName: string) => {
        history.push(`/LockCeloOtherUser/${otherUserAdr}/${otherUserName}`);
    }, [history])

    const toBalance = useCallback((otherUserAdr: string,) => {
        history.push(`/LockCeloBalanceOtherUser/${otherUserAdr}`)
    }, [history])

    const readData = useCallback(async () => {
        try {
            const userAdrBookContract = new web3.eth.Contract(userAdrBookAbi as AbiItem[], CELOadr.userAdrBook)
            const result = await userAdrBookContract.methods.getUserRecords(userAdr).call({ from: userAdr })
            SendDataToParent(result)
            let list = result.map((item: any, index: any) => (
                <ListItem key={index}>
                    <ListContent>
                        <ListHeader>{item.userTEL}</ListHeader>
                        {item.userAdr}
                        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                            <Button icon size='mini' loading={delLoading} color='red' onClick={() => { delUser(index) }}>删除</Button>
                            <ButtonGroup icon size='mini'>
                                <Button icon size='mini' loading={delLoading} onClick={() => { toOtherUserList(item.userAdr, item.userTEL) }}>礼盒</Button>
                                <Button icon size='mini' loading={delLoading} onClick={() => { toBalance(item.userAdr) }}>余额</Button>
                            </ButtonGroup>
                            <Button icon size='mini' color='teal' onClick={() => { setUserTELandAdr(item.userTEL, item.userAdr) }}>选择</Button>
                        </div>

                    </ListContent>
                </ListItem>
            ));
            setUserBookList(list)
            setUserBook(result)
        } catch (error) {
            console.log(error)
        }

    }, [SendDataToParent, delLoading, delUser, setUserTELandAdr, toBalance, toOtherUserList, userAdr])

    useEffect(() => {
        readData()
    }, [readData]);

    useEffect(() => {
        // 执行一些副作用，比如订阅事件或设置定时器
        // 返回一个清理函数，在组件卸载时执行
        return () => {
          // 取消订阅事件
          // subscription.unsubscribe();
      
          // 清除定时器
          // clearInterval(timer);
        };
      }, []);

    return (
        <>
            <Modal
                closeIcon
                size='mini'
                open={open}
                onClose={onClose}
                style={{ position: 'fixed', top: '100px' }}
            >
                <ModalHeader style={{ textAlign: 'center', fontSize: '1.2em', padding: '0.5em 0' }}>地址本</ModalHeader>
                <ModalContent>
                    <div style={{ display: 'flex' }}>
                        <Input fluid
                            action={{
                                color: 'blue',
                                labelPosition: 'left',
                                icon: 'pencil alternate',
                                content: '备注',
                            }}
                            actionPosition='left'
                            value={tel}
                            placeholder="输入用户备注"
                            style={{ marginBottom: '3px', flex: '1' }}
                            onChange={(e, { value }) => SetTel(value)}
                        />

                    </div>
                    <div style={{ display: 'flex' }}>
                        <Input fluid
                            action={{
                                color: 'blue',
                                labelPosition: 'left',
                                icon: 'address book',
                                content: '地址',
                            }}
                            actionPosition='left'
                            value={Adr}
                            placeholder="输入地址"
                            style={{ marginBottom: '3px', flex: '1' }}
                            onChange={(e, { value }) => SetUserAdr(value)}
                        />

                    </div>
                    <div style={{ display: 'flex' }}>
                        <Button fluid
                            loading={saveLoading}
                            color='teal'
                            style={{ width: '200px', margin: 'auto' }}
                            onClick={saveUser}
                        >添 加
                        </Button>
                    </div>
                    <List divided relaxed>
                        {userBookList}
                    </List>
                </ModalContent>
            </Modal>
            <LoadingReport ref={LoadingReportRef} />
        </>
    )

}

export default CreatConfim

