import { Modal, ModalHeader, ModalContent, Table, TableFooter } from 'semantic-ui-react'

interface AddOrSub {
    key: number;
    blockNumber: number
    time: string;
    addOrSub: string;
    value: string;
}

interface Props {
    open: boolean;
    onClose: () => void;
    tokenName: string;
    addOrSubList: AddOrSub[];
    loading: boolean;
    windowMsg:string
}

function LockInfoWindow(props: Props) {
    const renderTableRow = (item: AddOrSub) => (
        <Table.Row key={item.key}>
            <Table.Cell>{item.time}钟之前</Table.Cell>
            <Table.Cell textAlign="right">{item.addOrSub}{item.value}</Table.Cell>
        </Table.Row>
    );

    return (
        <>
            <Modal
                size='mini'
                open={props.open}
                onClose={props.onClose}
                style={{ position: 'fixed', top: '180px' }}
                closeIcon={true}
            >
                <ModalHeader style={{ textAlign: 'center', fontSize: '1em', padding: '0.5em 0' }}>
                    <div style={{
                        display: 'flex', justifyContent: 'space-between', paddingLeft: '0.5em', paddingRight: '0.5em', alignItems: 'center'
                    }}>
                        <div style={{ textAlign: 'center', flex: '1' }}>最近2小时“{props.tokenName}”余额变化</div>
                        
                    </div>
                </ModalHeader>
                <ModalContent >
                    <Table celled fixed singleLine unstackable color='blue' inverted>
                        <Table.Header  >
                            <Table.Row >
                                <Table.HeaderCell textAlign="left" >时间</Table.HeaderCell>
                                <Table.HeaderCell textAlign="right" style={{ width: '30%' }}>数量</Table.HeaderCell>
                            </Table.Row>
                        </Table.Header>
                        <Table.Body >
                            {props.addOrSubList.map((item) => renderTableRow(item))}

                        </Table.Body>
                        <TableFooter>
                            <Table.Cell colSpan={2} textAlign="center">
                                {props.loading === true &&
                                    <div style={{ background: '#1c71b1', color: 'white', paddingLeft: '5px', fontSize: '15px', textAlign: 'center' }}>
                                        {props.windowMsg}</div>}
                            </Table.Cell>

                        </TableFooter>

                    </Table>

                </ModalContent>

            </Modal>

        </>
    )

}

export default LockInfoWindow;