import { Button,Modal } from 'antd';
import { Group } from 'antd/lib/avatar';
import React, { Component } from 'react';
import Message from '../../../../ABI/message';
import LG from '../../../../ABI/LG';
import web3 from "../../../../ABI/web3";
import Loading from '../../../../components/loadingReport';

class buy extends Component {
    constructor(props){
        super(props);
        this.state={
            window:false,
            feeLG:'',
            discount:'',
            Approvedisplay:'none',
            buyButton:'',
        }
    }

    async componentDidMount(){
        const _feeLG = await Message.methods.feeLG().call();
        const discount = await Message.methods.discount().call();
        this.setState({feeLG:(_feeLG/1e18).toFixed(2),discount})
        const _allowance = await  LG.methods.allowance(window.defaultAccount,global.constants.message).call();
        const allowance =  web3.utils.fromWei(_allowance,'ether');
        if (allowance < 1000000000000000){this.setState({Approvedisplay: 'block',buyButton:'disabled',}) }else{
          this.setState({Approvedisplay: 'none',buyButton:''}) 
        }
    }
    
      //加载窗开关
    windowClose = () => {
        this.setState({window: false})
    };
    windowOpen = () => {
        this.setState({window: true})
    };

    approve = async()=>{
        await LG.methods.approve(global.constants.message,'115792089237316195423570985008687907853269984665640564039457584007913129639935').send({from:window.defaultAccount})
        .on('transactionHash',(transactionHash)=>{
          if(transactionHash.length !== 0){
            this.loading.loadingOpen('block','none','none',"打包中……")
          }
        })
        .on('receipt',(receipt)=>{
          if(receipt.status === true){
            this.loading.loadingOpen('none','block','none',"授权成功，请选择天数购买")
          }
        })
        .on('error',(error,receipt)=>{
          if(receipt !== undefined && receipt.status === false){
            this.loading.loadingOpen('none','none','block',"出错！请检查")
          }
        })
        this.componentDidMount();
    }

    buy = async(days)=>{
          await Message.methods.buy(
            days
          ).send({from:window.defaultAccount})
          .on('transactionHash',(transactionHash)=>{
            if(transactionHash.length !== 0){
              this.loading.loadingOpen('block','none','none',"打包中……")
            }
          })
          .on('receipt',(receipt)=>{
            if(receipt.status === true){
              this.loading.loadingOpen('none','block','none',"购买成功,现在可以正常发送信息了")
            }
          })
          .on('error',(error,receipt)=>{
            if(receipt !== undefined && receipt.status === false){
                this.loading.loadingOpen('none','none','block',"出错！请检查")
            }
          })
      }

    render() {
        let fee = {
            textAlign:'center',
            width:'24.7%',
            float:'left'
        }
        let feeLG=this.state.feeLG;
        let discount = this.state.discount;
        return (
            <div>
                <Modal  visible={this.state.window} tit
                    closable={true} 
                    onCancel={this.windowClose} 
                    footer={null}
                >   
                    <div style={{marginBottom:'5px'}}>{this.props.title}</div>
                    <div style={{display:this.state.Approvedisplay}}>
                        <Button type='primary' block onClick={()=> this.approve()}>先授权LG</Button>
                    </div>
                    <div style={{width:'100%', marginTop:'5px'}}>
                        <div style={fee} >&asymp;{(feeLG*(30-(30-30)*discount/100)).toFixed(0)}LG</div>
                        <div style={fee} >&asymp;{(feeLG*(60-(60-30)*discount/100)).toFixed(0)}LG</div>
                        <div style={fee} >&asymp;{(feeLG*(120-(120-30)*discount/100)).toFixed(0)}LG</div>
                        <div style={fee} >&asymp;{(feeLG*(365-(365-30)*discount/100)).toFixed(0)}LG</div>
                    </div>
                    <div style={{width:'100%'}}>
                        <div style={fee} >{(feeLG*(30-(30-30)*discount/100)/30).toFixed(1)}LG/天</div>
                        <div style={fee} >{(feeLG*(60-(60-30)*discount/100)/60).toFixed(1)}LG/天</div>
                        <div style={fee} >{(feeLG*(120-(120-30)*discount/100)/120).toFixed(1)}LG/天</div>
                        <div style={fee} >{(feeLG*(365-(365-30)*discount/100)/365).toFixed(1)}LG/天</div>
                    </div>
                    <Group style={{width:'100%'}}>
                        <Button type='primary' style={{width:'25%'}} disabled={this.state.buyButton} onClick={()=> this.buy(30)}>30天</Button>&nbsp;
                        <Button type='primary' style={{width:'25%'}} disabled={this.state.buyButton} onClick={()=> this.buy(60)}>60天</Button>&nbsp;
                        <Button type='primary' style={{width:'25%'}} disabled={this.state.buyButton} onClick={()=> this.buy(120)}>120天</Button>&nbsp;
                        <Button type='primary' style={{width:'25%'}} disabled={this.state.buyButton} onClick={()=> this.buy(365)}>365天</Button>&nbsp;
                    </Group>
                    
                </Modal>
                <Loading ref={loadwindow=>{this.loading=loadwindow}}/>
            </div>
        )
    }
}

export default buy
