import React,{ Component }  from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Link } from 'react-router-dom';
import { Table, Card ,Divider, Modal,Button,Input,Popover} from 'antd';
import { QuestionCircleOutlined, CopyOutlined} from '@ant-design/icons';
import erc20abi from '../../../ABI/ERC20.abi.json';
import LockFactory from '../../../ABI/LockFactory';
import LockLoad from '../../../ABI/LockLoad';
import LockABI from '../../../ABI/Lock.abi.json';
import {CopyToClipboard} from 'react-copy-to-clipboard';
const BigNumber = require('bignumber.js');

class App extends Component {
      state = {
        dataList:[], //个人拥有并已生效
        dataList2:[], //肩负指导员身份的锁
        dataList3:[], //调试中的锁
        display2:'none', //指导员身份页面开关
        display3:'none', //调试中的锁页面开关
        display4:'none', //移交身份前设置页面开关
        _sender:'',
        msgcontent:'',
        onoff2:'none',
        onoff1:'none',
        message:"",
        visible:false,
        visible2:false,
        symbol:'',
        tokenAdr:'',
        lockAdr:'',
        admin:'',
        owner:'',
        balance:'',
        time:'',
        amount:'',
        day:'',
        time2:'',
        AllLocks:'',
        _addDay:'',
        _day:'',
        _amount:'',
        newOwer:'',
        otherTokenAdr:'',
        newAdmin:'',
        addDay2:'', //特殊操作延长锁仓天数
        newOwer2:'',//特殊操作更换拥有者
        searchAdminLock:'', //搜索单个指导锁
      };

  async componentDidMount(){

    const AllLocks = await LockFactory.methods.getAllLocks().call({from:window.defaultAccount});
    this.setState({AllLocks});

    //通过加载器加载个人锁地址
    const Lockowner = await LockLoad.methods.Lockowner().call({from:window.defaultAccount});
    const _Lockowner=[]; //过滤后的个人锁地址
    for(var i1=0; i1<Lockowner.length;i1++){
      if(Lockowner[i1]!=='0x0000000000000000000000000000000000000000'){
        _Lockowner.push(Lockowner[i1]);
      }
    }

    

    const userLocks = []; //个人拥有的锁
    const dataSource = []; //个人拥有并已生效
    const dataSource3 = []; //调试中的锁
    for(var i=0; i<=_Lockowner.length-1; i++ ){
      const Lock =  new web3.eth.Contract(LockABI.abi,_Lockowner[i]);
      const owner = await Lock.methods.owner().call();
      // const admin = await Lock.methods.admin().call();
      if(owner.toLowerCase() === window.defaultAccount.toLowerCase()
        ){
          userLocks.push(_Lockowner[i]);
        }
      this.setState({message:"搜索中……"+ i + '=>' + _Lockowner.length });
    }
    for(var iiii=userLocks.length-1; iiii>=0; iiii-- ){
      const Lock =  new web3.eth.Contract(LockABI.abi,userLocks[iiii]);
      const lock1 = await Lock.methods.lock1().call();
      const tokenAdr = await Lock.methods.tokenAdr().call();
      const ERC20 =  new web3.eth.Contract(erc20abi.abi,tokenAdr);
      const balance = await ERC20.methods.balanceOf(userLocks[iiii]).call();
      const owner = await Lock.methods.owner().call();
      const admin = await Lock.methods.admin().call();
      //个人拥有并已生效
      if( balance !== '0'  
        && owner.toLowerCase() === window.defaultAccount.toLowerCase()
        && lock1 === '1'
        ){
      const symbol = await ERC20.methods.symbol().call();
      const decimals = await ERC20.methods.decimals().call();
      const amount = await Lock.methods.amount().call();
      const getTime = await Lock.methods.getTime().call();
      const day = await Lock.methods.day().call();
      const time2 = await Lock.methods.getTime2().call();
      const a = [];
        a[0] = symbol;   //通证符号
        a[1] = tokenAdr;  //通证地址
        a[2] = userLocks[iiii];  //锁地址
        a[3] = admin; //指导员
        a[4] = owner;  //拥有者
        a[5] = String( (web3.utils.fromWei(balance,'ether'))*10**(18-decimals).toFixed(0) ); //锁仓余额
        a[6] = String( Math.ceil(getTime/3600/24) );  //锁仓倒计时
        a[7] = String( (web3.utils.fromWei(amount,'ether'))*10**(18-decimals).toFixed(0) ); //释放量
        a[8] = day;  //释放间隔天数
        a[9] = String( Math.ceil(time2/3600/24) );  //释放间隔倒计时
      dataSource.push(a);
      }
      //调试中的锁
      if( balance !== '0'  
        && admin.toLowerCase() === window.defaultAccount.toLowerCase()
        && lock1 === '0'
        ){
      const symbol = await ERC20.methods.symbol().call();
      const decimals = await ERC20.methods.decimals().call();
      const amount = await Lock.methods.amount().call();
      const getTime = await Lock.methods.getTime().call();
      const day = await Lock.methods.day().call();
      const time2 = await Lock.methods.getTime2().call();
      const a = [];
        a[0] = symbol;   //通证符号
        a[1] = tokenAdr;  //通证地址
        a[2] = userLocks[iiii];  //锁地址
        a[3] = admin; //指导员
        a[4] = owner;  //拥有者
        a[5] = String( (web3.utils.fromWei(balance,'ether'))*10**(18-decimals).toFixed(0) ); //锁仓余额
        a[6] = String( Math.ceil(getTime/3600/24) );  //锁仓倒计时
        a[7] = String( (web3.utils.fromWei(amount,'ether'))*10**(18-decimals).toFixed(0) ); //释放量
        a[8] = day;  //释放间隔天数
        a[9] = String( Math.ceil(time2/3600/24) );  //释放间隔倒计时
      dataSource3.push(a);
      }
      this.setState({message:"加载中……"+ iiii});
    }
    this.setState({dataList:dataSource}) 

    if(dataSource3.length !== 0){
      this.setState({dataList3:dataSource3 , display3:'block'});
    }
    this.setState({message:""});

  }

  //加载指导员身份的锁
  loadAdmin = async () =>{
    //通过加载器加载指导锁地址
    const Lockadmin = await LockLoad.methods.Lockadmin().call({from:window.defaultAccount});
    const _Lockadmin=[]; //过滤后的个人锁地址
    for(var i1=0; i1<Lockadmin.length;i1++){
      if(Lockadmin[i1]!=='0x0000000000000000000000000000000000000000'){
        _Lockadmin.push(Lockadmin[i1]);
      }
    }
    const adminLocks = []; //指导员身份的锁
    for(var ii=_Lockadmin.length-1; ii>=0; ii-- ){
      const Lock =  new web3.eth.Contract(LockABI.abi,_Lockadmin[ii]);
      const admin = await Lock.methods.admin().call();
      if(admin.toLowerCase() === window.defaultAccount.toLowerCase()
        ){
          adminLocks.push(_Lockadmin[ii]);
        }
      this.setState({message:"搜索中……"+ ii});
    }
    const dataSource2 = []; //肩负指导员身份的锁
    for(var iii=adminLocks.length-1; iii>=0; iii-- ){
      const Lock =  new web3.eth.Contract(LockABI.abi,adminLocks[iii]);
      const lock1 = await Lock.methods.lock1().call();
      const tokenAdr = await Lock.methods.tokenAdr().call();
      const ERC20 =  new web3.eth.Contract(erc20abi.abi,tokenAdr);
      const balance = await ERC20.methods.balanceOf(adminLocks[iii]).call();
      const owner = await Lock.methods.owner().call();
      const admin = await Lock.methods.admin().call();
      //肩负指导员身份的锁
      if( balance !== '0'  
        && admin.toLowerCase() === window.defaultAccount.toLowerCase()
        && lock1 === '1'
        ){
      const symbol = await ERC20.methods.symbol().call();
      const decimals = await ERC20.methods.decimals().call();
      const amount = await Lock.methods.amount().call();
      const getTime = await Lock.methods.getTime().call();
      const day = await Lock.methods.day().call();
      const time2 = await Lock.methods.getTime2().call();
      const a = [];
        a[0] = symbol;   //通证符号
        a[1] = tokenAdr;  //通证地址
        a[2] = adminLocks[iii];  //锁地址
        a[3] = admin; //指导员
        a[4] = owner;  //拥有者
        a[5] = String( (web3.utils.fromWei(balance,'ether'))*10**(18-decimals).toFixed(0) ); //锁仓余额
        a[6] = String( Math.ceil(getTime/3600/24) );  //锁仓倒计时
        a[7] = String( (web3.utils.fromWei(amount,'ether'))*10**(18-decimals).toFixed(0) ); //释放量
        a[8] = day;  //释放间隔天数
        a[9] = String( Math.ceil(time2/3600/24) );  //释放间隔倒计时
      dataSource2.push(a);
      }
      this.setState({message:"加载中……"+ iii});
    }
    if(dataSource2.length !== 0){
      this.setState({dataList2:dataSource2 , display2:'block'});
    }
    this.setState({message:""});
  }

  //搜索指导锁
  searchAdminLock = async () =>{
      const dataSource2 = []; //肩负指导员身份的锁
      this.setState({message:"搜索中……"});
      const Lock =  new web3.eth.Contract(LockABI.abi,this.state.searchAdminLock);
      const lock1 = await Lock.methods.lock1().call();
      const tokenAdr = await Lock.methods.tokenAdr().call();
      const ERC20 =  new web3.eth.Contract(erc20abi.abi,tokenAdr);
      const balance = await ERC20.methods.balanceOf(this.state.searchAdminLock).call();
      const admin = await Lock.methods.admin().call();
      //肩负指导员身份的锁
      if( balance !== '0'  
        && admin.toLowerCase() === window.defaultAccount.toLowerCase()
        && lock1 === '1'
        ){
      const owner = await Lock.methods.owner().call();
      const symbol = await ERC20.methods.symbol().call();
      const decimals = await ERC20.methods.decimals().call();
      const amount = await Lock.methods.amount().call();
      const getTime = await Lock.methods.getTime().call();
      const day = await Lock.methods.day().call();
      const time2 = await Lock.methods.getTime2().call();
      const a = [];
        a[0] = symbol;   //通证符号
        a[1] = tokenAdr;  //通证地址
        a[2] = this.state.searchAdminLock;  //锁地址
        a[3] = admin; //指导员
        a[4] = owner;  //拥有者
        a[5] = String( (web3.utils.fromWei(balance,'ether'))*10**(18-decimals).toFixed(0) ); //锁仓余额
        a[6] = String( Math.ceil(getTime/3600/24) );  //锁仓倒计时
        a[7] = String( (web3.utils.fromWei(amount,'ether'))*10**(18-decimals).toFixed(0) ); //释放量
        a[8] = day;  //释放间隔天数
        a[9] = String( Math.ceil(time2/3600/24) );  //释放间隔倒计时
      dataSource2.push(a);
      }
    if(dataSource2.length !== 0){
      this.setState({dataList2:dataSource2 , display2:'block'});
    }
    this.setState({message:""});
  }

  confirm = async() =>{
    const Lock =  new web3.eth.Contract(LockABI.abi,this.state.lockAdr);
    const tokenAdr = await Lock.methods.tokenAdr().call();
    const ERC20 =  new web3.eth.Contract(erc20abi.abi,tokenAdr);
    const balance = await ERC20.methods.balanceOf(this.state.lockAdr).call();
    const admin = await Lock.methods.admin().call();
    const symbol = await ERC20.methods.symbol().call();
    const decimals = await ERC20.methods.decimals().call();
    const amount = await Lock.methods.amount().call();
    const getTime = await Lock.methods.getTime().call();
    const day = await Lock.methods.day().call();
      this.setState({
        symbol: symbol,
        admin:admin,
        balance: String( (web3.utils.fromWei(balance,'ether'))*10**(18-decimals).toFixed(0) ),
        time: String( Math.ceil(getTime/3600/24) ),
        amount: String( (web3.utils.fromWei(amount,'ether'))*10**(18-decimals).toFixed(0) ),
        day: day,
      })
    this.setState({visible2: true});
  }

  clam = async() =>{
    const Lock =  new web3.eth.Contract(LockABI.abi,this.state.lockAdr);
    await Lock.methods.clam().send({from:window.defaultAccount})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.setState({message:"打包中……"});
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.setState({visible: false})
        // this.setState({message:"提取成功请回钱包首页查看"});
      }
    })
    this.componentDidMount();
  }

  clamOther = async() =>{
    const Lock =  new web3.eth.Contract(LockABI.abi,this.state.lockAdr);
    await Lock.methods.clamOther(this.state.otherTokenAdr)
    .send({from:window.defaultAccount})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.setState({message:"打包中……"});
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.setState({message:"提取成功"});
      }
    })
  }

  set = async() =>{
    const Lock =  new web3.eth.Contract(LockABI.abi,this.state.lockAdr);
    const ERC20 =  new web3.eth.Contract(erc20abi.abi,this.state.tokenAdr);
    const decimals = await ERC20.methods.decimals().call();
    const amount = new BigNumber(this.state._amount/10**(18-decimals) ).toFixed() ;
    await Lock.methods.set(
      this.state._addDay, 
      this.state._day,
      web3.utils.toWei(amount,'ether'),
    ).send({from:window.defaultAccount})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.setState({message:"打包中……"});
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.setState({message:"设置成功"});
      }
    });
    const amount2 = await Lock.methods.amount().call();
    const getTime2 = await Lock.methods.getTime().call();
    const day2 = await Lock.methods.day().call();
    this.setState({
      time: String( Math.ceil(getTime2/3600/24) ),
      amount: String( (web3.utils.fromWei(amount2,'ether'))*10**(18-decimals).toFixed(0) ),
      day: day2,
      time2: String( Math.ceil(getTime2/3600/24) ),
    })
    this.componentDidMount();
  }

  addTime = async() =>{
    const Lock =  new web3.eth.Contract(LockABI.abi,this.state.lockAdr);
    await Lock.methods.addTime(
      this.state.addDay2, 
    ).send({from:window.defaultAccount})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.setState({message:"打包中……"});
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.setState({visible: false});
      }
    })
    this.componentDidMount();
  }

  handleCancel = () => {
    this.setState({visible: false})
  };
  handleCance2 = () => {
    this.setState({visible2: false})
  };

  transferOwer = async() =>{
    const Lock =  new web3.eth.Contract(LockABI.abi,this.state.lockAdr);
    await Lock.methods.transferOwer(
      this.state.newOwer
    ).send({from:window.defaultAccount})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.setState({message:"打包中……"});
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.setState({message:"移交成功"});
      }
    })
    this.setState({visible2: false})
    this.setState({visible: false})
    this.componentDidMount();
  }

  newOwer = async() =>{
    const Lock =  new web3.eth.Contract(LockABI.abi,this.state.lockAdr);
    await Lock.methods.newOwer(
      this.state.newOwer2
    ).send({from:window.defaultAccount})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.setState({message:"打包中……"});
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.setState({visible: false});
      }
    })
    this.componentDidMount();
  }

  transferAdmin = async() =>{
    const Lock =  new web3.eth.Contract(LockABI.abi,this.state.lockAdr);
    await Lock.methods.transferAdmin(
      this.state.newAdmin
    ).send({from:window.defaultAccount})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.setState({message:"打包中……"});
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.setState({visible: false});
      }
    })
    this.componentDidMount();
  }

  open2 = () => {
    if(this.state.onoff2 ==='none'){
      this.setState({onoff2:"block"});
    }else{
      this.setState({onoff2:"none"});
    }
  }

  open1 = () => {
    if(this.state.onoff1 ==='none'){
      this.setState({onoff1:"block"});
    }else{
      this.setState({onoff1:"none"});
    }
  }

  render(){

    const columns = [
      {
        title: '通证',
        dataIndex: '0',
        key:'0',
        width: 50,
        align: 'left',
        ellipsis: 'true',
      },
      {
        title: '锁额',
        dataIndex: '5',
        ellipsis: 'true',
        align: 'center',
      },
      {
        title: '天数',
        dataIndex: '6',
        width: 60,
        ellipsis: 'true',
        align: 'center',
      },
      {
        title: '锁地址',
        dataIndex: '2',
        ellipsis: 'true',
        align: 'center',
      },
    ];


      const content = (
        <div>
          通证名：{this.state.symbol}&emsp;&emsp;
          <CopyToClipboard text={this.state.tokenAdr}>
          <Button size='small' type='primary' >复制通证地址</Button>
          </CopyToClipboard>
        </div>
      );

      const content1_1 = (
        <div>
            <div>指导员与拥有者必须为同一地址才可操作。</div>
            <div>比如：要延长1年则输入‘365’。</div>
            <div>开始释放后将不可再延长锁仓时间</div>
            <div>注意！一旦延长时间则不可以减少时间!</div>
        </div>
      );
      const content1 = (
        <div>
          <Popover content={content1_1}  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          延长锁仓天数
        </div>
      );

      const content2 = (
        <div>
          <Popover content='移交拥有者身份'  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          移交拥有者
        </div>
      );

      const content3_1 = (
        <div>
            <div>以下操作需要拥有指导员身份</div>
            <div>1.延长锁仓时间</div>
            <div>2.更换拥有者地址</div>
            <div>并建议在操作后将指导员身份交回原指导员。</div>
            <div>通常情况指导员不要将该身份移交给拥有者，</div>
            <div>除非拥有者能充分理解操作</div>
            <div>防止拥有者在不理解操作的情况下造成误操作。</div>
        </div>
      );
      const content3 = (
        <div>
          <Popover content={content3_1}  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          移交指导员
        </div>
      );
      const content4_1 = (
        <div>
            <div>指导员与拥有者必须为同一地址才可操作。</div>
            <div>更换拥有者地址</div>
        </div>
      );
      const content4 = (
        <div>
          <Popover content={content4_1}  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          更换拥有者
        </div>
      );
      const content5_1 = (
        <div>
            <div>设置锁仓天数</div>
            <div>即多少天之后才可以释放</div>
        </div>
      );
      const content5 = (
        <div>
          <Popover content={content5_1}  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          锁仓天数
        </div>
      );
      const content6_1 = (
        <div>
            <div>释放间隔天数</div>
            <div>当解锁时间到期之后，设置每多少天进行一次释放</div>
        </div>
      );
      const content6 = (
        <div>
          <Popover content={content6_1}  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          释放间隔
        </div>
      );
      const content7_1 = (
        <div>
            <div>释放数量</div>
            <div>即每次释放多少数量。</div>
            <div>如果想一次性释放则输入全部额度</div>
        </div>
      );
      const content7 = (
        <div>
          <Popover content={content7_1}  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          释放数量
        </div>
      );
      const content8_1 = (
        <div>
            <div>提取误转入该锁的其它通证，不受锁仓时间限制</div>
        </div>
      );
      const content8 = (
        <div>
          <Popover content={content8_1}  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          误转通证
        </div>
      );
      const content9 = (
        <div>
          通证名：{this.state.symbol}&emsp;移交拥有者
        </div>
      );

  return (
    <div>
           <div style={{marginTop:'10px'}}>
              <Button   color='green' size="middle">
                <Link to="/Lockhome">所有锁仓</Link>
              </Button>
              &nbsp;
              <Button  color='green' size='middle'  >
                <Link to="/LockMy">我的锁仓</Link>
              </Button>
              <Button   type="primary"  color='green' size="middle" style={{marginBottom:'5px',float:'right'}} >
                <Link to="/LockCreat">创建新锁</Link>
              </Button>
            </div>
        <Card  type="inner" size='small'  >
            <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                {this.state.message}
            </div>
              <div style={{display:this.state.display3}}>
                <Divider  style={{marginTop:'8px',marginBottom:'8px',fontSize:'12px'}}>调试中的锁&darr;</Divider>
                <Table size='small' 
                  pagination={false}
                  columns={columns}  
                  dataSource={this.state.dataList3} 
                  customRow="click"
                  onRow = {(record) => {
                    return {
                      onClick: () => {
                          // console.log(record)
                          this.setState({visible: true})
                          this.setState({symbol: record[0]})
                          this.setState({tokenAdr: record[1]})
                          this.setState({lockAdr: record[2]})
                          this.setState({admin: record[3]})
                          this.setState({owner: record[4]})
                          this.setState({balance: record[5]})
                          this.setState({time: record[6]})
                          this.setState({amount: record[7]})
                          this.setState({day: record[8]})
                          this.setState({time2: record[9]})
                          this.setState({display4:'block'})
                        }
                      }
                      }
                      }
                />
              </div>
          <Divider  style={{marginTop:'8px',marginBottom:'8px',fontSize:'12px'}}>我的锁仓通证&darr;</Divider>
          <Table size='small' 
              pagination={false}
              columns={columns}  
              dataSource={this.state.dataList} 
              rowClassName={(record) => record[6] === '0' && record[9] === '0' ? "lockCanGet":''}
              customRow="click"
              onRow = {(record) => {
                return {
                  onClick: () => {
                      // console.log(record)
                      this.setState({visible: true})
                      this.setState({symbol: record[0]})
                      this.setState({tokenAdr: record[1]})
                      this.setState({lockAdr: record[2]})
                      this.setState({admin: record[3]})
                      this.setState({owner: record[4]})
                      this.setState({balance: record[5]})
                      this.setState({time: record[6]})
                      this.setState({amount: record[7]})
                      this.setState({day: record[8]})
                      this.setState({time2: record[9]})
                      this.setState({display4:'none'})
                    }
                  }
                  }
                  }
              />

              <div style={{textAlign:'center'}}>
                  <Input style={{marginTop:'10px'}}
                        onChange={event=>{this.setState({searchAdminLock:event.target.value})}}
                        placeholder="输入单个指导锁地址"
                        suffix={
                          <Button type="primary" size="middle" 
                            onClick={this.searchAdminLock}
                            >搜索
                          </Button>
                      }
                    />
                    <Button   size="middle"  onClick={() => {this.loadAdmin()}}>加载全部指导锁</Button>
              </div>

              <div style={{display:this.state.display2}}>
                <Divider  style={{marginTop:'8px',marginBottom:'8px',fontSize:'12px'}}>肩负指导员&darr;</Divider>
                <Table size='small' 
                  columns={columns} 
                  dataSource={this.state.dataList2} 
                  pagination={{ pageSize: 10 }}
                  customRow="click"
                  onRow = {(record) => {
                    return {
                      onClick: () => {
                          // console.log(record)
                          this.setState({visible: true})
                          this.setState({symbol: record[0]})
                          this.setState({tokenAdr: record[1]})
                          this.setState({lockAdr: record[2]})
                          this.setState({admin: record[3]})
                          this.setState({owner: record[4]})
                          this.setState({balance: record[5]})
                          this.setState({time: record[6]})
                          this.setState({amount: record[7]})
                          this.setState({day: record[8]})
                          this.setState({time2: record[9]})
                          this.setState({display4:'none'})
                        }
                      }
                      }
                      }
                  />
              </div>

              
        </Card>

    <Modal title={content} visible={this.state.visible} 
        onOk={this.handleCancel} onCancel={this.handleCancel} 
        footer={[
          <Button type='primary' onClick={this.handleCancel}>关闭</Button>,
           ]}
    >
        <p>
          <Input id='i1' addonBefore="锁地址" value={this.state.lockAdr}
            disabled
            suffix={
                <CopyToClipboard  text={this.state.lockAdr}>
                <Button  size="small" ><CopyOutlined /></Button>
                </CopyToClipboard>
            }
          />
        </p>
        <p>
          <Input id='i1' addonBefore="指导员" value={this.state.admin}  
            disabled
            suffix={
                <CopyToClipboard  text={this.state.admin}>
                <Button  size="small" ><CopyOutlined /></Button>
                </CopyToClipboard>
            }
          />
        </p>
        <p>
          <Input id='i1' addonBefore="拥有者" value={this.state.owner} 
            disabled
            suffix={
                <CopyToClipboard  text={this.state.owner}>
                <Button  size="small" ><CopyOutlined/></Button>
                </CopyToClipboard>
            }
          />
        </p>
        <p>
          <Input id='i1' addonBefore="锁仓余额" value={this.state.balance} 
            disabled
          />
        </p>
        <p>
          <Input id='i1' addonBefore="解锁倒计时" value={this.state.time + '天'} 
            disabled
          />
        </p>
        <p>
          <Input id='i1' addonBefore="释放规则" value={this.state.amount + '/' + this.state.day + '天'}
            disabled
          />
        </p>
        <p>
          <Input id='i1' addonBefore="释放间隔倒计时" value={this.state.time2 + '天'}
            disabled
          />
        </p>
        <Button shape='round' type='primary' block size="middle"
          onClick={() => {this.clam()}}
        >提取通证</Button>

        <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                {this.state.message}
        </div>
        
        <Card type="inner" size='small'  >
                <div style={{display:this.state.display4}}>
                  <Divider  style={{marginTop:'8px',marginBottom:'8px',fontSize:'10px'}}
                  >移交身份前设置
                  </Divider>
                    <p>
                      <Input  addonBefore={content5} placeholder="如：365"
                        style={{marginBottom:'3px'}}
                        onChange={event=>{this.setState({_addDay:event.target.value})}}
                      />
                      <Input  addonBefore={content6}  placeholder="如：30"
                        style={{marginBottom:'3px'}}
                        onChange={event=>{this.setState({_day:event.target.value})}}
                      />
                      <Input  addonBefore={content7}  placeholder="如：10000"
                        style={{marginBottom:'3px'}}
                        onChange={event=>{this.setState({_amount:event.target.value})}}
                      />
                      <Button type='primary' block size="small" 
                        onClick={() => {this.set()}}
                      >提交设置</Button>
                    </p>
                  <Divider  style={{marginTop:'8px',marginBottom:'8px',fontSize:'10px'}}
                  >设置无误后移交给拥有者
                  </Divider>
                    <p>
                      <Input  addonBefore={content2}  placeholder="输入拥有者地址"
                        onChange={event=>{this.setState({newOwer:event.target.value})}}
                        suffix={
                            <Button type='danger' size="small"
                              onClick={() => {this.confirm()}}
                             >提交</Button>
                        }
                      />
                    </p>
                </div>
            <Button  shape='round' type="dashed" block  size="small"  
                style={{marginTop:'12px',marginBottom:'12px'}}
                onClick={() => {this.open1()}}
            >特殊操作</Button>
              <div style={{display:this.state.onoff1}}>
                        <div>
                          <Input  addonBefore={content8}  placeholder="通证合约地址"
                            onChange={event=>{this.setState({otherTokenAdr:event.target.value})}}
                            suffix={
                                <Button type='primary' size="small" 
                                  onClick={() => {this.clamOther()}}
                                >提取</Button>
                            }
                          />
                        </div>
                  
                  <Divider  style={{marginTop:'8px',marginBottom:'8px',fontSize:'10px'}}
                  >移交身份后的特殊操作
                  </Divider>
                    <p>
                      <Input  addonBefore={content3}  placeholder="拥有者地址"
                        onChange={event=>{this.setState({newAdmin:event.target.value})}}
                        suffix={
                            <Button type='danger' size="small"
                                onClick={() => {this.transferAdmin()}}
                             >提交</Button>
                        }
                      />
                    </p>
                    <p>
                      <Input  addonBefore={content1}  placeholder="如：365"
                        onChange={event=>{this.setState({addDay2:event.target.value})}}
                        suffix={
                            <Button type='danger' size="small"
                              onClick={() => {this.addTime()}}
                            >提交</Button>
                        }
                      />
                    </p>
                    <p>
                      <Input  addonBefore={content4}  placeholder="输入新地址"
                        onChange={event=>{this.setState({newOwer2:event.target.value})}}
                        suffix={
                            <Button type='danger' size="small"
                              onClick={() => {this.newOwer()}}
                            >提交</Button>
                        }
                      />
                    </p>
              </div>
        </Card>

        <Modal title={content9} visible={this.state.visible2}  //移交身份时的确认窗口
          onCancel={this.handleCance2} 
          footer={[
            <Button type='primary' onClick={this.handleCance2}>取消</Button>,
             ]}
        >
        <p>
          <Input id='i1' addonBefore="锁地址" value={this.state.lockAdr} 
            disabled
            suffix={
                <CopyToClipboard  text={this.state.lockAdr}>
                <Button  size="small" ><CopyOutlined /></Button>
                </CopyToClipboard>
            }
          />
        </p>
        <p>
          <Input id='i1' addonBefore="锁仓余额" value={this.state.balance} 
            disabled
          />
        </p>
        <p>
          <Input id='i1' addonBefore="解锁倒计时" value={this.state.time + '天'}  
            disabled
          />
        </p>
        <p>
          <Input id='i1' addonBefore="释放规则" value={this.state.amount + '/' + this.state.day + '天'}
            disabled
          />
        </p>
        <p>
          <div>该锁将移交给以下地址(请仔细核对)</div>
          <div id='i2'>{this.state.newOwer}</div>
        </p>
        <div style={{textAlign:'center'}}>
            <Button type='primary' danger onClick={this.transferOwer}>核对无误确认移交</Button>
        </div>
        <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                {this.state.message}
        </div>
        
      </Modal>

      </Modal>

    </div>
  );
}
}

export default App;
