import React, { Component } from 'react'
import { getChainInfo, SelectWeb3 } from '../../../../components/global/functions';
import { Dropdown } from 'semantic-ui-react'
import web3 from "../../../../ABI/web3"
import bridgeAbi from "../../../../ABI/Bridge.abi.json"
import { connect } from "react-redux"
import fetch from 'node-fetch';
import erc20abi from '../../../../ABI/ERC20.abi.json';
import { Multicall } from 'ethereum-multicall';
import httpAbi from '../../../../ABI/http.json'
import { Toast } from 'antd-mobile'

export class tokensSelect extends Component {
  constructor(props) {
    super(props)
    this.state = {
      bridgeAdr: '',
      LGadr: '',
      bridgeContract: '',
      currentCoin: global.HECO.LG,
      multicallAdr: '',
      TOKENLIST: '',
      selectCoin: ''
    }
  }

  async componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.ChainID !== this.props.ChainID
      || nextProps.UserAdr !== this.props.UserAdr) {
      this.setState({ TOKENLIST: '' })
      this.first()
    }
  }

  async componentDidMount() {
    await this.first()
    this.props.sendStore("selectCoinOrAdr", this.state.currentCoin)
  }

  getChain = async () => {
    await getChainInfo(window.ChainID).then((result) => {
      this.setState({
        bridgeAdr: result.ChainAdr.bridge,
        LGadr: result.ChainAdr.LG,
        multicallAdr: result.ChainAdr.multicall,
      })
    })
  }

  first = async () => {
    if(window.ChainID===null){
      return Toast.show({ content: '未连接钱包' })
    }
    await this.getChain()
    if (this.state.bridgeAdr === undefined) {
      return Toast.show({ content: '当前链暂未部署跨链桥' })
    }
    const bridgeContract = new web3.eth.Contract(bridgeAbi.abi, this.state.bridgeAdr)
    this.setState({ bridgeContract })
    if (localStorage.bridgeTokenList !== undefined) {
      const TOKENLIST = JSON.parse(localStorage.bridgeTokenList)
      this.setState({ TOKENLIST })
    }
    this.firstTokenList()
    this.tokenlist()
  }

  firstTokenList = async () => {
    let TOKENLIST = []
    const _TOKENLIST = JSON.parse(localStorage.bridgeTokenList)
    for (let i = 0; i < _TOKENLIST.length; i++) {
      let URI = _TOKENLIST[i].flag.props.src
      const token = {
        key: i,
        flag: <img style={{ margin: '0px 5px 0px 0px' }}
          src={URI === undefined ? global.constants.bridgeTokenLogo + 'unknow.svg' : URI}
          alt='auto' width='auto' height='15px'
        />,
        text: _TOKENLIST[i].text,
        value: _TOKENLIST[i].value
      }
      TOKENLIST.push(token)
    }
    this.setState({ TOKENLIST })
  }

  tokenlist = async () => {
    const selectWeb3 = await SelectWeb3('0x89')
    const httpContract = new selectWeb3.eth.Contract(httpAbi, global.constants.http)
    const version = await httpContract.methods.version('bridgeLogo').call()
    if (localStorage.bridgeLogoVer !== version) {
      const http = await httpContract.methods.mapInfo('bridgeLogo').call()
      const _logoList = await fetch(http.http);
      const __logoList = await _logoList.json();
      const logoList = __logoList.tokens
      localStorage['bridgeLogo'] = JSON.stringify(logoList)
      localStorage['bridgeLogoVer'] = version
    }
    const allToken = await this.state.bridgeContract.methods.getallToken().call()
    if(allToken.length===0){
      return
    }
    if (localStorage.allTokenLength !== allToken.length + '') {
      //读取缓存中的LOGO列表
      const logoListLocal = JSON.parse(localStorage.bridgeLogo)
      const tokensList = []  //最终通证列表
      //过滤LOGO列表
      for (let i = 0; i < logoListLocal.length; i++) {
        if (allToken.some((x) => { return x === logoListLocal[i].address }) === true) {
          tokensList.push(logoListLocal[i].address)
        }
      }
      //过滤智能合约中的通证列表
      for (let i = 0; i < allToken.length; i++) {
        if (logoListLocal.some((x) => { return x.address === allToken[i] }) !== true) {
          tokensList.push(allToken[i])
        }
      }
      //多呼
      const multicall = new Multicall({
        web3Instance: web3, tryAggregate: true,
        multicallCustomContractAddress: this.state.multicallAdr
      });

      //获取当前链下的各通证地址
      const calls1 = []
      for (let i = 0; i < tokensList.length; i++) {
        const call = {
          reference: 'tokenAdr',
          methodName: 'tokenAdr',
          methodParameters: [tokensList[i], this.props.ChainID]
        }
        calls1.push(call)
      }
      const CallCurChainTokenAdr = [{
        reference: 'Adr',
        contractAddress: this.state.bridgeAdr,
        abi: bridgeAbi.abi,
        calls: calls1
      }]
      const curChainTokensAdr = await multicall.call(CallCurChainTokenAdr);
      //处理最终通证列表下的各通证地址
      const curChainTokensList = []  //当前链下的各通证地址
      let listAdr = curChainTokensAdr.results.Adr.callsReturnContext
      for (let i = 0; i < listAdr.length; i++) {
        curChainTokensList.push(listAdr[i].returnValues[0])
      }

      //获取通证列表的通证名字
      const Calls = []
      for (let i = 0; i < curChainTokensList.length; i++) {
        const call = {
          reference: i,
          orChainAdr: tokensList[i],
          contractAddress: curChainTokensList[i],
          abi: erc20abi.abi,
          calls: [{
            reference: curChainTokensList[i],
            methodName: 'symbol'
          }]
        }
        Calls.push(call)
      }
      const names = await multicall.call(Calls);
      let _TOKENList = names.results  //最终通证列表(原链地址，当链地址，名字)
      let TOKENList = [] //最终通证列表(数组)
      for (let i in _TOKENList) {
        TOKENList.push(_TOKENList[i])
      }

      // 处理通证列表最终效果
      let TOKENLIST = []
      for (let i = 0; i < TOKENList.length; i++) {
        let orAdr = TOKENList[i].originalContractCallContext.orChainAdr
        let URI
        for (let ii = 0; ii < logoListLocal.length; ii++) {
          if (logoListLocal[ii].address === orAdr) {
            URI = logoListLocal[ii].logoURI
          }
        }
        const token = {
          key: i,
          flag: <img style={{ margin: '0px 5px 0px 0px' }}
            src={URI === undefined ? global.constants.bridgeTokenLogo + 'unknow.svg' : URI}
            alt='auto' width='auto' height='15px'
          />,
          text: TOKENList[i].callsReturnContext[0].returnValues[0],
          value: orAdr
        }
        TOKENLIST.push(token)
      }
      localStorage['bridgeTokenList'] = JSON.stringify(TOKENLIST)
      this.setState({ TOKENLIST })
      localStorage['allTokenLength'] = allToken.length
    }
  }

  selectChang = async (value) => {
    this.props.sendStore("selectCoinOrAdr", value)
  }

  render() {
    return (
      <div>
        <Dropdown search
          defaultValue={this.state.currentCoin} options={this.state.TOKENLIST}
          onChange={(e, { value }) => this.selectChang(value)}
        />
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(tokensSelect) 
