import React, { Component } from 'react';
import NFTCard from './components/card';
import { Row, Col, Card, BackTop } from 'antd';

export class NFTEX extends Component {
  constructor(props) {
    super(props)

    this.state = {

    }
  }

  componentDidMount() {

  }

  render() {
    let cardList = [];
    for (var i = 0; i < 8; i++) {
      cardList.push(
        <Col xs={{ span: 12 }} lg={{ span: 6 }} md={{ span: 8 }}>
          <NFTCard />
        </Col>
      )
    }
    return (
      <div>
        <Card title='LGNFT交易' style={{ textAlign: 'center' }} size='small'
          bodyStyle={{ padding: '5px' }}
        >
          {/* {cardList} */}
          <Row gutter={[6, 8]} justify="space-between" style={{ background: 'white' }}>
            {cardList}
          </Row>
        </Card>
        <BackTop />
      </div>
    )
  }
}

export default NFTEX
