import React,{ Component }  from 'react';
import '../../../App.css';
import {Card ,Button,Input,Checkbox,Popover} from 'antd';
import Message from '../../../ABI/message';
import MyCrypt from '../../../components/crypt';
import Head from './head';
import Loading from '../../../components/loadingReport';
import Introduce from './components/introduce';
import Buy from './components/buy';
import nowTime from '../../../ABI/nowTime';
import moment from 'moment';
import { QuestionCircleOutlined} from '@ant-design/icons';
import { Link } from 'react-router-dom';
const { TextArea } = Input;

class newMsg extends Component {
  constructor(props){
    super(props);
    this.state = {
      addressTo:'',
      msg:'',
      _message:'',
      toDisabled:'',
      buyTitle:'',
      addDate:true
    };
  }

  componentDidMount(){
    const _to = this.props.match.params.to;
    if(_to !=='0'){
      this.setState({addressTo:_to, toDisabled:'disabled'})
    }else{this.setState({addressTo:'', toDisabled:''})}
  }

  send = async()=>{
    if(this.state.addressTo === ''){
      this.loading.loadingOpen('none','none','block',"未填写收件人地址")
    }else{
      const freeTimes = await Message.methods.freeTimesMap(window.defaultAccount).call()
      const userTime = await Message.methods.userTime(window.defaultAccount).call()
      const now = await nowTime.methods._now().call();
      if(freeTimes>='5' && userTime==='0'){
        this.setState({buyTitle:'免费次数已用完，请购买使用天数'})
        this.buy.windowOpen();
      }
      if(userTime!=='0' && userTime<now){
        this.setState({buyTitle:'购买时效已用完，请购买使用天数'})
        this.buy.windowOpen();
      }
      if(freeTimes<'5' || userTime>now){
        if(this.state.addDate === true){
          const date = moment().format("MM-DD HH:mm")
          this.setState({_message:this.state.msg + '\n' + date}) 
        }else{
          this.setState({_message:this.state.msg})
        }
        const msg = await this.MyCrypt.Encrypt(this.state._message);
        await Message.methods.sendMsg(
          this.state.addressTo,
          msg
        ).send({from:window.defaultAccount})
        .on('transactionHash',(transactionHash)=>{
          if(transactionHash.length !== 0){
            this.loading.loadingOpen('block','none','none',"打包中……")
          }
        })
        .on('receipt',(receipt)=>{
          if(receipt.status === true){
            this.loading.loadingOpen('none','block','none',"发送成功")
          }
        })
        .on('error',(error,receipt)=>{
          if(receipt !== undefined && receipt.status === false){
            this.loading.loadingOpen('none','none','block',"出错！请检查")
          }
        })
      }
    } 
  }

  delDate=()=>{
    if(this.state.addDate === true){
      this.setState({addDate:false})
    }else{
      this.setState({addDate:true})
    }
  }

  toAdmin=()=>{
    if(this.state.toDisabled !== 'disabled'){
      this.setState({addressTo:global.constants.admin})
    }
  }

  render(){
    const info = (
      <div>对于无需对方知道发送时间的信息<br/>取消该选项可节省少量GAS</div>
    )
  return (
    <div>
      <Head/>
      <Card type="inner" size='small' className='cardMsg'>
        <div>
          <div style={{width:'100%'}}>
            <div style={{width:'33%',float:'left'}}>&nbsp;</div>
            <div style={{textAlign:'center',fontWeight:'800',float:'left',width:'33%'}}>写短信息</div>
            <div style={{float:'left',width:'33%',textAlign:'right'}} onClick={()=>{this.toAdmin()}}>联系我们</div>
          </div>
              <Input placeholder="粘贴地址：如'0x0123...'" 
                addonBefore='收件人'
                value={this.state.addressTo}
                disabled={this.state.toDisabled}
                onChange={event=>{this.setState({addressTo:event.target.value})}}
              />
          <div>内容(切勿输入助记词与密钥)：
              <TextArea rows={12} placeholder="输入内容,切勿输入助记词与密钥" 
                value={this.state.msg}
                onChange={event=>{this.setState({msg:event.target.value})}}
              />
          </div>
          <div style={{textAlign:'center'}}>
            <Checkbox onChange={()=>this.delDate()} defaultChecked={true}>添加发送时间
              <Popover content={info}
                style={{width:100}}
                trigger="click">
                <QuestionCircleOutlined /> 
              </Popover>
            </Checkbox>
          </div>
          <Button type="primary" block color='green' size="middle" style={{marginTop:'5px'}} 
            onClick={this.send}
          >发送
          </Button>
          <div style={{textAlign:'center'}}>
          <Link to='/MsgSent'>
            <Button shape="round" size="small" style={{background:'#ff4dbb9c',color:'white',marginTop:'8px'}} 
            >发件箱
            </Button>
          </Link>
          </div>
        </div>
      </Card>
    <Introduce open='block'/>
    <Buy ref={window=>{this.buy=window}} title={this.state.buyTitle}/>
    <MyCrypt ref={MyCrypt=>{this.MyCrypt=MyCrypt}} />
    <Loading ref={loadwindow=>{this.loading=loadwindow}}/>
    </div>
  );
}
}

export default newMsg;
