import React, { Component } from 'react'
import web3metamask from '../../../ABI/web3'
import { Button, Table, Input } from 'semantic-ui-react'

export class test extends Component {
  constructor(props) {
    super(props)
    this.state = {
      historyList: [],
      time: 0,
      adr: '',
      loseAdr:'',
      losePassword:'',
      gas:100,
      speed:500,
    }
  }

  //0x7DBF80680722e3150e2Ae838b38F60A5A4ca5465
  //更快抢ETH   0xb89d8a7c56241b550A6f8a0938BBBB2E2fe3166F
  //  0x8f43c62fd4a9ba481cddcab45fe98e5b4a0c560105bf2682faf3e966b0681ee6
  getpending = async () => {
    let state = this.state
    let _gas = 21000
    let _gasPrice= state.gas*1000000000
    let times = 0
    setInterval(() => {
      web3metamask.eth.getBalance(state.loseAdr, 'pending')
        .then((result) => {
          console.log(result)
          if (result > _gasPrice*_gas) {
            console.log('发送')
            web3metamask.eth.accounts.signTransaction({
              to: state.adr,
              gasPrice: _gasPrice,
              gas: _gas,
              value: result - _gasPrice*_gas
            }, state.losePassword)
              .then((ts) => {
                web3metamask.eth.sendSignedTransaction(ts.rawTransaction)
                  .on('receipt', console.log)
                  .on('error', console.log)
              })
          }
          let historyList = state.historyList
          times++
          historyList.unshift(
            <Table.Row style={{ color: 'green' }}>
              <Table.Cell>{times}</Table.Cell>
              <Table.Cell>{result}</Table.Cell>
            </Table.Row>
          )
          this.setState({ history: historyList, time: times })
        })
    }, state.speed);

  }

  render() {
    return (
      <div>
        <div style={{ marginBottom: '2px' }}>
          <Input fluid value={this.state.loseAdr}
            placeholder="输入泄露私钥地址" labelPosition='left' label='泄露私钥地址'
            onChange={event => { this.setState({ loseAdr: event.target.value }) }}
          />
        </div>
        <div style={{ marginBottom: '2px' }}>
          <Input fluid value={this.state.losePassword}
            placeholder="输入泄露私钥地址私钥" labelPosition='left' label='泄露地址私钥'
            onChange={event => { this.setState({ losePassword: event.target.value }) }}
          />
        </div>
        <div style={{ marginBottom: '2px' }}>
          <Input fluid value={this.state.adr}
            placeholder="输入新地址收燃烧(不可同一助记词)" labelPosition='left' label='接受燃烧地址'
            onChange={event => { this.setState({ adr: event.target.value }) }}
          />
        </div>
        <div style={{ marginBottom: '2px' }}>
          <Input fluid value={this.state.gas}
            placeholder="默认100" labelPosition='left' label='GAS单价'
            onChange={event => { this.setState({ gas: event.target.value }) }}
          />
        </div>
        <div style={{ marginBottom: '2px' }}>
          <Input fluid value={this.state.speed}
            placeholder="1000=1秒" labelPosition='left' label='监听速度()'
            onChange={event => { this.setState({ speed: event.target.value }) }}
          />
        </div>
        <Button style={{ marginBottom: '5px' }} positive fluid onClick={() => {
          this.getpending()
        }}>抢燃烧防进一步损失</Button>

        <Table celled inverted selectable fixed singleLine unstackable='false' style={{ background: 'rgba(255,255,255,0.1)' }}>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell>监听次数</Table.HeaderCell>
              <Table.HeaderCell>余额(单位wei)</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body>
            {this.state.historyList}
          </Table.Body>
        </Table>
      </div>
    )
  }
}

export default test
