import { useState, useEffect } from "react";
import { NavLink, useLocation } from "react-router-dom";
import { Menu } from 'semantic-ui-react';


function LockCeloHead() {
  const location = useLocation();
  const [activeItem, setActiveItem] = useState('');

  useEffect(() => {
    setActiveItem(location.pathname);
  }, [location]);
  return (
    <div>
      <Menu inverted className='bridgeUser' style={{
        maxWidth: '500px', margin: '2px auto auto auto',
        borderRadius: '10px',
        background: 'rgba(28,100,177,1)'
      }}>
        <NavLink to="/LGswap" activeClassName="active">
          <Menu.Item
            name='兑换'
            active={activeItem === '/LGswap'}
            onClick={() => setActiveItem('/LGswap')}
          />
        </NavLink>
       
        <Menu.Menu position='left' >
          <NavLink to="/WETH" activeClassName="active">
            <Menu.Item
              name='WMATIC'
              active={activeItem === '/WETH'}
              onClick={() => setActiveItem('/WETH')}
            />
          </NavLink>

        </Menu.Menu>
      </Menu>
    </div>
  );
}

export default LockCeloHead;