import React,{ Component }  from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Link } from 'react-router-dom';
import { Card ,Button,Input,Popover,Modal,Divider} from 'antd';
import { QuestionCircleOutlined} from '@ant-design/icons';
import LockFactory from '../../../ABI/LockFactory';
import erc20abi from '../../../ABI/ERC20.abi.json';
import '../../../components/global'
const BigNumber = require('bignumber.js');

class App extends Component {
      state = {
        LockFactoryAdr:global.constants.LockFactory,  //锁仓机合约地址
        fee:'',
        feeUSD:'',
        Unlockfee:'',
        message:'',
        GXvalue:'',
        Disabled2:'',
        Approvedisplay:'none',
        tokenAdr:'',
        total:'',
        addDay:'',
        amount:'',
        onoff2:'none',
        visible2:true,
      };

  async componentDidMount(){

    //获取HT费用
    const _fee = await LockFactory.methods.feeHT().call();
    const fee = parseFloat(web3.utils.fromWei(_fee,"ether")).toFixed(3)
    this.setState({fee});

    //获取USD费用
    const feeUSD = await LockFactory.methods.Factoryfee().call();
    this.setState({feeUSD});

    //获取解锁费用
    const Unlockfee = await LockFactory.methods.Unlockfee().call();
    this.setState({Unlockfee});

    const GXvalue = await LockFactory.methods.GXvalue().call();
    this.setState({GXvalue});
    
  }


  createLock = async () =>{
    const _fee = await LockFactory.methods.feeHT().call();
    const ERC20 =  new web3.eth.Contract(erc20abi.abi,this.state.tokenAdr);
    const decimals = await ERC20.methods.decimals().call();
    const _total = new BigNumber(this.state.total/10**(18-decimals) ).toFixed() ;
    const _amount = new BigNumber(this.state.amount/10**(18-decimals) ).toFixed() ;
    await LockFactory.methods.createLock(
        this.state.tokenAdr,
        web3.utils.toWei(_total,'ether'),
        this.state.addDay,
        web3.utils.toWei(_amount,'ether'),
        )
    .send({from:window.defaultAccount,value:_fee})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.setState({message:"打包中……"});
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.setState({message:"创建成功,前往“我的锁仓”查看并调整参数"});
      }
    })
  }


  Approve = async () =>{
    const ERC20 = await new web3.eth.Contract(erc20abi.abi,this.state.tokenAdr);
    await ERC20.methods.approve(this.state.LockFactoryAdr, web3.utils.toWei('10000000000000000000','ether'))
    .send({from:window.defaultAccount})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.setState({message:"打包中……"});
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.setState({message:"授权成功可以进行下一步操作"});
        this.setState({Approvedisplay: 'none'});
        this.setState({Disabled2:''})
      }
    })
  }

  //判断该通证有无授权
  isApprove = async(event)=> {
    await this.setState({tokenAdr:event.target.value})
    const ERC20 =  new web3.eth.Contract(erc20abi.abi,this.state.tokenAdr);
    const _allowance = await ERC20.methods.allowance(window.defaultAccount,this.state.LockFactoryAdr).call();
    const allowance = web3.utils.fromWei(_allowance,'ether');
    this.setState({message:""});
    if (allowance < 100000000){this.setState({Approvedisplay: 'block'}) 
      this.setState({Disabled2:"disabled"})
      }else{
      this.setState({Approvedisplay: 'none'}) 
      this.setState({Disabled2:''})
      }
  }

  //锁仓机介绍开关
  open2 = () => {
    if(this.state.onoff2 ==='none'){
      this.setState({onoff2:"block"});
    }else{
      this.setState({onoff2:"none"});
    }
  }

  handleCance2 = () => {
    if(this.state.visible2 === true){
      this.setState({visible2: false})
    }else{
      this.setState({visible2: true})
    }
  };

  render(){
      const content1 = (
        <div>
          <p>粘贴需锁仓的通证合约地址</p>
          <p>如：0x7cc0c1a34b1ae55f9bd2a19ece9d4b166b66e70c</p>
        </div>
      );
      const content2 = (
        <div>
          <p>输入该锁需锁仓的通证数量</p>
        </div>
      );
      const content4 = (
        <div>
          <p>输入锁仓的天数，创建好后在您未移交前还可以修改</p>
        </div>
      );
      const content5 = (
        <div>
          <p>指单次解锁数量，如果想一次性全部释放则填写与锁仓数量相同的数量</p>
          <p>默认释放间隔为每30天（同样可以在移交前调整）</p>
          <p>单次释放数量在移交前可调整</p>
        </div>
      );

  return (
    <div>
          <div style={{marginTop:'10px'}}>
              <Button   color='green' size="middle">
                <Link to="/Lockhome">所有锁仓</Link>
              </Button>
              &nbsp;
              <Button  color='green' size='middle'  >
                <Link to="/LockMy">我的锁仓</Link>
              </Button>
              <Button   type="primary"  color='green' size="middle" style={{marginBottom:'5px',float:'right'}} >
                <Link to="/LockCreat">创建新锁</Link>
              </Button>
          </div>
        <Card type="inner" size='small'  >
            <Divider  style={{marginTop:'8px',marginBottom:'8px',fontSize:'15px'}}
                  >创建新锁
            </Divider>

                <div style={{marginTop:'10px'}}>
                      <Input  addonBefore='通证地址'   
                          placeholder="需锁仓的通证合约地址" size="large"
                          suffix={
                            <Popover content={content1} trigger="click">
                              <QuestionCircleOutlined />  
                            </Popover>
                          }
                          onChange={this.isApprove}
                          />
                </div>
                <div style={{display:this.state.Approvedisplay}}>
                    <Button  shape='round' block type="primary"  size="middle" style={{marginTop:'10px'}} 
                          onClick={() => {this.Approve()}}>先授权</Button>
                </div>

                <div style={{marginTop:'10px'}}>
                      <Input  addonBefore='锁仓数量'
                          placeholder="如：10000" size="large"
                          suffix={
                            <Popover content={content2} trigger="click">
                              <QuestionCircleOutlined />  
                            </Popover>
                          }
                          onChange={event=>{this.setState({total:event.target.value})}}
                          />
                </div>

                <div style={{marginTop:'10px'}}>
                      <Input  addonBefore='锁仓天数'
                            placeholder="如：365" size="large"
                            suffix={
                              <Popover content={content4} trigger="click">
                                <QuestionCircleOutlined />  
                              </Popover>
                            }
                            onChange={event=>{this.setState({addDay:event.target.value})}}/>
                </div>

                <div style={{marginTop:'10px'}}>
                      <Input  addonBefore='单次释放'
                            placeholder="如：10000" size="large"
                            suffix={
                              <Popover content={content5} trigger="click">
                                <QuestionCircleOutlined />  
                              </Popover>
                            }
                            onChange={event=>{this.setState({amount:event.target.value})}}/>
                </div>

                <Button  shape='round' block type="primary"  size="middle" style={{marginTop:'10px'}} 
                      disabled = {this.state.Disabled2}
                      onClick={() => {this.createLock()}}>创建</Button>

                <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                        {this.state.message}
                </div>
            <Divider  style={{marginTop:'8px',marginBottom:'8px',fontSize:'15px'}}
                  >
            </Divider>
            <Button shape='round' type='primary' block  size="small"  onClick={() => {this.open2()}}>说明</Button>
            <div style={{marginLeft:'10px', display:this.state.onoff2}}>
                <div style={{marginTop:'10px'}}>
                  <Button size="small" danger onClick={this.handleCance2}>图片说明</Button>
                </div>
                    <b>费用说明</b>
                <div>&emsp;1.创建新锁：{this.state.fee}HT(${this.state.feeUSD})</div>
                <div>&emsp;2.解锁时收取解锁额的{this.state.Unlockfee/10}%</div>
                <div>
                    <b>免责声明</b>
                    <div>&emsp;
                      您通过本工具创建的锁并将通证锁在该锁下等同于该通证由该锁合约与Heco链共同保管，
                      本平台并没有保管您的通证。</div>
                      <div>&emsp;
                      同时区块链技术属高前沿性技术，存在各种无法预知的风险,
                      本平台不承担任何原因导致的您的资产损失
                    </div>
                </div>
            </div>
        </Card>

        <Modal  visible={this.state.visible2}  //创建流程
          onCancel={this.handleCance2} 
          footer={[
            <Button type='primary' onClick={this.handleCance2}>关闭</Button>,
             ]}
        >
          <div style={{textAlign:'center'}}>
              <div className='lockcreatinfo' ></div>
          </div>
        </Modal>


    </div>
  );
}
}

export default App;
