import React from 'react';
import {  Card } from 'antd';

function Edu() {
  return(
    <div>
      <Card>
        ​​​​<b>冲动、孤独、无聊与矛盾</b>
        <h5>ahr999  2018-09-24</h5>
        <p>What doesn′t kill you makes you stronger.</p>
        <p>如果你认同前面三篇文章的观点：</p>
        <p>《比特币与理想主义》我们在参与一场社会实验，它存在失败的可能性，但是我们无怨无悔。</p>
        <p>《下车太早只因愿景太小》这场实验的目标很大，如果一切顺利，比特币的价格可能会在20年后涨到1.6亿人民币。</p>
        <p>《囤比特币：你离财富自由还有多远？》我们没有其他能耐，只能靠囤积比特币，并耐心地等待属于自己的财富自由。</p>
        <p>那么你或许已经下定决心囤比特币了。从过往的经验看，绝大多数人没能成功。外部因素当然很重要，比如，出了一点意外，你需要急用钱。但是，决定性因素还是心态。</p>
        <p>你可能以为最需要克服的是恐惧，其实不是，因为你早就对币价暴涨暴跌有了准备。</p>
        <p>冲动</p>
        <p>比特币价格的巨大波动，山寨币动则十倍百倍的涨幅，常常会让我们产生进去炒一把的冲动。这是一种非常非常不理性的冲动，但是我们常常难以抵抗。</p>
        <p>人们天生对赌博就缺乏抵抗力，明知是个零和或者负和游戏，却常常忍不住要去ALL IN，多少人因此而倾家荡产。</p>

        <p>炒山寨币就像是买彩票，中奖了十倍百倍千倍都有可能，但是概率极低。少数币确实收益很高，但是大多数最终都会归零。而且认真你就输了，你认为的“价值币”就是不涨，反倒是那些垃圾币涨得稀里哗啦的，他们管这个叫“市值管理”。</p>

        <p>杠杆炒币就像是赌大小，赢了十倍百倍，输了倾家荡产。我不否认这里面可能有些技术含量，但是这明显是个零和或者负和游戏，你赢的必然是别人亏的币。</p>

        <p>囤比特币则是个正和游戏，所有人都会随着比特币的发展壮大而获利。过去十年里，比特币从未辜负任何一个相信它的人。</p>

        <p>但是，我也很清楚地知道，说这些道理都没用，赌博这玩意历史悠久，多少血的教训，却依然火爆异常。人们骨子里的赌性是与生俱来的，我们常常难以与之对抗。</p>

        <p>我的策略是，交易所不放钱也不放币，把私钥藏得深一点。每当行情到来，冲动的时候，我没法立刻操作私钥；而当可以操作私钥的时候，行情已经过去，理性又占据了上风。</p>

        <p>孤独</p>
        <p>相信比特币的人都是孤独的。</p>

        <p>你爱上了一个姑娘，她美丽温柔且善良，但她穿着前卫，特立独行，于是其他人就认为她品行不端，你的家人反对你们在一起。你该怎么办？</p>

        <p>你相信自己的眼光，坚信自己是对的，别人是错的。但是，你同时又发现，讲道理根本没人认真听，你站在了所有人的对立面。时不时还有好心人来劝你，让你回头是岸，号称是为你好。你该怎么办？</p>

        <p>我从未跟周围的人提过比特币，这是一段地下感情，是一段煎熬。更煎熬的是，在长达三年的时间里，周围的人也没有跟我提过比特币。</p>

        <p>我是个双面人。一方面，我在网上很活跃，学习很多比特币相关技术，写很多的文章；另一方面，我在一个完全没有比特币的圈子里生活，也很活跃。</p>

        <p>我时常会想象中本聪的生活状态。一方面，他在网上是神一般的存在；另一方面，在他的生活圈子中，恐怕没有一个人知道他就是中本聪吧。</p>

        <p>我甚至能感受到你们的痛苦，即使非常认可我的文章，也不能点赞，更不能转发，因为你们不想让朋友圈知道你们囤币的秘密。</p>

        <p>很多人感到孤独的时候，就会去参加币圈各种聚会。但是，相信我，那并没有什么卵用。你会发现，圈内跟圈外一样，真正的信仰者寥寥无几。</p>

        <p>到了2017年，居然出现了那么两次，别人在我面前主动提比特币。但是，那时的我已经习惯了孤独，甚至开始享受孤独。</p>

        <p>一次是公事会议。结束后闲聊，话题不知道怎么谈到了外汇管制问题，C老板年龄50左右，中年油腻男，一家连锁酒店的董事长，提到了比特币。另外有几个人似乎也感兴趣，就开始问问题。我装作一脸茫然的样子。C老板兴致勃勃的告诉大家，这玩意可以买一点，正在涨，估计还会涨。</p>

        <p>另一次是私人饭局，老G管理一个私募基金，正在介绍他最近关注的机器学习和量化交易。他提到说，他有很多同行在进行数字货币的交易。</p>

        <p>他们讲的时候，我就认真的听着。我发现他们的理解都非常的肤浅，有些甚至是错误的，但是我也不能打断他们。他们或许永远想不到，我才是对这事理解最深的人。</p>

        <p>不过那种感觉超好，因为傻子也能判断，牛市真的来了，赶紧回家再买两个币，呵呵。</p>

        <p>对于孤独，我的建议是，疯狂写东西。有的事情你想跟别人说，别人又不认可，你憋着又难受，你就把它全部写下来。等到你适应孤独的时候，你会发现，不仅你的思维更成熟了，而且你的文笔更好了。</p>

        <p>无聊</p>
        <p>囤比特币这个行为本身是极其无聊的。</p>

        <p>你喜欢一个姑娘，你却要把她晾在一边，什么也不干。这TM是反人性的！</p>

        <p>你热爱比特币，你觉得自己一定要做点什么。这种心情我很理解。但是，请相信我，囤币行为本身已经对比特币网络做出了巨大的贡献。</p>

        <p>只有你相信比特币的去中心化，你相信比特币的安全性，你相信比特币的前途无限，你才有可能长期囤币，你才有可能把自己辛辛苦苦赚来的财富，保存在比特币那一串“虚幻的”私钥字符串上。</p>

        <p>你的这种行为，你对待比特币的态度，已经远超你对待爱人的态度。请问有多少人愿意把自己辛辛苦苦赚来的财富，无怨无悔的交给自己的爱人？</p>

        <p>所以，你真的不需要再做什么了。如果实在感觉无聊，就去健身去养生，别等到财富自由的时候，你身体也挂了。给你嫩模，你也只能看看，那还有什么意义呢？</p>

        <p>矛盾</p>
        <p>这是一种很复杂的心情，你既希望比特币涨，又希望比特币跌。</p>

        <p>我曾经希望自己不要看行情，于是把所有比特币行情相关的APP都删了。但是，发现自己根本做不到，因为手机或电脑浏览器打开网站也能看行情。</p>

        <p>对于囤比特币而言，币价涨是没用的。长期囤币，又不准备卖，涨多高都是“浮盈”。但是，涨意味着自己的坚持是正确的，总是另人高兴的。</p>

        <p>反过来，币价跌虽然心情上会有些郁闷，但是有机会抄底补仓，币量将成倍增长。</p>

        <p>人会变得有些分裂。这是正常现象，你去看看某些币圈老人。他们一会儿跑出来唱多，一会儿跑出来唱空，被粉丝喷得不要不要的。</p>

        <p>我个人的判断是，他们绝大部分币都是囤住不动的，既没有在做多，也没有在做空。之所以变来变去，是因为他们自己也搞不清楚，到底想要涨，还是想要跌，矛盾得很。</p>

        <p>这种矛盾的心情我也不知道该如何解决，因为我至今也还是矛盾的。不过似乎问题也不大，因为并没有影响我囤币。</p>

        <p>我们会经历心理上的考验：冲动、孤独、无聊和矛盾。但是，那些没有打倒我们的，终将帮助我们成长，就像那些没有打倒比特币的，终将让比特币变得更强大。</p>

        <p>几年过去了，我跟随比特币一起成长，如果你一直读我的文章，你或许能感受到思维方式、写作风格和文笔上变化，甚至连我的性格都变得越来越温和呢。​​​​</p>
      </Card>
    </div>
  )

}

export default Edu;
