import React, { Component } from 'react'
import { List } from 'semantic-ui-react'

export class creactList extends Component {
  

  render() {
    return (
      <div>
        <div style={{display: this.props.name === 'N/A' ? 'none' : 'block' }}>
          <List divided inverted relaxed style={{ color: 'white', wordWrap: 'break-word' }}>
            <List.Item >
              全名：{this.props.name}
            </List.Item>
            <List.Item>
              简称：{this.props.symbol}
            </List.Item>
            <List.Item>
              发行量：{this.props.totalSupply}
            </List.Item>
          </List>
        </div>
      </div>
    )
  }
}

export default creactList
