import React, { useState, useEffect, useCallback, useRef } from 'react'
import { Card, Button, Input, Dropdown, ButtonGroup } from 'semantic-ui-react'
import { connect } from "react-redux"
import Head from './components/head';
import '../../../../App.css';
import LockList from '../../../../tokenList/lockTokenList/index.json';
import { getChainInfo, isApprove } from '../../../../components/global/functions';
import { Toast } from 'antd-mobile'
import web3 from "../../../../ABI/web3";
import lockV2ABI from "../../../../ABI/LockV2.json";
import { AbiItem } from 'ethereum-multicall/dist/esm/models';
import { Contract } from 'web3-eth-contract';
import Fun from "../../../../components/global/function";
import LoadingReport from './components/LoadingClose';
import { To18decimals, chackTokenBlance, TokenPrice, TokenPriceThisChain, getTokenPriceAdr } from "../../../../components/global/functionTS"
import CreatConfim from './components/creatConfim';
import UserAdrBook from './components/userAdrBook';
import { CELOadr } from '../../../../components/global/globals';
import erc20abi from '../../../../ABI/ERC20.abi.json';

function LockCreat(props: any) {
    const [isMounted, setIsMounted] = useState(true);
    // 在组件卸载时设置 isMounted 为 false
    useEffect(() => {
        return () => {
            setIsMounted(false);
        };
    }, []);

    const [state, setState] = useState
        ({
            tokenAdr: '0x6ab4Bb486Cb15C9900b7aA89FE170FD26A7bcc51',
            user: '', //用户地址
            userTEL: '',  //用户备注
            unlockTime: 30 as number | undefined,
            betweenTime: 30 as number | undefined,
            onceAmount: undefined as number | undefined,
            onceAmountRMB: 0 as number,
            balance: undefined as number | undefined,
            balanceRMB: 0 as number,
        });

    const RMBConversionTokenValue = () => {
        let tokenValue: number
        if (state.balance && state.balanceRMB) {
            tokenValue = Number((state.balance / (state.balanceRMB / state.balance)).toFixed(2))
            setState({
                ...state,
                balance: tokenValue
            })
            handleBalanceChange(tokenValue)
        }
    }

    const tokenListSelect = LockList.tokens

    const periods = [
        {
            "key": 1,
            "text": '6',
            "value": 6
        },
        {
            "key": 2,
            "text": '12',
            "value": 12
        },
        {
            "key": 3,
            "text": "24",
            "value": 24
        },
        {
            "key": 4,
            "text": "36",
            "value": 36
        }
    ]
    const [period, setPeriod] = useState<number>(12);

    const [tokenLogo, setTokenLogo] = useState<string>('积分');
    const [tokenLogoONOFF, setTokenLogoONOFF] = useState<boolean>(true);
    const [tokenName, setTokenName] = useState<string>('积分');
    const [userBookListParent, setUserBookListParent] = useState<any[] | []>([]);

    const sendDataToParent = useCallback((list: any[] | []) => {
        setUserBookListParent(list);
    }, [])

    const setUserTELandAdr = useCallback((userTEL: string, userAdr: string) => {
        setState({
            ...state,
            user: userAdr,
            userTEL: userTEL
        })
        setUserAdrBook(false)
    }, [state])

    const inputToken = async (value: any) => {
        const selectedTokenName = tokenListSelect.find(token => token.text === value);
        const selectedTokenAdr = tokenListSelect.find(token => token.value.toLowerCase() === value.toLowerCase());
        if (selectedTokenAdr) {
            setState({
                ...state,
                tokenAdr: selectedTokenAdr.value,
                balance: 0,
                onceAmount: 0,
                onceAmountRMB: 0,
                balanceRMB: 0,
            });
            setTokenLogoONOFF(true)
            setTokenLogo(selectedTokenAdr.text);
            try {
                const ERC20 = new web3.eth.Contract(erc20abi.abi as AbiItem[], value)
                const name = await ERC20.methods.symbol().call()
                setTokenName(name)
            } catch (error) {
                console.log(error)
            }
            return
        }
        if (selectedTokenName) {
            setState({
                ...state,
                tokenAdr: selectedTokenName.value,
                balance: 0,
                onceAmount: 0,
                onceAmountRMB: 0,
                balanceRMB: 0,
            });
            setTokenLogoONOFF(true)
            setTokenLogo(selectedTokenName.text);
            try {
                const ERC20 = new web3.eth.Contract(erc20abi.abi as AbiItem[], value)
                const name = await ERC20.methods.symbol().call()
                setTokenName(name)
            } catch (error) {
                console.log(error)
            }
            return
        }
        if (!selectedTokenName && !selectedTokenName) {
            setState({
                ...state,
                tokenAdr: value,
                balance: 0,
                onceAmount: 0,
                onceAmountRMB: 0,
                balanceRMB: 0,
            });
            setTokenLogoONOFF(false)
            try {
                const ERC20 = new web3.eth.Contract(erc20abi.abi as AbiItem[], value)
                const name = await ERC20.methods.symbol().call()
                setTokenName(name)
            } catch (error) {
                console.log(error)
                setTokenLogoONOFF(true)
                setTokenLogo('unknow');
                setTokenName('?')
            }
        }


    }
    const selectToken = (value: any) => {
        setState({
            ...state,
            tokenAdr: value
        })
        inputToken(value)
    }
    const delInputToken = () => {
        setState({
            ...state,
            tokenAdr: '',
        })
        setTokenLogoONOFF(true)
        setTokenLogo('unknow');
    }

    const [globalState, setGlobalState] = useState
        ({
            lockV2Adr: '',
            tokenApprove: false,
            LGtokenApprove: false,
            LGadr: '',
            CreateFee: 0,
            UnlockFee: 0,
            LGPrice: 0,
            lockV2Contract: undefined as Contract | undefined
        });

    //加载初始化数据
    const first = useCallback(async () => {
        try {
            const chainInfo = await getChainInfo(props.ChainID)
            const tokenIsApprove = await isApprove(state.tokenAdr, chainInfo.ChainAdr.lockV0)
            const LGisApprove = await isApprove(chainInfo.ChainAdr.LG, chainInfo.ChainAdr.lockV0)
            const _lockV2Contract = new web3.eth.Contract(lockV2ABI as AbiItem[], chainInfo.ChainAdr.lockV0)
            let CreateFee = await _lockV2Contract.methods.CreateFee().call()
            let UnlockFee = await _lockV2Contract.methods.UnlockFee().call()
            let LGPrice = parseInt(await TokenPriceThisChain(CELOadr.LG, CELOadr.LGOracle))
            setGlobalState(prevState => ({
                ...prevState,
                lockV2Adr: chainInfo.ChainAdr.lockV0,
                tokenApprove: tokenIsApprove === 'no' ? true : false,
                LGtokenApprove: LGisApprove === 'no' ? true : false,
                LGadr: chainInfo.ChainAdr.LG,
                lockV2Contract: _lockV2Contract,
                CreateFee,
                UnlockFee,
                LGPrice
            }))
            LoadingReportRef.current.loadingClose()
        } catch (error) {
            console.log(error)
        }
    }, [props.ChainID, state.tokenAdr])

    const handleOnceAmountChange = useCallback(async (value: undefined | number) => {
        let filteredValue: undefined | number
        filteredValue = value
        try {
            if (isMounted) {
                setState(prevState => ({ ...prevState, onceAmount: Number(filteredValue) }))
                setTimeout(async () => {
                    try {
                        const _tokenPriceAdr = await getTokenPriceAdr(state.tokenAdr)
                        console.log(_tokenPriceAdr)
                        let tokenPrice = parseInt(await TokenPrice(_tokenPriceAdr))
                        let onceAmountRMB = parseFloat((Number(filteredValue) * (tokenPrice / 1e8)).toFixed(1))
                        if (isMounted) {
                            setState(prevState => ({ ...prevState, onceAmountRMB }));
                        }
                    } catch (error) {
                        console.log(error)
                    }

                }, 500); // 1000毫秒，即1秒延迟
            }
        } catch (error) {
            console.log(error)
        }
    }, [isMounted, state])

    // 更新 balance 状态的处理函数
    const handleBalanceChange = useCallback(async (value: undefined | number) => {
        let filteredValue: undefined | number
        filteredValue = value;
        try {
            if (isMounted) {
                setState({ ...state, balance: Number(filteredValue) });
                handleOnceAmountChange(parseFloat((Number(filteredValue) / period).toFixed(2)))
                setTimeout(async () => {
                    try {
                        const _tokenPriceAdr = await getTokenPriceAdr(state.tokenAdr)
                        let tokenPrice = parseInt(await TokenPrice(_tokenPriceAdr))
                        let balanceRMB = parseFloat((Number(filteredValue) * (tokenPrice / 1e8)).toFixed(1));
                        if (isMounted) {
                            setState(prevState => ({ ...prevState, balanceRMB }));
                        }
                    } catch (error) {
                        console.log(error)
                    }
                }, 500); // 1000毫秒，即1秒延迟
            }

        } catch (error) {
            console.log(error)
        }
    }, [isMounted, state, handleOnceAmountChange, period])

    //设定期数
    const SetPeriod = useCallback(async (value: number) => {
        setPeriod(value);
        if (state.balance) {
            handleOnceAmountChange(parseFloat((state.balance / value).toFixed(2)))
        }
    }, [handleOnceAmountChange, state.balance])


    // 更新 unlockTime 状态的处理函数
    const handleUnlockTimeChange = (value: undefined | number) => {
        setState({ ...state, unlockTime: Number(value) });
    };
    // 更新 betweenTime 状态的处理函数
    const handleBetweenTimeChange = (value: undefined | number) => {
        setState({ ...state, betweenTime: Number(value) });
    };
    // 
    const handleUserTEL = (value: string) => {
        setState({ ...state, userTEL: value, user: '' });
    }

    const searchUserAdr = () => {
        const UserTEL = userBookListParent.find(userTEL => userTEL.userTEL === state.userTEL);
        if (UserTEL) {
            setState({ ...state, user: UserTEL.userAdr });
        } else {
            setState({ ...state, user: '无结果' });
        }
    }

    const handleUserChange = (value: string) => {
        setState({ ...state, user: value });
    };
    useEffect(() => {
        first()
    }, [first, props.UserAdr, props.ChainID]);

    const FunRef = useRef<any>(null);
    const LoadingReportRef = useRef<any>(null);

    const Approve = async (tokenAdr: string) => {
        await FunRef.current.Approve(tokenAdr, globalState.lockV2Adr)
        first()
    }

    const createLock = async () => {
        try {
            if (globalState.lockV2Contract) {
                if (state.onceAmount === undefined) { return Toast.show({ content: '单次释放数量不能为空' }) }
                if (state.balance === undefined) { return Toast.show({ content: '封装总量不能为空' }) }
                let onceAmount = await To18decimals(state.tokenAdr, state.onceAmount)
                let _balance = await To18decimals(state.tokenAdr, state.balance)
                let balance = web3.utils.toWei(_balance, 'ether')
                if (await chackTokenBlance(state.tokenAdr, props.UserAdr, balance) === false) { return Toast.show({ content: '您帐户中的该封装通证余额不足' }) }
                if (state.unlockTime === undefined) { return Toast.show({ content: '解锁时间不能为空' }) }
                if (state.betweenTime === undefined) { return Toast.show({ content: '解锁间隔不能为空' }) }
                await globalState.lockV2Contract.methods.createLock(
                    state.tokenAdr,
                    state.user,
                    state.unlockTime * 86400,
                    state.betweenTime * 86400,
                    web3.utils.toWei(onceAmount, 'ether'),
                    balance
                ).send({ from: props.UserAdr, gas: '450000' })
                    .on('transactionHash', (transactionHash: string | any[]) => {
                        if (transactionHash.length !== 0) {
                            LoadingReportRef.current.loadingOpen('block', 'none', 'none', "封装中……")
                            setCreatConfimOpen(false)
                        }
                    })
                    .on('receipt', (receipt: { status: boolean; }) => {
                        if (receipt.status === true) {
                            LoadingReportRef.current.loadingOpen('none', 'block', 'none', "封装完成！通知用户 ")

                        }
                    })
                    .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                        if (receipt !== undefined && receipt.status === false) {
                            LoadingReportRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                        }
                    })
            }
        } catch (error) {
            console.error("Error creating lock:", error);
            // 可以添加适当的错误处理，例如显示错误提示给用户
        }
    }

    const [creatConfimOpen, setCreatConfimOpen] = useState(false);

    const openConfimWindow = () => {
        console.log(state)
        if (!state.balance) { return Toast.show({ content: '请填写封装总额' }) }
        if (state.betweenTime === undefined) { return Toast.show({ content: '请填写间隔天数' }) }
        if (!state.onceAmount) { return Toast.show({ content: '请填写单次释放' }) }
        if (state.unlockTime === undefined) { return Toast.show({ content: '请填写锁盒天数' }) }
        if (!state.tokenAdr) { return Toast.show({ content: '请填写封装通证地址' }) }
        if (!state.user) { return Toast.show({ content: '请填写用户地址' }) }
        if (state.user === '无结果') { return Toast.show({ content: '薄中无，重搜或填写用户地址' }) }
        setCreatConfimOpen(true)
    }

    const [userAdrBook, setUserAdrBook] = useState(false);
    const openUserAdrBook = () => {
        setUserAdrBook(true)
    }

    return (
        <div>
            <Head />
            <Card type="inner" style={{ width: 'inherit', minWidth: '312px', maxWidth: '500px', margin: '0px auto auto auto', backgroundColor: 'rgba(33,133,208,0.5)' }}>

                <div style={{ display: 'flex' }}>
                    <ButtonGroup color='blue' style={{ marginBottom: '3px', marginRight: '0px' }}>
                        <Button icon style={{ padding: '4px 3px 4px 3px' }}>
                            {tokenLogoONOFF &&
                                <div >
                                    <img
                                        src={`${process.env.PUBLIC_URL}/logo/${tokenLogo}.svg`}
                                        alt='auto' width='auto' height='30px'
                                    />
                                </div>
                            }
                            {!tokenLogoONOFF &&
                                <div>
                                    {tokenName}
                                </div>
                            }
                        </Button>
                        <Button style={{ borderRadius: '0px 0px 0px 0px ' }}>封装通证</Button>
                    </ButtonGroup>

                    <Button
                        color='blue'
                        icon
                        style={{ margin: '0px 0px 3px 2px', padding: '11px 5px 11px 5px', borderRadius: '5px 0px 0px 5px ' }}
                    >
                        <Dropdown
                            options={tokenListSelect}
                            value={state.tokenAdr}
                            onChange={(e, { value }) => selectToken(value)}
                        />
                    </Button>
                    <Input
                        fluid
                        placeholder="选择或输入通证合约地址"
                        style={{ marginBottom: '3px', flex: '1', borderRadius: '0px 0px 0px 0px ' }}
                        value={state.tokenAdr}
                        onChange={(e, { value }) => inputToken(value)}
                    />

                    <Button icon='trash alternate outline' onClick={delInputToken} color='blue'
                        style={{ marginBottom: '3px', marginRight: '0px' }}
                    />
                </div>

                {globalState.tokenApprove &&
                    <Button fluid
                        color='teal'
                        size='tiny'
                        style={{ width: '200px', margin: 'auto', marginBottom: '3px' }}
                        onClick={() => { Approve(state.tokenAdr) }}
                    >授权封装通证
                    </Button>
                }
                {globalState.LGtokenApprove &&
                    <Button fluid
                        color='teal'
                        size='tiny'
                        style={{ width: '200px', margin: 'auto', marginBottom: '3px' }}
                        onClick={() => { Approve(globalState.LGadr) }}
                    >授权LG用于支付费用
                    </Button>
                }

                <div style={{ display: 'flex' }}>
                    <Input fluid
                        action={{
                            color: 'blue',
                            labelPosition: 'left',
                            icon: 'warehouse',
                            content: '封装总量',

                        }}
                        actionPosition='left'
                        value={state.balance}
                        placeholder="输入数量"
                        type="number"
                        step="any"
                        style={{ marginBottom: '3px', flex: '1' }}
                        onChange={(e, { value }) => handleBalanceChange(value === '' ? undefined : parseFloat(value))}
                    />
                    <Button icon color='blue' content={`≈${state.balanceRMB}`}
                        onClick={RMBConversionTokenValue}
                        style={{ marginBottom: '3px', borderRadius: '0px 5px 5px 0px ', marginRight: '0px' }}
                    />
                </div>

                <div style={{ display: 'flex' }}>
                    <Input fluid
                        action={{
                            color: 'blue',
                            labelPosition: 'left',
                            icon: 'truck',
                            content: '单次释放',
                        }}
                        actionPosition='left'
                        value={state.onceAmount}
                        placeholder="单次释放数量"
                        type="number"
                        step="any"
                        style={{ marginBottom: '3px', flex: '1' }}
                        onChange={(e) => handleOnceAmountChange(e.target.value === '' ? undefined : parseFloat(e.target.value))}
                    />
                    <Button color='blue' icon
                        style={{ marginBottom: '3px', borderRadius: '0px 0px 0px 0px ', marginRight: '0px' }}
                    >
                        <Dropdown
                            options={periods}
                            value={period}
                            onChange={(e, { value }) => SetPeriod(value as number)}
                        />
                    </Button>
                    <Button icon color='blue' content={`≈${state.onceAmountRMB}`}
                        style={{ marginBottom: '3px', borderRadius: '0px 5px 5px 0px ', marginRight: '0px', pointerEvents: 'none' }} />
                </div>

                <Input fluid
                    action={{
                        color: 'blue',
                        labelPosition: 'left',
                        icon: 'hourglass start',
                        content: '锁盒天数',

                    }}
                    actionPosition='left'
                    value={state.unlockTime}
                    placeholder="第一次的锁盒天数"
                    style={{ marginBottom: '3px' }}
                    type="number"
                    step="any"
                    onChange={(e, { value }) => handleUnlockTimeChange(value === '' ? undefined : parseFloat(value))}
                />
                <Input fluid
                    action={{
                        color: 'blue',
                        labelPosition: 'left',
                        icon: 'coffee',
                        content: '间隔天数',

                    }}
                    actionPosition='left'
                    value={state.betweenTime}
                    placeholder="释放间隔天数"
                    style={{ marginBottom: '3px' }}
                    type="number"
                    step="any"
                    onChange={(e, { value }) => handleBetweenTimeChange(value === '' ? undefined : parseFloat(value))}
                />
                <div style={{ display: 'flex' }}>
                    <Input fluid
                        action={{
                            color: 'blue',
                            labelPosition: 'left',
                            icon: 'address card',
                            content: '用户备注',
                        }}
                        actionPosition='left'
                        value={state.userTEL}
                        placeholder="薄名寻址,非必填"
                        style={{ marginBottom: '3px', flex: '1' }}
                        onChange={(e, { value }) => handleUserTEL(value)}
                    />
                    <Button icon='search' color='blue'
                        onClick={() => { searchUserAdr() }}
                        style={{ marginBottom: '3px', borderRadius: '0px 0px 0px 0px ', marginRight: '0px' }} />
                    <Button icon='address book' color='blue'
                        onClick={() => { openUserAdrBook() }}
                        style={{ marginBottom: '3px', borderRadius: '0px 5px 5px 0px ', marginRight: '0px' }} />
                </div>
                <div style={{ display: 'flex' }}>
                    <Input fluid
                        action={{
                            color: 'blue',
                            labelPosition: 'left',
                            icon: 'user',
                            content: '用户地址',
                        }}
                        actionPosition='left'
                        value={state.user}
                        placeholder="输入用户地址"
                        style={{ marginBottom: '3px', flex: '1' }}
                        onChange={(e, { value }) => handleUserChange(value)}
                    />
                </div>
                <Button fluid
                    color='teal'
                    style={{ width: '200px', margin: 'auto' }}
                    onClick={() => { openConfimWindow() }}
                >封 装
                </Button>

                <Card type="inner" style={{ width: 'inherit', minWidth: '312px', maxWidth: '500px', margin: '5px auto auto auto', backgroundColor: '#cce2ff' }}>
                    <div style={{ marginLeft: '10px' }}>
                        <b>费用说明</b>
                        <div>&emsp;1.创建新锁：{((globalState.CreateFee / 100) / (globalState.LGPrice / 1e8)).toFixed(1)}LG({globalState.CreateFee / 100})</div>
                        <div>&emsp;2.解锁时收取解锁额的{globalState.UnlockFee / 10}%</div>
                        <div>
                            <b>免责声明</b>
                            <div>&emsp;
                                您通过本工具将通证锁在该应用下等同于该通证由智能合约与CELO链共同保管,
                                本平台并没有保管您的通证。
                            </div>
                            <div>&emsp;
                                请合法使用该应用，如利用该应用从事一切违法违规活动与本平台无关。
                            </div>
                            <div>&emsp;
                                区块链技术属高前沿性技术，存在各种无法预知的风险,
                                本平台不承担任何原因导致的您的资产损失
                            </div>
                        </div>
                    </div>
                </Card>

            </Card>
            <CreatConfim
                open={creatConfimOpen}
                onClose={() => setCreatConfimOpen(false)}
                tokenName={tokenName}
                user={state.user}
                unlockTime={state.unlockTime}
                betweenTime={state.betweenTime}
                onceAmount={state.onceAmount}
                balance={state.balance}
                CreateLock={createLock}
            />
            <UserAdrBook
                open={userAdrBook}
                onClose={() => setUserAdrBook(false)}
                userAdr={props.UserAdr}
                SendDataToParent={sendDataToParent}
                customerTel={state.userTEL}
                customerAdr={state.user}
                setUserTELandAdr={setUserTELandAdr}
            />
            <Fun ref={FunRef} />
            <LoadingReport ref={LoadingReportRef} />
        </div>
    )

}

const mapStateToProps = (state: any) => {
    return {
        ChainID: state.ChainID,
        UserAdr: state.UserAdr,
        LGbalance: state.LGbalance,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        sendStore(tp: any, val: any) {
            const action = { type: tp, value: val }
            dispatch(action)
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LockCreat)



