import React, { useState, useEffect, useCallback, useRef } from 'react'
import { Image, Table, Card, Button, Header } from 'semantic-ui-react'
import BigNumber from 'bignumber.js';
import web3 from "../../../../../ABI/web3";
import { getLockInfoList, getUserLocksID, getAllLocks } from './lockListFun';
import LockTokenList from '../../../../../tokenList/lockTokenList/index.json';
import erc20abi from '../../../../../ABI/ERC20.abi.json';
import { AbiItem } from 'ethereum-multicall/dist/esm/models';
import { TokenPrice, getTokenPriceAdr, SecondToDay } from "../../../../../components/global/functionTS"
import giftLogo from '../../../../../img/giftBox.png';
import LockInfoWindow from './lockInfoWindow';
import { getChainInfo } from '../../../../../components/global/functions';
import lockV2ABI from "../../../../../ABI/LockV2.json";
import LoadingReport from './LoadingOpen';
import { Toast } from 'antd-mobile'

interface LockInfo {
    id: number
    tokenAdr: string
    symbol: string
    tokenLogo: string
    balance: number
    valueRMB: number
    remainTime: number
    unlockTime: number
    betweenTime: number
    onceAmount: number
    user: string
}
function LockList(props: any) {
    const [List, setList] = useState<LockInfo[]>([]);

    const [loading, setLoading] = useState(false)

    const [valueRMBTotal, setValueRMBTotal] = useState(0)

    const updateRMB = useCallback(async (locks: LockInfo[]): Promise<{ updatedList: LockInfo[], rmbTotal: number }> => {
        const updatedList = [...locks];
        const prices: { [key: string]: number } = {}; // 用一个对象来存储 tokenAdr 对应的价格
        let rmbTotal: number = 0;
        try {

            for (let i = 0; i < locks.length; i++) {
                const tokenAdr = locks[i].tokenAdr;
                if (!prices[tokenAdr]) {
                    const tokenPriceAdr = await getTokenPriceAdr(tokenAdr);
                    const tokenPrice = parseInt(await TokenPrice(tokenPriceAdr));
                    prices[tokenAdr] = tokenPrice;
                }
                const tokenPrice = prices[tokenAdr];
                const valueRMB = parseFloat((locks[i].balance * (tokenPrice / 1e8)).toFixed(1));
                updatedList[i] = { ...updatedList[i], valueRMB: valueRMB };
                rmbTotal = rmbTotal + valueRMB;
            }
        } catch (error) {
            console.error("Error updating RMB values:", error);
        }
        return { updatedList, rmbTotal }
    }, [])

    const getLockList = useCallback(async (ChainID: string, locksID: string[]): Promise<{ locksInfo: LockInfo[] }> => {
        const lockListResult = await getLockInfoList(ChainID, locksID);
        const newlocksInfo = await Promise.all(lockListResult.map(async lock => {
            const symbol = LockTokenList.tokens.find(token => token.value === lock.tokenAdr);
            let Symbol = '';
            let tokenLogo = '';
            if (symbol) {
                Symbol = symbol.text
                tokenLogo = symbol.text
            } else {
                try {
                    const ERC20 = new web3.eth.Contract(erc20abi.abi as AbiItem[], lock.tokenAdr);
                    const otherSymbol = await ERC20.methods.symbol().call();
                    Symbol = otherSymbol
                    tokenLogo = 'unknow'
                } catch (error) {
                    console.log(error)
                }
            }
            return {
                id: lock.id,
                tokenAdr: lock.tokenAdr,
                symbol: Symbol,
                tokenLogo,
                balance: parseFloat(web3.utils.fromWei(lock.balance, 'ether')),
                valueRMB: 0,
                remainTime: new BigNumber(lock.remainTime).toNumber(),
                unlockTime: new BigNumber(lock.unlockTime).toNumber(),
                betweenTime: new BigNumber(lock.betweenTime).toNumber(),
                onceAmount: parseFloat(web3.utils.fromWei(lock.onceAmount, 'ether')),
                user: lock.user
            }
        }));
        let locksInfo = newlocksInfo
        return { locksInfo }
    }, [])


    // 获取用户锁仓信息
    const setLockList = useCallback(async (ChainID: string, userAdr: string) => {
        setList([])
        setLoading(true)

        if (props.isUser === true) {
            const locksListLocal = localStorage.getItem('locksListLocalMy');
            if (locksListLocal !== null) {
                setList(JSON.parse(locksListLocal))
            }
        }

        let lockBalanceIsZero: string[] = [];
        const lockBalanceIsZeroString = localStorage.getItem('lockBalanceIsZeroV2');
        if (lockBalanceIsZeroString !== null) {
            lockBalanceIsZero = JSON.parse(lockBalanceIsZeroString);
        }

        let _locksID = await getUserLocksID(userAdr, ChainID);
        const locksID = _locksID.filter(item => !lockBalanceIsZero.includes(item));

        let locksIDArry = [];
        let onceNumber = 20
        if (locksID.length > onceNumber) {
            for (let i = locksID.length; i > 0; i -= onceNumber) {
                let a = locksID.slice(Math.max(0, i - onceNumber), i)
                a.reverse()
                locksIDArry.push(a)
            }
        } else {
            let a = locksID.slice()
            a.reverse()
            locksIDArry.push(a)
        }

        let locksInfoTemp = [] as LockInfo[]
        for (let i = 0; i < locksIDArry.length; i++) {
            let _locks = await getLockList(ChainID, locksIDArry[i])
            let locks = _locks.locksInfo
            // setList(prevList => [...prevList, ...locks])
            locksInfoTemp = [...locksInfoTemp, ...locks]
        }
        let locksInfo = await updateRMB(locksInfoTemp)
        setList(locksInfo.updatedList)

        if (props.isUser === true) {
            const arrayToStoreString = JSON.stringify(locksInfo.updatedList);
            localStorage.setItem('locksListLocalMy', arrayToStoreString);
        }

        let endRmbTotal = Number(locksInfo.rmbTotal.toFixed(1))
        setValueRMBTotal(endRmbTotal)
        setLoading(false)
    }, [getLockList, props.isUser, updateRMB])

    const [loadNum, setLoadNum] = useState(20)
    const [loadNumAll, setLoadNumAll] = useState(0)
    const [loadNow, setLoadNow] = useState(0)
    console.log(loadNow)
    //加了缓存的
    const setLockListForHome = useCallback(async (ChainID: string) => {
        setList([])
        setLoading(true)

        const locksListLocal = localStorage.getItem('locksListLocalHome');
        if (locksListLocal !== null) {
            setList(JSON.parse(locksListLocal))
        }

        let lockBalanceIsZero: string[] = [];
        const lockBalanceIsZeroString = localStorage.getItem('lockBalanceIsZeroV2');
        if (lockBalanceIsZeroString !== null) {
            lockBalanceIsZero = JSON.parse(lockBalanceIsZeroString);
        }
        let locksID = parseFloat(await getAllLocks(ChainID))
        let _locksIDArry: string[] = [];
        for (let i = locksID - 1; i > 0; i--) {
            _locksIDArry.push(i.toString());
        }

        const filterLocksIDArry = _locksIDArry.filter(item => !lockBalanceIsZero.includes(item));

        let locksIDArry: string[][] = [];
        let onceNumber = 20

        console.log(filterLocksIDArry)
        setLoadNumAll(filterLocksIDArry.length)
        for (let i = 0; i < loadNum; i += onceNumber) {

            const chunk: any[] = filterLocksIDArry.slice(i, i + onceNumber);
            locksIDArry.push(chunk);
        }
        let locksInfoTemp = [] as LockInfo[]
        for (let i = 0; i < locksIDArry.length; i++) {
            let _locks = await getLockList(ChainID, locksIDArry[i])
            let locks = _locks.locksInfo
            locksInfoTemp = [...locksInfoTemp, ...locks]
            setLoadNow(i * onceNumber + 20)
        }
        let locksInfo = await updateRMB(locksInfoTemp)
        setList(locksInfo.updatedList)

        const arrayToStoreString = JSON.stringify(locksInfo.updatedList);
        localStorage.setItem('locksListLocalHome', arrayToStoreString);

        let endRmbTotal = Number(locksInfo.rmbTotal.toFixed(1))
        setValueRMBTotal(endRmbTotal)
        setLoading(false)

    }, [getLockList, updateRMB, loadNum])

    // //加了缓存的
    // const setLockListForHome = useCallback(async (ChainID: string) => {
    //     setList([])
    //     setLoading(true)

    //     const locksListLocal = localStorage.getItem('locksListLocalHome');
    //     if (locksListLocal !== null) {
    //         setList(JSON.parse(locksListLocal))
    //     }

    //     let lockBalanceIsZero: string[] = [];
    //     const lockBalanceIsZeroString = localStorage.getItem('lockBalanceIsZeroV2');
    //     if (lockBalanceIsZeroString !== null) {
    //         lockBalanceIsZero = JSON.parse(lockBalanceIsZeroString);
    //     }
    //     let locksID = parseFloat(await getAllLocks(ChainID))
    //     let _locksIDArry: string[] = [];
    //     for (let i = locksID - 1; i > 0; i--) {
    //         _locksIDArry.push(i.toString());
    //     }

    //     const filterLocksIDArry = _locksIDArry.filter(item => !lockBalanceIsZero.includes(item));

    //     let locksIDArry: string[][] = [];
    //     let onceNumber = 20

    //     for (let i = 0; i < filterLocksIDArry.length; i += onceNumber) {
    //         const chunk: any[] = filterLocksIDArry.slice(i, i + onceNumber);
    //         locksIDArry.push(chunk);
    //     }
    //     let locksInfoTemp = [] as LockInfo[]
    //     for (let i = 0; i < locksIDArry.length; i++) {
    //         let _locks = await getLockList(ChainID, locksIDArry[i])
    //         let locks = _locks.locksInfo
    //         locksInfoTemp = [...locksInfoTemp, ...locks]
    //     }
    //     let locksInfo = await updateRMB(locksInfoTemp)
    //     setList(locksInfo.updatedList)

    //     const arrayToStoreString = JSON.stringify(locksInfo.updatedList);
    //     localStorage.setItem('locksListLocalHome', arrayToStoreString);

    //     let endRmbTotal = Number(locksInfo.rmbTotal.toFixed(1))
    //     setValueRMBTotal(endRmbTotal)
    //     setLoading(false)
    // }, [getLockList, updateRMB])

    useEffect(() => {
        if (props.isHome === true) {
            setLockListForHome(props.ChainID)
        } else {
            setLockList(props.ChainID, props.UserAdr)
        }
    }, [getLockList, props.ChainID, props.UserAdr, props.isHome, setLockList, setLockListForHome]);

    const [InfoWindow, setInfoWindow] = useState(false)
    const [LockInfo, setLockInfo] = useState<LockInfo>({} as LockInfo)
    const openInfoWindow = (lockInfo: LockInfo) => {
        setInfoWindow(true)
        setLockInfo(lockInfo)
    }

    const LoadingReportRef = useRef<any>(null);

    const multiClam = async () => {
        let canClamLock = []
        for (let i = 0; i < List.length; i++) {
            if (List[i].remainTime === 0) {
                canClamLock.push(List[i].id)
            }
        }
        if (canClamLock.length === 0) { return Toast.show({ content: '没有到期的礼品盒' }) }
        try {
            const chainInfo = await getChainInfo(props.ChainID)
            const lockV2Contract = new web3.eth.Contract(lockV2ABI as AbiItem[], chainInfo.ChainAdr.lockV2)
            await lockV2Contract.methods.multiClam(canClamLock).send({ from: props.isOtherUser === true ? props.operatorAdr : props.UserAdr, gas: `${canClamLock.length * 100000}` })

                .on('transactionHash', (transactionHash: string | any[]) => {
                    if (transactionHash.length !== 0) {
                        LoadingReportRef.current.loadingOpen('block', 'none', 'none', "开盒中……")
                    }
                })
                .on('receipt', (receipt: { status: boolean }) => {
                    if (receipt.status === true) {
                        setLockList(props.ChainID, props.UserAdr)
                        LoadingReportRef.current.loadingOpen('none', 'block', 'none', `开盒全部完成，至余额处查询`)
                    }
                })
                .on('error', (_error: any, receipt: { status: boolean; } | undefined) => {
                    if (receipt !== undefined && receipt.status === false) {
                        LoadingReportRef.current.loadingOpen('none', 'none', 'block', "出错！请检查")
                    }
                })
        } catch (error) {
            console.error("Error creating lock:", error);
        }
    }

    const renderTableRow = (item: LockInfo) => (
        <Table.Row key={item.id}
            style={{ backgroundColor: item.remainTime === 0 ? '#2199d0' : '#2185d0' }}
            onClick={() => openInfoWindow(item)}
        >
            <Table.Cell>
                <Header as='h4' image>
                    <Image src={`${process.env.PUBLIC_URL}/logo/${item.tokenLogo}.svg`} rounded style={{ width: '22px', height: '22px' }} />
                    <Header.Content style={{ color: 'white' }}>
                        {item.symbol}
                    </Header.Content>
                </Header>
            </Table.Cell>
            <Table.Cell>{item.balance}</Table.Cell>
            <Table.Cell textAlign="right" >{item.valueRMB}</Table.Cell>
            <Table.Cell textAlign="right">{SecondToDay(item.remainTime)}</Table.Cell>
        </Table.Row>
    );

    useEffect(() => { return () => { }; }, []);

    return (
        <div>
            <Card type="inner" style={{ width: 'inherit', minWidth: '312px', maxWidth: '500px', margin: '0px auto auto auto', backgroundColor: '#82bbe7' }}>
                {props.isUser === true && <div
                    style={{ background: '#1c71b1', color: 'white', textAlign: 'center', fontSize: '13px', marginBottom: '1px' }}>
                    {props.UserAdr}
                </div>}
                {props.isOtherUser === true && <div
                    style={{ background: '#1c71b1', color: 'red', textAlign: 'center', fontSize: '11px', marginBottom: '1px', whiteSpace: 'nowrap' }}>
                    “{props.otherUserName}”&nbsp;&nbsp;{props.UserAdr}
                </div>}
                {List.length === loadNumAll &&
                    <div style={{
                        background: '#1c71b1', color: 'white', display: 'flex', alignItems: 'center', padding: '0 5px 0 5px',
                        justifyContent: 'space-between', marginBottom: '1px'
                    }}>
                        < div style={{ textAlign: 'left' }}>
                            <img src={giftLogo} alt='auto' width='auto' height='15px' style={{ marginBottom: '3px', borderRadius: '3px' }}
                            />总盒数: {List.length}</div>
                        <div style={{ textAlign: 'right' }}>{valueRMBTotal} ：总价值</div>
                    </div>
                }
                <div>
                    <Table celled fixed singleLine unstackable color='blue' inverted>
                        <Table.Header  >
                            <Table.Row >
                                <Table.HeaderCell textAlign="left" style={{ width: '30%' }}>礼品</Table.HeaderCell>
                                <Table.HeaderCell textAlign="left">盒额</Table.HeaderCell>
                                <Table.HeaderCell textAlign="right" >价值</Table.HeaderCell>
                                <Table.HeaderCell textAlign="right" >开盒</Table.HeaderCell>
                            </Table.Row>
                        </Table.Header>
                        <Table.Body >
                            {List.map((item) => renderTableRow(item))}
                        </Table.Body>
                    </Table>
                </div>
                {props.isHome !== true && <Button fluid
                    color='teal'
                    style={{ width: '200px', margin: 'auto' }}
                    onClick={() => { multiClam() }}
                >提取所有
                </Button>
                }
                {props.isHome === true && List.length !== loadNumAll && <Button fluid
                    color='teal'
                    style={{ margin: 'auto' }}
                    onClick={() => { setLoadNum(loadNumAll) }}
                >显示所有礼品盒(已读取{loadNow})
                </Button>
                }
                {loading === true && <div
                    style={{ background: '#1c71b1', color: 'white', paddingLeft: '5px', fontSize: '15px' }}>
                    <div style={{ textAlign: 'center' }}>更新中……</div>
                    <div>如果加载缓慢请将以下节点添加到钱包并切换</div>
                    <div>https://rpc.ankr.com/celo</div>
                    <div>https://1rpc.io/celo</div>
                    <div>https://forno.celo.org</div>
                    <div>https://celo.api.onfinality.io/public</div>
                </div>}
            </Card>
            <LockInfoWindow
                open={InfoWindow}
                onClose={() => setInfoWindow(false)}
                lockInfo={LockInfo}
                ChainID={props.ChainID}
                userAdr={props.UserAdr}
                refreshLockList={setLockList}
                isHome={props.isHome}
                operatorAdr={props.operatorAdr}
                isOtherUser={props.isOtherUser}
                isUser={props.isUser}
            />
            <LoadingReport ref={LoadingReportRef} />
        </div>
    )

}

export default LockList

