const defaultState = {
  ChainID: '0x00',
  ChainName: '刷新/重进',
  LGbalance: "0",
  UserAdr: '',
  selectCoinOrAdr:'',
}

const index = (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state))  //深拷贝
  switch (action.type) {
    case "ChainID":
      newState.ChainID = action.value;
      break;
    case "ChainName":
      newState.ChainName = action.value;
      break;
    case "LGbalance":
      newState.LGbalance = action.value;
      break;
    case "UserAdr":
      newState.UserAdr = action.value;
      break;
    case "selectCoinOrAdr":
      newState.selectCoinOrAdr = action.value;
      break;
    default:
      break;
  }

  return newState;
}

export default index
